
import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Home from '../screens/home'
import Login from '../screens/login'
import Register from '../screens/register'
import PaxList from '../screens/clinic/paxList'
import PaxDetail from '../screens/clinic/paxdetail'
import QRScreen from "../screens/clinic/qrscreen";
import KCreateAdmin from '../screens/voltrasadmin/createuser'
import KAdminList from '../screens/voltrasadmin/userlist'
import KSetting from '../screens/clinic/clinicSetting'
import KClinicList from '../screens/clinic/clinicList'
import KClinicRegistration from '../screens/clinic/clinicRegistration'
import VCreateUser from '../screens/voltrasadmin/createuser'
import VUserList from '../screens/voltrasadmin/userlist'
import VClientList from '../screens/voltrasadmin/clientlist'
import VCreateClient from '../screens/voltrasadmin/createclient'
import VRequestList from '../screens/voltrasadmin/requestlist'
import clinicReport from '../screens/clinic/clinicReport'
import KOrderReportAll from '../screens/clinic/clinicReport/AllReport'
import UserProfile from '../screens/userprofile'
import GenericJson from '../screens/genericjson/GenericJson';
import CatalogProduct from '../screens/genericjson/CatalogProduct'
import ClinicScope from '../screens/clinic/clinic_scope/ClinicScope';
import AllOrderSummary from '../screens/clinic/clinic_report_summary/AllOrder'
import RedeemOrderSummary from '../screens/clinic/clinic_report_summary/RedeemedOrder'

const Routing = () => (
  <div>
    <Switch>
      <Route exact path='/' component={Home} />
      <Route path='/Home' component={Home} />
      <Route path='/Login' component={Login} />
      <Route path='/Register' component={Register} />
      <Route path='/PaxList' component={PaxList} />
      <Route path='/PaxDetail' component={PaxDetail} />
      <Route path='/QrScreen' component={QRScreen} />
      <Route path='/VCreateUser' component={VCreateUser} />
      <Route path='/VUserList' component={VUserList} />
      <Route path='/KCreateAdmin' component={KCreateAdmin} />
      <Route path='/KAdminList' component={KAdminList} />
      <Route path='/KSetting' component={KSetting} />
      <Route path='/KClinicList' component={KClinicList} />
      <Route path='/KClinicRegistration' component={KClinicRegistration} />
      <Route path='/VClientList' component={VClientList} />
      <Route path='/VRequestList' component={VRequestList} />
      <Route path='/VCreateClient' component={VCreateClient} />
      <Route path='/OrderReport' component={clinicReport} />
      <Route path='/KOrderReportAll' component={KOrderReportAll} />
      <Route path='/UserProfile' component={UserProfile} />
      <Route path='/GenericConfiguration' component={GenericJson} />
      <Route path='/CatalogProduct' component={CatalogProduct} />
      <Route path='/ClinicScope' component={ClinicScope} />
      <Route path='/KOrderSummaryAll' component={AllOrderSummary} />
      <Route path='/KOrderSummaryRedeem' component={RedeemOrderSummary} />
    </Switch>
  </div>
)

export default Routing
