
import React, { useState ,useEffect} from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Grid, Typography, Button} from '@material-ui/core'
import MenuItem from '@material-ui/core/MenuItem';
import { FormValidation } from '../../../utils/FormValidation';

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const UpdateBasicInfo = ({ ...props }) => {
    // const [roleList, setRoleList] = useState([])
    // const [available, setAvailable] = useState([])
    // const [newRoleList, setNewRoleList] = useState([])
    // console.log('data',{ [props.dataField.id]:props.dataField.field})
    // const [showAdd, setShowAdd] = useState(true)
    const [state, setState] = useState({
        basicInfo:{},
        [props.dataField.id]:props.dataField.field
    })
    const profile = [
      "Web Application",
      "Android",
      "IOS"
    ]
    useEffect(() => {
        setState({
            [props.dataField.id]:props.dataField.field
        })
    },[props.dataField])
    
    const handleClose = () => {
        setState({ addRoleList: [], })
        props.handleOpen({open:false})
        // setShowAdd(true)
    };

    const handleChange = val => e => {
        let valid = FormValidation(val, e.target.value)
        setState({
            ...state,
            basicInfo:{
                ...state.basicInfo,
                [props.dataField.id]: e.target.value,
            },
            [props.dataField.id]: e.target.value,
            [val + 'ErrorMessage']: valid.errorMessage,
        })
    }

    const handleSave = () => {
        props.handleSave(state.basicInfo)
        props.handleOpen(false)
        // setShowAdd(true)
        setState({ ...state, addRoleList: [], })
    }

    const disableButton = ()=>{
        if (props.dataField.id==='emailSender'){
            if(state[`${props.dataField.id}ErrorMessage`]){
                return true
            }
            // else if (state[props.dataField.id]==""){
            //     return true
            // }
            else {
               return false
            }
        }
        else if (props.dataField.id==='emailSenderMaskingName'){
            if(state[`${props.dataField.id}ErrorMessage`]){
                return true
            }
            // else if (state[props.dataField.id]==""){
            //     return true
            // }
            else {
                return false
            }
        }
        else if (!state[props.dataField.id]){
            return true
        }
        else if (state[`${props.dataField.id}ErrorMessage`]){
            return true
        }
        else {
            return false
        }
    }

    return (
        <Dialog fullWidth maxWidth={'sm'} open={props.open} onClose={handleClose} scroll={'paper'} >
            <DialogTitle
                id="max-width-dialog-title"
                onClose={handleClose}
            >
                Client: {props.data ? props.data.clientName : ''}
            </DialogTitle>
            <DialogContent dividers>
              {props.dataField.id==='initialUserRole' || props.dataField.id==='profile'?
                <TextField
                autoFocus
                fullWidth
                select
                margin="dense"
                size="small"
                onChange={handleChange(props.dataField.id)}
                id="standard-roleId"
                label={props.dataField.title}
                placeholder={props.dataField.title}
                value={state[props.dataField.id]}
                
            >
              { props.dataField.id==='profile'? profile.map(item=>(
                 <MenuItem key={item} value={item}>
                 {item}
                </MenuItem>
              )):props.data.roleDetails.map(item=>
                (
                  <MenuItem key={item.roleId} value={item.roleId}>
                      {item.roleId}
                  </MenuItem>
                )
              )}
            </TextField>:
            <TextField
                    autoFocus
                    fullWidth
                    margin="dense"
                    size="small"
                    onChange={handleChange(props.dataField.id)}
                    id="standard-roleId"
                    label={props.dataField.title}
                    placeholder={props.dataField.title}
                    value={state[props.dataField.id]}
                    helperText={state[`${props.dataField.id}ErrorMessage`] ? state[`${props.dataField.id}ErrorMessage`] : ''}
                    error={state[`${props.dataField.id}ErrorMessage`] ? true : false}
                />  
            
            }
                {/* <TextField
                    autoFocus
                    fullWidth
                    margin="dense"
                    size="small"
                    onChange={handleChange(props.dataField.id)}
                    id="standard-roleId"
                    label={props.dataField.title}
                    placeholder={props.dataField.title}
                    value={state[props.dataField.id]}
                    // helperText={state.roleIdErrorMessage ? state.roleIdErrorMessage : ''}
                    // error={state.roleIdErrorMessage ? true : false}
                /> */}
            </DialogContent>

            <DialogActions>
                <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button onClick={handleClose} variant='outlined' color="primary">
                            Cancel
          </Button>
                    </Grid>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button onClick={handleSave} variant='contained' color="primary" disabled={disableButton()}>
                            Edit
          </Button>
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    )
}

export default UpdateBasicInfo
