export const menuCreator = (domain, clientId, roleChoosen) => {
  // console.log("MENU CREATOR INIT: ", domain, clientId, roleChoosen)
  let userMenu = []
  let Structures = [...AppStructures]
  Structures.forEach(item => {
    if (item.aud === clientId) {
      let allMenu = item.loggedMenu

      if (clientId === 'voltras-health') {
        if (roleChoosen === 'member') {
          let arrMenu = allMenu.map(item => {
            if (item.label === 'Clinic Management' || item.label === 'Clinic') {
              item.label = 'Clinic'
              item = {
                ...item,
                children: (item.children.filter(child => child.label !== "Clinic Setting"))
              }
            }
            return item
          })
          userMenu = arrMenu.filter(item => item.label !== 'Order List' && item.label !== 'Order Report' && item.label !== "Generic Configuration")
        }
        else if (roleChoosen === 'clinic.operator') {
          userMenu = allMenu.filter(
            item => item.label !== 'Order Report'
              && item.label !== 'Order Summary'
              && item.label !== 'Clinic Management'
              && item.label !== 'Clinic'
              && item.label !== "Generic Configuration"
          )
          // console.log("YANG BENER: ", userMenu)
        }
        else if (roleChoosen === 'clinic.admin') {
          let arrMenu = allMenu.map(item => {
            if (item.label === 'Clinic Management' || item.label === 'Clinic') {
              item = {
                ...item,
                children: (item.children.filter(child => child.label === "Clinic Setting"))
              }
            }
            return item
          })
          userMenu = arrMenu.filter(item => item.label !== 'Order Report' && item.label !== "Generic Configuration")
        }
        else if (roleChoosen === 'biofarma.admin') {
          let arrMenu = allMenu.map(item => {
            if (item.label === 'Clinic Management' || item.label === 'Clinic') {
              item = {
                ...item,
                children: (item.children.filter(child => child.label !== "Clinic Setting"))
              }
            }
            return item
          })
          userMenu = arrMenu.filter(item => item.label !== 'Order List' && item.label !== "Generic Configuration")
        }
        else {
          let arrMenu = allMenu.map(item => {
            if (item.label === 'Clinic Management' || item.label === 'Clinic') {
              item = {
                ...item,
                children: (item.children.filter(child => child.label !== "Clinic Setting"))
              }
            }
            return item
          })
          userMenu = arrMenu
        }
      }
      else userMenu = allMenu
      // console.log(userMenu)
    }
  })
  return userMenu
}

export const resetMenu = (modifiedMenu, clientId) => {
  let oldMenu = []
  if (clientId === 'voltras-health') {
    let arrMenu = modifiedMenu.map(item => {
      if (item.label === 'Clinic') item.label = 'Clinic Management'
      return item
    })
    oldMenu = arrMenu
  } else oldMenu = modifiedMenu
  return oldMenu
}
