
const initialstate = {
  list: [],
  detail: {},
  product: [],
  filteredList: [],
  klinikList: [],
  clinicDetail: {},
  cityList: [],
  clinicUser: null,
  clinicRecapt: [],
  myClinic: null,
  clinic_parents: [],
  parentList: [],
  clinic_recapt_logistic: []
}

const paxList = (state = initialstate, action) => {
  switch (action.type) {
    case 'SET_PAX_LIST':
      return {
        ...state,
        list: action.val
      }
    case 'SET_PAX_DETAIL':
      return {
        ...state,
        detail: action.val
      }
    case 'SET_PAX_PRODUCT':
      return {
        ...state,
        product: action.val
      }
    case 'SET_KLINIK_LIST':
      return {
        ...state,
        klinikList: action.val
      }
    case 'SET_CLINIC_DETAIL':
      return {
        ...state,
        clinicDetail: action.val
      }
    case 'SET_CLINIC_USER':
      return {
        ...state,
        clinicUser: action.val
      }
    case 'SET_CLINIC_RECAPT':
      return {
        ...state,
        clinicRecapt: action.val
      }
    case 'SET_FILTERED_LIST':
      return {
        ...state,
        filteredList: action.val
      }
    case 'SET_CITY_LIST':
      return {
        ...state,
        cityList: action.val
      }
    case 'SET_MY_CLINIC':
      return { ...state, myClinic: action.val }
    case 'SET_CLINIC_PARENTS':
      return { ...state, clinic_parents: action.val }
    case 'SET_PARENT_LIST':
      return { ...state, parentList: action.val }
    case 'SET_CLINIC_RECAPT_LOGISTIC':
      return { ...state, clinic_recapt_logistic: action.val }
    case 'LOGOUT':
      return initialstate

    default:
      return state
  }
}

export default paxList
