import React, { useState, useEffect } from 'react'
import Button from '@material-ui/core/Button'
import { FormValidation } from '../../../utils/FormValidation'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { Dialog, DialogActions, DialogContent } from '@material-ui/core'
import Grid from '@material-ui/core/Grid'
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MenuItem from '@material-ui/core/MenuItem'
import { withStyles } from '@material-ui/core/styles'


const styles = (theme) => ({
    rootForm: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            // width: '25ch',
        },
    },
    rootUpload: {
        '& > *': {
            margin: theme.spacing(1),
        },
    },
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    asterisk: {
        color: 'red'
    },
    addButton: {
        color: 'white'
    },
    inputUpload: {
        display: 'none',
    },
});


const DialogTitle = withStyles(styles)(props => {
    const { children, onClose, classes, ...other } = props


    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
})


const UpdateUserBasicInfoDialog = ({ ...props }) => {
    const { classes } = props
    const [data, setData] = useState({
        basicInformation: {}
    })
    const [capture, setCapture] = useState({
        images: [],
        data: ""
    })

    useEffect(() => {
        if (props.clientData) {
            setData({
                // ...data,
                ...props.clientData,
                emailVerified: props.clientData.emailVerified ? "Verified" : "Not Verified",
                phoneVerified: props.clientData.phoneVerified ? 'Verified' : "Not Verified"
            })
        }
    }, [props.clientData])

    const handleClose = () => {
        setCapture({
            ...capture,
            images: []
        })
        setData({
            ...data,
            basicInformation: {}
        })
        props.handleClose(false)
    }

    const handleChange = id => (event) => {
        const value = event.target.value
        let valid = FormValidation(id === 'profile' ? 'urlDestination' : id, event.target.value)
        // console.log('ID', id)
        // console.log('value',value)
        if (id === 'address' || id === 'gender' || id === 'profile' || id === 'name') {
            setData({
                ...data,
                basicInformation: {
                    ...data.basicInformation,
                    [id]: value,
                    [id + 'Edit']: value,
                    // [id + 'ErrorMessage']: valid.errorMessage,
                },
                [id]: value,
                [id + 'Edit']: value,
                [id === 'profile' ? 'urlDestinationErrorMessage' : id + 'ErrorMessage']: valid.errorMessage,
            })
        }
        else {
            setData({
                ...data,
                basicInformation: {
                    ...data.basicInformation,
                    [id]: value,
                    [id + 'Edit']: value,
                },
                [id]: value,
                [id + 'Edit']: value,
                [id + 'ErrorMessage']: valid.errorMessage,
            })
        }
    }

    const handleCapture = ({ target }) => {
        const fileReader = new FileReader();
        // console.log('targets',target.files)
        fileReader.readAsDataURL(target.files[0]);
        fileReader.onload = (e) => {
            // console.log('target',e.target)
            setCapture({
                ...capture,
                data: e.target.result,
                images: target.files
            })
        };
    }
    const handleEditRequest = () => {
        props.handleClose(false)
        props.completeRequest(data, capture)
    }
    
    return (
        <Dialog fullWidth maxWidth={'sm'} open={props.open} onClose={handleClose} scroll='paper'>
            <DialogTitle id="max-width-dialog-title"
                onClose={handleClose}>
                {props.title}
            </DialogTitle>
            <DialogContent>
                <form className={classes.rootForm} noValidate autoComplete="off">
                    {/* <TextField
                        fullWidth
                        label='Photo'
                        placeholder='Update Your Photo Here'
                        id='photo'
                        InputLabelProps={{
                            shrink: true
                        }}
                    /> */}
                    <div className={classes.rootUpload}>
                        <Grid container>
                            <Grid item sm={8}>
                                <TextField
                                    fullWidth
                                    value={capture.images[0] ? capture.images[0].name : ""}
                                    variant='outlined'
                                    disabled={true}
                                    label='Photo'
                                    id='photo'
                                    InputLabelProps={{
                                        shrink: true
                                    }} />
                            </Grid>
                            <Grid item sm={4} align='center'>
                                <input
                                    accept="image/*"
                                    className={classes.inputUpload}
                                    id="contained-button-file"
                                    onChange={handleCapture}
                                    multiple
                                    type="file"
                                />
                                <label htmlFor="contained-button-file">
                                    <Button variant="contained" color="primary" component="span">
                                        Upload
                                    </Button>
                                </label>
                            </Grid>

                        </Grid>
                    </div>
                    <TextField
                        fullWidth
                        label='Email'
                        // placeholder='Update Your Photo Here'
                        value={data.email}
                        onChange={handleChange('email')}
                        id='email'
                        error={data.emailErrorMessage ? true : false}
                        helperText={data.emailErrorMessage ? data.emailErrorMessage : ""}
                        InputLabelProps={{
                            shrink: true
                        }}
                    />
                    <TextField
                        fullWidth
                        select
                        disabled={props.userInfo ? props.userInfo.roleUsed === 'root' ? false : true : true}
                        label='Email Status'
                        value={data.emailVerified}
                        onChange={handleChange('emailVerified')}
                        // onChange={handleChange}
                        // placeholder='Update Your Photo Here'
                        id='emailStatus'
                        InputLabelProps={{
                            shrink: true
                        }}
                    >
                        <MenuItem value='Verified'>
                            Verified
                        </MenuItem>
                        <MenuItem value='Not Verified'>
                            Not Verified
                        </MenuItem>
                    </TextField>
                    <TextField
                        fullWidth
                        label='Phone'
                        id='phone'
                        onChange={handleChange('phone')}
                        value={data.phone}
                        error={data.phoneErrorMessage ? true : false}
                        helperText={data.phoneErrorMessage ? data.phoneErrorMessage : ""}
                        InputLabelProps={{
                            shrink: true
                        }}
                    />
                    <TextField
                        fullWidth
                        select
                        disabled={props.userInfo ? props.userInfo.roleUsed === 'root' ? false : true : true}
                        label='Phone Status'
                        value={data.phoneVerified}
                        onChange={handleChange('phoneVerified')}
                        InputLabelProps={{
                            shrink: true
                        }}
                    >
                        <MenuItem value='Verified'>
                            Verified
                        </MenuItem>
                        <MenuItem value='Not Verified'>
                            Not Verified
                        </MenuItem>
                    </TextField>
                    <TextField
                        fullWidth
                        label='Name'
                        id='name'
                        onChange={handleChange('name')}
                        value={data.basicInformation ? data.basicInformation.name : ""}
                        // error={data.nameErrorMessage?true:false}
                        // helperText={data.nameErrorMessage?data.nameErrorMessage:""}
                        InputLabelProps={{
                            shrink: true
                        }}
                    />
                    <TextField
                        fullWidth
                        label='Profile URL'
                        id='profile'
                        onChange={handleChange('profile')}
                        value={data.basicInformation ? data.basicInformation.profile : ""}
                        error={data.urlDestinationErrorMessage ? true : false}
                        helperText={data.urlDestinationErrorMessage ? data.urlDestinationErrorMessage : ""}
                        InputLabelProps={{
                            shrink: true
                        }}
                    />
                    <TextField
                        fullWidth
                        select
                        label='Gender'
                        onChange={handleChange('gender')}
                        id='gender'
                        value={data.basicInformation ? data.basicInformation.gender : ""}
                        InputLabelProps={{
                            shrink: true
                        }}
                    >
                        <MenuItem key={'male'} value={'male'}>
                            Male
                        </MenuItem>
                        <MenuItem key={'female'} value={'female'}>
                            Female
                        </MenuItem>
                    </TextField>
                    <TextField
                        fullWidth
                        label='Address'
                        id='address'
                        onChange={handleChange('address')}
                        value={data.basicInformation ? data.basicInformation.address : ""}
                        InputLabelProps={{
                            shrink: true
                        }}
                    />
                </form>
            </DialogContent>
            <DialogActions>
                <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button color='primary' variant='contained' onClick={handleEditRequest}
                            className={classes.addButton}>
                            Edit
                        </Button>
                    </Grid>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button onClick={handleClose} color='secondary' variant='contained' className={classes.addButton}>
                            Cancel
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    )
}

export default withStyles(styles)(UpdateUserBasicInfoDialog)