
const initialstate = {
  menuItems: [],
}

const menu = (state = initialstate, action) => {
  switch (action.type) {
    case 'SET_USER_MENU':
      return {
        ...state,
        menuItems: action.val
      }
    case 'SET_MENU_COLLAPSE_STATUS':
      return {
        ...state,
        ...action.val
      }
    case 'TOGGLE_COLLAPSE_MENU':
      return {
        ...state,
        [action.val.name]: action.val.value
      }
    case 'LOGOUT':
      return initialstate
    default:
      return state
  }
}

export default menu
