
import React, { useState, useEffect } from 'react';
import MaterialTable from 'material-table';
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';
import BackIcon from '@material-ui/icons/ArrowBack';
import Button from '@material-ui/core/Button';
import AddClientDialog from './AddClientDialog'
import UpdateBasicInfo from './updateBasicInfo'
import AddUrlOriginDialog from './AddUrlOriginDialog'
import Paper from '@material-ui/core/Paper'
import { Divider } from '@material-ui/core';
import TableCustomize from '../../../components/table/TableCustomize'
import EditMessagingTemplateDialog from './EditMessagingTemplate'
import { connect } from 'react-redux'
import {setClientMessagingEdit} from '../../../actions/ClientListActions'

const mapStateToProps = store => ({
  messagingList: store.clientList.messagingList,
  userInfo: store.userInfo.info,
})

const mapDispatchToProps = dispatch => {
  return {
    _setClientMessagingEdit: val =>{
      dispatch(setClientMessagingEdit(val))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(function FormDialog({ ...props }) {

  const { userInfo, messagingList, _setClientMessagingEdit, completeRequest, } = props
  const [data, setData] = useState([])
  const [originUri, setOriginUri] = useState([])
  const [redirectUri, setRedirectUri] = useState([])
  const [clientData, setClientData] = useState({})
  const [basicInfoReq, setBasicInfoReq] = useState({})

  const [basicInfo, setBasicInfo] = useState([])
  const [messagingTemplate, setMessagingTemplate] = useState([])

  const [state, setState] = useState({
    clientList: [],
    dataField: {},
    dataToUpdate: null,
    messageToUpdate: null,
    open: false,
    openBasicInfo: false,
    openOriginOri: false,
    openRedirectOri: false,
    openTemplateMessage: false
  })
  const [disabledBtn, setDisabledBtn] = useState(true)
  // console.log('client', props.data)

  useEffect(() => {
    const {name, email, phone} = userInfo
    // let name = userInfo.name
    // let email = userInfo.email
    // let phone = userInfo.phone
    let customHeaders = { name, email }
    let clientId = props.data.clientId
    if (phone) customHeaders.phone = phone

    let requestAllClient = {
      request: {
        clientId
      },
      method: "GET",
      serviceCode: "SELECT_ALL_MESSAGE",
      // customHeaders,
      urlParams: `/clients/${clientId}/messaging-templates/`
    }

    completeRequest(requestAllClient)

  }, [userInfo, completeRequest, props.data.clientId])

  useEffect(() => {
    // let arrayOriginUri = []
    let arrayObjOrigin = []
    let arrayRedirectUri = []
    const basicData = { ...props.data }
    if (Array.isArray(props.data.originURIs)) {
      props.data.originURIs.map((item, index) => {
        arrayObjOrigin.push({ uri: item, id: index })
        return null
      })
    }
    if (Array.isArray(props.data.redirectURIs)) {
      props.data.redirectURIs.map((item, index) => {
        arrayRedirectUri.push({ uri: item, id: index })
        return null
      })
    }
    setOriginUri(arrayObjOrigin)
    setRedirectUri(arrayRedirectUri)
    setClientData(props.data)
    setBasicInfo([
      {
        title: "Client ID",
        id: "clientId",
        field: basicData.clientId,
        edit: false
      },
      {
        title: "Client Secret",
        field: basicData.clientSecret,
        id: "clientSecret",
        edit: false
      },
      {
        title: "Client Name",
        field: basicData.clientName,
        id: "clientName",
        edit: true
      },
      {
        title: "Initial Role",
        id: "initialUserRole",
        field: basicData.initialUserRole,
        edit: true
      },
      {
        title: "Profile",
        id: "profile",
        field: basicData.profile,
        edit: true
      },
      {
        title: "Email Sender",
        id: "emailSender",
        field: basicData.emailSender,
        edit: true
      },
      {
        title: "Email Masking Name",
        id: "emailSenderMaskingName",
        field: basicData.emailSenderMaskingName,
        edit: true
      },
      {
        title: 'Client Domain',
        id: 'clientDomain',
        field: basicData.clientDomain,
        edit: true
      }
    ])
    setData(props.data.roleDetails ? props.data.roleDetails : [{ roleId: 'jadul', roleDescription: 'save to delete' }])
    setMessagingTemplate(messagingList.length !== 0 ? messagingList : [{ messagingTemplateCode: '', messagingTemplateLang: '' }])
  }, [props.data, messagingList])

  const [updateMessageTemplate, setUpdateMessage] = useState(false)
  const [messagingTemp, setMessagingTemp] = useState({})

  const handleClose = () => {
    props.handleClose()
  }

  const handleDeleteClientRole = oldData => new Promise((resolve, reject) => {
    // let clientId = props.data.clientId
    // let roleId = oldData.roleId
    // let request = {}
    // let method = 'DELETE'
    // let serviceCode = 'DELETE_CLIENT_ROLE'
    // let urlParams = `/clients/${clientId}/roles/${roleId}`

    // let completeRequest = { request, method, serviceCode, urlParams }
    // props.completeRequest(completeRequest)

    let newData = data.filter(item => item.roleId !== oldData.roleId)
    setData(newData)
    setDisabledBtn(false)
    // props.handleClose()
    resolve()
  })

  const handleOpenEditCLient = val => {
    setState({
      ...state,
      open: val
    })
  }
  const handleOpenAddOriginUrl = val => {
    setState({
      ...state,
      openOriginOri: val
    })
  }
  const handleOpenAddRedirectUrl = val => {
    setState({
      ...state,
      openRedirectOri: val
    })
  }
  const handleOpenEditBasicInfo = val => {
    setState({
      ...state,
      openBasicInfo: val.open,
      dataField: val
    })
  }

  const handleOpenEditMessageTemplate = (open, edit, data) => {
    setState({
      ...state,
      openTemplateMessage: open,
      messageToUpdate: data
    })
  }
  const handleSaveBasicInfo = (val) => {
    // basicData[state.dataField.id]=val[state.dataField.id]
    basicInfo.map(item => {
      if (item.id === state.dataField.id) {
        item.field = val[state.dataField.id]
      }
      return null
    })
    delete val.emailSenderErrorMessage
    delete val.emailSenderMaskingNameErrorMessage
    if (state.dataField.id === 'emailSender') {
      val[state.dataField.id] = null
    }

    if (state.dataField.id === 'emailSenderMaskingName') {
      val[state.dataField.id] = null
    }
    setBasicInfo(basicInfo)
    setBasicInfoReq(val)
    setDisabledBtn(false)
  }
  const handleSaveRole = val => {
    setData([...data, ...val])
    setDisabledBtn(false)
  }
  const handleSaveOriginUris = val => {
    setOriginUri([...originUri, val])
    setDisabledBtn(false)
  }
  const handleSaveRedirectUris = val => {
    setRedirectUri([...redirectUri, val])
    setDisabledBtn(false)
  }

  const handleSaveMessageTemplate = (val) => {
    // let tempMess = []

    messagingList.forEach((item, index) => {
      if (item.messagingTemplateId === val['messagingTemplateId']) {
        messagingList[index] = val
      }
    })
    // console.log('tempMess', tempMess)
    setMessagingTemplate(messagingList)
    setMessagingTemp(val)
    setUpdateMessage(true)
    setDisabledBtn(false)
    // setState({
    //   ...state,
    //   openTemplateMessage: false
    // })
  }
  // const sendUpdateRoleRequest = () => {
  //   let clientId = props.data.clientId
  //   let roleId = data.slice(data.length - 1)[0].roleId
  //   let roleDescription = data.slice(data.length - 1)[0].roleDescription
  //   let request = {
  //     roleId,
  //     roleDescription,
  //   }
  //   let method = 'POST'
  //   let serviceCode = 'ADD_CLIENT_ROLE'
  //   let urlParams = `/clients/${clientId}/roles`

  //   let completeRequest = { request, method, serviceCode, urlParams }
  //   props.completeRequest(completeRequest)
  //   setDisabledBtn(!disabledBtn)
  // }

  const sendUpdateClientDetail = () => {
    let clientRoles = data
    let originURIs = []
    let redirectURIs = []
    // console.log('wkwkwkw',originUri)
    originUri.map(item => {
      originURIs.push(item.uri)
      return null
    })
    redirectUri.map(item => {
      redirectURIs.push(item.uri)
      return null
    })
    let clientId = props.data.clientId

    let method = "PATCH"
    let serviceCode = "UPDATE_CLIENT_DETAIL"
    let urlParams = `/clients/${clientId}/`
    let request = {
      ...basicInfoReq,
      clientRoles,
      originURIs,
      redirectURIs,
      clientId
    }
    let completeRequest = { request, method, serviceCode, urlParams }
    let requestMessagetTemp = {
      ...messagingTemp,
      updateMessageTemplate,
      clientId
    }
    // console.log('request',requestMessagetTemp)
    _setClientMessagingEdit(requestMessagetTemp)
    props.completeRequest(completeRequest)
    props.handleClose()
  }

  return (
    <Card style={{ display: 'flex', flexWrap: 'wrap', }}>
      <Grid style={{ margin: 35, marginTop: 15 }} container direction="column" justify="flex-start" alignItems="flex-start" >
        <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
          <Button size='large' onClick={handleClose} color="secondary" startIcon={<BackIcon />} variant='outlined' >
            Back
          </Button>
        </Grid>
        {/* <Grid style={{ marginTop: 15 }} container direction="row" justify="flex-start" alignItems="flex-start" spacing={2} >
          <Grid item xs={2}><Typography >Client ID</Typography></Grid>
          <Grid item xs={10}><Typography >: {clientData ? clientData.clientId : ""}</Typography></Grid>
        </Grid>
        <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={2} >
          <Grid item xs={2}><Typography >Client Name</Typography></Grid>
          <Grid item xs={10} > <Typography >: {clientData ? clientData.clientName : ""}</Typography></Grid>
        </Grid> */}
        <Grid style={{ marginTop: 20 }} container direction="row" justify="flex-start" alignItems="flex-start" >
          <Grid item xs={12} style={{ marginTop: 10, marginBottom: 10 }}>
            <Paper style={{ paddingLeft: 20 }}>
              <Typography variant='h6' style={{ paddingBottom: 20 }}>
                Basic Information
              </Typography>
              <Grid container style={{ marginBottom: 15 }}>
                <Grid item xs={6}>
                  <Typography>
                    Information
                  </Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography style={{ textAlign: "right", paddingRight: 5 }}>
                    Actions
                  </Typography>
                </Grid>
              </Grid>
              {
                basicInfo.map(item => {
                  return (
                    <div>
                      <Grid container style={{ marginBottom: 8 }}>
                        <Grid item xs={2}>
                          <Typography>
                            {item.title}
                          </Typography>
                        </Grid>
                        <Grid item xs={8}>
                          <Typography style={{ fontSize: 14 }}>
                            {item.field}
                          </Typography>
                        </Grid>
                        {item.edit ? <Grid container item xs={2} justify='flex-end' style={{ paddingRight: 15 }}>
                          <EditIcon onClick={() => handleOpenEditBasicInfo({ ...item, open: true })} />
                        </Grid> : ""}
                      </Grid>
                      <Divider />
                    </div>
                  )
                })
              }
            </Paper>
          </Grid>

          <Grid item xs={12} style={{ marginTop: 15 }} >
            {/* <MaterialTable
              title='Messaging Template'
              columns={[
                { title: 'Code', field: 'messagingTemplateCode' },
                { title: 'Languange', field: 'messagingTemplateLang' },
                { title: 'Subject', field: 'subjectTemplate' }
              ]}
              data={messagingTemplate}
              options={{
                actionsColumnIndex: -1,
                padding: 'dense',
                search: false,
                paging: false
                // pageSize: 10,
              }}
              editable={{
                onRowUpdate:(newData, oldData)=>
                  new Promise((resolve)=>{

                  })
              }}
            /> */}
            <TableCustomize
              title='Messaging Template'
              column={[
                { title: 'Code', field: 'messagingTemplateCode' },
                { title: 'Languange', field: 'messagingTemplateLang' },
                { title: 'Subject', field: 'subjectTemplate' }
              ]}
              handleEditOpen={handleOpenEditMessageTemplate}
              data={messagingList.length !== 0 ? messagingList : [{ messagingTemplateCode: '', messagingTemplateLang: '' }]}
              options={{
                pagination: true,
                filterClientId: false
              }}
            />
          </Grid>
          <Grid item xs={12} style={{ marginTop: 25 }} >
            <MaterialTable
              style={{ width: '100%' }}
              title="Available Role"
              columns={[
                { title: 'Role ID', field: 'roleId', width: 300 },
                { title: 'Description', field: 'roleDescription' },
              ]}
              data={data}
              editable={{
                onRowUpdate: (newData, oldData) =>
                  new Promise((resolve) => {
                    const dataUpdate = [...data]
                    // const roleId = oldData.id
                    dataUpdate.map(item => {
                      if (item.roleId === oldData.roleId) {
                        item.roleId = newData.roleId
                        item.roleDescription = newData.roleDescription
                      }
                      return null
                    })
                    setData([...dataUpdate])
                    setDisabledBtn(false)
                    resolve()
                  }),
                onRowDelete: (oldData) =>
                  new Promise((resolve) => {
                    handleDeleteClientRole(oldData).then(result => resolve())
                  }),
              }}
              localization={{
                body: {
                  editRow: {
                    deleteText: 'Apakah anda yakin menghapus data ini?'
                  }
                }
              }}
              options={{
                actionsColumnIndex: -1,
                padding: 'dense',
                search: false,
                paging: false
                // pageSize: 10,
              }}
            />
          </Grid>
        </Grid>
        <Grid style={{ marginTop: 20 }} container direction="row" alignItems="flex-start" >
          <Button style={{ fontSize: 12, fontWeight: 'bold' }} onClick={() => handleOpenEditCLient(true)} variant='outlined' color="secondary"
            startIcon={<AddIcon />}
          >
            Add New Role
        </Button>
          {/* <Button onClick={sendUpdateRoleRequest} variant='contained' color="primary" disabled={!disabledBtn}
            startIcon={<EditIcon />}
          >
            Save Role
        </Button> */}
          <Grid item xs={12} style={{ marginTop: 15 }} >
            <MaterialTable
              style={{ width: '100%' }}
              title="Origin URI"
              columns={[
                { title: 'URI', field: 'uri' },
                // 
              ]}
              data={originUri}
              editable={{
                onRowUpdate: (newData, oldData) =>
                  new Promise((resolve) => {
                    const dataUpdate = [...originUri]
                    const index = oldData.id
                    dataUpdate[index].uri = newData.uri
                    setOriginUri([...dataUpdate])
                    setDisabledBtn(false)
                    resolve()
                  }),
                onRowDelete: (oldData) =>
                  new Promise((resolve) => {
                    const dataUpdate = [...originUri]
                    const index = oldData.id
                    dataUpdate.splice(index, 1)
                    setOriginUri([...dataUpdate])
                    setDisabledBtn(false)
                    resolve()
                  }),
              }}
              localization={{
                body: {
                  editRow: {
                    deleteText: 'Apakah anda yakin menghapus data ini?'
                  }
                }
              }}
              options={{
                actionsColumnIndex: -1,
                padding: 'dense',
                search: false,
                paging: false
                // pageSize: 10,
              }}
            />
          </Grid>
          <Grid style={{ marginTop: 20 }} container direction="row" alignItems="flex-start" >
            <Button style={{ fontSize: 12, fontWeight: 'bold' }} onClick={() => handleOpenAddOriginUrl(true)} variant='outlined' color="secondary"
              startIcon={<AddIcon />}
            >
              Add Origin URI
            </Button>
          </Grid>
          <Grid item xs={12} style={{ marginTop: 15 }} >
            <MaterialTable
              style={{ width: '100%' }}
              title="Redirect URI"
              columns={[
                { title: 'URI', field: 'uri' },
                // 
              ]}
              data={redirectUri}
              editable={{
                onRowUpdate: (newData, oldData) =>
                  new Promise((resolve) => {
                    const dataUpdate = [...redirectUri]
                    const index = oldData.id
                    dataUpdate[index].uri = newData.uri
                    setOriginUri([...dataUpdate])
                    setDisabledBtn(false)
                    resolve()
                  }),
                onRowDelete: (oldData) =>
                  new Promise((resolve) => {
                    const dataUpdate = [...redirectUri]
                    const index = oldData.id
                    dataUpdate.splice(index, 1)
                    setRedirectUri([...dataUpdate])
                    setDisabledBtn(false)
                    resolve()
                  }),
              }}
              localization={{
                body: {
                  editRow: {
                    deleteText: 'Apakah anda yakin menghapus data ini?'
                  }
                }
              }}
              options={{
                actionsColumnIndex: -1,
                padding: 'dense',
                // pageSize: 10,
                search: false,
                paging: false
              }}
            />
          </Grid>
          <Grid style={{ marginTop: 20 }} container direction="row" alignItems="flex-start" >
            <Button style={{ fontSize: 12, fontWeight: 'bold' }} onClick={() => handleOpenAddRedirectUrl(true)} variant='outlined' color="secondary"
              startIcon={<AddIcon />}
            >
              Add Redirect URI
            </Button>
          </Grid>

          <Grid style={{ marginTop: 30 }} container direction="row" alignItems="flex-start" >
            <Button onClick={sendUpdateClientDetail} style={{
              marginRight: '15px',
              paddingLeft: "50px",
              paddingRight: "50px",
              color: "white",
              // marginRight: 10,
              fontSize: 18,
              lineHeight: 2.1,

            }} variant='contained' color="primary" disabled={disabledBtn}
            // startIcon={<AddIcon />}
            >
              Save
            </Button>
            <Button variant='contained' color="secondary" style={{
              marginRight: '15px',
              paddingLeft: "50px",
              paddingRight: "50px",
              // marginRight: 10,
              color: 'white',
              fontSize: 18,
              lineHeight: 2.1,

            }} onClick={handleClose}
            // startIcon={<EditIcon />}
            >
              Cancel
          </Button>
          </Grid>
        </Grid>
      </Grid>

      <AddClientDialog
        handleSave={handleSaveRole}
        dataToUpdate={state.dataToUpdate}
        data={clientData}
        clientList={state.clientList}
        open={state.open}
        handleOpen={handleOpenEditCLient}
      />
      <UpdateBasicInfo
        handleSave={handleSaveBasicInfo}
        open={state.openBasicInfo}
        dataToUpdate={state.dataToUpdate}
        data={clientData}
        dataField={state.dataField}
        clientList={state.clientList}
        handleOpen={handleOpenEditBasicInfo}
      />
      <AddUrlOriginDialog
        open={state.openOriginOri}
        handleOpen={handleOpenAddOriginUrl}
        handleSave={handleSaveOriginUris}
        data={clientData}
        redirect={false}
      />
      <AddUrlOriginDialog
        open={state.openRedirectOri}
        handleOpen={handleOpenAddRedirectUrl}
        handleSave={handleSaveRedirectUris}
        data={clientData}
        redirect={true}
      />
      <EditMessagingTemplateDialog
        open={state.openTemplateMessage}
        handleClose={handleOpenEditMessageTemplate}
        dataToUpdate={state.messageToUpdate}
        data={messagingTemplate}
        handleSave={handleSaveMessageTemplate}
      />
    </Card>
  );
})
