
import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux'
import { Grid, Paper, Typography, Button, Divider, List, ListItemText, FormHelperText } from '@material-ui/core';
import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus,
  setServiceMethod, setServiceParams, setServiceCustomHeaders, setServiceRole,
} from '../../../actions/ServiceActions'
import { FormValidation } from '../../../utils/FormValidation';
import AddCLientRole from './AddClientRole'
import CreateClientForm from './CreateClientForm'


const CreateClient = ({ ...props }) => {
  const { _setOpenScreen, _setCompleteRequest, userInfo, } = props
  const [showAddRoles, setShowAddRoles] = useState(false)
  const [helpTextOrigin, setHelpTextOrigin] = useState(false)
  const [helpTextRedirect, setHelpTextRedirect] = useState(false)
  // const [helpTextRoleId, setHelpTextRoleId] = useState(false)
  const [formFocus, setFormFocus] = useState({
    clientId: false,
    clientName: false,

  })
  const [form, setForm] = useState({
    clientRoles: [],
    clientType: '',
    // clientGroupId: "7ff9bf27-23bd-4253-a015-b2ffbe067f04",
    clientGroupId: '',
    clientGroup: '7ff9bf27-23bd-4253-a015-b2ffbe067f04',
    clientDomain: null,
    profile: '',
    emailSender:null,
    emailSenderMaskingName:null
  })
  const [, setIsRoleAdded] = useState(false)
  const [submitError, setSubmitError] = useState(false)

  useEffect(() => {
    _setOpenScreen("Create Client")
  }, [_setOpenScreen])

  const handleChange = prop => e => {
    e.preventDefault()
    setSubmitError(false)
    let valid = FormValidation(prop, e.target.value)

    if ((prop === 'originURIs' || prop === 'redirectURIs')) {
      setForm({
        ...form,
        [prop]: e.target.value
      })
    }
    else if (prop === 'initialRole') {
      // let newClientRole = form.clientRoles.splice(0, 0, { roleId: e.target.value, roleDescription: "Initial role" })
      setForm({
        ...form,
        [prop]: e.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        clientRoles: [{ roleId: e.target.value, roleDescription: "Initial role" }],
      })
    }
    else {
      setForm({
        ...form,
        [prop]: e.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
      })
    }
  }

  const handleSubmit = e => {
    e.preventDefault()
    setSubmitError(false)
    const { clientGroupId, clientId, clientName, clientRoles,
      clientType, originURIs, profile, redirectURIs, initialRole, clientGroup, clientDomain, emailSender, emailSenderMaskingName } = form

    if (clientId && clientName && clientRoles && clientType && originURIs && profile && redirectURIs && initialRole) {
      let request = {
        clientGroupId, clientId, clientName, clientRoles, originURIs: originURIs.split(/\r?\n/), clientGroup, clientDomain, emailSender, emailSenderMaskingName,
        profile, redirectURIs: redirectURIs.split(/\r?\n/), 'type': clientType, initialUserRole: initialRole
      }
      let name = userInfo.name
      let email = userInfo.email
      let phone = userInfo.phone
      let customHeaders = { name, email }
      if (phone) customHeaders.phone = phone

      let submitRequest = {
        request,
        method: "POST",
        serviceCode: "CREATE_CLIENT",
        customHeaders
      }
      _setCompleteRequest(submitRequest)
      setIsRoleAdded(false)
    }
    else {
      setSubmitError(true)
    }
  }

  const handleAddClientRole = e => {
    e.preventDefault()
    setForm({
      ...form,
      clientRoles: [...form.clientRoles, { roleId: form.roleId, roleDescription: form.roleDescription }],
      roleId: '',
      roleDescription: '',
    })
    setShowAddRoles(false)
    setIsRoleAdded(true)
  }

  const handleCancel = e => {
    e.preventDefault()
    setHelpTextOrigin(false)
    setHelpTextRedirect(false)
    setIsRoleAdded(false)
    setSubmitError(false)
    setForm({
      ...form,
      clientId: '',
      clientName: '',
      clientType: '',
      clientRoles: [],
      originURIs: '',
      profile: '',
      redirectURIs: '',
      initialRole: '',
    })
    setFormFocus({
      clientId: false,
      clientName: false,
      clientRoles: [],
      clientType: false,
      originURIs: false,
      profile: false,
      redirectURIs: false,
      initialRole: false,
    })
  }

  return (
    <Grid
      container
      direction="column"
      justify="center"
      alignItems="center"
    >
      <Grid item xs={12} md={9}>
        <Paper>
          <Grid container direction="column" justify="center" alignItems="center">
            <Typography variant='h6' color='primary' style={{ marginTop: 20 }} >Create Client</Typography>
          </Grid>

          <Grid container direction="row"  >
            <CreateClientForm
              setFormFocus={setFormFocus}
              formFocus={formFocus}
              helpTextOrigin={helpTextOrigin}
              setHelpTextOrigin={setHelpTextOrigin}
              helpTextRedirect={helpTextRedirect}
              setHelpTextRedirect={setHelpTextRedirect}
              handleChange={handleChange}
              form={form}
            />
            <Grid item xs={12} md={6} style={{ marginTop: 10 }}>
              <Grid container direction="column" justify="center" alignItems="center" >
                <Typography color='primary' variant='subtitle2' >Client Roles:</Typography>
                <Grid container direction="row" justify="center" alignItems="center" style={{ marginTop: 10 }} >
                  <Grid item xs={6}>
                    <Grid container direction="column" justify='center' alignItems='center' >
                      <Grid item>
                        Role Id
                        </Grid>
                    </Grid>
                    <Grid container direction="row" justify='flex-start' alignItems='flex-start' >
                      <Grid item>
                        <List>
                          {form.clientRoles[0]
                            ? form.clientRoles.map((item, i) => (<ListItemText key={i} secondary={'- ' + item.roleId} />))
                            : <ListItemText secondary={'- none'} />}
                        </List>
                      </Grid>
                    </Grid>
                  </Grid>
                  <Grid item xs={6}>
                    <Grid container direction="column" justify='center' alignItems='center' >
                      <Grid item>
                        Description
                      </Grid>
                    </Grid>
                    <Grid container direction="row" justify='flex-start' alignItems='flex-start' >
                      <Grid item>
                        <List>
                          {form.clientRoles[0]
                            ? form.clientRoles.map((item, i) => (<ListItemText key={i} secondary={'- ' + item.roleDescription} />))
                            : <ListItemText secondary={'- none'} />}
                        </List>
                      </Grid>
                    </Grid>
                  </Grid>
                  <Grid container direction="column" justify='flex-start' alignItems='flex-start' style={{ margin: 20 }}>
                    {
                      !showAddRoles
                        ? <Button disabled={form.initialRole ? false : true} size='small' variant='outlined' color='secondary' onClick={() => setShowAddRoles(true)} >
                          Add Client Roles
                          </Button>
                        : <AddCLientRole
                          form={form}
                          setForm={setForm}
                          handleChange={handleChange}
                          setShowAddRoles={setShowAddRoles}
                          handleAddClientRole={handleAddClientRole} />
                    }

                    {/* {
                      isRoleAdded
                        ? null
                        : !showAddRoles
                          ? <Button size='small' variant='outlined' color='secondary' onClick={() => setShowAddRoles(true)} >
                            Add Client Roles
                          </Button>
                          : <AddCLientRole
                            form={form}
                            setForm={setForm}
                            handleChange={handleChange}
                            setShowAddRoles={setShowAddRoles}
                            handleAddClientRole={handleAddClientRole}
                          />
                    } */}
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>

          <Divider style={{ margin: 20 }} />

          <Grid container direction="row" justify="center" alignItems="center" >
            <Grid item>
              <Button onClick={handleCancel} variant="outlined" color="primary">Cancel</Button>
            </Grid>
            <Grid style={{ margin: 10 }} item>
            </Grid>
            <Grid item>
              <Button onClick={handleSubmit} variant="contained" color="primary">Create</Button>
            </Grid>
          </Grid>
          <Grid container direction="row" justify="center" alignItems="center" >
            <FormHelperText error >{submitError ? 'Form not complete' : null}</FormHelperText>
          </Grid>
          <br />
        </Paper>
      </Grid>
    </Grid>
  );
}

const mapStateToProps = store => {
  return {
    userList: store.userList.list,
    userDetail: store.userList.detail,
    userFiltered: store.userList.filteredList,
    userInfo: store.userInfo.info,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setOpenScreen: val => dispatch(setOpenScreen(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.params))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateClient)
