
const initialstate = {
  list: [],
  detail: {},
  filteredList:[]
}

const UserList = (state = initialstate, action) => {
  switch (action.type) {
    case 'SET_USER_LIST':
      return {
        ...state,
        list: action.val
      }
    case 'SET_USER_DETAIL':
      return {
        ...state,
        detail: action.val
      }
    case 'SET_USER_FILTERED_LIST':
      return {
        ...state,
        filteredList: action.val
      }
    case 'LOGOUT':
      return initialstate
      
    default:
      return state
  }
}

export default UserList
