const initialstate = {
    list: [],
    detail: {},
    filteredList: []
  }
  
  const RequestList = (state = initialstate, action) => {
    switch (action.type) {
      case 'SET_REQUEST_LIST':
        return {
          ...state,
          list: action.val
        }
      case 'SET_REQUEST_DETAIL':
        return {
          ...state,
          detail: action.val
        }
      case 'SET_CLIENT_FILTERED_LIST':
        return {
          ...state,
          filteredList: action.val
        }
      case 'LOGOUT':
        return initialstate
  
      default:
        return state
    }
  }
  
  export default RequestList