
import React, { useState, useEffect } from 'react';
import TextField from '@material-ui/core/TextField';
import { Grid, MenuItem, InputAdornment, IconButton, } from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { createUserTypes } from '../../../utils/utils';

const CreateUserForm = ({ ...props }) => {
  const { handleChange, form, setFormFocus, formFocus, setShowPassword, showPassword,
    setShowConfirmPassword, showConfirmPassword, serviceRole, } = props
  const [userType, setUserType] = useState([])

  useEffect(() => {
    if (serviceRole) setUserType(createUserTypes(serviceRole))
  }, [serviceRole])

  return (
    <Grid item xs={12} md={6}>
      <Grid container direction="column" justify="center" alignItems="center">
        <Grid item xs={12} md={8} style={{ marginLeft: 10, marginRight: 10 }} >
          <TextField
            size="small"
            onChange={handleChange('name')}
            id="standard-name"
            label="Full Name"
            placeholder="Full Name"
            helperText={form.nameErrorMessage ? form.nameErrorMessage : ""}
            error={form.nameErrorMessage ? true : false}
            fullWidth
            margin='dense'
            value={form.name}
            onFocus={() => setFormFocus({ ...formFocus, name: true })}
            onBlur={() => form.name ? setFormFocus({ ...formFocus, name: true }) : setFormFocus({ ...formFocus, name: false })}
            InputLabelProps={{
              shrink: formFocus.name ? true : false
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('phone')}
            id="standard-phone"
            label="Phone"
            placeholder="+6281234567890"
            helperText={form.phoneErrorMessage ? form.phoneErrorMessage : ""}
            error={form.phoneErrorMessage ? true : false}
            fullWidth
            margin="dense"
            value={form.phone}
            onFocus={() => setFormFocus({ ...formFocus, phone: true })}
            onBlur={() => form.phone ? setFormFocus({ ...formFocus, phone: true }) : setFormFocus({ ...formFocus, phone: false })}
            InputLabelProps={{
              shrink: formFocus.phone ? true : false
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('email')}
            id="standard-email"
            label="Email"
            placeholder="Email"
            helperText={form.emailErrorMessage ? form.emailErrorMessage : ""}
            error={form.emailErrorMessage ? true : false}
            fullWidth
            margin="dense"
            value={form.email}
            onFocus={() => setFormFocus({ ...formFocus, email: true })}
            onBlur={() => form.email ? setFormFocus({ ...formFocus, email: true }) : setFormFocus({ ...formFocus, email: false })}
            InputLabelProps={{
              shrink: formFocus.email ? true : false
            }}
          />
          <TextField
            type={showPassword ? 'text' : 'password'}
            size="small"
            onChange={handleChange('password')}
            id="standard-password"
            label="Password"
            // placeholder="Placeholder"
            helperText={form.passwordErrorMessage ? form.passwordErrorMessage : ""}
            error={form.passwordErrorMessage ? true : false}
            fullWidth
            margin="dense"
            value={form.password}
            onFocus={() => setFormFocus({ ...formFocus, password: true })}
            onBlur={() => form.password ? setFormFocus({ ...formFocus, password: true }) : setFormFocus({ ...formFocus, password: false })}
            InputLabelProps={{
              shrink: formFocus.password ? true : false
            }}
            InputProps={{
              endAdornment: <InputAdornment position='end' >
                <IconButton
                  aria-label="toggle password visibility"
                  onMouseDown={e => e.preventDefault()}
                  onClick={() => setShowPassword(!showPassword)}
                >
                  {showPassword ? <Visibility /> : <VisibilityOff />}
                </IconButton>
              </InputAdornment>
            }}
          />
          <TextField
            type={showConfirmPassword ? 'text' : 'password'}
            size="small"
            onChange={handleChange('confirmPassword')}
            id="standard-confirmPassword"
            label="Confirm Password"
            // placeholder="Placeholder"
            helperText={form.confirmPasswordErrorMessage ? form.confirmPasswordErrorMessage : ""}
            error={form.confirmPasswordErrorMessage ? true : false}
            fullWidth
            margin="dense"
            value={form.confirmPassword}
            onFocus={() => setFormFocus({ ...formFocus, confirmPassword: true })}
            onBlur={() => form.confirmPassword ? setFormFocus({ ...formFocus, confirmPassword: true }) : setFormFocus({ ...formFocus, confirmPassword: false })}
            InputLabelProps={{
              shrink: formFocus.confirmPassword ? true : false
            }}
            InputProps={{
              endAdornment: <InputAdornment position='end' >
                <IconButton
                  aria-label="toggle confirmPassword visibility"
                  onMouseDown={e => e.preventDefault()}
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                >
                  {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                </IconButton>
              </InputAdornment>
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('userType')}
            id="standard-userType"
            label="User Type"
            select
            value={form.userType}
            // placeholder="Placeholder"
            // helperText="Full width!"
            fullWidth
            margin="dense"
          >
            {userType.map((option, i) => (
              <MenuItem value={option.value} key={i + 33}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
      </Grid>
    </Grid>
  )
}

export default CreateUserForm
