
import React, { useState, useEffect } from 'react'
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import {
  Button, Typography, Card, CardContent, Grid, IconButton, Input, InputLabel, InputAdornment,
  FormHelperText, FormControl,
} from '@material-ui/core';
import FormatText from '../../i18n';
import GoogleLogin from './GoogleLogin'
import ChooseAppCard from './ChooseAppCard'

// eslint-disable-next-line
let regexEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
// let regexPhone = /^(^\+62\s?|^0)(\d{3,4}-?){2}\d{3,4}$/


const LoginCard = ({ ...props }) => {
  let domain = window.location.origin
  const { handleChange, handleClient, handleGoogleLogin, handleEnter, handleClickShowPassword, handleMouseDownPassword,
    handleSubmit, handleRegister, handleForgotOpen, state, system, history } = props
  const [aud, setAud] = useState("")
  const [showLogin, setShowLogin] = useState(false)

  useEffect(() => {
    if (system.appConstant) {
      setAud(system.appConstant.aud)
    }
  }, [system.appConstant])

  useEffect(() => {
    if (showLogin === false && state.clientId) setShowLogin(true)
  }, [state.clientId, showLogin])

  useEffect(() => {
    // return () => {
    // && history.location.pathname === "any specific path")
    if (history.action === "POP") {
      setShowLogin(false)
      // history.replace(history.location.pathname, /* the new state */);
    }
    // };
  }, [history.action])


  useEffect(() => {
    if (aud === 'voltras-health') setShowLogin(true)
  }, [aud])

  window.onpopstate = e => { }

  return (
    <div>
      {showLogin ? null : <ChooseAppCard handleClient={handleClient} />}

      <Card style={{
        display: showLogin ? 'flex' : 'none',
        flexWrap: 'wrap',
        width: 285,
      }} elevation={4}>
        <center>
          <CardContent >
            <Typography color='primary' variant="h5"><b>{FormatText('app_sign_in')}</b> </Typography>
            <div>
              {/* <FormControl style={{ width: '30ch', margin: 7, marginBottom: 20, display: aud === 'voltras-health' ? 'none' : '' }} >
                <SimpleAutocomplete
                  id="LoginChooseApp"
                  label={'App to Login'}
                  options={[{ name: 'Klinik App' }, { name: 'Voltras Back Office' }, { name: 'Almanak' },]}
                  onPick={v => handleClient(v)}
                  notShrink={state.clientId ? false : true}
                  optionItem={'name'}
                />
              </FormControl> */}
              {
                state.clientId === 'voltras-health' || domain === 'https://health.voltras.net'
                  ? <div>
                    {/* <Typography variant="caption">{FormatText('common_with')}</Typography> */}
                    <div style={{ marginBottom: 15, marginTop: 20 }}>
                      <GoogleLogin
                        params={system.googleParams}
                        handleGoogleLogin={handleGoogleLogin}
                        buttonText={FormatText('common_signin_with')}
                      />
                      {/* <IconButton aria-label="google" onClick={handleGoogle}>
                                      <i style={{ fontSize: 20 }} className={"fab fa-google"} />
                                    </IconButton>
                                    <IconButton disabled aria-label="facebook">
                                      <FacebookIcon />
                                    </IconButton>
                                    <IconButton disabled aria-label="facebook">
                                      <Twitter />
                                    </IconButton> */}
                    </div>
                    <Typography variant='subtitle2' >{FormatText('common_or')}</Typography>
                  </div>
                  : null
              }

              <Grid container direction="row" justify="center" alignItems="center" >
                <FormControl style={{ width: '30ch', margin: 7, }} >
                  <InputLabel error={state.emailErrorMessage ? true : false} htmlFor="standard-adornment-password">
                    {FormatText('app_email_or_phone')}
                  </InputLabel>
                  <Input
                    autoFocus
                    autoComplete='on'
                    error={state.emailErrorMessage ? true : false}
                    id="standard-adornment-email"
                    type='text'
                    value={state.email}
                    onChange={handleChange('email')}
                    onKeyPress={handleEnter}
                  />
                  {
                    state.emailErrorMessage
                      ? <FormHelperText error id="email-helper-text">{state.emailErrorMessage}</FormHelperText>
                      : null
                  }
                </FormControl>
              </Grid>
              {
                regexEmail.test(state.email)
                  ? <FormControl style={{ width: '30ch', margin: 7, }} >
                    <InputLabel error={state.passwordErrorMessage ? true : false} htmlFor="standard-adornment-password">
                      {FormatText('app_password')}
                    </InputLabel>
                    <Input
                      error={state.passwordErrorMessage ? true : false}
                      id="standard-adornment-password"
                      type={state.showPassword ? 'text' : 'password'}
                      value={state.password}
                      onChange={handleChange('password')}
                      endAdornment={
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleClickShowPassword}
                            onMouseDown={handleMouseDownPassword}
                          >
                            {state.showPassword ? <Visibility /> : <VisibilityOff />}
                          </IconButton>
                        </InputAdornment>
                      }
                      onKeyPress={handleEnter}
                    />
                    {
                      state.passwordErrorMessage
                        ? <FormHelperText error id="password-helper-text">{state.passwordErrorMessage}</FormHelperText>
                        : null
                    }
                  </FormControl>
                  : null
              }

            </div>
            {
              regexEmail.test(state.email)
                ? <Grid container direction="row" justify="flex-end" alignItems="flex-start" >
                  <Button
                    size='small'
                    color="primary"
                    style={{ textTransform: 'none', fontWeight: 'bold', marginTop: 10 }}
                    onClick={handleForgotOpen}     //{() => setState({ ...state, forgotOpen: true })}
                  >
                    {FormatText('app_forgot_password')}
                  </Button>
                </Grid>
                : null
            }
            <FormControl>
              <Button
                size='large'
                onClick={handleSubmit}
                style={{
                  width: 240,
                  textTransform: 'none',
                  fontWeight: 'bold',
                  marginTop: regexEmail.test(state.email) ? 1 : 25
                }}
                variant="contained" color="primary">
                {FormatText('app_sign_in')}
              </Button>
              {
                state.onLoginErrorMessage
                  ? <FormHelperText error id="password-helper-text">{state.onLoginErrorMessage}</FormHelperText>
                  : null
              }
            </FormControl>
          </CardContent>

          <div style={{ marginBottom: 15 }}>
            <Typography variant='caption' >{FormatText('app_no_account')}</Typography>
            <Button onClick={handleRegister} color="primary" style={{ textTransform: 'none', fontWeight: 'bold' }} >
              {FormatText('app_sign_up')}
            </Button>
          </div>
        </center>
        <br />
        <br />
      </Card>
    </div>
  )
}

export default LoginCard
