
import { connect } from 'react-redux'
import LoginScreen, { useLoginStyles } from './LoginScreen'
import { withStyles } from '@material-ui/core/styles';

import { setServiceRequest, setServiceCode, setServiceStatus, setServiceCustomHeaders, setServiceMethod, setServiceParams } from '../../actions/ServiceActions'
import { setOpenScreen, dispatchLogout, setAppConstant, setPopChooseRole, setLocale } from '../../actions/SystemActions'
import { setUserInfo, setUserOtpId, setRoleUsed, setVerificationCode } from "../../actions/UserInfoActions";
import { setUserMenu, setMenuCollapseStatus } from "../../actions/MenuActions";

const mapStateToProps = state => {
  return {
    system: state.system,
    userInfo: state.userInfo,
    menu: state.menu,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setLocale: val => dispatch(setLocale(val)),
    _setVerificationCode: val => dispatch(setVerificationCode(val)),
    _setServiceMethod: val => dispatch(setServiceMethod(val)),
    _setRequest: val => dispatch(setServiceRequest(val)),
    _setServiceCode: val => dispatch(setServiceCode(val)),
    _setServiceStatus: val => dispatch(setServiceStatus(val)),
    _setServiceCustomHeaders: val => dispatch(setServiceCustomHeaders(val)),
    _setOpenScreen: val => dispatch(setOpenScreen(val)),
    _setUserInfo: val => dispatch(setUserInfo(val)),
    _setUserOtpId: val => dispatch(setUserOtpId(val)),
    _setAppConstant: val => dispatch(setAppConstant(val)),
    _setUserMenu: val => dispatch(setUserMenu(val)),
    _setRoleUsed: val => dispatch(setRoleUsed(val)),
    _setMenuCollapseStatus: val => dispatch(setMenuCollapseStatus(val)),
    _dispatchLogout: () => dispatch(dispatchLogout()),
    _setPopChooseRole: val => dispatch(setPopChooseRole(val)),
    _setCompleteRequest: val => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.serviceMethod))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceParams(val.serviceParams))
      dispatch(setServiceStatus(val.serviceStatus || 'active'))
    }
  }
}

const Login = connect(mapStateToProps, mapDispatchToProps)(LoginScreen)

export default withStyles(useLoginStyles)(Login)