
import React, { Component } from 'react';
import { connect } from 'react-redux'
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

const mapStateToProps = state => {
  return {
    response: state.response
  }
}

class MySnackBar extends Component {
  constructor(props) {
    super(props)
    this.state = {
      open: false,
      message: "",
      autoHideDuration: 2000,
    }
  }

  static getDerivedStateFromProps(props, state) {
    // console.log("STATIC PROPS: ", props, state)
    let error = props.response.response
      ? props.response.response.body
        ? props.response.response.body.error
        : false
      : false
    let message = props.response.response
      ? props.response.response.body
        ? props.response.response.body.errorDescription
        : ""
      : ""

    if (error) {
      if (error === 'error.auth.securityValidation.invalidAccessToken') {
        localStorage.clear()
        setTimeout(() => {
          props.history.push('/Login')
        }, 2000);
        return { ...state, open: true, message }
      }
      else if (error === "error.auth.securityValidation.emailNotVerified") return { ...state, open: false, }
      else if (error === "error.auth.securityValidation.phoneNotVerified") return { ...state, open: false, }
      else return { ...state, open: true, message }
    }

    // Return null to indicate no change to state.
    return null;
  }

  // componentWillReceiveProps(nextProps) {
  //   let error = nextProps.response.response.body.error
  //   let message = nextProps.response.response.body.errorDescription ? nextProps.response.response.body.errorDescription : ""
  //   if (error) {
  //     if (error === 'error.auth.securityValidation.invalidAccessToken') {
  //       this.setState({ ...this.state, open: true, message })
  //       localStorage.clear()
  //       setTimeout(() => {
  //         this.props.history.push('/Login')
  //       }, 2000);
  //     }
  //     else if (error === "error.auth.securityValidation.emailNotVerified") this.setState({ ...this.state, open: false, })
  //     else if (error === "error.auth.securityValidation.phoneNotVerified") this.setState({ ...this.state, open: false, })
  //     else this.setState({ ...this.state, open: true, message })
  //   }
  //   else {
  //     this.setState({ ...this.state, open: false, })
  //   }
  // }

  handleClose = () => {
    this.setState({ ...this.state, open: false })
  }

  render() {
    return (
      <div >
        <Snackbar open={this.state.open} autoHideDuration={this.state.autoHideDuration} onClose={this.handleClose}>
          <MuiAlert elevation={6} variant="filled" onClose={this.handleClose} severity="error">
            {this.state.message}
          </MuiAlert>
        </Snackbar>
      </div>
    )
  }
}

export default connect(mapStateToProps)(MySnackBar)
