import React, { useState } from 'react'
import { Dialog, DialogActions, DialogContent } from '@material-ui/core'
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { Grid, Typography, Button, MenuItem, TextField } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import { FormValidation } from '../../../utils/FormValidation';

const styles = (theme) => ({
    rootForm: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            // width: '25ch',
        },
    },
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    asterisk: {
        color: 'red'
    },
    addButton: {
        color: 'white'
    }
});

const DialogTitle = withStyles(styles)(props => {
    const { children, onClose, classes, ...other } = props

    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
})

const AddRequestSpesificationDialog = ({ ...props }) => {
    const { classes, dataClient } = props
    const [state, setState] = useState({
        clientId: "",

    })
    let dataRoles = []
    const handleClose = () => {
        setState({})
        props.handleClose(false, false)
    }
    // console.log('dataCliet', dataClient)
    const findDuplicateClientId = (arrayOfObj, key) => {
        return arrayOfObj.filter((item, index, array) => {
            return array.map((mapItem) => mapItem[key]).indexOf(item[key]) === index
        })
    }
    // console.log('dupkcate', findDuplicateClientId())

    const handleAddRequest = () => {
        // let request = {
        //     ...state,
        // }
        // let requestAllClient = {
        //     request,
        //     method:"POST",
        //     serviceCode:"ADD_REQUEST_SPESIFICATION",
        //     urlParams:'/request-spesifications'
        // }
        // console.log('Request',request)
        delete state.clientIdErrorMessage
        delete state.defaultRoleErrorMessage
        delete state.pathErrorMessage
        delete state.urlDestinationErrorMessage
        delete state.isNeedLoginErrorMessage

        props.completeRequest(state)
        // props.handleClose(false)
    }

    const handleChange = id => e => {
        let valid = FormValidation(id, e.target.value)
        setState({
            ...state,
            [id]: e.target.value,
            [id + 'ErrorMessage']: valid.errorMessage,

            // dataRoles : dataClient.filter(item=> item.clientId===e.target.value)[0]
        })
    }
    const setDataRoles = () => {
        dataRoles = dataClient.filter(item => item.clientId === state.clientId)[0]
        if (!dataRoles) return []
        else {
            return dataRoles.clientRoles
        }
        // return []
    }
    const needRole = [{ label: "Yes", boolean: true }, { label: "No", boolean: false }]

    const disableButton = () => {
        let isNeedLog = state.isNeedLogin === '' || state.isNeedLogin === null || state.isNeedLogin === undefined ? false : true
        if (state.clientId && state.path && state.urlDestination && isNeedLog) return false
        else return true
    }
    return (
        <Dialog fullWidth maxWidth={'xs'} open={props.open} onClose={handleClose} scroll={'paper'}>
            <DialogTitle
                id="max-width-dialog-title"
                onClose={handleClose}
            >
                Add Request Specification
            </DialogTitle>
            <DialogContent>
                <form className={classes.rootForm} noValidate autoComplete="off">
                    <TextField
                        fullWidth
                        placeholder='Contoh : authentication-backoffice'
                        required
                        select
                        id='clientId'
                        label='Client Id'
                        value={state.clientId}
                        onChange={handleChange('clientId')}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    >
                        {findDuplicateClientId(dataClient, 'clientId').map(item =>
                            <MenuItem key={item.clientId} value={item.clientId}>
                                {item.clientId}
                            </MenuItem>
                        )}
                    </TextField>
                    <TextField
                        fullWidth
                        required
                        placeholder='Contoh : /users/*/orders/**'
                        label='Path'
                        onChange={handleChange('path')}
                        error={state.pathErrorMessage ? true : false}
                        value={state.path ? state.path : ''}
                        helperText={state.pathErrorMessage ? state.pathErrorMessage : ''}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    />
                    <TextField
                        fullWidth
                        required
                        placeholder='Contoh : http//172.16.10.51:32170/users/*/orders/**'
                        label='URL Destination'
                        onChange={handleChange('urlDestination')}
                        error={state.urlDestinationErrorMessage ? true : false}
                        helperText={state.urlDestinationErrorMessage ? state.urlDestinationErrorMessage : ''}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    />
                    <TextField
                        fullWidth
                        required
                        select
                        label='Need Login'
                        onChange={handleChange('isNeedLogin')}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    >
                        {needRole.map(item =>
                            <MenuItem key={item.label} value={item.boolean}>
                                {item.label}
                            </MenuItem>
                        )}
                    </TextField>
                    <TextField
                        fullWidth
                        select
                        label='Default Role'
                        onChange={handleChange('defaultRole')}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    >
                        <MenuItem key={'null'} value={null}>
                            Null
                        </MenuItem>
                        {setDataRoles().map(item => <MenuItem key={item.roleId} value={item.roleId}>{item.roleId}</MenuItem>)}
                    </TextField>
                    <TextField
                        fullWidth
                        placeholder='Contoh: 100'
                        label='Priority Value'
                        onChange={handleChange('priorityValue')}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />
                </form>
            </DialogContent>
            <DialogActions>
                <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button color='primary' variant='contained'
                            className={classes.addButton} onClick={handleAddRequest} disabled={disableButton()}>
                            Add
                        </Button>
                    </Grid>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button onClick={handleClose} color='secondary' variant='contained' className={classes.addButton}>
                            Cancel
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    )
}

export default withStyles(styles)(AddRequestSpesificationDialog)