
import React, { useState } from 'react';
import { Grid, Typography, Button, } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import { FormValidation } from '../../../utils/FormValidation';
// import AddUserClient from '../createuser/AddUserClient'
import TextField from '@material-ui/core/TextField';
import {
  MenuItem,
  
} from '@material-ui/core';
// import Checkbox from '@material-ui/core/Checkbox';
// import FormGroup from '@material-ui/core/FormGroup';
// import FormControlLabel from '@material-ui/core/FormControlLabel';
// import FormControl from '@material-ui/core/FormControl';
// import FormLabel from '@material-ui/core/FormLabel';


const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const AddClientDialog = ({ ...props }) => {
  const { handleAddClient, handleOpen, clientData, open, clientList, } = props
  // const [showAddUserClient, setShowAddUserClient] = useState(false)
  const [form, setForm] = useState({
    resource: ['member'],
    assignedUserRole: [],
    resourcelist: [],
  })

  const handleClose = () => {
    handleOpen(false)
    setForm({
      resource: [],
      assignedUserRole: [],
      resourcelist: [],
      clientRoles: [],
    })
  };

  const handleChange = prop => e => {
    e.preventDefault()
    let valid = FormValidation(prop, e.target.value)
    if (prop === 'clientId') {
      let newResourcelist = []
      clientList.map(item => {
        if (item.clientId === e.target.value) {
          return newResourcelist = Array.isArray(item.clientRoles)
            ? item.clientRoles
            : item.clientRoles
              ? item.clientRoles.split(', ').map(item => ({ roleId: item }))
              : [{ roleId: 'client jadul jgn dipake' }]
        }
        return null
      })
      setForm({
        ...form,
        resourcelist: newResourcelist,
        [prop]: e.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        submitNotError: true,
      })
    }
    else {
      setForm({
        ...form,
        [prop]: e.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        submitNotError: true,
      })
    }
  }

  // const handleAddClientRole = e => {
  //   e.preventDefault()
  //   setForm({
  //     ...form,
  //     roleId: '',
  //     roleDescription: '',
  //     assignedUserRole: [...form.assignedUserRole, { clientId: form.clientId, resourcesAllowedList: form.resource }]
  //   })
  //   setShowAddUserClient(false)
  // }

  const handleCancel = e => {
    handleOpen(false)
    e.preventDefault()
    setForm({
      resource: [],
      assignedUserRole: [],
      resourcelist: [],
      clientRoles: [],
    })
  }

  let clientDataToFilter = clientData ? clientData.userRole ? clientData.userRole.map(item => item.client) : [] : []

  let chooseClientList = clientList
    ? clientList.filter(({ clientId }) => (!clientDataToFilter.includes(clientId))).map(item => {
      return {
        value: item.clientId,
        label: item.clientName
      }
    })
    : [{ value: '', label: '' }]

  // const handleResource = e => {
  //   let newResource = form.resource
  //   if (e.target.name && e.target.checked) {
  //     newResource.push(e.target.name)
  //     setForm({
  //       ...form,
  //       resource: newResource
  //     })
  //   }
  //   else if (e.target.name && !e.target.checked) {
  //     let filteredResource = []
  //     filteredResource = newResource.filter(item => item !== e.target.name)
  //     setForm({
  //       ...form,
  //       resource: filteredResource
  //     })
  //   }
  // }

  const handleSave = (e) => {
    e.preventDefault()
    let clientId = form.clientId
    let resourcesAllowedList = form.resource
    let request = { "userRoleToUpdate": [{ clientId, resourcesAllowedList }] }
    let method = 'PATCH'
    let serviceCode = 'UPDATE_USER_ROLE'
    let urlParams = `/users/${clientData.userId}/roles`

    let completeRequest = { request, method, serviceCode, urlParams }
    handleAddClient(completeRequest)
    handleOpen(false)
  }

  return (
    <Dialog fullWidth maxWidth={'xs'} open={open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle id="max-width-dialog-title" onClose={handleClose}>Add Client</DialogTitle>
      <DialogContent dividers>
        <center>
          <Grid container >
            <Grid item sm={12}>
              <TextField
                style={{width: 300}}
                onChange={handleChange('clientId')}
                id="standard-clientId"
                label="ClientId"
                placeholder="ClientId"
                select
                // fullWidth
                error={form.roleIdErrorMessage ? true : false}
                // value={form.clientId}
              >
                {chooseClientList
                  ? chooseClientList.map((option, i) => (
                    <MenuItem value={option.value} key={i+66}>
                      {option.value}
                    </MenuItem>
                  ))
                  : <MenuItem >
                    None
                  </MenuItem>
                }
              </TextField>

              {/* <AddUserClient
                form={form}
                showAddUserClient={showAddUserClient}
                setShowAddUserClient={setShowAddUserClient}
                handleChange={handleChange}
                chooseClientList={chooseClientList}
                handleResource={handleResource}
                handleAddClientRole={handleAddClientRole}
                fullmd
              /> */}

            </Grid>
          </Grid>
        </center>
      </DialogContent>

      <DialogActions>
        <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button variant='outlined' color="primary" onClick={handleCancel}>
              Cancel
          </Button>
          </Grid>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button variant='contained' color="primary" onClick={handleSave}>
              Add
          </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  )
}

export default AddClientDialog
