
import { connect } from 'react-redux'
import RegisterScreen, { useLoginStyles } from './RegisterScreen'
import { withStyles } from '@material-ui/core/styles';

import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceRole, setServiceCustomHeaders,
  setServiceMethod, setServiceParams,
} from '../../actions/ServiceActions'
import { setOpenScreen } from '../../actions/SystemActions'

const mapStateToProps = state => {
  return {
    system: state.system,

  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setRequest: val => {
      dispatch(setServiceRequest(val))
    },
    _setServiceRole: val => {
      dispatch(setServiceRole(val))
    },
    _setServiceCode: val => {
      dispatch(setServiceCode(val))
    },
    _setServiceStatus: val => {
      dispatch(setServiceStatus(val))
    },
    _setOpenScreen: val => {
      dispatch(setOpenScreen(val))
    },
    _setServiceCustomHeaders: val => dispatch(setServiceCustomHeaders(val)),
    _setCompleteRequest: val => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.serviceMethod))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceParams(val.serviceParams))
      dispatch(setServiceStatus(val.serviceStatus || 'active'))
    }
  }
}

const Login = connect(mapStateToProps, mapDispatchToProps)(RegisterScreen)

export default withStyles(useLoginStyles)(Login)