
import React from 'react';
import TextField from '@material-ui/core/TextField';
import { Grid, Typography, Button, Card, CardContent, CardActions } from '@material-ui/core'


const AddCLientRole = ({ ...props }) => {
  const { form, handleChange, setShowAddRoles, handleAddClientRole } = props
  
  return (
    <Card >
      <CardContent>
        <Grid>
          <Typography variant='subtitle2'>
            Add RoleId and Description
          </Typography>
        </Grid>
        <Grid>
          <TextField
            margin="dense"
            size="small"
            onChange={handleChange('roleId')}
            id="standard-roleId"
            label="RoleId"
            placeholder="RoleId"
            // onFocus={() => setHelpTextRoleId(true)}
            // onBlur={form.blur ? true : false}
            // helperText={helpTextRoleId ? "Go to Client List to add more Roles!" : form.roleIdErrorMessage ? form.roleIdErrorMessage : ''}
            error={form.roleIdErrorMessage ? true : false}
            value={form.roleId}
          />
        </Grid>
        <Grid >
          <TextField
            size="small"
            onChange={handleChange('roleDescription')}
            id="standard-roleDesc"
            label="Description"
            placeholder="Role description"
            helperText={form.roleDescriptionErrorMessage ? form.roleDescriptionErrorMessage : ''}
            error={form.roleDescriptionErrorMessage ? true : false}
            value={form.roleDescription}
          // onBlur={form.blur ? true : false}
          />
        </Grid>
      </CardContent>
      <CardActions>
        <Button size="small" color="primary" onClick={() => setShowAddRoles(false)} >
          Cancel
        </Button>
        <Button size="small" color="secondary" onClick={handleAddClientRole} >
          Add
        </Button>
      </CardActions>
    </Card>
  )
}

export default AddCLientRole