
import React, { useState, useEffect } from 'react';
import MaterialTable from 'material-table';
import Grid from '@material-ui/core/Grid'
import { connect } from 'react-redux'
// import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';
import BackIcon from '@material-ui/icons/ArrowBack';
import Button from '@material-ui/core/Button';
import UpdateUserDialog from './UpdateUserDialog'
import AddClientDialog from './AddClientDialog'
import BasicUserInfomation from './BasicUserInformation';
import UpdateUserBasicInfoDialog from './UpdateUserBasicInfoDialog';
import { sendImageToService } from '../../../services/ServiceClient'
// import { ca } from 'date-fns/locale';
import Config from '../../../config'


const FormDialog = ({ ...props }) => {

  const { userList ,userInfo} = props
  const [saveDisabled, setSaveDisabled] = useState(true)
  const [data, setData] = useState([])
  const [clientData, setClientData] = useState({})
  const [state, setState] = useState({ clientList: [], dataToUpdate: null, open: false, addClientOpen: false, openEditBasicInfo: false })
  const [saveRequest, setSaveRequest] = useState(null)
  const [updateUser, setUpdateUser] = useState(false)

  // console.log('userList', userList)

  useEffect(() => {
    if (updateUser) {
      // console.log('update user is click')
      let userIsId = userList.filter(item => item.userId === clientData.userId)
      setClientData(userIsId[0])
    }
  }, [clientData, updateUser, userList])

  const callbackUseEffect = () =>{
    let oldData = props.data ? props.data.userRole : []
    let data = []
    let newList = []
    if (oldData.length !== 0) {
      oldData.forEach(item => {
        let roleList = item.resourcesAllowedList
          ? Array.isArray(item.resourcesAllowedList)
            ? item.resourcesAllowedList.join(', ')
            : item.resourcesAllowedList
          : '-'
        item.roleList = roleList
        data.push(item)
      })
      setData(data)
      setClientData(props.data)
    }

    props.clientList && props.clientList.forEach(item => {
      let obj = {}
      let newRoleList = Array.isArray(item.clientRoles)
        ? item.clientRoles.map(child => child.roleId)
        : item.clientRoles ? item.clientRoles
          : ['client jadul jgn dipake']
      obj.client = item.clientId
      obj.roleList = newRoleList
      newList.push(obj)
    })
    setState({
      ...state,
      clientList: newList,
    })
  }

  useEffect(callbackUseEffect, [])

  useEffect(() => {
    if (props.updateUser && props.updateUser.length === 1) {
      let oldData = props.updateUser[0].userRole
      let data = []

      oldData.forEach(item => {
        let roleList = item.resourcesAllowedList
          ? Array.isArray(item.resourcesAllowedList)
            ? item.resourcesAllowedList.join(', ')
            : item.resourcesAllowedList
          : '-'
        item.roleList = roleList
        data.push(item)
      })
      setData(data)
      setClientData(props.updateUser[0])


      ////
      // setData(props.updateUser[0].userRole)
      // setClientData(props.updateUser[0])
    }
  }, [props.updateUser])

  const handleClose = () => {
    setData([])
    setClientData({})
    props._setUserFilteredList([])
    props.handleClose()
  };

  const handleUpdate = dataToUpdate => {
    setState({
      ...state,
      dataToUpdate,
      open: true,
    })
  }

  const handleDeleteClient = oldData => new Promise((resolve, reject) => {
    // console.log("Data to Delete: ", oldData)
    // console.log("Data to Delete: ", data)
    resolve()
  })

  const handleOpenEditCLient = val => {
    setState({
      ...state,
      open: val
    })
  }

  const handleSaveRole = val => {
    setSaveDisabled(false)
    let userRoleToUpdate = []
    data.forEach(item => {
      let dataToPush = {
        clientId: item.client,
        resourcesAllowedList: item.resourcesAllowedList
      }
      userRoleToUpdate.push(dataToPush)
    })

    let request = { userRoleToUpdate, }
    let method = 'PATCH'
    let serviceCode = 'UPDATE_USER_ROLE'
    let urlParams = `/users/${clientData.userId}/roles`

    let completeRequest = { request, method, serviceCode, urlParams }
    setSaveRequest(completeRequest)
  }

  const sendUpdateRoleRequest = () => {
    props.sendUpdateRequest(saveRequest)
    setSaveDisabled(true)
    // props.handleClose()
  }

  const handleAddClient = (addClientRequest) => {
    props.sendUpdateRequest(addClientRequest)
    // props.handleClose()
  }


  const handleOpenAddClient = val => {
    setState({
      ...state,
      addClientOpen: val
    })
  }

  const handleOpenEditBasic = val => {
    setState({
      ...state,
      openEditBasicInfo: val
    })
  }
  const backToTable = () => {
    props.handleClose(false)
  }
  const handleEditBasicRequest = (state, capture) => {
    let id = state.userId
    let emailVerified = state.basicInformation.emailVerifiedEdit ?
      state.basicInformation.emailVerifiedEdit === 'Verified' ? true : false : null
    let phoneVerified = state.basicInformation.phoneVerifiedEdit ?
      state.basicInformation.phoneVerifiedEdit === 'Verified' ? true : false : null
    let request = {
      attributeToUpdate: {
        email: state.emailEdit,
        phone: state.phoneEdit,
        gender: state.basicInformation.genderEdit,
        address: state.basicInformation.addressEdit,
        profile: state.basicInformation.profileEdit,
        name: state.basicInformation.nameEdit,
        picture: capture.data,
        emailVerified: emailVerified,
        phoneVerified: phoneVerified
      },
      id: state.userId
    }
    Object.keys(request.attributeToUpdate).forEach(item => {
      if (request.attributeToUpdate[item] === null || request.attributeToUpdate[item] === undefined || request.attributeToUpdate[item] === '') {
        delete request.attributeToUpdate[item]
      }
    })
    // console.log('useerId',id)
    let requestAllUser = {
      request,
      method: "PATCH",
      serviceCode: "UPDATE_BASIC_USER",
      urlParams: `/users/${id}`
    }
    
    if (capture.data) {
      const promise1 = () => new Promise((resolve, reject) => {

        resolve('next-promise')
      })
      sendImageToService(capture.images[0], capture.images[0].type)
        .then(result => {
          if (result.type === 'error') {
            return Promise.reject()
          }
          else {
            let body = result
            let uuid = body.ID
            requestAllUser.request.attributeToUpdate.picture = `${Config.gatewayRepoFile}/static-file-manager/retrieve?file=${uuid}`
            props.sendUpdateRequest(requestAllUser)
            return promise1()
          }
        }).then(res => {
          setUpdateUser(true)
        })
        .catch(e => {
          console.log(e)
        })
    }
    else {
      const promise = () => new Promise((resolve, reject) => {
        props.sendUpdateRequest(requestAllUser)
        resolve('next-promise')
      })
      promise()
        .then(res => {
          setUpdateUser(true)
        })
    }
    handleOpenEditBasic(false)
  }
  return (
    <Card style={{ display: 'flex', flexWrap: 'wrap', }}>

      <UpdateUserDialog
        handleSave={handleSaveRole}
        dataToUpdate={state.dataToUpdate}
        data={clientData}
        clientList={state.clientList}
        open={state.open}
        handleOpen={handleOpenEditCLient}

      />

      <AddClientDialog
        open={state.addClientOpen}
        handleOpen={handleOpenAddClient}
        clientList={props.clientList}
        clientData={clientData}
        handleAddClient={handleAddClient}
      />
      <UpdateUserBasicInfoDialog
        open={state.openEditBasicInfo}
        handleClose={handleOpenEditBasic}
        clientData={clientData}
        userInfo ={userInfo}
        title={'Edit Basic Information'}
        completeRequest={handleEditBasicRequest}
        backToTable={backToTable}
      />

      <Grid style={{ margin: 35, marginTop: 15 }} container direction="column" justify="flex-start" alignItems="flex-start" >
        <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
          <Button onClick={handleClose} color="secondary" startIcon={<BackIcon />} size='large' variant='outlined' >
            Back
          </Button>
        </Grid>
        <BasicUserInfomation
          clientData={clientData}
          handleOpenEdit={handleOpenEditBasic}
        // handleClose ={handleClose}
        />
        {/* <Grid style={{ marginTop: 15 }} container direction="row" justify="flex-start" alignItems="flex-start" spacing={2} >
          <Grid item xs={2}><Typography >Name</Typography></Grid>
          <Grid item xs={10}><Typography >: {clientData ? clientData.name : ""}</Typography></Grid>
        </Grid>
        <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={2} >
          <Grid item xs={2}><Typography >Email</Typography></Grid>
          <Grid item xs={10} > <Typography >: {clientData ? clientData.email : ""}</Typography></Grid>
        </Grid>
        <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={2} >
          <Grid item xs={2}><Typography >Phone</Typography></Grid>
          <Grid item xs={10} > <Typography >: {clientData ? clientData.phone : ""}</Typography></Grid>
        </Grid> */}
        <Grid style={{ marginTop: 20 }} container direction="row" justify="flex-start" alignItems="flex-start" >
          <Grid item xs={12} >
            <MaterialTable
              style={{ width: '100%' }}
              title="User Role List"
              columns={[
                { title: 'Client', field: 'client', width: 300 },
                { title: 'Role', field: 'roleList' },
              ]}
              data={data}
              editable={{
                // onRowAdd: (newData) =>
                //   new Promise((resolve) => {
                //     setTimeout(() => {
                //       resolve();
                //       setState((prevState) => {
                //         const data = [...prevState.data];
                //         data.push(newData);
                //         return { ...prevState, data };
                //       });
                //     }, 600);
                //   }),
                // onRowUpdate: (newData, oldData) =>
                //   new Promise((resolve) => {
                //     handleUpdate(newData, oldData).then(result => resolve())
                //   }),
                onRowDelete: (oldData) =>
                  new Promise((resolve) => {
                    handleDeleteClient(oldData).then(result => resolve())
                  }),
              }}
              actions={[
                {
                  icon: 'edit',
                  tooltip: 'Update Role',
                  onClick: (event, rowData) => handleUpdate(rowData)
                },
                // {
                //   icon: 'delete',
                //   tooltip: 'Delete Client',
                //   onClick: (event, rowData) => handleDeleteClient(rowData)
                // }
              ]}
              options={{
                actionsColumnIndex: -1,
                padding: 'dense'
              }}
            />
          </Grid>
        </Grid>
        <Grid style={{ marginTop: 20 }} container direction="row" justify='space-evenly' alignItems="flex-start" >
          {
            state.clientList.length > 1
              ? <Button onClick={() => setState({ ...state, addClientOpen: true })} variant='contained' color="primary"
                startIcon={<AddIcon />}
              >
                Add Client to User
              </Button>
              : null
          }

          <Button onClick={sendUpdateRoleRequest} variant='contained' color="primary" disabled={saveDisabled}
            startIcon={<EditIcon />}
          >
            Save Role
        </Button>
        </Grid>
      </Grid>
    </Card>
  );
}

const mapStateToProps = store => ({
  userList: store.userList.list,
  userInfo: store.userInfo
})

const mapDispatchToProps = dispatch => ({

})

export default connect(mapStateToProps, mapDispatchToProps)(FormDialog)
