
import React, { useState, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
// import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography';
// import Card from '@material-ui/core/Card';
// import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid'
// import Input from '@material-ui/core/Input';
// import InputLabel from '@material-ui/core/InputLabel';
// import FormHelperText from '@material-ui/core/FormHelperText';
// import FormControl from '@material-ui/core/FormControl';
// import Link from '@material-ui/core/Link';
import Paper from '@material-ui/core/Paper';
// import { CircularProgress } from '@material-ui/core';
import Background from '../../assets/image/backoffice02.jpg'

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

const VoltrasHome = ({ ...props }) => {
  const classes = useStyles();
  const [state, setstate] = useState({
    userEmail: '',
    roleUsed: '',
  })

  useEffect(() => {
    setstate(prevState => ({
      ...prevState,
      userEmail: props.userInfo.info.email,
      roleUsed: props.userInfo.roleUsed,
    }))
  }, [props.userInfo.info, props.userInfo.roleUsed])

  return (
    <div style={{
      backgroundImage: `url(${Background})`,
      backgroundRepeat: 'no-repeat',
      backgroundPositionX: 'center',
      height: 525,
      width: 600
    }} >
      <Grid container direction="column" justify="center" alignItems="center">
        <Paper className={classes.paper} elevation={0} >
          <Grid container direction='column' justify='flex-start' alignItems='center'  >
            {/* <Grid item xs={12} >
            Hello Voltras
          </Grid>
          <Grid item xs={12} >
            WELCOME
          </Grid> */}
            <Grid item xs={12} >
              <Typography variant='subtitle2' >
                {state.roleUsed}
              </Typography>
            </Grid>
            <Grid item xs={12} >
              {/* <CircularProgress/> */}
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    </div>
  )
}

export default VoltrasHome