
import React from 'react'
import { IconButton, Grid, Paper, Typography } from '@material-ui/core'
import { AppStructures } from '../../utils/AppStructures';

const ChooseAppCard = ({ ...props }) => {

  const handleClick = name => e => {
    props.handleClient({ name })
  }

  return (
    <Paper elevation={1} style={{ width: 300 }} >
      <Grid container direction="row" justify="center" alignItems="center" >
        <Grid item style={{ marginTop: 20, marginBottom: 10 }}><Typography variant='h6' >Choose Client</Typography> </Grid>
      </Grid>

      <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
        {AppStructures.map((item, i) => {
          return (
            <Grid item style={{ margin: 12, width: 75 }} onClick={handleClick(item.name)} key={i} >
              <IconButton aria-label="delete" color="primary">
                {item.icon}
              </IconButton>
              <center><Typography variant='subtitle2' >{item.name}</Typography> </center>
            </Grid>
          )
        })}
      </Grid>

      <Grid container direction="row" justify="flex-start" alignItems="flex-start" style={{ height: 100 }} > </Grid>

    </Paper>
  )
}

export default ChooseAppCard
