
// let domain = window.location.origin

const initialstate = {
  file: null,
  request: {},
  secondRequest: false,
  thirdRequest: false,
  status: "inactive",
  serviceCode: null,
  method: "POST",
  params: "",
  customHeaders: null,
  role: null,
  test: false,
  headersContent: {
    // "connection": "keep-alive",
    // "gadget-id": "mygadgetId",
    // "role": "end-user",
    // "client-id": "voltras-health",
    // "content-type": "application/json",
    // "accept": "*/*",
    // "origin": `${domain}`,
    // "accept-encoding": "gzip, deflate",
  },
}

const service = (state = initialstate, action) => {
  switch (action.type) {
    case 'SET_FILE':
      return { ...state, file: action.val }
    case 'SET_SERVICE_CODE':
      return { ...state, serviceCode: action.val }
    case 'SET_SERVICE_STATUS':
      return { ...state, status: action.val }
    case 'SET_SERVICE_METHOD':
      return { ...state, method: action.val }
    case 'SET_SERVICE_REQUEST':
      return { ...state, request: action.val }
    case 'SET_SERVICE_PARAMS':
      return { ...state, params: action.val }
    case 'SET_SERVICE_CUSTOM_HEADERS':
      return { ...state, customHeaders: action.val }
    case 'SET_SERVICE_ROLE':
      return { ...state, role: action.val }
    case 'SET_HEADERS_CONTENT':
      return { ...state, headersContent: { ...state.headersContent, ...action.val } }
    case 'SET_SECOND_REQUEST':
      return { ...state, secondRequest: action.val, }
    case 'CLEAR_SECOND_REQUEST':
      return { ...state, secondRequest: false }
    case 'SET_THIRD_REQUEST':
      return { ...state, thirdRequest: action.val, }
    case 'CLEAR_THIRD_REQUEST':
      return { ...state, thirdRequest: false }
    case 'SET_TEST':
      return { ...state, test: action.val, }
    case 'RESET_SERVICE':
      // reset service except second and third request
      return {
        ...state,
        "request": {},
        "status": "inactive",
        "serviceCode": null,
        "method": "POST",
        "params": "",
        'customHeaders': null,
        'role': null,
        "test": false,
        'headersContent': {},
      }
    case 'LOGOUT':
      return initialstate
    default:
      return state
  }
}

export default service





