
import React, { useState, useEffect, } from 'react';
import { Grid, Typography, Button, Avatar, } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
// import { FormValidation } from '../../../utils/FormValidation';
// import TextField from '@material-ui/core/TextField';
// import { FormHelperText } from '@material-ui/core';
import FormatText from '../../../i18n'
import { resizeImage } from "../../../utils/utils";

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const LogoPreviewDialog = ({ ...props }) => {
  const { handleOpen, imgPreview, setImgPreview, open, setImageFile, handleSaveImage, _setSnackbar } = props
  const [validation, setValidation] = useState({ error: false, errorMessage: "" })
  const [ownPreview, setOwnPreview] = useState(null)
  const [changeDisabled, setChangeDisabled] = useState(true)

  useEffect(() => {
    if (open && !ownPreview) setOwnPreview(imgPreview)
  }, [open, ownPreview, imgPreview])

  const handleClose = () => {
    handleOpen(false)
  };

  const handleCancel = e => {
    setOwnPreview(imgPreview)
    handleOpen(false)
    e.preventDefault()
  }

  const handleSave = (e) => {
    e.preventDefault()
    setImgPreview(ownPreview)
    setOwnPreview(null)
    handleOpen(false)
    handleSaveImage()
  }

  const fileSelected = async e => {
    setValidation({ error: false, errorMessage: '', })

    let errorSnackbar = { open: true, severity: "warning", }

    let file = e.target.files[0]
    if (file) {
      if (!file.type.includes('image')) setValidation({ error: true, errorMessage: 'Must be image file', })
      else if (file.size > 2000000) setValidation({ error: true, errorMessage: 'Image is bigger than 2 MB', })
      else {
        const data = await resizeImage(file);
        if (data.message) {
          errorSnackbar.message = data.message
          _setSnackbar(errorSnackbar)
        }
        setOwnPreview(data.blobUrl)
        setImageFile(data.file)
        // setImageType(data.type)
        setChangeDisabled(false)
      }
    }
  }

  return (
    <Dialog fullWidth open={open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle id="max-width-dialog-title" onClose={handleClose}>{props.label}</DialogTitle>
      <DialogContent dividers>
        <center>
          <Grid container direction="row" justify="center" alignItems='center' >
            <Grid item sm={12}>

              <Avatar
                src={ownPreview}
                variant='rounded'
                style={{ width: 150, height: 150 }}
              />

              <div>
                <input
                  accept="image/*"
                  onChange={fileSelected}
                  style={{ display: 'none' }}
                  id="raised-button-file"
                  multiple
                  type="file"
                />
                <div style={{ marginTop: 10 }} >
                  <label htmlFor="raised-button-file">
                    <Typography color='error' variant='caption' >{validation.errorMessage}</Typography>
                    <Button variant='outlined' component="span" size='small' color='primary' >
                      {FormatText('app_select_photo')}
                    </Button>
                  </label>
                </div>
              </div>

            </Grid>
          </Grid>
        </center>
      </DialogContent>

      <DialogActions>
        <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button variant='outlined' color="primary" onClick={handleCancel}>
              {FormatText('app_cancel')}
            </Button>
          </Grid>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button disabled={changeDisabled} variant='contained' color="primary" onClick={handleSave}>
              {FormatText('common_change')}
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  )
}

export default LogoPreviewDialog
