
/* eslint-disable no-use-before-define */
import React from 'react';
// import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Hidden from '@material-ui/core/Hidden'
import { withStyles } from '@material-ui/core/styles'


// const customStyle = makeStyles({
//   root: {
//     "& .MuiFormLabel-root": {
//       fontSize: ".9rem"
//     },
//     "& .MuiOutlinedInput-input": {
//       padding: "14px 10px"
//     },
//     "& .MuiInputBase-input": {
//       fontSize: "14px"
//     },

//     "& .MuiInputLabel-shrink": {
//       transform: "translate(0, 1.5px) scale(0.75)"
//     }
//   }
// });

const style = theme => (
  {
    labelRoot: {
      fontSize: 12,
      fontFamily: 'Arial, Helvetica, sans-serif',
    },
    inputRoot: {
      fontSize: 12,
      fontFamily: 'Arial, Helvetica, sans-serif',
      // height: 25
    }
  }
)

export default withStyles(style)(function ComboBox({ ...props }) {

  const { options, optionItem, onPick, id, helperText, label, width, autoFocus, handleInputChange,
    setIndex, freeSolo, clearOnBlur, values, handleFocus, small, classes } = props

  // const classes = customStyle();

  const [value, setValue] = React.useState({});
  const [inputValue, setInputValue] = React.useState('');
  // const [disabled, setDisabled] = React.useState(false);
  // const [error, setError] = React.useState(false);

  const handleChange = (event, newValue) => {
    // console.log("DI CLICK?")
    // if (newValue) {
    let listIndex = setIndex
    setValue(newValue)
    onPick(newValue, listIndex)
    // }
    // else {
    //   setValue({})
    // }
  }

  const handleInputValueChange = (event, newValue) => {
    let listIndex = setIndex
    let enterPressed = event && event.key === 'Enter' ? true : false
    setInputValue(newValue);
    if (handleInputChange) handleInputChange(newValue, listIndex, enterPressed)
  }

  const onFocus = () => {
    if (handleFocus) handleFocus()
  }

  return (
    <>
      <Hidden smDown>
        <Autocomplete
          size={small ? "small" : "medium"}
          fullWidth
          onFocus={onFocus}
          clearOnBlur={clearOnBlur}
          freeSolo={freeSolo}
          autoFocus={autoFocus}
          blurOnSelect
          value={values ? values : value}
          onChange={handleChange}
          inputValue={inputValue}
          onInputChange={handleInputValueChange}
          disabled={false}
          id={id}
          style={{ width: width ? width : '100%' }}
          options={options}
          getOptionLabel={(option) => optionItem ? option[optionItem] ? option[optionItem] : '' : ''}
          renderInput={(params) => (
            <TextField
              id={id}
              autoFocus={autoFocus}
              // className={classes.root}
              error={false}
              helperText={helperText}
              {...params}
              label={label}
              variant='standard'
            // InputLabelProps={{
            //   shrink: notShrink ? false : true,
            // }}
            />
          )}
        />
      </Hidden>
      <Hidden smUp>
        <Autocomplete
          size={small ? "small" : "medium"}
          fullWidth
          onFocus={onFocus}
          clearOnBlur={clearOnBlur}
          freeSolo={freeSolo}
          autoFocus={autoFocus}
          blurOnSelect
          value={values ? values : value}
          onChange={handleChange}
          inputValue={inputValue}
          onInputChange={handleInputValueChange}
          disabled={false}
          id={id}
          style={{ width: width ? width : '100%' }}
          classes={{
            option: classes.inputRoot
          }}
          options={options}
          getOptionLabel={(option) => optionItem ? option[optionItem] ? option[optionItem] : '' : ''}
          renderInput={(params) => (
            <TextField
              id={id}
              autoFocus={autoFocus}
              // className={classes.root}
              error={false}
              helperText={helperText}
              {...params}
              label={label}
              variant='standard'
              InputProps={{
                ...params.InputProps,
                style: {
                  fontSize: 12,
                  fontFamily: 'Arial, Helvetica, sans-serif'
                }
              }}
            />
          )}
        />
      </Hidden>
    </>
  );
})

