
import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'

import { Grid, Card, Typography, Button, Tooltip, Paper, IconButton } from '@material-ui/core'
import MaterialTable from 'material-table'
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import ArrowBack from '@material-ui/icons/ArrowBack'
import Hidden from '@material-ui/core/Hidden';
import Avatar from '@material-ui/core/Avatar';
// import Divider from '@material-ui/core/Divider'

import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, setSecondRequest,
} from '../../../actions/ServiceActions'

import { setUserFilteredList } from '../../../actions/UserListActions';
import AddUserDialog from './AddUserDialog'
import ConfirmationDialogRaw from '../../voltrasadmin/userlist/ConfirmationDIalog';
import FormatText from '../../../i18n'
import LogoPreviewDialog from './LogoPreviewDialog';
import { sendImageToService, SendToService } from '../../../services/ServiceClient';
import Config from '../../../config';
import { setSnackbar } from '../../../actions/FeedbackActions';
import ListTableMobile from '../../../components/list/ListTableMobile'
import { setPaxProduct } from '../../../actions/PaxListActions';
import EditDialog from './EditWithReplaceDialog'

const ClinicSetting = ({ ...props }) => {
  const { _setPaxProduct, _setOpenScreen, _setCompleteRequest, _setSecondRequest,
    userInfo, roleUsed, paxList, _setSnackbar, history } = props
  const [clinic, setClinic] = useState({})
  const [clinicUser, setClinicUser] = useState([])
  const [clinicProduct, setCLinicProduct] = useState([])
  const [state, setState] = useState({ clientList: [], dataToUpdate: null, open: false, addClientOpen: false })
  // const [image, setImage] = useState(null)
  const [imgPreview, setImgPreview] = useState(null)
  const [imageFile, setImageFile] = useState(null)
  const [imageType, setImageType] = useState(null)
  const [popUpPreview, setPopUpPreview] = useState(false)
  const [open, setOpen] = useState(false);
  const [userToDelete, setUserToDelete] = useState(null)
  const [openProductEdit, setOpenProductEdit] = useState(false)
  const [openDaysEdit, setOpenDaysEdit] = useState(false)
  const [openDateEdit, setOpenDateEdit] = useState(false)
  const days = ["Haha", 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday']
  const daysAvailable = [
    { day: 1, name: "Monday", open: new Date('2020-10-30T08:00:00'), close: new Date('2020-10-30T17:00:00') },
    { day: 2, name: "Tuesday", open: new Date('2020-10-30T08:00:00'), close: new Date('2020-10-30T17:00:00') },
    { day: 3, name: "Wednesday", open: new Date('2020-10-30T08:00:00'), close: new Date('2020-10-30T17:00:00') },
    { day: 4, name: "Thursday", open: new Date('2020-10-30T08:00:00'), close: new Date('2020-10-30T17:00:00') },
    { day: 5, name: "Friday", open: new Date('2020-10-30T08:00:00'), close: new Date('2020-10-30T17:00:00') },
    { day: 6, name: "Saturday", open: new Date('2020-10-30T08:00:00'), close: new Date('2020-10-30T17:00:00') },
    { day: 7, name: "Sunday", open: new Date('2020-10-30T08:00:00'), close: new Date('2020-10-30T17:00:00') },
  ]

  useEffect(() => {
    const { name, email, phone } = userInfo
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    if (name && email) {
      SendToService(
        {},
        "GET",
        "healthProduct",
        null,
        customHeaders,
        null,
        null,
        product => {
          _setPaxProduct(product.body)
          if ((roleUsed === 'biofarma.admin') || (roleUsed === 'voltras.admin')) {
            let clinicClickedId = paxList.clinicDetail.id
            let getClinicUserRequest = {
              request: {},
              method: "GET",
              serviceCode: "GET_CLINIC_USER",
              urlParams: `/${clinicClickedId}`,
              customHeaders,
            }
            _setCompleteRequest(getClinicUserRequest)
          }
          else if ((roleUsed === 'clinic.admin') || (roleUsed === 'clinic.operator')) {

            let myClinicRequest = {
              request: {},
              method: "GET",
              serviceCode: "GET_MY_CLINIC",
              customHeaders,
            }
            _setCompleteRequest(myClinicRequest)
            _setSecondRequest(true)

          }
          else {
            console.log("NO ROLE", userInfo)
          }
        }
      )
    }
    _setOpenScreen("Clinic Setting")
  }, [userInfo, roleUsed, _setOpenScreen, paxList.clinicDetail, _setCompleteRequest, _setSecondRequest, _setPaxProduct])

  useEffect(() => {
    let clinicToSet = {}
    if (paxList) {
      if (paxList.clinicDetail && paxList.clinicDetail.id) clinicToSet = paxList.clinicDetail
      else if (paxList.clinicUser && paxList.clinicUser.id) clinicToSet = paxList.clinicUser
      else clinicToSet = {}
    }
    else clinicToSet = {}
    setClinic(clinicToSet)
    if (clinicToSet.logo_url) setImgPreview(clinicToSet.logo_url)
    if (paxList.clinicUser && paxList.clinicUser.users.length > 0) {
      let oldUsers = paxList.clinicUser.users
      let newUsers = oldUsers.map(item => {
        let rolesStr = Array.isArray(item.roles) ? item.roles.join(', ').replace('member, ', '') : ""
        item.roles = rolesStr
        return item
      })
      setClinicUser(newUsers)
    }

    let availableProducts = clinicToSet.available_products_id
    let productList = paxList.product
    let clinicProduct = []
    if (availableProducts && availableProducts.length > 0 && productList.length > 0) {
      availableProducts.forEach(item => {
        productList.forEach(prod => {
          if (prod.id === item) clinicProduct.push(prod.name)
        })
      })
      setCLinicProduct(clinicProduct)
    }
  }, [paxList])

  const handleOpenAddClient = val => {
    setState({
      ...state,
      addClientOpen: val
    })
  }

  const handleAddUser = (request, secondRequest) => {
    _setCompleteRequest(request)
    _setSecondRequest(secondRequest)
    handleOpenAddClient(false)
  }

  const handleDeleteUser = oldData => new Promise((resolve, reject) => {
    let creatorEmail = paxList.clinicDetail.creation
      ? paxList.clinicDetail.creation.email
      : paxList.clinicUser.creation
        ? paxList.clinicUser.creation.email
        : null
    let errorSnackbar = { open: true, severity: "warning", message: "Cannot delete clinic creator", }

    if (creatorEmail && (creatorEmail !== oldData.email)) {
      setUserToDelete(oldData)
      setOpen(true)
    }
    else _setSnackbar(errorSnackbar)
    resolve()
  })

  const handleOpenLogoPreview = val => {
    setPopUpPreview(val)
  }

  const handleSaveImage = () => {
    let clinic_id = paxList.clinicUser.id || paxList.myClinic.clinic_id
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    sendImageToService(imageFile, imageType, imageFile).then(response => {
      if (response.ID) {
        let imgUrl = Config.gatewayRepoFile + `/static-file-manager/retrieve?file=${response.ID}`

        // POST /health/admin/clinic/{id}/logo_url
        let addClinicLogoRequest = {
          request: { url: imgUrl },
          method: "POST",
          serviceCode: "ADD_CLINIC_LOGO",
          urlParams: `/${clinic_id}/logo_url`,
          customHeaders,
        }
        _setCompleteRequest(addClinicLogoRequest)


      }
      else {
        // setImgPreview(image)
        console.log('wrong response')
      }
    })
  }

  const handleClose = () => {
    setOpen(false);
  };

  const handleChoose = () => {
    // DELETE /health/admin/clinics/{id}/users/{userid}
    let oldData = userToDelete
    let id = oldData.clinic_id
    let userid = oldData.id
    let customHeaders = { name: userInfo.name, email: userInfo.email }
    if (userInfo.phone) customHeaders.phone = userInfo.phone
    let deleteRequest = {
      request: {},
      method: "DELETE",
      serviceCode: "DELETE_CLINIC_USER",
      urlParams: `/${id}/users/${userid}`,
      customHeaders,
    }
    let getClinicUserRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CLINIC_USER",
      urlParams: `/${id}`,
      customHeaders,
    }
    _setCompleteRequest(deleteRequest)
    _setSecondRequest(getClinicUserRequest)

  }

  const handleOpenProductEdit = val => setOpenProductEdit(val)

  const handleProductEditSave = val => {
    const { name, email, phone } = userInfo
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    let clinic_id = paxList.clinicUser.id || paxList.myClinic.clinic_id
    let urlParams = `/${clinic_id}`
    let available_products_id = []
    val.forEach(item => {
      paxList.product.forEach(prod => {
        if (prod.name === item) available_products_id.push(prod.id)
      })
    })
    let editRequest = {
      request: { available_products_id },
      method: "PATCH",
      serviceCode: "EDIT_CLINIC_SETTING",
      urlParams,
      customHeaders,
    }
    _setCompleteRequest(editRequest)
  }

  const handleOpenDaysEdit = val => setOpenDaysEdit(val)

  const handleDaysEditSave = val => {
    let operational = []
    val.forEach(item => {
      let dayId = []
      days.forEach((el, i) => {
        if (el === item.slice(0, item.length - 13)) {
          dayId.push(i)
        }
      })
      let obj = {
        day: dayId[0],
        hour: item.slice(item.length - 11, item.length)
      }
      operational.push(obj)
    })

    let oldVendorClose = clinic.vendor_close
    let clinic_id = paxList.clinicUser.id || paxList.myClinic.clinic_id
    let urlParams = `/${clinic_id}`
    const { name, email, phone } = userInfo
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let arrDays = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday']
    let valueDays = val.map(item => {
      let text = item.slice(0, item.length - 13)
      return text
    })

    let day = []
    let arrOffDay = arrDays.filter(el => !valueDays.includes(el))
    days.forEach((item, i) => { if (arrOffDay.includes(item)) day.push(i) })

    let vendor_close = {
      date: oldVendorClose.date,
      day
    }

    let editRequest = {
      request: { vendor_close, operational },
      method: "PATCH",
      serviceCode: "EDIT_CLINIC_SETTING",
      urlParams,
      customHeaders,
    }
    _setCompleteRequest(editRequest)
  }

  const handleOpenDateEdit = val => setOpenDateEdit(val)

  const handleDateEditSave = val => {
    let oldVendorClose = clinic.vendor_close
    let clinic_id = paxList.clinicUser.id || paxList.myClinic.clinic_id
    let urlParams = `/${clinic_id}`
    const { name, email, phone } = userInfo
    let customHeaders = { name, email }
    // let day = []
    if (phone) customHeaders.phone = phone

    let vendor_close = {
      date: val,
      day: oldVendorClose.day
    }

    let editRequest = {
      request: { vendor_close },
      method: "PATCH",
      serviceCode: "EDIT_CLINIC_SETTING",
      urlParams,
      customHeaders,
    }
    _setCompleteRequest(editRequest)
  }


  return (
    <div>
      <AddUserDialog
        label={FormatText('clinic_add_user')}
        handleOpen={handleOpenAddClient}
        open={state.addClientOpen}
        clinicInfo={clinic}
        userInfo={userInfo}
        handleAddUser={handleAddUser}
        paxList={paxList}
      />
      <LogoPreviewDialog
        label={FormatText('clinic_logo')}
        handleOpen={handleOpenLogoPreview}
        open={popUpPreview}
        imgPreview={imgPreview}
        setImgPreview={setImgPreview}
        setImageFile={setImageFile}
        setImageType={setImageType}
        handleSaveImage={handleSaveImage}
        _setSnackbar={_setSnackbar}
      />
      <ConfirmationDialogRaw
        id="delete-client"
        keepMounted
        open={open}
        onClose={handleClose}
        header="Delete User"
        content={userToDelete ? userToDelete.email : ''}
        handleChoose={handleChoose}
      />
      <EditDialog
        id="ProductEditDialog"
        dialogTitle="Edit Product"
        availTitle="Product Available"
        existingTitle="Products"
        open={openProductEdit}
        handleOpen={handleOpenProductEdit}
        availableList={paxList.product}
        existingList={clinic.available_products_id}
        handleSave={handleProductEditSave}
      />
      <EditDialog
        id="DaysEditDialog"
        dialogWidth="sm"
        dialogTitle="Edit Vendor Open Days"
        availTitle="Days Available"
        existingTitle="Days"
        open={openDaysEdit}
        handleOpen={handleOpenDaysEdit}
        availableList={daysAvailable}
        existingList={clinic.operational ? clinic.operational : []}
        handleSave={handleDaysEditSave}
        timePicker
      />
      <EditDialog
        id="DateEditDialog"
        dialogTitle="Edit Vendor Close Date"
        availTitle="Pick Date"
        existingTitle="Date"
        open={openDateEdit}
        handleOpen={handleOpenDateEdit}
        availableList={[]}
        existingList={clinic.vendor_close ? clinic.vendor_close.date : []}
        handleSave={handleDateEditSave}
        datePicker
      />

      <Hidden xsDown>
        <Card style={{ display: 'flex', flexWrap: 'wrap', }}>
          <Grid style={{ margin: 40, marginTop: 1 }} container direction="column" justify="flex-start" alignItems="flex-start" >



            <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
              <Grid item xs={7}>
                <Grid container direction="row" justify="flex-start" alignItems="center" style={{ marginTop: 5 }} >
                  <Grid item xs={3}><Typography ><b>{FormatText('clinic_logo')}</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={5} style={{ marginLeft: 15 }}>
                    {
                      imgPreview
                        ? <Tooltip title={roleUsed === "clinic.admin" ? "Click to change logo" : "Clinic Logo"}>
                          <Grid container direction="row" justify="flex-start" alignItems="center">
                            <Grid item >
                              <Paper elevation={1} onClick={() => roleUsed === "clinic.admin" ? handleOpenLogoPreview(true) : null}>
                                <img
                                  src={imgPreview}
                                  style={{ height: 100 }}
                                  alt="Logo"
                                />
                              </Paper>
                            </Grid>
                            <Grid item style={{ marginLeft: 15, visibility: roleUsed === "clinic.admin" ? 'visible' : 'hidden' }} >
                              <IconButton color='inherit' onClick={() => roleUsed === "clinic.admin" ? handleOpenLogoPreview(true) : null} >
                                <EditIcon />
                              </IconButton>
                            </Grid>

                          </Grid>
                        </Tooltip>
                        : roleUsed === "clinic.admin"
                          ? <Button color='primary' variant='outlined' size='small' onClick={() => handleOpenLogoPreview(true)}>
                            {FormatText('clinic_set_logo')}
                          </Button>
                          : null
                    }

                  </Grid>
                </Grid>
                <Grid style={{ marginTop: 15 }} container direction="row" justify="flex-start" alignItems="flex-start" >
                  <Grid item xs={3}><Typography ><b>{FormatText('clinic')} ID</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={7} style={{ marginLeft: 15 }}><Typography >{clinic.id}</Typography></Grid>
                </Grid>
                <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                  <Grid item xs={3}><Typography ><b>{FormatText('clinic_name')}</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={7} style={{ marginLeft: 15 }}><Typography >{clinic.name}</Typography></Grid>
                </Grid>
                <Grid container direction="row" justify="flex-start" alignItems="flex-start">
                  <Grid item xs={3}><Typography ><b>{FormatText('common_address')}</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={7} style={{ marginLeft: 15 }}><Typography >{clinic.address}</Typography></Grid>
                </Grid>
              </Grid>
              <Grid item xs={5}>
              </Grid>
            </Grid>




            <Grid container direction="row" justify="flex-start" alignItems="flex-start" style={{ marginTop: 15 }} >
              <Grid item xs={7}>
                <Grid container direction="row" justify="flex-start" alignItems="flex-start" style={{ marginTop: 5 }} >
                  <Grid item xs={3}><Typography ><b>Vendor Open</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={5} style={{ marginLeft: 15 }}>
                    {
                      clinic.operational && clinic.operational.length > 0
                        ? clinic.operational.map((item, i) => {
                          let day = ["null", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]
                          return <Typography key={i} > - {day[item.day]}, {item.hour}</Typography>
                        })
                        : <Typography color='textSecondary' style={{ display: roleUsed === "clinic.admin" ? "" : "none" }} > Click button to add/edit days</Typography>
                    }
                  </Grid>
                  <Grid xs={1} item style={{ display: roleUsed === "clinic.admin" ? "" : "none" }} >
                    <Tooltip title={"Add/Edit Days"}>
                      <IconButton color='inherit' onClick={() => roleUsed === "clinic.admin" ? handleOpenDaysEdit(true) : null} >
                        <EditIcon />
                      </IconButton>
                    </Tooltip>
                  </Grid>
                </Grid>

                <Grid container direction="row" justify="flex-start" alignItems="flex-start" style={{ marginTop: 5 }} >
                  <Grid item xs={3}><Typography ><b>Vendor Close Day</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={5} style={{ marginLeft: 15 }}>
                    {
                      clinic.vendor_close && clinic.vendor_close.day.length > 0
                        ? clinic.vendor_close && clinic.vendor_close.day.map((item, i) => {
                          let day = ["null", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]
                          return <Typography key={i} > - {day[item]}</Typography>
                        })
                        : <Typography color='textSecondary' style={{ display: roleUsed === "clinic.admin" ? "" : "none" }} > Click button to add/edit days</Typography>
                    }
                  </Grid>
                </Grid>



                <Grid container direction="row" justify="flex-start" alignItems="flex-start" spacing={1} style={{ marginTop: 5 }} >
                  <Grid item xs={3}><Typography ><b>Vendor Close Date</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={5} style={{ marginLeft: 15 }}>
                    {
                      clinic.vendor_close && clinic.vendor_close.date.length > 0
                        ? clinic.vendor_close && clinic.vendor_close.date.map((item, i) => {
                          return <Typography key={i} > - {item}</Typography>
                        })
                        : <Typography color='textSecondary' style={{ display: roleUsed === "clinic.admin" ? "" : "none" }} > Click button to add/edit date</Typography>
                    }
                  </Grid>
                  <Grid xs={1} item style={{ display: roleUsed === "clinic.admin" ? "" : "none" }} >
                    <Tooltip title={"Add/Edit Days"}>
                      <IconButton color='inherit' onClick={() => roleUsed === "clinic.admin" ? handleOpenDateEdit(true) : null} >
                        <EditIcon />
                      </IconButton>
                    </Tooltip>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={5}>
                <Grid container direction="row" justify="flex-start" alignItems="flex-start"  >
                  <Grid item xs={2}><Typography ><b>Products</b></Typography></Grid>
                  <Grid item container xs={1} justify="flex-end" alignItems="flex-end" ><Typography >:</Typography></Grid>
                  <Grid item xs={7} style={{ marginLeft: 15 }}>
                    {
                      clinicProduct.length > 0
                        ? clinicProduct.map((item, i) => {
                          return <Typography key={i} > - {item}</Typography>
                        })
                        : <Typography color='textSecondary' style={{ visibility: roleUsed === "clinic.admin" ? 'visible' : 'hidden' }} > Click button to add/edit products</Typography>
                    }
                  </Grid>
                  <Grid xs={1} item style={{ display: roleUsed === "clinic.admin" ? "" : "none" }} >
                    <Tooltip title={"Add/Edit Product"}>
                      <IconButton color='inherit' onClick={() => roleUsed === "clinic.admin" ? handleOpenProductEdit(true) : null} >
                        <EditIcon />
                      </IconButton>
                    </Tooltip>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>

            <Grid style={{ marginTop: 20 }} container direction="row" justify="flex-start" alignItems="flex-start" >
              <Grid item xs={12} >
                <MaterialTable
                  style={{ width: '100%' }}
                  title={FormatText('clinic_user_list')}
                  columns={[
                    { title: FormatText('app_name'), field: 'name' },
                    { title: FormatText('app_email'), field: 'email' },
                    { title: "Role", field: 'roles' },
                    { title: FormatText('app_phone'), field: 'phone' },
                    // { title: 'Role', field: 'role' },
                  ]}
                  data={clinicUser}
                  actions={[
                    {
                      icon: 'delete',
                      tooltip: 'Delete User',
                      onClick: (event, rowData) => handleDeleteUser(rowData),
                    }
                  ]}
                  options={{
                    actionsColumnIndex: -1,
                    padding: 'dense',
                    paging: false,

                  }}
                />
              </Grid>
            </Grid>
            <Grid style={{ marginTop: 25 }} container direction="row" justify='center' alignItems="center" >
              <Button style={{ width: 150, marginRight: 10 }} variant='outlined' color="secondary"
                onClick={() => history.push('/KClinicList')}
                startIcon={<ArrowBack />}
              >
                {FormatText('app_back')}
              </Button>
              <Button style={{ width: 150, marginLeft: 10 }} variant='contained' color="primary"
                onClick={() => handleOpenAddClient(true)}
                startIcon={<AddIcon />}
              >
                {FormatText('clinic_add_user')}
              </Button>

              {/* <Button variant='contained' color="primary"
            startIcon={<EditIcon />}
          >
            Save Role
        </Button> */}
            </Grid>
          </Grid>
        </Card>
      </Hidden>

      <Hidden smUp>
        <Card style={{ marginRight: 5 }}>
          <Grid container >
            <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
              <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
                Clinic Info
              </Typography>
            </div>
            <Grid item xs={12} sm={12}>
              {
                imgPreview
                  ?
                  <div style={{ marginLeft: 11, marginBottom: 7 }}>
                    <Grid container direction="row">
                      <Grid item sm={5} xs={5} align='left'>
                        <Typography style={{ fontSize: 11, padding: 3, paddingTop: 15, color: 'grey', fontFamily: 'Arial, Helvetica, sans-serif' }}>{FormatText('clinic_logo')}</Typography>
                      </Grid>
                      <Grid item xs={7} sm={7} style={{ padding: 3 }}>
                        <Tooltip title={roleUsed === "clinic.admin" ? "Click to change logo" : "Clinic Logo"}>
                          <Avatar src={imgPreview ? imgPreview : '/broken-image.jpg'} style={{ height: 35, width: 35 }} onClick={() => roleUsed === "clinic.admin" ? handleOpenLogoPreview(true) : null} />
                        </Tooltip>
                      </Grid>
                      {/* <Divider style={{ width: '100%' }} /> */}
                      {/* <div style={{ borderStyle: 'solid', width: '100%', borderColor: 'grey' }} /> */}
                      <Grid item sm={5} xs={5} align='left'>
                        <Typography style={{ fontSize: 11, padding: 3, color: 'grey', fontFamily: 'Arial, Helvetica, sans-serif' }}>{FormatText('clinic')} ID</Typography>
                      </Grid>
                      <Grid item sm={7} xs={7}>
                        <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>{clinic.id}</Typography>
                      </Grid>
                      {/* <Divider style={{ width: '100%' }} /> */}
                      <Grid item sm={5} xs={5} align='left'>
                        <Typography style={{ fontSize: 11, color: 'grey', padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>{FormatText('clinic_name')}</Typography>
                      </Grid>
                      <Grid item sm={7} xs={7}>
                        <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} >{clinic.name}</Typography>
                      </Grid>
                      {/* <Divider style={{ width: '100%' }} /> */}
                      <Grid item sm={5} xs={5} align='left'>
                        <Typography style={{ color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>{FormatText('common_address')}</Typography>
                      </Grid>
                      <Grid item sm={7} xs={7}>
                        <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>{clinic.address}</Typography>
                      </Grid>
                      {/* <Divider style={{ width: '100%' }} /> */}
                      <Grid item sm={5} xs={5} align='left'>
                        <Typography style={{ color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>{FormatText('Clinic Status')}</Typography>
                      </Grid>
                      <Grid item sm={7} xs={7}>
                        <Typography style={{ fontSize: 11, fontWeight: 'bold', padding: 3, color: '#00cb00', fontFamily: 'Arial, Helvetica, sans-serif' }}>{clinic.status}</Typography>
                      </Grid>
                      <Grid item sm={5} xs={5} align='left'>
                        <Typography style={{ color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Vendor Open</Typography>
                      </Grid>
                      <Grid item sm={5} xs={5}>
                        {
                          clinic.operational && clinic.operational.length > 0
                            ? clinic.operational.map((item, i) => {
                              let day = ["null", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]
                              return <Typography key={i} style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} > - {day[item.day]}, {item.hour}</Typography>
                            })
                            : <Typography style={{ display: roleUsed === "clinic.admin" ? "" : "none", fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}> Click button to add/edit days</Typography>
                        }
                      </Grid>
                      <Grid item sm={2} xs={2} style={{ display: roleUsed === "clinic.admin" ? "" : "none" }}>
                        <Tooltip title={"Add/Edit Days"} style={{ padding: 0 }}>
                          <IconButton color='inherit' onClick={() => roleUsed === "clinic.admin" ? handleOpenDaysEdit(true) : null} >
                            <EditIcon style={{ fontSize: 18 }} />
                          </IconButton>
                        </Tooltip>
                      </Grid>
                      <Grid item sm={5} xs={5} align='left' style={{ marginTop: 5 }}>
                        <Typography style={{ color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Vendor Close Day</Typography>
                      </Grid>
                      <Grid item sm={7} xs={7} style={{ marginTop: 5 }}>
                        {
                          clinic.vendor_close && clinic.vendor_close.day.length > 0
                            ? clinic.vendor_close && clinic.vendor_close.day.map((item, i) => {
                              let day = ["null", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]
                              return <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} key={i} > - {day[item]}</Typography>
                            })
                            : <Typography style={{ display: roleUsed === "clinic.admin" ? "" : "none", color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} > Click button to add/edit days</Typography>
                        }
                      </Grid>
                      <Grid item sm={5} xs={5} align='left' style={{ marginTop: 5 }}>
                        <Typography style={{ color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Vendor Close Date</Typography>
                      </Grid>
                      <Grid item sm={5} xs={5} style={{ marginTop: 5 }}>
                        {
                          clinic.vendor_close && clinic.vendor_close.date.length > 0
                            ? clinic.vendor_close && clinic.vendor_close.date.map((item, i) => {
                              return <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} key={i} > - {item}</Typography>
                            })
                            : <Typography style={{ display: roleUsed === "clinic.admin" ? "" : "none", color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} > Click button to add/edit date</Typography>
                        }
                      </Grid>
                      <Grid item sm={2} xs={2} style={{ marginTop: 5, display: roleUsed === "clinic.admin" ? "" : "none" }}>
                        <Tooltip title={"Add/Edit Days"} style={{ padding: 0 }}>
                          <IconButton color='inherit' onClick={() => roleUsed === "clinic.admin" ? handleOpenDateEdit(true) : null} >
                            <EditIcon style={{ fontSize: 18 }} />
                          </IconButton>
                        </Tooltip>
                      </Grid>
                      <Grid item sm={5} xs={5} align='left' style={{ marginTop: 5 }}>
                        <Typography style={{ color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Product</Typography>
                      </Grid>
                      <Grid item sm={5} xs={5} style={{ marginTop: 5 }}>
                        {
                          clinicProduct.length > 0
                            ? clinicProduct.map((item, i) => {
                              return <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} key={i} > - {item}</Typography>
                            })
                            : <Typography color='textSecondary' style={{ visibility: roleUsed === "clinic.admin" ? 'visible' : 'hidden', color: 'grey', fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} > Click button to add/edit products</Typography>
                        }
                      </Grid>
                      <Grid item sm={2} xs={2} style={{ marginTop: 5, display: roleUsed === "clinic.admin" ? "" : "none" }}>
                        <Tooltip title={"Add/Edit Product"} style={{ padding: 0 }}>
                          <IconButton color='inherit' onClick={() => roleUsed === "clinic.admin" ? handleOpenProductEdit(true) : null} >
                            <EditIcon style={{ fontSize: 18 }} />
                          </IconButton>
                        </Tooltip>
                      </Grid>
                    </Grid>

                  </div>

                  : roleUsed === "clinic.admin"
                    ? <Button color='primary' variant='outlined' size='small' onClick={() => handleOpenLogoPreview(true)}>
                      {FormatText('clinic_set_logo')}
                    </Button>
                    : null
              }

            </Grid>
          </Grid>
        </Card>
        <Card style={{ marginRight: 5, marginTop: 15 }}>
          <Grid container >
            <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
              <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
                User List
              </Typography>
            </div>
            <Grid item sm={12} xs={12}>
              <ListTableMobile data={clinicUser} column={[
                { title: FormatText('app_name'), field: 'name' },
                { title: FormatText('app_email'), field: 'email' },
                { title: "Role", field: 'roles' },
                { title: FormatText('app_phone'), field: 'phone' },
                // { title: 'Role', field: 'role' },
              ]} button={false} />

              {/* </div> */}
            </Grid>
            <Grid style={{ marginTop: 25, marginBottom: 25, marginRight: 15, marginLeft: 15 }} item sm={12} xs={12}>
              <Button style={{
                backgroundColor: '#00cb00', color: 'white', fontSize: 15, fontWeight: 'bold',
                textTransform: 'none', width: '100%', borderRadius: 10, fontFamily: 'Arial, Helvetica, sans-serif'
              }} variant='contained' color="primary"
                onClick={() => handleOpenAddClient(true)}
              // startIcon={<AddIcon />}
              >
                {FormatText('clinic_add_user')}
              </Button>

              {/* <Button variant='contained' color="primary"
            startIcon={<EditIcon />}
          >
            Save Role
        </Button> */}
            </Grid>
          </Grid>
        </Card>
        <div style={{ height: 100 }} />
        <canvas id="canvas" style={{ width: "300", display: 'none' }}></canvas>
      </Hidden>
      {/* <div style={{ height: 100 }} />
      <canvas id="canvas" style={{ width: "300", display: 'none' }}></canvas> */}
    </div >
  )
}

const mapStateToProps = store => ({
  userList: store.userList.list,
  userDetail: store.userList.detail,
  userFiltered: store.userList.filteredList,
  userInfo: store.userInfo.info,
  clientList: store.clientList.list,
  roleAuth: store.system.roleAuth,
  updateUser: store.userList.filteredList,
  paxlistStatus: store.menu.paxList,
  paxList: store.paxList,
  roleUsed: store.userInfo.roleUsed,
})

const mapDispatchToProps = dispatch => ({
  _setOpenScreen: val => dispatch(setOpenScreen(val)), _setUserFilteredList: val => dispatch(setUserFilteredList(val)),
  _setSecondRequest: val => dispatch(setSecondRequest(val)), _setSnackbar: val => dispatch(setSnackbar(val)),
  _setCompleteRequest: (val) => {
    dispatch(setServiceRequest(val.request))
    dispatch(setServiceMethod(val.method))
    dispatch(setServiceCode(val.serviceCode))
    dispatch(setServiceParams(val.urlParams))
    dispatch(setServiceCustomHeaders(val.customHeaders))
    dispatch(setServiceRole(val.serviceRole))
    dispatch(setServiceStatus("active"))
  },
  _setPaxProduct: val => dispatch(setPaxProduct(val))
})

export default connect(mapStateToProps, mapDispatchToProps)(ClinicSetting)
