
import { combineReducers } from 'redux'
import system from './SystemReducer'
import service from './ServiceReducer'
import response from './ResponseReducer'
import userInfo from './UserInfoReducer'
import menu from './MenuReducer';
import paxList from './PaxListReducers'
import feedback from './FeedbackReducer'
import autocomplete from './AutoCompleteReducer'
import userList from './UserListReducers'
import clientList from './ClientListReducer'
import genericConfiguration from './GenericConfigurationReducer';
import requestList from './RequestListReducer'

const appReducers = combineReducers({
  autocomplete,
  clientList,
  feedback,
  menu,
  paxList,
  response,
  system,
  service,
  userInfo,
  userList,
  genericConfiguration,
  requestList
})

export default appReducers