
const initialstate = {
  list: [],
  detail: {},
  filteredList: [],
  messagingList: [],
  messagingEdit: {}
}

const ClientList = (state = initialstate, action) => {
  switch (action.type) {
    case 'SET_CLIENT_LIST':
      return {
        ...state,
        list: action.val
      }
    case 'SET_CLIENT_DETAIL':
      return {
        ...state,
        detail: action.val
      }
    case 'SET_CLIENT_FILTERED_LIST':
      return {
        ...state,
        filteredList: action.val
      }
    case 'SET_CLIENT_MESSAGE_LIST':
      return {
        ...state,
        messagingList: action.val
      }
    case'SET_CLIENT_MESSAGE_EDIT':
      return {
        ...state,
        messagingEdit:action.val
      }
    case 'LOGOUT':
      return initialstate

    default:
      return state
  }
}

export default ClientList
