import React, { useState, useEffect } from 'react'
import Divider from '@material-ui/core/Divider'
import EditIcon from '@material-ui/icons/Edit'
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar'



const BasicUserInfomation = ({ ...props }) => {
    const [capture, setCapture] = useState({
        data: ""
    })
    const [basicInfo, setBasicInfo] = useState([])
    useEffect(() => {
        let data = props.clientData
        setBasicInfo([
            {
                title: 'Email',
                field: data.email,
                verified: data.emailVerified && data.email ? 'Verified' : 'Not Verified'
            },
            {
                title: 'Phone',
                field: data.phone,
                verified: data.phoneVerified && data.phone ? 'Verified' : 'Not Verified'
            },
            {
                title: 'Name',
                field: data.basicInformation ? data.basicInformation.name : ""
            },
            {
                title: 'Profile URL',
                field: data.basicInformation ? data.basicInformation.profile : ""
            },
            {
                title: 'Gender',
                field: data.basicInformation ? data.basicInformation.gender : ""
            },
            {
                title: 'Address',
                field: data.basicInformation ? data.basicInformation.address : ""
            }
        ])
        setCapture({
            data: data.basicInformation ? data.basicInformation.picture : ""
        })
    }, [props.clientData])
    const handleOpenEdit = () => {
        props.handleOpenEdit(true)
    }
    return (
        <Grid style={{ marginTop: 20 }} container direction="row" justify="flex-start" alignItems="flex-start">
            <Grid item xs={8} style={{ marginTop: 10, marginBottom: 10 }}>
                <Paper style={{ paddingLeft: 20 }}>
                    <Grid container>
                        <Grid item xs={10}>
                            <Typography variant='h6' style={{ paddingBottom: 20 }}>
                                Basic Information
                            </Typography>
                        </Grid>
                        <Grid container item xs={2} justify='flex-end' style={{ paddingRight: 15 }}>
                            <Tooltip title='Edit'>
                                <EditIcon onClick={handleOpenEdit} />
                            </Tooltip>
                        </Grid>
                    </Grid>
                    {
                        basicInfo.map(item =>
                            (
                                <div>
                                    <Grid container style={{ marginBottom: 8 }}>
                                        <Grid item xs={2}>
                                            <Typography style={{fontSize:13,fontWeight:'bold',fontStyle:'Times New Roman'}}>
                                                {item.title}
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={8}>
                                            {item.title === 'Profile URL' ? <a href={item.field}  target="_blank" rel="noopener noreferrer">
                                                {item.field}
                                            </a> : <Typography style={{ fontSize: 14 }}>
                                                    {item.field}
                                                </Typography>}

                                        </Grid>
                                        <Grid>
                                            <Typography style={{
                                                color: item.verified === 'Verified' ? '#79d70f' : '#d32626',
                                                fontSize: 14
                                            }}>
                                                {(item.title === 'phone' || item.title === 'email') && !item.field ? "" : item.verified}
                                            </Typography>
                                        </Grid>
                                    </Grid>
                                    <Divider />
                                </div>
                            ))
                    }
                </Paper>
            </Grid>
            <Grid item xs={4} align='center' style={{ padding: 50 }}>
                <Avatar style={{ width: 160, height: 160, borderWidth: '4px', borderColor: 'black', borderStyle: 'solid' }} src={capture.data} />
            </Grid>
        </Grid>
    )
}

export default BasicUserInfomation