
import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux'
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import CircularProgress from '@material-ui/core/CircularProgress';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import Button from '@material-ui/core/Button';
import ReactExport from 'react-data-export';

import {
  setServiceRequest, setServiceMethod, setServiceParams, setServiceCustomHeaders, setServiceCode,
  setServiceRole, setServiceStatus
} from '../../../actions/ServiceActions';
import { setPaxProduct } from '../../../actions/PaxListActions';
import { setOpenScreen } from '../../../actions/SystemActions';
import FormatText from '../../../i18n';
import { convertDateyyyymmdd, yyyymmddWithParams } from '../../../utils/utils';
import { SendToService } from '../../../services/ServiceClient';
import { createClinicSummaryTable, createBiofarmaSummaryTable } from "./TableCreator";
import Hidden from '@material-ui/core/Hidden'
import Card from '@material-ui/core/Card'
import ListTableMobile from '../../../components/list/ListTableMobile'
import DialogDetail from './DialogDetail'


let from = yyyymmddWithParams('lastWeek')
let dateto = yyyymmddWithParams('nextWeek')
const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});


const RedeemedOrderSummaryTable = ({ ...props }) => {
  const { setOpenScreen, setServiceRequest, setServiceMethod, setServiceParams, setServiceCustomHeaders, setServiceCode,
    setServiceRole, setServiceStatus, setPaxProduct, roleUsed, userInfo, paxList, system } = props

  const _setCompleteRequest = obj => {
    setServiceRequest(obj.request)
    setServiceMethod(obj.method)
    setServiceCode(obj.serviceCode)
    setServiceParams(obj.params)
    setServiceCustomHeaders(obj.customHeaders)
    setServiceRole(obj.serviceRole)
    setServiceStatus("active")
  }

  const classes = useStyles();

  const [state, setState] = useState({
    fromdate: from,
    todate: dateto,
    clinic_recapt_logistic: [],
    clinic_product: [],
    table_component: { allRows: [], allColumns: [] },
    table_biofarma: [],
    dataSet: [{
      ySteps: 1,
      columns: [
        { title: "OrderID", width: { wch: 7 } },
        { title: "", width: { wch: 12 } },
        { title: "Test Date", width: { wch: 12 } },
        { title: "", width: { wch: 12 } }
      ],
      data: [[{ value: 33 }, { value: 2 }, { value: 33 }]]
    }]
  })


  /**
   * FIRST EFFECT: COMPONENT DID MOUNT GET SUMMARY
   * Get the summary from service then put the response in
   * paxList.clinic_recapt_logistic
   */
  useEffect(() => {
    setOpenScreen("Redeemed Order Summary")
    let clinic_id = paxList.clinicUser ? paxList.clinicUser.id : 0
    let name = userInfo.name
    let email = userInfo.email
    let phone = userInfo.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let serviceCode = roleUsed === 'clinic.admin' ? "RECAPT_CLINIC_SUMMARY" : "RECAPT_BIOFARMA_SUMMARY"
    let params = roleUsed === 'clinic.admin'
      ? `/${clinic_id}?&from_date=${from}&to_date=${dateto}&redeemed=True`
      : `/transaction?&from_date=${from}&to_date=${dateto}&redeemed=True`

    if (name && email) {
      SendToService({}, "GET", "healthProduct", null, customHeaders, null, null,
        product => {
          setPaxProduct(product.body)
          let recaptClinicReq = {
            request: {},
            method: "GET",
            serviceCode,
            params,
            customHeaders
          }
          _setCompleteRequest(recaptClinicReq)
        }
      )
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  /**
   * SECOND EFFECT: ON RECEIVE RESPONSE
   * - Put products in state
   * - Put recapt in state
   */
  useEffect(() => {
    if (roleUsed === 'clinic.admin') {
      let available_product = paxList.clinicUser ? paxList.clinicUser.available_products_id : []
      let clinic_product_id = paxList.product
      let clinic_product = []
      if (available_product[0] && clinic_product_id[0]) {
        clinic_product_id.forEach(item => {
          if (available_product.includes(item.id)) clinic_product.push(item.name)
        })
      }
      setState(state => ({ ...state, clinic_recapt_logistic: paxList.clinic_recapt_logistic, clinic_product }))
    }
    else {
      let clinic_product = paxList.product.map(item => item.name)
      setState(state => ({ ...state, clinic_recapt_logistic: paxList.clinic_recapt_logistic, clinic_product }))
    }
  }, [paxList.clinic_recapt_logistic, paxList.clinicUser, paxList.product, roleUsed])

  /**
   * THIRD EFFECT: CREATE TABLE & XLS DATASET
   */
  useEffect(() => {
    if (roleUsed === 'clinic.admin') {
      let table_component = createClinicSummaryTable(state.clinic_recapt_logistic, state.clinic_product)
      setState(state => ({ ...state, table_component }))
    }
    else {
      let table_biofarma = createBiofarmaSummaryTable(state.clinic_recapt_logistic, state.clinic_product)
      setState(state => ({ ...state, table_biofarma: table_biofarma.allRows, dataSet: table_biofarma.dataSet }))
    }
  }, [state.clinic_recapt_logistic, state.clinic_product, roleUsed])


  const handleDateChange = prop => date => {
    setState({
      ...state,
      [prop]: convertDateyyyymmdd(date),
    })

    let clinic_id = paxList.clinicUser ? paxList.clinicUser.id : 0
    let name = userInfo.name
    let email = userInfo.email
    let phone = userInfo.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    let request_date_obj = { fromdate: state.fromdate, todate: state.todate }
    request_date_obj[prop] = convertDateyyyymmdd(date)
    let serviceCode = roleUsed === 'clinic.admin' ? "RECAPT_CLINIC_SUMMARY" : "RECAPT_BIOFARMA_SUMMARY"
    let params = roleUsed === 'clinic.admin'
      ? `/${clinic_id}?&from_date=${request_date_obj.fromdate}&to_date=${request_date_obj.todate}&redeemed=True`
      : `/transaction?&from_date=${request_date_obj.fromdate}&to_date=${request_date_obj.todate}&redeemed=True`

    let recaptClinicReq = {
      request: {},
      method: "GET",
      serviceCode,
      params,
      customHeaders
    }
    _setCompleteRequest(recaptClinicReq)
  }
  const [openDetail, setOpenDetail] = useState(false)
  const [detailData, setDetailData] = useState({})
  const handleOpenDetail = (item) => {
    setOpenDetail(true)
    setDetailData(item)
  }

  return (
    <>
      <Hidden smDown>
        <Grid container direction='row' justify='flex-start' alignItems='center' style={{ margin: 30 }} >
          <Grid container direction='row' justify='center' alignItems='center'>
            <CircularProgress
              style={{ visibility: system.serviceStart && system.openScreen === 'Redeemed Order Summary' ? "visible" : "hidden" }}
              color="secondary"
              size={30}
              thickness={5}
            />
          </Grid>
          <Grid item sm={2}>
            <Typography variant="subtitle2">Testdate Periode:</Typography>
          </Grid>
          <Grid item sm={2} >
            <MuiPickersUtilsProvider utils={DateFnsUtils} >
              <KeyboardDatePicker
                autoOk
                style={{ width: 150 }}
                size="small"
                clearable
                format="yyyy/MM/dd"
                margin="dense"
                id="date-picker-from"
                label="From"
                value={state.fromdate}
                onChange={handleDateChange('fromdate')}
                KeyboardButtonProps={{
                  'aria-label': 'change date',
                }}
              />
            </MuiPickersUtilsProvider>
          </Grid>
          <Grid item sm={2} >
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <KeyboardDatePicker
                autoOk
                style={{ width: 150 }}
                clearable
                size="small"
                format="yyyy/MM/dd"
                margin="dense"
                id="date-picker-to"
                label="To"
                value={state.todate}
                onChange={handleDateChange('todate')}
                KeyboardButtonProps={{
                  'aria-label': 'change date',
                }}
              />
            </MuiPickersUtilsProvider>
          </Grid>
          <Grid item>
            <ExcelFile
              filename={`Order Summary ${state.fromdate} to ${state.todate}`}
              element={
                <Button size='small' variant='contained' color="primary">
                  {FormatText('app_export_xls')}
                </Button>}
            >
              <ExcelSheet
                dataSet={roleUsed === 'clinic.admin' ? state.table_component.dataSet : state.dataSet}
                name="Order Report Summary"
              />
            </ExcelFile>
          </Grid>
        </Grid>

        {
          roleUsed === 'biofarma.admin' || roleUsed === 'voltras.admin'
            ? <TableContainer component={Paper}>
              <Table className={classes.table} aria-label="spanning table" size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell align="center"  ></TableCell>
                    {state.clinic_product.map((item, i) => (
                      <TableCell align="center" colSpan={2} key={i + 21}>{item}</TableCell>
                    ))}
                    <TableCell align="center" colSpan={2}>TOTAL</TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell align="center"  >CLINICS</TableCell>
                    {state.clinic_product.map((item, i) => ["Pax", "NTA"].map((el, g) => (
                      <TableCell align="center" key={i + g}>{el}</TableCell>
                    )))}
                    <TableCell align="center">Pax</TableCell>
                    <TableCell align="center">NTA</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {state.table_biofarma.map((row, i) => (
                    <TableRow key={i}>
                      {row.map((item, i) => (
                        <TableCell align="center" key={i + 555}>{item}</TableCell>
                      ))}
                    </TableRow>
                  ))}

                </TableBody>
              </Table>
            </TableContainer>
            : roleUsed === 'clinic.admin'
              ? <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="spanning table">

                  <TableHead>
                    <TableRow>
                      <TableCell align="center" colSpan={9} >TEST DATE</TableCell>
                    </TableRow>
                    <TableRow>
                      {state.table_component.allColumns.map((column, i) => {
                        return <TableCell align="center" key={99 + i}>{column}</TableCell>
                      })}
                    </TableRow>
                  </TableHead>

                  <TableBody>
                    {state.table_component.allRows.map((rows, i) => (
                      <TableRow key={i + 54}>
                        {rows.map((row, i) => (
                          <TableCell align="center" key={i + 212}>{row}</TableCell>
                        ))}
                      </TableRow>
                    ))}
                  </TableBody>

                </Table>
              </TableContainer>
              : null
        }

      </Hidden>
      <Hidden smUp>
        <DialogDetail
          open={openDetail}
          title={'Order Detail'}
          handleOpen={() => setOpenDetail(false)}
          detailData={detailData}
          column={roleUsed === 'biofarma.admin' || roleUsed === 'voltras.admin' ? [
            { title: 'Product Name', field: 'name' },
            { title: 'Pax', field: 'patient' },
            { title: 'NTA', field: 'nta' },
            { title: 'Total NTA', field: 'sum_nta' }
          ] : [
              { title: 'Product Name', field: 'name' },
              { title: 'Total', field: 'total' }
            ]}
        />
        {
          roleUsed === 'biofarma.admin' || roleUsed === 'voltras.admin' ?
            <Card>
              <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
                <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
                  Redemeed Summary
              </Typography>
              </div>
              <div style={{ marginLeft: 15, marginRight: 15 }}>
                <Grid container>
                  <Grid item xs={5} sm={5}>
                    <Typography style={{ fontSize: 11, padding: 3, color: 'grey', fontFamily: 'Arial, Helvetica, sans-serif' }}>Total Pax</Typography>
                  </Grid>
                  <Grid item xs={7} sm={7}>
                    <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>
                      {state.clinic_recapt_logistic[0]
                        ? state.clinic_recapt_logistic && state.clinic_recapt_logistic.total.reduce((total, num) => {
                          return Number(total) + Number(num.patient)
                        }, 0)
                        : ''
                      }
                    </Typography>
                  </Grid>
                  <Grid item xs={5} sm={5}>
                    <Typography style={{ fontSize: 11, padding: 3, color: 'grey', fontFamily: 'Arial, Helvetica, sans-serif' }}>Total NTA</Typography>
                  </Grid>
                  <Grid item xs={7} sm={7}>
                    <Typography style={{ fontSize: 11, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>
                      {
                        state.clinic_recapt_logistic[0]
                          ? state.clinic_recapt_logistic && state.clinic_recapt_logistic.total.reduce((total, num) => {
                            return Number(total) + Number(num.nta)
                          }, 0)
                          : ''
                      }
                    </Typography>
                  </Grid>
                  {/* <Grid item xs={5} sm={5}>
                <Typography style={{ fontSize: 13, padding: 3, color: 'grey', fontFamily: 'Arial, Helvetica, sans-serif' }}>Total Pax</Typography>
              </Grid>
              <Grid item xs={7} sm={7}>
                <Typography style={{ fontSize: 13, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>{state.clinic_recapt_logistic.total.reduce((total, num) => {
                  return Number(total.patient) + num
                }, 0)}</Typography>
              </Grid> */}
                </Grid>
              </div>
            </Card> : <div></div>
        }
        <Card style={{ marginTop: 10 }}>
          <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
            <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
              Filter By
              </Typography>
          </div>
          <div style={{ marginLeft: 15, marginRight: 15 }}>
            <Typography style={{ marginTop: 10, fontSize: 12, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
              Testdate Periode
            </Typography>
            <Grid container>
              <Grid item sm={6} xs={6}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    autoOk
                    style={{ width: 150 }}
                    size="small"
                    clearable
                    format="yyyy/MM/dd"
                    margin="dense"
                    id="date-picker-from"
                    label="From"
                    value={state.fromdate}
                    onChange={handleDateChange('fromdate')}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    InputLabelProps={{
                      style: {
                        fontSize: 12,
                        fontFamily: 'Arial, Helvetica, sans-serif',
                      }
                    }}
                    InputProps={{
                      style: {
                        fontSize: 12,
                        fontFamily: 'Arial, Helvetica, sans-serif',
                      }
                    }}
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item sm={6} xs={6}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    autoOk
                    style={{ width: 150 }}
                    clearable
                    size="small"
                    format="yyyy/MM/dd"
                    margin="dense"
                    id="date-picker-to"
                    label="To"
                    value={state.todate}
                    onChange={handleDateChange('todate')}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    InputLabelProps={{
                      style: {
                        fontSize: 12,
                        fontFamily: 'Arial, Helvetica, sans-serif',
                      }
                    }}
                    InputProps={{
                      style: {
                        fontSize: 12,
                        fontFamily: 'Arial, Helvetica, sans-serif',
                      }
                    }}
                  />
                </MuiPickersUtilsProvider>
              </Grid>
            </Grid>
            <Grid container justify='center' style={{ paddingTop: 10, marginBottom: 10 }}>
              <ExcelFile
                filename={`Order Summary ${state.fromdate} to ${state.todate}`}
                element={
                  <Button size='small' variant='outlined' color="primary" style={{
                    textTransform: 'none', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                    fontWeight: 'bold', fontSize: 12, paddingLeft: 30, paddingRight: 30
                  }} >
                    {FormatText('app_export_xls')}
                  </Button>}
              >
                <ExcelSheet
                  dataSet={roleUsed === 'clinic.admin' ? state.table_component.dataSet : state.dataSet}
                  name="Order Report Summary"
                />
              </ExcelFile>
            </Grid>
          </div>
        </Card>
        <Card style={{ marginTop: 10 }}>
          <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
            <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
              All Redeemed
              </Typography>
          </div>
          <div style={{ marginLeft: 15, marginRight: 15 }}>
            {
              roleUsed === 'biofarma.admin' || roleUsed === 'voltras.admin' ?
                <ListTableMobile
                  column={[
                    { title: "Clinic", field: 'name' },
                    { title: "Total Pax", field: 'total_patient' },
                    { title: "Total NTA", field: 'total_nta' },
                  ]}
                  data={state.clinic_recapt_logistic.detail}
                  button={true}
                  arrow={true}
                  onClickList={handleOpenDetail}
                /> : <div>
                  <ListTableMobile
                    column={[
                      { title: "Test Date", field: 'testdate' },
                    ]}
                    data={state.clinic_recapt_logistic}
                    button={true}
                    arrow={true}
                    onClickList={handleOpenDetail}
                  />
                </div>
            }
          </div>
        </Card>
      </Hidden>
    </>
  )
}

const mapStateToProps = store => ({
  userInfo: store.userInfo.info,
  paxList: store.paxList,
  roleUsed: store.userInfo.roleUsed,
  system: store.system,
  // store
})

const mapDispatchToProps = {
  setOpenScreen, setServiceRequest, setServiceMethod, setServiceParams, setServiceCustomHeaders, setServiceCode,
  setServiceRole, setServiceStatus, setPaxProduct
}


export default connect(mapStateToProps, mapDispatchToProps)(RedeemedOrderSummaryTable)
