
import React, { useState, } from 'react';
import { Grid, Typography, Button, } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import { FormValidation } from '../../../utils/FormValidation';
import TextField from '@material-ui/core/TextField';
import { FormHelperText } from '@material-ui/core';
import FormatText from '../../../i18n'


const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const AddClientDialog = ({ ...props }) => {
  const { handleAddUser, handleOpen, open, userInfo, paxList } = props
  const [form, setForm] = useState({
    // resource: [],
    // assignedUserRole: [],
    // resourcelist: [],
    nameErrorMessage: false,
    emailErrorMessage: false,
    phoneErrorMessage: false,
    notComplete: false
  })

  const handleClose = () => {
    handleOpen(false)
    setForm({
      // resource: [],
      // assignedUserRole: [],
      // resourcelist: [],
      clientRoles: [],
      nameErrorMessage: false,
      emailErrorMessage: false,
      phoneErrorMessage: false,
      notComplete: false
    })
  };

  const handleChange = prop => e => {
    e.preventDefault()
    let val = e.target.value
    let valid = FormValidation(prop, val)
    // console.log(val)

    if (val[0] === "0") console.log("+62" + val.slice(1))

    setForm({
      ...form,
      [prop]: val,
      [prop + 'ErrorMessage']: valid.errorMessage,
      notComplete: false
    })
  }

  const handleCancel = e => {
    handleOpen(false)
    e.preventDefault()
    setForm({
      // resource: [],
      // assignedUserRole: [],
      // resourcelist: [],
      clientRoles: [],
    })
  }

  const handleSave = (e) => {
    e.preventDefault()
    const { emailErrorMessage, nameErrorMessage, phoneErrorMessage } = form
    if (!emailErrorMessage && !nameErrorMessage && !phoneErrorMessage) {
      const id = paxList.clinicDetail.id ? paxList.clinicDetail.id : paxList.clinicUser.id ? paxList.clinicUser.id : 0
      const { email, } = form
      let customHeaders = { name: userInfo.name, email: userInfo.email, }
      if (userInfo.phone) customHeaders.phone = userInfo.phone

      let objReq = { email }
      // if (phone) objReq.phone = phone

      let addClinicUserRequest = {
        request: [objReq],
        method: "POST",
        serviceCode: "ADD_CLINIC_USER",
        urlParams: `/${id}/users`,
        customHeaders,
      }

      let getClinicUserRequest = {
        request: {},
        method: "GET",
        serviceCode: "GET_CLINIC_USER",
        urlParams: `/${id}`,
        customHeaders,
      }
      // console.log("HALAH: ", addClinicUserRequest, getClinicUserRequest)
      handleAddUser(addClinicUserRequest, getClinicUserRequest)
    }
    else {
      setForm({
        ...form,
        notComplete: true
      })
    }
  }

  return (
    <Dialog fullWidth maxWidth={'xs'} open={open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle id="max-width-dialog-title" onClose={handleClose}>{props.label}</DialogTitle>
      <DialogContent dividers>
        <center>
          <Grid container direction="row" justify="center" alignItems='center' >
            <Grid item sm={12}>
              {/* <TextField
                size="small"
                onChange={handleChange('name')}
                id="standard-name"
                label={FormatText('app_name')}
                placeholder="Full Name"
                helperText={form.nameErrorMessage ? form.nameErrorMessage : ""}
                error={form.nameErrorMessage ? true : false}
                fullWidth
                margin='dense'
                value={form.name}
              /> */}
              <TextField
                size="small"
                onChange={handleChange('email')}
                id="standard-email"
                label={FormatText('app_email')}
                placeholder="Email"
                helperText={form.emailErrorMessage ? form.emailErrorMessage : ""}
                error={form.emailErrorMessage ? true : false}
                fullWidth
                margin='dense'
                value={form.email}
              />
              {/* <TextField
                size="small"
                onChange={handleChange('phone')}
                id="standard-phone"
                label={FormatText('app_phone')}
                placeholder="081234567890"
                helperText={form.phoneErrorMessage ? form.phoneErrorMessage : ""}
                error={form.phoneErrorMessage ? true : false}
                fullWidth
                margin='dense'
                value={form.phone}
              /> */}

            </Grid>
          </Grid>
          {form.notComplete
            ? <Grid container direction="row" justify="center" alignItems='center' >
              <FormHelperText error >{FormatText('err_submit')}</FormHelperText>
            </Grid>
            : null
          }
        </center>
      </DialogContent>

      <DialogActions>
        <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
          <Grid container item direction="row" justify='center' alignItems='center' md={6} sm={6} xs={6}>
            <Button variant='outlined' color="primary" onClick={handleCancel}>
              {FormatText('app_cancel')}
            </Button>
          </Grid>
          <Grid container item direction="row" justify='center' alignItems='center' md={6} sm={6} xs={6}>
            <Button variant='contained' color="primary" onClick={handleSave}>
              {FormatText('common_add')}
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  )
}

export default AddClientDialog
