
import React, { Component } from 'react'
import JwtDecoder from 'jwt-decode';
import Grid from '@material-ui/core/Grid'
import KlinikHome from './KlinikHomeScreen'
import VoltrasHome from './VoltrasHomeScreen'
import { menuCreator, AppStructures } from "../../utils/AppStructures";

class HomeScreen extends Component {
  constructor(props) {
    super(props)
    this.state = {
      voucherId: "",
      voucherIdErrorMessage: "",
      isError: true,
      submitErrorMessage: "",
      isQrShow: false,
      result: 'no result',
      aud: '',
    }
  }

  UNSAFE_componentWillMount() {
    let domain = window.location.origin
    const { _setUserMenu, _setMenuCollapseStatus, _setAppConstant, _setUserInfo, _setOpenScreen,
      _setUserSessionInfo, _setRoleUsed, history } = this.props

    // console.log("HOME INI WILL MOUNT...!", this.props)
    let accessToken = localStorage.getItem('accessToken')
    let idToken = localStorage.getItem('idToken')
    let key = localStorage.getItem('key')
    let aud = localStorage.getItem('aud')
    let roleUsed = localStorage.getItem('roleUsed')
    if (accessToken && idToken && key && aud && roleUsed) {
      this.setState({ ...this.state, aud })
      let app = AppStructures.filter(item => item.aud === aud)
      let newMenu = menuCreator(domain, aud, roleUsed)
      let obj = {}
      if (newMenu.length > 0) newMenu.map(item => {
        return obj = {
          ...obj,
          [item.url]: false,
        }
      })
      document.title = app[0].title
      _setAppConstant(app[0])
      _setMenuCollapseStatus(obj)
      _setUserMenu(newMenu)
      _setUserSessionInfo({ accessToken, key })
      _setUserInfo(JwtDecoder(idToken))
      _setRoleUsed(roleUsed)
      _setOpenScreen("Home")
    }
    else {
      history.push('/Login')
    }
  }

  render() {
    return (
      <Grid container direction="row" justify="center" alignItems="center" >
        {
          this.state.aud
            ? this.state.aud === 'voltras-health'
              ? <KlinikHome { ...this.props } />
              : <VoltrasHome { ...this.props } />
            : null
        }
      </Grid>
    )
  }
}

export default HomeScreen