
import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux'
import { Grid } from '@material-ui/core';
import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole,
} from '../../../actions/ServiceActions'

import UserRUDTable from './UserRUDTable'
import UserDetail from './UserDetail'
import ConfirmationDialogRaw from './ConfirmationDIalog'
import { setUserFilteredList } from '../../../actions/UserListActions';

const UserList = ({ ...props }) => {
  const { _setOpenScreen, _setCompleteRequest, _setUserFilteredList, clientList,
    userInfo, userList, updateUser } = props
  const [open, setOpen] = useState(false);
  const value = 'Soft Delete'
  const [requestDeleteUser, setRequestDeleteUser] = useState({})
  const [userName, setUserName] = useState('')

  useEffect(() => {
    // console.log("KOK DUA KALI: ", userList)
    const { name, email, phone } = userInfo
    // let name = userInfo.name
    // let email = userInfo.email
    // let phone = userInfo.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    _setOpenScreen("User List")
    let requestAllUser = {
      request: {},
      method: "GET",
      serviceCode: "SELECT_ALL_USER",
      customHeaders
    }
    if (userList.length < 1) _setCompleteRequest(requestAllUser)
  }, [_setOpenScreen, userList.length, _setCompleteRequest, userInfo])

  const [values, setValues] = useState({
    columns: [
      { title: 'Name', field: 'name', },
      { title: 'Email', field: 'email', },
      { title: 'Phone', field: 'phone', },
      { title: 'isVerified', field: 'isverified', lookup: { '01': 'False', '02': 'True' } },
    ],
    data: [],
    dataToProcess: null,
  });

  const [states, setStates] = useState({
    open: false,

  })

  useEffect(() => {
    // let aud = ''
    // if (userInfo.aud) aud = userInfo.aud
    // else if (localStorage.getItem('aud')) aud = localStorage.getItem('aud')
    // if (aud === 'authentication-backoffice') aud = ''

    // let role = ''
    // if (userInfo.role && userInfo.role.includes(roleAuth.endUser)) role = roleAuth.endUser
    // if (userInfo.role && userInfo.role.includes(roleAuth.admin)) role = roleAuth.admin
    // if (userInfo.role && userInfo.role.includes(roleAuth.root)) role = roleAuth.root
    // console.log("DISINIKAH")
    const {name, email, phone} = userInfo
    // let name = userInfo.name
    // let email = userInfo.email
    // let phone = userInfo.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let requestAllClient = {
      request: {},
      method: "GET",
      serviceCode: "SELECT_ALL_CLIENT",
      // urlParams: '/clients' + (aud ? '/' + aud : ''),
      customHeaders
    }
    if (clientList.length < 1 && userInfo.email) {
      setTimeout(() => {
        _setCompleteRequest(requestAllClient)
      }, 100);
    }
    let newList = [...userList]
    newList.forEach(item => {
      item.name = item.basicInformation.name
      item.isverified = (item.phoneVerified || item.emailVerified) ? '02' : '01'
    })
    setValues(oldVal => ({
      ...oldVal,
      data: newList
    }))
  }, [userList, userInfo, _setCompleteRequest, clientList.length])

  useEffect(() => {
    if (clientList[0]) {
      clientList.forEach(item => {
        item.originURIs = Array.isArray(item.originURIs) ? item.originURIs.join(', ') : item.originURIs
        item.roleDetails = item.roleDetails ? item.roleDetails : Array.isArray(item.clientRoles) ? item.clientRoles : ""
        item.clientRoles = item.roleDetails
      })
    }
  }, [clientList])

  useEffect(() => {
    let newList = [...userList]
    newList.forEach(item => {
      item.name = item.basicInformation.name
      item.isverified = (item.phoneVerified || item.emailVerified) ? '02' : '01'
    })
    setValues(oldVal => ({
      ...oldVal,
      data: newList
    }))

  }, [updateUser, userList])

  const handleUpdate = (newData, oldData) => new Promise((resolve, reject) => {
    if (oldData) {
      setValues((prevState) => {
        const data = [...prevState.data];
        data[data.indexOf(oldData)] = newData;
        return { ...prevState, data };
      })
      resolve()
    }
  })

  const handleDelete = oldData => new Promise((resolve, reject) => {
    let userId = oldData.userId
    let request = {}
    let method = 'DELETE'
    let serviceCode = 'DELETE_USER'
    let urlParams = `/users/${userId}`

    let completeRequest = { request, method, serviceCode, urlParams }
    setRequestDeleteUser(completeRequest)
    setUserName(oldData.name)
    setOpen(true)
    resolve()
  })

  const handleChoose = (val) => {
    if (val === 'Soft Delete') {
      props._setCompleteRequest(requestDeleteUser)
    }
    else {
      requestDeleteUser.request.isForceDelete = true
      props._setCompleteRequest(requestDeleteUser)
    }
  }

  const handleClose = () => {
    setOpen(false);
  };

  const handleRowClicked = rowData => {
    setValues({ ...values, dataToProcess: rowData })
    setStates({ ...states, open: true })
  }

  const sendUpdateRequest = completeRequest => {
    _setCompleteRequest(completeRequest)
  }

  return (
    <div >
      {states.open
        ? <UserDetail
          clientList={clientList}
          open={states.open}
          handleClose={() => setStates({ ...states, open: false })}
          data={values.dataToProcess}
          sendUpdateRequest={sendUpdateRequest}
          updateUser={updateUser}
          _setUserFilteredList={_setUserFilteredList}
        />
        : <Grid container direction="row" justify="center" alignItems="flex-start" >
          <UserRUDTable
            confirmdeletetext="Apakah anda yakin menghapus data ini?"
            title="Backoffice User List"
            columns={values.columns}
            data={values.data}
            updatedata={handleUpdate}
            deletedata={handleDelete}
            rowClicked={handleRowClicked}
            action
          />

          < ConfirmationDialogRaw
            id="ringtone-menu"
            keepMounted
            open={open}
            onClose={handleClose}
            value={value}
            handleChoose={handleChoose}
            userName={userName}
          />
        </Grid>}
    </div>
  );
}

const mapStateToProps = store => ({
  userList: store.userList.list,
  userDetail: store.userList.detail,
  userFiltered: store.userList.filteredList,
  userInfo: store.userInfo.info,
  clientList: store.clientList.list,
  roleAuth: store.system.roleAuth,
  updateUser: store.userList.filteredList
})

const mapDispatchToProps = dispatch => ({
  _setOpenScreen: val => dispatch(setOpenScreen(val)),
  _setUserFilteredList: val => dispatch(setUserFilteredList(val)),
  _setCompleteRequest: (val) => {
    dispatch(setServiceRequest(val.request))
    dispatch(setServiceMethod(val.method))
    dispatch(setServiceCode(val.serviceCode))
    dispatch(setServiceParams(val.urlParams))
    dispatch(setServiceCustomHeaders(val.customHeaders))
    dispatch(setServiceRole(val.serviceRole))
    dispatch(setServiceStatus("active"))
  },
})

export default connect(mapStateToProps, mapDispatchToProps)(UserList)
