
import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const UpdateUserDialog = ({ ...props }) => {
  const [roleList, setRoleList] = useState([])
  const [available, setAvailable] = useState([])
  const [newRoleList, setNewRoleList] = useState([])

  const handleClose = () => {
    props.handleOpen(false)
  };

  let clientDetail = props.clientList && props.dataToUpdate ? props.clientList.filter(item => item.client === props.dataToUpdate.client) : []
  let clientName = clientDetail[0] ? clientDetail[0].client : ''

  const callbackUseEffect = () => {
    setRoleList([])
    setAvailable([])
    setNewRoleList([])
    let newList = []

    let propRoleList = props.dataToUpdate ? props.dataToUpdate.resourcesAllowedList ? props.dataToUpdate.resourcesAllowedList : ['none'] : ['none']
    if (!Array.isArray(propRoleList)) propRoleList = [propRoleList]
    setRoleList(propRoleList)
    if (props.dataToUpdate && !props.dataToUpdate.roleList) {
      let insertAvailable = props.clientList.filter(item => item.client === props.dataToUpdate.client)
      newList = insertAvailable[0].roleList
    }
    if (propRoleList[0] && clientDetail[0]) {
      clientDetail[0].roleList.map(item => {
        if (!propRoleList.includes(item)) newList.push(item)
        return null
      })
    }
    setAvailable(newList)
  }
  useEffect(callbackUseEffect, [props.dataToUpdate])
 
  const handleAddItemRole = item => value => {
    setNewRoleList([...newRoleList, item])
    let newList = available.filter(items => items !== item)
    setAvailable(newList)
  }

  const handleRemoveItemRole = item => value => {
    setAvailable([...available, item])
    let newList = newRoleList.filter(items => items !== item)
    setNewRoleList(newList)
  }

  const handleRemoveRole = item => value => {
    setAvailable([...available, item])
    let newList = roleList.filter(items => items !== item)
    setRoleList(newList)
  }

  const handleSave = () => {
    let saveRoleList = roleList.concat(newRoleList)
    let dataToSave = props.dataToUpdate
    dataToSave.roleList = saveRoleList.join(', ')
    dataToSave.resourcesAllowedList = saveRoleList
    props.handleSave(dataToSave)
    props.handleOpen(false)
  }

  return (
    <Dialog fullWidth maxWidth={'sm'} open={props.open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle id="max-width-dialog-title" onClose={handleClose}>Client: {clientName}</DialogTitle>
      <DialogContent dividers>
        <Grid container direction="row" justify="flex-start" alignItems='flex-start' >
          <Grid item md={6}>
            <Typography variant='subtitle2'>Role Name</Typography>
            <Typography variant='caption' color='textSecondary'>Click to remove role</Typography>
            <List dense >
              {
                roleList.map((item, i) => {
                  if (item === 'none') {
                    return <ListItemText primary={'- none'} key={i} />
                  }
                  else {
                    return <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                      <ListItemIcon>
                        <Checkbox
                          edge="start"
                          id={item}
                          value={item}
                          checked
                          onChange={handleRemoveRole(item)}
                        />
                      </ListItemIcon>
                      <ListItemText primary={item} />
                    </Grid >
                  }
                })
              }
              {
                newRoleList[0] ? newRoleList.map((item, i) => (
                  <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                    <ListItemIcon>
                      <Checkbox
                        edge="start"
                        id={item}
                        value={item}
                        checked
                        onChange={handleRemoveItemRole(item)}
                      />
                    </ListItemIcon>
                    <ListItemText secondary={item} />
                  </Grid >
                ))
                  : roleList[0]
                    ? <div></div>
                    : <ListItemText primary={'- none'} />
              }
            </List>
          </Grid>

          <Grid item md={6}>
            <Typography variant='subtitle2' >Available Role</Typography>
            <Typography variant='caption' color='textSecondary'>Click to add role</Typography>
            <List dense >
              {available[0] ? available.map((item, i) => (
                <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                  <ListItemIcon>
                    <Checkbox
                      edge="start"
                      onChange={handleAddItemRole(item)}
                      checked={false}
                    />
                  </ListItemIcon>
                  <ListItemText secondary={item} />
                </Grid >
              )) : <ListItemText primary={'- none'} />}
            </List>
          </Grid>

        </Grid>
      </DialogContent>

      <DialogActions>
        <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button onClick={handleClose} variant='outlined' color="primary">
              Cancel
          </Button>
          </Grid>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button onClick={handleSave} variant='contained' color="primary">
              Save
          </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  )
}

export default UpdateUserDialog
