
const initialstate = {
  info: {},
  sessionInfo: {},
  otpId: '',
  roleUsed: '',
  resetPasswordCode: '',
  verificationCode: '',
}

const userInfo = (state = initialstate, action) => {
  switch (action.type) {
    case 'SET_USER_INFO': return { ...state, info: { ...state.info, ...action.val } }
    case 'SET_USER_SESSION_INFO': return { ...state, sessionInfo: action.val }
    case 'SET_USER_OTP_ID': return { ...state, otpId: action.val }
    case 'SET_ROLE_USED': return { ...state, roleUsed: action.val }
    case 'RESET_PASSWORD_CODE': return { ...state, resetPasswordCode: action.val }
    case 'SET_VERIFICATION_CODE': return { ...state, verificationCode: action.val }
    case 'LOGOUT': return initialstate
    default: return state
  }
}

export default userInfo
