
import React, { Component } from 'react';
import { connect } from 'react-redux'
import clsx from 'clsx';
import { withStyles } from '@material-ui/core/styles';
import { Paper, Avatar, Chip } from '@material-ui/core';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import CssBaseline from '@material-ui/core/CssBaseline';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import LinearProgress from '@material-ui/core/LinearProgress'
import Grid from '@material-ui/core/Grid'
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

import Routing from '../../router/Routing'
import { setMenuCollapseStatus, toggleCollapseMenu } from "../../actions/MenuActions";
import { dispatchLogout, setMenuClickUrl } from '../../actions/SystemActions'
import { setUserMenu } from "../../actions/MenuActions"
import {
  setServiceRole, setServiceRequest, setServiceCode, setServiceCustomHeaders,
  setServiceStatus, setServiceMethod, setServiceParams
} from '../../actions/ServiceActions'
import { openMenuStructure } from "../../utils/MenuStructure";
import SnackbarFeed from '../snackBar/SnackBar'
import OtpDialog from '../dialog/OtpDialog'
import FormatText from '../../i18n'
import Config from '../../config'

const mapStateToProps = state => {
  return {
    store: state.system,
    menuItems: state.menu.menuItems,
    menu: state.menu,
    userInfo: state.userInfo,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setMenuCollapseStatus: val => {
      dispatch(setMenuCollapseStatus(val))
    },
    _setMenuClickUrl: val => {
      dispatch(setMenuClickUrl(val))
    },
    _toggleCollapseMenu: val => {
      dispatch(toggleCollapseMenu(val))
    },
    _logout: () => {
      dispatch(dispatchLogout())
      dispatch(dispatch(setUserMenu(openMenuStructure)))
    },
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.params))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.role))
      dispatch(setServiceStatus("active"))
    },
  }
}

class NavBarRightMenu extends Component {
  constructor(props) {
    super(props)
    this.state = {
      open: false,
      mainHidden: false,
      openLogout: false,
      dialogOpen: false,
      otpID: ""
    }
  }

  // UNSAFE_componentWillMount() {
  //   console.log('NAV WILL MOUNT: ', this.props.store)
  // }

  static getDerivedStateFromProps(props, state) {
    // console.log("STATIC PROPS: ", props, state)
    if (props.store.otpID && (props.store.otpID !== state.otpID)) {
      return {
        ...state,
        dialogOpen: true,
        otpID: props.store.otpID
      }
    }
    // Return null to indicate no change to state.
    return null;
  }

  handleDrawerOpen = () => {
    this.setState({ open: true, mainHidden: true })
  };

  handleDrawerClose = () => {
    this.setState({ open: false, mainHidden: false })
  };

  handleDrawerClick = (menuItem) => {
    let windowscreen = window.screen.width
    let screen = menuItem.url
    // console.log("screen : ",screen)
    this.props._setMenuClickUrl(screen)
    if (!menuItem.children[0]) {
      if (screen === "Logout") {
        this.setState({
          ...this.state,
          openLogout: true
        })
      }
      else {
        // TOGGLE MENU BUTTON
        if (windowscreen < 1000) {
          this.setState({
            open: !this.state.open,
            mainHidden: !this.state.mainHidden,
          })
          this.props.history.push(`/${screen}`)
        } else {
          this.props.history.push(`/${screen}`)
        }
      }
    }
    else {
      // Uncoment to make submenu toggle collapse
      Object.entries(this.props.menu).forEach(([key, val]) => {
        if (key !== 'menuItems') this.props._toggleCollapseMenu({ name: key, value: false })
      })

      let val = { name: screen, value: !this.props.menu[screen] }
      this.props._toggleCollapseMenu(val)
    }
  }

  handleClickOpen = () => {
    this.setState({
      ...this.state,
      openLogout: true
    })
  };

  handleClose = () => {
    this.setState({
      ...this.state,
      openLogout: false
    })
  };

  handleMainClick = () => {
    let windowscreen = window.screen.width
    if (windowscreen < 1000 && this.state.mainHidden) {
      this.setState({
        open: !this.state.open,
        mainHidden: !this.state.mainHidden,
      })
    }
  }

  handleLogout = () => {
    // let userRole = this.props.userInfo.info.userRole
    // this.props.history.push('/Home')
    const logoutRequest = {
      request: { "redirectURI": "http://login.page" },
      method: "POST",
      serviceCode: "logout",
    }
    localStorage.clear()
    this.props._setCompleteRequest(logoutRequest)
    this.setState({
      ...this.state,
      openLogout: false,
      open: false
    })
  }

  handleDialogClose = () => {
    // console.log("dipanggil tak")
    this.setState({
      ...this.state,
      dialogOpen: !this.state.dialogOpen
    })
  }

  handleSubmitOtp = fullRequest => {
    // console.log(fullRequest)
    this.props._setCompleteRequest(fullRequest)
  }

  handleClick = () => {
    // console.log('Clicked')
    this.props.history.push('UserProfile')
  }

  render() {
    const { classes, store, menu, menuItems, userInfo } = this.props;
    const loginStyle = {
      display: 'flex',
      backgroundImage: `url(${store.appConstant && store.openScreen === 'Login'
        ? store.appConstant.backgroundLogin
        : Config.gatewayRepoFile + '/static-file-manager/retrieve?file=voltras_login_background'})`,
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundAttachment: 'fixed',
      // position: "fixed",
      top: 0,
      left: 0,
      width: '100%',
      // height: '100%',
    }
    const defaultStyle = {
      display: 'flex',
      backgroundImage: `url(${store.appConstant ? store.appConstant.backgroundApp : ""})`,
      backgroundSize: 'cover',
      backgroundRepeat: 'no-repeat',
      backgroundAttachment: 'fixed',
      // position: "fixed",
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
    }

    return (
      <div style={store.openScreen === 'Login' ? loginStyle : defaultStyle}
      >
        <CssBaseline />
        {
          store.openScreen === 'Login'
            ? null
            : <AppBar
              position="fixed"
              className={clsx(classes.appBar, {
                [classes.appBarShift]: this.state.open,
              })}
            >
              <Toolbar>

                {store.openScreen === 'Home'
                  ? <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                    <i className="fas fa-clinic-medical"></i>
                  </IconButton>
                  : store.openScreen === 'Order List'
                    ? <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                      <i className="fas fa-file-medical"></i>
                    </IconButton>
                    : <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                      <i className="fas fa-laptop-house"></i>
                    </IconButton>

                }
                <Typography variant="h6" noWrap className={classes.title}>
                  {store.openScreen ? store.openScreen : "Clinic Backoffice"}
                </Typography>

                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  edge="end"
                  onClick={this.handleDrawerOpen}
                  className={clsx(this.state.open && classes.hide)}
                >
                  <MenuIcon />
                </IconButton>

              </Toolbar>
              {store.serviceStart ? <LinearProgress color="secondary" /> : null}
            </AppBar>
        }
        <main
          onClick={this.handleMainClick}
          className={clsx(classes.content, {
            [classes.contentShift]: this.state.open,
          })}
          style={store.openScreen === 'Login' ? { marginRight: 0 } : null}
        >
          {/* <Hidden xsDown={this.state.mainHidden}> */}
          <Grid style={{ marginTop: 50 }}>
            <OtpDialog
              handleDialogClose={this.handleDialogClose}
              open={this.state.dialogOpen}
              otpID={this.state.otpID}
              phone={userInfo.info.phone}
              clientId={userInfo.info.aud}
              handleSubmitOtp={this.handleSubmitOtp}
            />
            <Routing />
            <SnackbarFeed history={this.props.history} />
          </Grid>
          {/* </Hidden> */}
        </main>
        {
          store.openScreen === 'Login'
            ? null
            : <Drawer
              className={classes.drawer}
              variant="persistent"
              anchor="right"
              open={this.state.open}
              classes={{
                paper: classes.drawerPaper,
              }}
            >
              <List>
                <ListItem>
                  <br />
                  <Paper elevation={0} style={{ width: 240, }} >
                    <Grid container direction="row" justify="center" alignItems="center"  >
                      <Grid container direction="column" justify="center" alignItems="center"  >
                        <Grid item onClick={this.handleDrawerClose} >
                          <Avatar
                            style={{ width: 75, height: 75, marginBottom: 5 }}
                            src={userInfo.info.picture ? userInfo.info.picture : ''}
                          />
                        </Grid>
                        <Grid item  >
                          <center><Typography variant='subtitle2' >{userInfo.info ? userInfo.info.name : ''}</Typography></center>
                        </Grid>
                        <Grid item >
                          <center>
                            <Typography variant='caption' >{userInfo.info ? userInfo.info.email || userInfo.info.phone : ''}</Typography>
                          </center>
                        </Grid>
                        <Grid item style={{ marginTop: 15 }} >
                          <Chip onClick={this.handleClick} label={FormatText('app_manage_acount')} variant="outlined" size="small" />
                        </Grid>
                      </Grid>
                    </Grid>
                  </Paper>
                  <br />
                </ListItem>
              </List>
              <Divider />

              {
                menuItems && menuItems.map((menuItem, i) => {
                  return <div key={i}>
                    <List>
                      <ListItem button onClick={() => this.handleDrawerClick(menuItem)} >
                        <Typography style={{ fontSize: "18px" }}>{menuItem.label}</Typography>
                      </ListItem>
                    </List>
                    {
                      menu[menuItem.url]
                        ? <div>
                          {
                            menuItem.children.map((childItem, i) => {
                              return childItem
                                ? <div key={i}>
                                  <List style={{ marginLeft: 15, }}>
                                    <ListItem button onClick={() => this.handleDrawerClick(childItem)} key={i}>
                                      <ChevronRightIcon />
                                      <ListItemText primary={childItem.label} />
                                    </ListItem>
                                  </List>
                                </div>
                                : null
                            })
                          }
                        </div>
                        : null
                    }

                    <Divider />
                  </div>
                })
              }
              <Grid style={{ marginTop: 10 }} container direction="column" justify="center" alignItems="center"  >
                <Typography variant="caption" color='textSecondary' >version {Config.appVersion}</Typography>
                <Typography variant="caption" color='textSecondary' >Copyright  2020, voltras.net</Typography>
              </Grid>
            </Drawer>
        }

        <Dialog
          open={this.state.openLogout}
          onClose={this.handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{FormatText('app_logout')}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">{FormatText('app_confirm_logout')}?
          </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              {FormatText('app_cancel')}
            </Button>
            <Button onClick={this.handleLogout} color="primary" autoFocus>
              {FormatText('app_logout')}
            </Button>
          </DialogActions>
        </Dialog>

      </div>
    )
  }
}

const drawerWidth = 240;
const useNavStyles = (theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginRight: drawerWidth,
  },
  title: {
    flexGrow: 1,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-start',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginRight: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    [theme.breakpoints.up('md')]: {
      marginRight: 0,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
});

const Navigation = connect(mapStateToProps, mapDispatchToProps)(NavBarRightMenu)

export default withStyles(useNavStyles)(Navigation)
