import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
// import TableContainer from '@material-ui/core/TableContainer';
import TableFooter from '@material-ui/core/TableFooter';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';
import EditIcon from '@material-ui/icons/Edit';
import Typography from '@material-ui/core/Typography';
import Toolbar from '@material-ui/core/Toolbar';
import Tooltip from '@material-ui/core/Tooltip'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem'
import Grid from '@material-ui/core/Grid'


const useStyles = makeStyles((theme) => ({
    root: {
        // overflowX: 'auto',
        // marginRight: 'auto',
        // marginLeft: 'auto',
        // marginTop: '50px',
        // padding: '10px',
        // margin: '10px',
        width: '100%'
    },
    container: {
        maxHeight: 440,
    },
    table: {
        minWidth: 500,
        // whiteSpace:"noWrap"
    },
    rootToolbar: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
}));

const TableCustomize = ({ ...props }) => {
    const column = props.column
    let data = props.data
    data.map(item => {
        column.map(items => {
            if (typeof item[items.field] === 'boolean') {
                if (item[items.field]) item[items.field] = 'True'
                else item[items.field] = 'False'
            }
            return null
        })
        return null
    })


    useEffect(() => {
        // let indexClient = 0
        // data.map((item, index) => {
        //     if (item.client) {
        //         indexClient = index
        //     }
        //     return null
        // })
        // console.log('index',indexClient)
        // data.splice(0, indexClient + 1)
        setDataSearch(data)
        return () => {
            setFilterValue('')
        }
    }, [data])
    const classes = useStyles();
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);
    // const [search, setSearch] = useState()
    const [dataSearch, setDataSearch] = useState([])
    const [filterValue, setFilterValue] = useState('')

    const emptyRows = rowsPerPage - Math.min(rowsPerPage, dataSearch.length - page * rowsPerPage);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const handleGetData = (item) => {
        props.handleEditOpen(true, true, item)
    }

    const handleSearch = (e) => {
        if (e.target.value) {
            setDataSearch(
                data.filter(item => item.clientId === e.target.value)
            )
            setFilterValue(e.target.value)
            setPage(0);
        }
        else {
            setDataSearch(
                data
            )
            setFilterValue(e.target.value)
        }
    }
    const findDuplicateClientId = (arrayOfObj, key) => {
        return arrayOfObj.filter((item, index, array) => {
            return array.map((mapItem) => mapItem[key]).indexOf(item[key]) === index
        })
    }
    // data.filter(item=>item.clientId===search)
    // console.log('datawkwkw',data)
    return (
        <Paper className={classes.root}>
            <Toolbar>
                <Grid container >
                    <Grid item sm={10}>
                        <Typography className={classes.title} variant="h6" id="tableTitle" component="div">
                            {props.title}
                        </Typography>
                    </Grid>
                    <Grid item sm={2}>
                        {props.options ? props.options.filterClientId ?
                            <form className={classes.rootForm} noValidate autoComplete="off">

                                <TextField
                                    label='filter By Client Id'
                                    fullWidth
                                    placeholder='ClientId'
                                    onChange={handleSearch}
                                    value={filterValue}
                                    select
                                    InputLabelProps={{
                                        shrink: true
                                    }}
                                >
                                    <MenuItem key={null} value={null}>
                                        All
                                    </MenuItem>
                                    {findDuplicateClientId(data, 'clientId').map(item =>
                                        <MenuItem key={item.clientId} value={item.clientId}>
                                            {item.clientId}
                                        </MenuItem>
                                    )}
                                </TextField>
                            </form> : '' : <form className={classes.rootForm} noValidate autoComplete="off">

                                <TextField
                                    label='filter By Client Id'
                                    fullWidth
                                    placeholder='ClientId'
                                    onChange={handleSearch}
                                    value={filterValue}
                                    select
                                    InputLabelProps={{
                                        shrink: true
                                    }}
                                >
                                    <MenuItem key={null} value={null}>
                                        All
                                    </MenuItem>
                                    {findDuplicateClientId(data, 'clientId').map(item =>
                                        <MenuItem key={item.clientId} value={item.clientId}>
                                            {item.clientId}
                                        </MenuItem>
                                    )}
                                </TextField>
                            </form>
                        }
                    </Grid>

                </Grid>
            </Toolbar>
            {/* <TableContainer component='paper'> */}
            <Table className={classes.table} size="small" aria-label="a dense table">
                <TableHead>
                    <TableRow>
                        {column.map((item, index) =>
                            (
                                <TableCell key={index} align='left'>
                                    {item.title}
                                </TableCell>
                            )
                        )}
                        <TableCell align='right'>
                            Actions
                        </TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {(rowsPerPage > 0
                        ? dataSearch.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                        : dataSearch).map((item, index) =>
                            (<TableRow key={index}>
                                {column.map((item1, index1) =>
                                    (<TableCell key={index1} align='left'>
                                        {item[item1.field]}
                                    </TableCell>)
                                )}
                                <TableCell align='right'>
                                    <Tooltip title='Edit'>
                                        <EditIcon onClick={() => handleGetData(item)} style={{cursor:'pointer'}}/>
                                    </Tooltip>
                                </TableCell>
                            </TableRow>)
                        )}
                    {emptyRows > 0 && (
                        <TableRow style={{ height: 53 * emptyRows }}>
                            <TableCell colSpan={6} />
                        </TableRow>
                    )}
                </TableBody>
                {props.options ? props.options.pagination ? <TableFooter>
                    <TableRow>
                        <TablePagination
                            rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                            colSpan={6}
                            count={dataSearch.length}
                            rowsPerPage={rowsPerPage}
                            page={page}
                            SelectProps={{
                                inputProps: { 'aria-label': 'rows per page' },
                                native: true,
                            }}
                            onChangePage={handleChangePage}
                            onChangeRowsPerPage={handleChangeRowsPerPage}
                            ActionsComponent={TablePaginationActions}
                        />
                    </TableRow>
                </TableFooter> : "" : <TableFooter>
                        <TableRow>
                            <TablePagination
                                rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                                colSpan={6}
                                count={dataSearch.length}
                                rowsPerPage={rowsPerPage}
                                page={page}
                                SelectProps={{
                                    inputProps: { 'aria-label': 'rows per page' },
                                    native: true,
                                }}
                                onChangePage={handleChangePage}
                                onChangeRowsPerPage={handleChangeRowsPerPage}
                                ActionsComponent={TablePaginationActions}
                            />
                        </TableRow>
                    </TableFooter>}

            </Table>
            {/* </TableContainer> */}
        </Paper>

    )
}

export default TableCustomize

const useStyles1 = makeStyles((theme) => ({
    root: {
        flexShrink: 0,
        marginLeft: theme.spacing(2.5),
    },
}));

function TablePaginationActions(props) {
    const classes = useStyles1();
    const theme = useTheme();
    const { count, page, rowsPerPage, onChangePage } = props;

    const handleFirstPageButtonClick = (event) => {
        onChangePage(event, 0);
    };

    const handleBackButtonClick = (event) => {
        onChangePage(event, page - 1);
    };

    const handleNextButtonClick = (event) => {
        onChangePage(event, page + 1);
    };

    const handleLastPageButtonClick = (event) => {
        onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };

    return (
        <div className={classes.root}>
            <IconButton
                onClick={handleFirstPageButtonClick}
                disabled={page === 0}
                aria-label="first page"
            >
                {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
            </IconButton>
            <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
                {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            </IconButton>
            <IconButton
                onClick={handleNextButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="next page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </IconButton>
            <IconButton
                onClick={handleLastPageButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="last page"
            >
                {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
            </IconButton>
        </div>
    );
}

TablePaginationActions.propTypes = {
    count: PropTypes.number.isRequired,
    onChangePage: PropTypes.func.isRequired,
    page: PropTypes.number.isRequired,
    rowsPerPage: PropTypes.number.isRequired,
};