const mapStateToProps = state => {
  // console.log("GLOBAL STATE: ", state)
  return {
    userInfo: state.userInfo
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setUserMenu: val => dispatch(setUserMenu(val)),
    _setAppConstant: val => dispatch(setAppConstant(val)),
    _setLocale: val => dispatch(setLocale(val)),
    _setResetPasswordCode: val => dispatch(setResetPasswordCode(val)),
    _setVerificationCode: val => dispatch(setVerificationCode(val)),
  }
}

class App extends Component {
  // constructor(props) {
  //   super(props)
  //   this.state = {
  //     open: true,
  //     setOpen: false
  //   }
  // }

  UNSAFE_componentWillMount() {
    function isMobile() {
      return ((window.innerWidth <= 800) && (window.innerHeight <= 600));
    }
    const { screen } = window
    if (!navigator.xr && isMobile() && screen.orientation && screen.orientation.lock) {
      screen.orientation.lock('landscape');
    }
    // console.log(
    //   "WILL MOUNT APP: ", window.location.origin
    //   , ', Language: ', navigator.language
    //   , ', HREF: ', window.location.href
    // )

    let locationHref = window.location.href
    // console.log("resetpasswordcode: ", locationHref.split('/').slice(-1)[0])
    if (locationHref.split('/')[3] && (locationHref.split('/')[3] === 'reset-password-recovery')) {
      this.props._setResetPasswordCode(locationHref.split('/').slice(-1)[0])
    }
    if (locationHref.split('/')[3] && (locationHref.split('/')[3] === 'verification-code')) {
      this.props._setVerificationCode(locationHref.split('/').slice(-1)[0])
    }
    // console.log(locationHref.split('/'))
    // console.log(locationHref.split('/')[3])
    // console.log(locationHref.split('/').slice(-1)[0])
    let origin = window.location.origin
    let app = AppStructures.filter(item => item.domain.includes(origin))
    this.props._setAppConstant(app[0] ? app[0] : AppStructures[0])
    // document.title = app[0].title
    console.log("SETTING CONSTANTS: ", origin, app[0] ? app[0] : AppStructures[0])

    let language = navigator.language
    this.props._setLocale(language)

    let accessToken = localStorage.getItem('accessToken')
    if (accessToken) { this.props.history.push('/Home') }
    else {
      this.props._setUserMenu(openMenuStructure)
      this.props.history.push('/Login')
    }
  }

  render() {
    return (
      <div >
        <Navbar history={this.props.history} />
        <ServiceExecutor history={this.props.history} />
        {/* <GlobalDialog history={this.props.history} /> */}
        {/* <SnackBar history={this.props.history} /> */}
        <SnackBarError history={this.props.history} />
      </div>
    )
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));

