
import React, { useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField';
import { Grid, Typography, Button, Card, CardContent, CardActions } from '@material-ui/core'
import { FormValidation } from '../../../utils/FormValidation';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const UpdateUserDialog = ({ ...props }) => {
  // const [roleList, setRoleList] = useState([])
  // const [available, setAvailable] = useState([])
  // const [newRoleList, setNewRoleList] = useState([])
  const [showAdd, setShowAdd] = useState(true)
  const [state, setState] = useState({
    addRoleList: [],
  })

  const handleClose = () => {
    setState({ addRoleList: [], })
    props.handleOpen(false)
    setShowAdd(true)
  };

  const handleChange = props => e => {
    let valid = FormValidation(props, e.target.value)
    setState({
      ...state,
      [props]: e.target.value,
      [props + 'ErrorMessage']: valid.errorMessage,
    })
  }

  const handleAddClientRole = () => {
    let objRole = {}
    if (state.roleId && state.roleDescription) {
      objRole.roleId = state.roleId
      objRole.roleDescription = state.roleDescription
      setState({
        ...state,
        addRoleList: [...state.addRoleList, objRole],
        roleId: '',
        roleDescription: '',
      })
      setShowAdd(false)
      objRole = {}
    }
  }

  const handleSave = () => {
    props.handleSave(state.addRoleList)
    props.handleOpen(false)
    setShowAdd(true)
    setState({ ...state, addRoleList: [], })
  }

  return (
    <Dialog fullWidth maxWidth={'sm'} open={props.open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle
        id="max-width-dialog-title"
        onClose={handleClose}
      >
        Client: {props.data ? props.data.clientName : ''}
      </DialogTitle>
      <DialogContent dividers>
        <Grid container direction="row" justify="flex-start" alignItems='flex-start' >
          <Grid item md={6}>
            <Typography variant='subtitle2'>Role Id</Typography>
            <List dense >
              {
                state.addRoleList[0]
                  ? state.addRoleList.map((item, i) => (
                    <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                      <ListItemText primary={'- ' + item.roleId} />
                    </Grid >
                  ))
                  : <ListItemText secondary={'- none'} />
              }
            </List>
          </Grid>

          <Grid item md={6}>
            <Typography variant='subtitle2' >Description</Typography>
            <List dense >
              {
                state.addRoleList[0]
                  ? state.addRoleList.map((item, i) => (
                    <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                      <ListItemText primary={'- ' + item.roleDescription} />
                    </Grid >
                  ))
                  : <ListItemText secondary={'- none'} />
              }
            </List>
          </Grid>
          {
            showAdd
              ? <Card style={{ marginTop: 20 }} >
                <CardContent>
                  <Grid>
                    <Typography variant='subtitle2'>
                      Add RoleId and Description
                </Typography>
                  </Grid>
                  <Grid>
                    <TextField
                      autoFocus
                      margin="dense"
                      size="small"
                      onChange={handleChange('roleId')}
                      id="standard-roleId"
                      label="RoleId"
                      placeholder="RoleId"
                      value={state.roleId}
                      helperText={state.roleIdErrorMessage ? state.roleIdErrorMessage : ''}
                      error={state.roleIdErrorMessage ? true : false}
                    />
                  </Grid>
                  <Grid >
                    <TextField
                      size="small"
                      onChange={handleChange('roleDescription')}
                      id="standard-roleDesc"
                      label="Description"
                      placeholder="Role description"
                      value={state.roleDescription}
                      helperText={state.roleDescriptionErrorMessage ? state.roleDescriptionErrorMessage : ''}
                      error={state.roleDescriptionErrorMessage ? true : false}
                    />
                  </Grid>
                </CardContent>
                <CardActions>
                  <Button
                    disabled={state.roleId && state.roleDescription ? false : true}
                    variant='outlined'
                    size="small"
                    color="secondary"
                    onClick={handleAddClientRole}
                  >
                    Add
              </Button>
                </CardActions>
              </Card>
              : null
          }

        </Grid>
      </DialogContent>

      <DialogActions>
        <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button onClick={handleClose} variant='outlined' color="primary">
              Cancel
          </Button>
          </Grid>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button onClick={handleSave} variant='contained' color="primary">
              Save
          </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  )
}

export default UpdateUserDialog
