
import React, { useEffect, useState, } from 'react';
import { Grid, Typography, Button, TextField } from '@material-ui/core';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import LinearProgress from '@material-ui/core/LinearProgress';
import { withStyles } from '@material-ui/core/styles';
import { FormValidation } from '../../../utils/FormValidation';
import { MuiPickersUtilsProvider, KeyboardDatePicker, } from '@material-ui/pickers';
import FormatText from '../../../i18n'
import { yyyymmddWithParams, convertDateyyyymmdd, datetoanydaysafter, isDateBeforeNow } from '../../../utils/utils';
import SimpleAutocomplete from '../../../components/autocomplete/SimpleAutocomplete';
import Hidden from '@material-ui/core/Hidden'
import { SendToService } from '../../../services/ServiceClient';

let now = yyyymmddWithParams('now')

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const AddClientDialog = ({ ...props }) => {
  const { cityList, klinikList, _setCompleteRequest, _setSecondRequest, open, userInfo, item, roleUsed, classes,
    setKlinikList, setCityList, system } = props

  const [state, setState] = useState({
    city: "",
    product: {},
    klinikList: [],
    fromdate: now,
    fromdateErrorMessage: "",
    originDate: "",
    minDate: "",
    maxValidDate: "",
    email: "",
    emailErrorMessage: "",
    reason: "",
    reasonErrorMessage: "",
    clinic_id: 0,
    order_id: 0,
    item_id: 0,
    submitErrorMessage: "",
    new_clinic_data: {},
  })

  useEffect(() => {
    let input_date = state.fromdate
    let date_off = state.product.clinic ? state.product.clinic.vendor_close.date : []
    let day_off_resp = state.product.clinic ? state.product.clinic.vendor_close.day : []
    let day_off = day_off_resp.map(item => {
      if (item === 7) item = 0
      return item
    })
    let d = new Date(input_date)
    let n = d.getDay()
    let newClinic = parseInt(state.clinic_id)
    let new_clinic_data = state.new_clinic_data
    let new_date_off = new_clinic_data.vendor_close ? new_clinic_data.vendor_close.date : []
    let new_day_off_resp = new_clinic_data.vendor_close ? new_clinic_data.vendor_close.day : []
    let new_day_off = new_day_off_resp.map(item => {
      if (item === 7) item = 0
      return item
    })
    let person_product = state.product.product_name ? state.product.product_name : ""
    let new_product_list = new_clinic_data.product ? new_clinic_data.product.map(item => item.name) : []

    if (newClinic) {
      if (new_date_off.includes(input_date)) {
        setState(state => ({ ...state, submitErrorMessage: "New clinic close, please choose another date or clinic." }))
      }
      else if (new_day_off.includes(n)) {
        setState(state => ({ ...state, submitErrorMessage: "New clinic close, please choose another day or clinic." }))
      }
      else if (!new_product_list.includes(person_product)) {
        setState(state => ({ ...state, submitErrorMessage: "Product not available, please choose another clinic." }))
      }
      else setState(state => ({ ...state, submitErrorMessage: "" }))
    }
    else if (date_off.includes(input_date)) {
      setState(state => ({ ...state, submitErrorMessage: "Clinic close, please choose another date or clinic." }))
    }
    else if (day_off.includes(n)) {
      setState(state => ({ ...state, submitErrorMessage: "Clinic close, please choose another day or clinic." }))
    }
    else setState(state => ({ ...state, submitErrorMessage: "" }))
  }, [state.fromdate, state.clinic_id, state.product, state.city, state.new_clinic_data])

  useEffect(() => {
    let product = item
    if (product) {
      const { order_id, item_id, valid_until, payment, testdate } = product
      let minDate = isDateBeforeNow(payment.date)
        ? convertDateyyyymmdd(datetoanydaysafter(now, 1))
        : convertDateyyyymmdd(datetoanydaysafter(payment.date, 1))
      setState(prevState => ({
        ...prevState,
        // klinikList,
        order_id,
        item_id,
        product,
        minDate,
        fromdate: minDate,  //   convertDateyyyymmdd(datetoanydaysafter(testdate, 1)),
        originDate: testdate,
        maxValidDate: valid_until
      }))
    }
  }, [item])

  useEffect(() => {
    if (klinikList.length === 1) setState(prevState => ({
      ...prevState,
      klinikList,
      clinic_id: klinikList[0].id
    }))
  }, [klinikList])


  const handleClose = () => {
    props.handleClose()
  };

  const handleChange = prop => e => {
    e.preventDefault()
    let val = e.target.value
    let valid = FormValidation(prop, val)
    setState({
      ...state,
      [prop]: val,
      [prop + 'ErrorMessage']: val ? valid.errorMessage : "",
      notComplete: false
    })
  }

  const handleCancel = e => {
    e.preventDefault()
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone
    const cityRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_LIST",
      params: `/cities?&citykey=a`,
      customHeaders
    }
    let cityClinicRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_CLINIC_LIST",
      urlParams: `/cities/none/clinics`,
      customHeaders
    }
    _setCompleteRequest(cityRequest)
    _setSecondRequest(cityClinicRequest)

    const { payment } = item
    let minDate = isDateBeforeNow(payment.date)
      ? convertDateyyyymmdd(datetoanydaysafter(now, 1))
      : convertDateyyyymmdd(datetoanydaysafter(payment.date, 1))
    setState({
      ...state,
      // klinikList: [],
      // product: {},
      clinic_id: 0,
      city: "",
      submitErrorMessage: "",
      minDate,
      fromdate: minDate,
    })
    props.handleClose()
  }

  const handleSave = (e) => {
    e.preventDefault()
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone

    let requestReschedule = {
      testdate: state.fromdate,
      email: state.email ? state.email : null,
      clinic: state.clinic_id ? parseInt(state.clinic_id) : null,
      reason: state.reason ? state.reason : null,
    }

    let rescheduleRequest = {
      request: requestReschedule,
      method: "PATCH",
      serviceCode: "RESCHEDULE_ORDER",
      params: `/${state.order_id}/${state.item_id}`,
      customHeaders
    }

    if (state.fromdate && state.order_id && state.item_id) {
      _setCompleteRequest(rescheduleRequest)
      props.handleClose()
    }
    else setState({ ...state, submitErrorMessage: "Form error, please check your form" })

  }

  const handleDateChange = prop => date => {
    const newDate = new Date(date);
    if (newDate > datetoanydaysafter(item.valid_until, 0)) {
      setState({
        ...state,
        [prop + 'ErrorMessage']: "Reschedule date not valid",
        [prop]: convertDateyyyymmdd(date),
      })
    } else {
      setState({
        ...state,
        [prop + 'ErrorMessage']: "",
        [prop]: convertDateyyyymmdd(date),
      })
    }
  }

  const handlePick = city => {
    console.log("ON PICK CITY: ", city)
    setKlinikList([])
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone

    if (city && city.city) {
      let cityRequest = {
        request: {},
        method: "GET",
        serviceCode: "GET_CITY_CLINIC_LIST",
        params: `/cities/${city.city}/clinics`,
        customHeaders
      }
      console.log(cityRequest)
      setState({
        ...state,
        city: city.city,
        submitErrorMessage: '',
      })
      SendToService(
        {}, "GET", "GET_CITY_LIST", `/cities?&citykey=${city.city}`, customHeaders, null, null,
        response => {
          let body = response.body
          let newArr = []
          if (Array.isArray(body)) {
            if (body.length > 0) {
              body.forEach(item => {
                if (item.name) newArr.push(item)
                else newArr.push({ city: item })
              })
              setCityList(newArr)
            }
          }

          _setCompleteRequest(cityRequest)
        }
      )
    } else {
      let cityRequest = {
        request: {},
        method: "GET",
        serviceCode: "GET_CITY_CLINIC_LIST",
        params: `/cities/none/clinics`,
        customHeaders
      }
      _setCompleteRequest(cityRequest)
      setState({
        ...state,
        city: "",
        clinic_id: 0,
        submitErrorMessage: '',
      })
    }
  }

  const handleInputChange = (val, index, enterPressed) => {
    console.log("KOK BISA SEHH....? ", val)
    let customHeaders = { name: userInfo.name, email: userInfo.email, }
    if (userInfo.phone) customHeaders.phone = userInfo.phone

    let cityRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_LIST",
      params: `/cities?&citykey=${val}`,
      customHeaders
    }
    if (val && !enterPressed) {
      _setCompleteRequest(cityRequest)
    }
  }

  const handlePickClinic = city => {
    if (city && city.id) {
      setState({
        ...state,
        clinic_id: city.id,
        new_clinic_data: city,
        submitErrorMessage: '',
      })
    } else {
      setState({
        ...state,
        clinic_id: 0,
        new_clinic_data: {},
        submitErrorMessage: '',
      })
    }
  }

  const handleInputChangeClinic = val => {
  }

  return (
    <Dialog fullWidth maxWidth={'xs'} open={open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle id="max-width-dialog-title" onClose={handleClose}>{props.label}</DialogTitle>
      <LinearProgress
        style={{ display: system.serviceStart ? "" : "none" }}
        color="secondary"
      />
      <DialogContent dividers>
        <Hidden smDown>
          <Grid container direction="row" justify="flex-start" alignItems='flex-start' >
            <Grid item sm={12}>
              <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Clinic</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography ><b>{state.product.clinic ? state.product.clinic.name : ""}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Product Name</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography ><b>{state.product.product_name}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Test Date</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography ><b>{state.originDate}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Reschedule valid until</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography ><b>{state.maxValidDate}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Reschedule Date</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils} >
                    <Grid container >
                      <KeyboardDatePicker
                        autoOk
                        // minDate={datetoanydaysafter(state.originDate, 1)}
                        minDate={state.minDate}
                        maxDate={state.maxValidDate}
                        error={state.fromdateErrorMessage ? true : false}
                        helperText={state.fromdateErrorMessage ? state.fromdateErrorMessage : ""}
                        style={{ width: 150 }}
                        size="small"
                        clearable
                        format="yyyy/MM/dd"
                        margin="dense"
                        id="date-picker-from"
                        value={state.fromdate}
                        onChange={handleDateChange('fromdate')}
                        KeyboardButtonProps={{
                          'aria-label': 'change date',
                        }}
                        disablePast={true}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >User Email</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <TextField
                    fullWidth
                    // size="small"
                    placeholder="Email"
                    onChange={handleChange('email')}
                    value={state.email}
                    error={state.emailErrorMessage ? true : false}
                    helperText={state.emailErrorMessage ? state.emailErrorMessage : ""}
                  />
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Reschedule Reason</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <TextField
                    fullWidth
                    // size="small"
                    placeholder="Reschedule Reason"
                    multiline
                    onChange={handleChange('reason')}
                    value={state.reason}
                    error={state.reasonErrorMessage ? true : false}
                    helperText={state.reasonErrorMessage ? state.reasonErrorMessage : ""}
                  />
                </Grid>
              </Grid>

              <Grid style={{ visibility: roleUsed === 'voltras.admin' ? 'visible' : 'hidden' }}
                container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Change Clinic</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  {}
                  <SimpleAutocomplete
                    id="changeClinicChooseCity"
                    options={cityList[0] ? cityList : []}
                    optionItem={'city'}
                    notShrink={true}
                    handleInputChange={handleInputChange}
                    onPick={handlePick}
                  />
                </Grid>
              </Grid>
              <Grid style={{ visibility: roleUsed === 'voltras.admin' ? 'visible' : 'hidden' }}
                container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  {
                    (klinikList[0]) && (klinikList[0].name) && (klinikList.length > 1)
                      ? <SimpleAutocomplete
                        id="changeClinicChooseClinic"
                        options={klinikList}
                        optionItem={'name'}
                        notShrink={true}
                        handleInputChange={handleInputChangeClinic}
                        onPick={handlePickClinic}
                      />
                      : (klinikList[0]) && (klinikList[0].name) && (klinikList.length === 1)
                        ? <TextField focused={false} value={klinikList[0].name} fullWidth />
                        : null
                  }
                </Grid>

              </Grid>

            </Grid>
          </Grid>
        </Hidden>
        <Hidden smUp>
          <Grid container direction="row" justify="flex-start" alignItems='flex-start' >
            <Grid item sm={12}>
              <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} >Clinic</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} ><b>{state.product.clinic ? state.product.clinic.name : ""}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Product Name</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}><b>{state.product.product_name}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }} >Test Date</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}><b>{state.originDate}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Reschedule valid until</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}><b>{state.maxValidDate}</b></Typography>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Reschedule Date</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <MuiPickersUtilsProvider utils={DateFnsUtils} >
                    <Grid container >
                      <KeyboardDatePicker
                        autoOk
                        // minDate={datetoanydaysafter(state.originDate, 1)}
                        minDate={state.minDate}
                        maxDate={state.maxValidDate}
                        error={state.fromdateErrorMessage ? true : false}
                        helperText={state.fromdateErrorMessage ? state.fromdateErrorMessage : ""}
                        style={{ width: 150 }}
                        size="small"
                        clearable
                        format="yyyy/MM/dd"
                        margin="dense"
                        id="date-picker-from"
                        value={state.fromdate}
                        onChange={handleDateChange('fromdate')}
                        KeyboardButtonProps={{
                          'aria-label': 'change date',
                        }}
                        disablePast={true}
                        InputLabelProps={{
                          shrink: true,
                          classes: {
                            root: classes.labelRoot
                          }
                        }}
                        InputProps={{
                          classes: {
                            root: classes.inputRoot
                          }
                        }}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>User Email</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <TextField
                    fullWidth
                    // size="small"
                    placeholder="Email"
                    onChange={handleChange('email')}
                    value={state.email}
                    error={state.emailErrorMessage ? true : false}
                    helperText={state.emailErrorMessage ? state.emailErrorMessage : ""}
                    InputLabelProps={{
                      shrink: true,
                      classes: {
                        root: classes.labelRoot
                      }
                    }}
                    InputProps={{
                      classes: {
                        root: classes.inputRoot
                      }
                    }}
                  />
                </Grid>
              </Grid>
              <Grid container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>Reschedule Reason</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography style={{ fontSize: 12, padding: 3, fontFamily: 'Arial, Helvetica, sans-serif' }}>:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  <TextField
                    fullWidth
                    // size="small"
                    placeholder="Reschedule Reason"
                    multiline
                    onChange={handleChange('reason')}
                    value={state.reason}
                    error={state.reasonErrorMessage ? true : false}
                    helperText={state.reasonErrorMessage ? state.reasonErrorMessage : ""}
                    InputLabelProps={{
                      shrink: true,
                      classes: {
                        root: classes.labelRoot
                      }
                    }}
                    InputProps={{
                      classes: {
                        root: classes.inputRoot
                      }
                    }}
                  />
                </Grid>
              </Grid>

              <Grid style={{ visibility: roleUsed === 'voltras.admin' ? 'visible' : 'hidden' }}
                container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                  <Typography >Change Clinic</Typography>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                  <Typography >:</Typography>
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  {}
                  <SimpleAutocomplete
                    id="changeClinicChooseCity"
                    options={cityList[0] ? cityList : []}
                    optionItem={'city'}
                    notShrink={true}
                    handleInputChange={handleInputChange}
                    onPick={handlePick}
                  />
                </Grid>
              </Grid>
              <Grid style={{ visibility: roleUsed === 'voltras.admin' ? 'visible' : 'hidden' }}
                container direction="row" justify="flex-start" alignItems="center" >
                <Grid item lg={5} sm={5} xs={5}>
                </Grid>
                <Grid item lg={1} sm={1} xs={1} component='div'  >
                </Grid>
                <Grid item lg={6} sm={6} xs={6}>
                  {
                    (klinikList[0]) && (klinikList[0].name) && (klinikList.length > 1)
                      ? <SimpleAutocomplete
                        id="changeClinicChooseClinic"
                        options={klinikList}
                        optionItem={'name'}
                        notShrink={true}
                        handleInputChange={handleInputChangeClinic}
                        onPick={handlePickClinic}
                      />
                      : (klinikList[0]) && (klinikList[0].name) && (klinikList.length === 1)
                        ? <TextField focused={false} value={klinikList[0].name} fullWidth />
                        : null
                  }
                </Grid>

              </Grid>

            </Grid>
          </Grid>
        </Hidden>

      </DialogContent>

      <DialogActions>
        <Hidden smDown>
          <Grid container direction="row" justify='center' alignItems='center' style={{ marginTop: 5, marginBottom: 5 }}>
            <Typography color="error" style={{ visibility: state.submitErrorMessage ? 'visible' : 'hidden' }} variant='subtitle2' >
              {state.submitErrorMessage}
            </Typography>
            <Grid container item direction="row" justify='center' alignItems='center' >
              <Button variant='outlined' color="primary" onClick={handleCancel} style={{ marginRight: 5 }}>
                {FormatText('app_cancel')}
              </Button>
              <Button
                disabled={state.submitErrorMessage ? true : false}
                variant='contained'
                color="primary"
                onClick={handleSave}
                style={{ marginLeft: 5 }}
              >
                Reschedule
            </Button>
            </Grid>
          </Grid>
        </Hidden>
        <Hidden smUp>
          {/* <Typography color="error" style={{ visibility: state.submitErrorMessage ? 'visible' : 'hidden', marginTop: 5,marginBottom: 5 }} variant='subtitle2' >
            {state.submitErrorMessage}
          </Typography> */}
          <Grid container>
            <Grid container item xs={12} sm={12} direction="row" justify='center' alignItems='center' style={{ paddingBottom: 5 }}>
              <Typography color="error" style={{ visibility: state.submitErrorMessage ? 'visible' : 'hidden', fontSize: 12, fontFamily: 'Arial, Helvetica, sans-serif', textAlign: 'center' }} variant='subtitle2' >
                {state.submitErrorMessage}
              </Typography>
            </Grid>
            <Grid container item xs={12} sm={12} justify='space-between' style={{ paddingLeft: 50, marginTop: 5, marginBottom: 5, paddingRight: 50 }}>
              <Button variant='outlined' color="primary" onClick={handleCancel}
                style={{
                  textTransform: 'none', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                  fontWeight: 'bold', fontSize: 12, marginRight: 5
                }}
              >
                {FormatText('app_cancel')}
              </Button>
              <Button
                disabled={state.submitErrorMessage ? true : false}
                style={{
                  textTransform: 'none', color: 'white', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                  fontWeight: 'bold', fontSize: 12
                }}
                variant='contained'
                color="primary"
                onClick={handleSave}
              // style={{ marginLeft: 5 }}
              >
                Reschedule
            </Button>
            </Grid>
          </Grid>
        </Hidden>
      </DialogActions>
    </Dialog>
  )
}

const style = (theme) => ({
  labelRoot: {
    fontSize: 12,
    fontFamily: 'Arial, Helvetica, sans-serif',
  },
  inputRoot: {
    fontSize: 12,
    fontFamily: 'Arial, Helvetica, sans-serif',
    height: 25
  }
})
export default withStyles(style)(AddClientDialog)

