import React, { useState, useEffect } from 'react'
import { Dialog, DialogActions, DialogContent } from '@material-ui/core'
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { Grid, Typography, Button, MenuItem, TextField } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import { FormValidation } from '../../../utils/FormValidation';

const styles = (theme) => ({
    rootForm: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            // width: '25ch',
        },
    },
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    asterisk: {
        color: 'red'
    },
    addButton: {
        color: 'white'
    }
});

const DialogTitle = withStyles(styles)(props => {
    const { children, onClose, classes, ...other } = props

    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
})

const EditRequestSpesificationDialog = ({ ...props }) => {
    const { classes, dataClient } = props
    const dataToUpdt = props.dataToUpdate
    const [state, setState] = useState({
        dataToUpdate: {
            clientId: "",
            path: "",
            urlDestination: "",
            isNeedLogin: ""
        },

    })
    useEffect(() => {
        setState({
            dataToUpdate: dataToUpdt
        })
    }, [dataToUpdt])
    let dataRoles = []
    const handleClose = () => {
        setState({})
        props.handleClose(false, true)
    }
    const findDuplicateClientId = (arrayOfObj, key) => {
        return arrayOfObj.filter((item, index, array) => {
            return array.map((mapItem) => mapItem[key]).indexOf(item[key]) === index
        })
    }
    // console.log('dupkcate', findDuplicateClientId())

    const handleAddRequest = () => {
        let isPriortyValueUpdate = true
        if (state.dataToUpdate['priorityValue'] === dataToUpdt['priorityValue']) {
            isPriortyValueUpdate = false
        }
        delete state.dataToUpdate.clientIdErrorMessage
        delete state.dataToUpdate.isNeedLoginErrorMessage
        delete state.dataToUpdate.pathErrorMessage
        delete state.dataToUpdate.priorityValueErrorMessage
        delete state.dataToUpdate.urlDestinationErrorMessage
        props.completeRequest(state.dataToUpdate, isPriortyValueUpdate)
    }

    const handleChange = id => e => {
        let valid = FormValidation(id, e.target.value)
        setState({
            ...state,
            dataToUpdate: {
                ...state.dataToUpdate,
                [id]: e.target.value,
            },
            [id + 'ErrorMessage']: valid.errorMessage,
        })
    }
    const setDataRoles = () => {
        let clientId = state.dataToUpdate ? state.dataToUpdate.clientId : ''
        dataRoles = dataClient.filter(item => item.clientId === clientId)[0]
        if (!dataRoles) return []
        else {
            return dataRoles.clientRoles
        }
        // return []
    }
    const needRole = [{ label: "Yes", boolean: true }, { label: "No", boolean: false }]

    const disableButton = () => {
        let isNeedLog = state.dataToUpdate ?
            state.dataToUpdate.isNeedLogin === '' || state.dataToUpdate.isNeedLogin === null || state.dataToUpdate.isNeedLogin === undefined ? false : true : ""
        let clientId = state.dataToUpdate ? state.dataToUpdate.clientId : ""
        let path = state.dataToUpdate ? state.dataToUpdate.path : ""
        let urlDestination = state.dataToUpdate ? state.dataToUpdate.urlDestination : ""
        // let isNeedLogin = state.dataToUpdate ? state.dataToUpdate.isNeedLogin : ""
        if (clientId && path && urlDestination && isNeedLog) return false
        else return true
    }
    // console.log('state',state)
    return (
        <Dialog fullWidth maxWidth={'xs'} open={props.open} onClose={handleClose} scroll={'paper'}>
            <DialogTitle
                id="max-width-dialog-title"
                onClose={handleClose}
            >
                Edit Request Specification
            </DialogTitle>
            <DialogContent>
                <form className={classes.rootForm} noValidate autoComplete="off">
                    <TextField
                        fullWidth
                        placeholder='Contoh : authentication-backoffice'
                        required
                        select
                        id='clientId'
                        label='Client Id'
                        value={state.dataToUpdate ? state.dataToUpdate.clientId : ""}
                        onChange={handleChange('clientId')}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    >
                        {findDuplicateClientId(dataClient, 'clientId').map(item =>
                            <MenuItem key={item.clientId} value={item.clientId}>
                                {item.clientId}
                            </MenuItem>
                        )}
                    </TextField>
                    <TextField
                        fullWidth
                        required
                        placeholder='Contoh : /users/*/orders/**'
                        label='Path'
                        onChange={handleChange('path')}
                        error={state.pathErrorMessage ? true : false}
                        value={state.dataToUpdate ? state.dataToUpdate.path : ""}
                        helperText={state.pathErrorMessage ? state.pathErrorMessage : ''}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    />
                    <TextField
                        fullWidth
                        required
                        placeholder='Contoh : http//172.16.10.51:32170/users/*/orders/**'
                        label='URL Destination'
                        onChange={handleChange('urlDestination')}
                        value={state.dataToUpdate ? state.dataToUpdate.urlDestination : ""}
                        error={state.urlDestinationErrorMessage ? true : false}
                        helperText={state.urlDestinationErrorMessage ? state.urlDestinationErrorMessage : ''}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    />
                    <TextField
                        fullWidth
                        required
                        select
                        label='Need Login'
                        value={state.dataToUpdate ? state.dataToUpdate.isNeedLogin === 'True' || state.dataToUpdate.isNeedLogin === true ? true : false : ""}
                        onChange={handleChange('isNeedLogin')}
                        InputLabelProps={{
                            shrink: true,
                            classes: {
                                asterisk: classes.asterisk
                            }
                        }}
                    >
                        {needRole.map(item =>
                            <MenuItem key={item.label} value={item.boolean}>
                                {item.label}
                            </MenuItem>
                        )}
                    </TextField>
                    <TextField
                        fullWidth
                        select
                        label='Default Role'
                        value={state.dataToUpdate ? state.dataToUpdate.defaultRole : ""}
                        onChange={handleChange('defaultRole')}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    >
                        <MenuItem key={'null'} value={null}>
                            Null
                        </MenuItem>
                        {setDataRoles().map(item => <MenuItem key={item.roleId} value={item.roleId}>{item.roleId}</MenuItem>)}
                    </TextField>
                    <TextField
                        fullWidth
                        placeholder='Contoh: 100'
                        label='Priority Value'
                        onChange={handleChange('priorityValue')}
                        value={state.dataToUpdate ? state.dataToUpdate.priorityValue : ""}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />
                </form>
            </DialogContent>
            <DialogActions>
                <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button color='primary' variant='contained'
                            className={classes.addButton} onClick={handleAddRequest} disabled={disableButton()}>
                            Edit
                        </Button>
                    </Grid>
                    <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                        <Button onClick={handleClose} color='secondary' variant='contained' className={classes.addButton}>
                            Cancel
                        </Button>
                    </Grid>
                </Grid>
            </DialogActions>
        </Dialog>
    )
}

export default withStyles(styles)(EditRequestSpesificationDialog)