
import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';
import Hidden from '@material-ui/core/Hidden'
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, KeyboardDatePicker, TimePicker } from '@material-ui/pickers';
import { convertDateyyyymmdd, timeConverter } from '../../../utils/utils';

const styles = (theme) => ({
  labelRoot: {
    fontSize: 15,
    fontFamily: 'Arial, Helvetica, sans-serif',
    marginLeft: 20
  },
  inputRoot: {
    fontSize: 15,
    fontFamily: 'Arial, Helvetica, sans-serif',
    height: 20,
    width: 60,
    marginLeft: 20
  },
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const EditWithReplaceDialog = ({ ...props }) => {

  const { existingList, availableList, dialogTitle, availTitle, existingTitle, datePicker, timePicker, dialogWidth, open, classes } = props

  const [availableItemInState, setAvailableItemInState] = useState([])
  const [existingListInState, setExistingListInState] = useState([])
  const [newExistingListInState, setNewExistingListInState] = useState([])
  const [operational, setOperational] = useState({
    openMonday: new Date('2020-10-30T08:00:00'), closeMonday: new Date('2020-10-30T17:00:00'),
    openTuesday: new Date('2020-10-30T08:00:00'), closeTuesday: new Date('2020-10-30T17:00:00'),
    openWednesday: new Date('2020-10-30T08:00:00'), closeWednesday: new Date('2020-10-30T17:00:00'),
    openThursday: new Date('2020-10-30T08:00:00'), closeThursday: new Date('2020-10-30T17:00:00'),
    openFriday: new Date('2020-10-30T08:00:00'), closeFriday: new Date('2020-10-30T17:00:00'),
    openSaturday: new Date('2020-10-30T08:00:00'), closeSaturday: new Date('2020-10-30T17:00:00'),
    openSunday: new Date('2020-10-30T08:00:00'), closeSunday: new Date('2020-10-30T17:00:00')
  })
  const days = ["Haha", 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday']

  const handleClose = () => {
    setAvailableItemInState([])
    setExistingListInState([])
    setNewExistingListInState([])
    props.handleOpen(false)
  };

  useEffect(() => {
    let exisListToState = []
    let availableToState = []
    if (datePicker) {
      existingList && existingList.forEach(item => {
        exisListToState.push(item)
      })
    }
    else if (timePicker) {
      existingList && existingList.forEach(item => {
        let clinicOpen = `${days[item.day]}, ${item.hour}`
        exisListToState.push(clinicOpen)
      })
    }
    else {
      existingList && existingList.forEach(item => {
        [...availableList].forEach(prod => {
          if (prod.id === item) exisListToState.push(prod.name)
        })
      })
    }

    if (exisListToState) {
      if (timePicker) {
        let dayList = existingList.map(item => item.day)
        setExistingListInState(exisListToState)
        availableToState = [...availableList].map(item => item.day)
        let filteredAvail = availableToState.filter(el => !dayList.includes(el));

        setAvailableItemInState(filteredAvail)
      }
      else {
        setExistingListInState(exisListToState)
        availableToState = [...availableList].map(item => item.name)
        let filteredAvail = availableToState.filter(el => !exisListToState.includes(el));
        setAvailableItemInState(filteredAvail)

      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [availableList, existingList, open])

  const handleAddItemExisting = item => event => {
    if (event.target.name === 'timepicker') {
      let opDay = days[item]
      let opOpen = timeConverter(operational[`open${opDay}`])
      let opClose = timeConverter(operational[`close${opDay}`])
      let opText = `${opDay}, ${opOpen}-${opClose}`

      setNewExistingListInState([...newExistingListInState, opText])
      let newList = availableItemInState.filter(items => items !== item)
      setAvailableItemInState(newList)
    }
    else {
      setNewExistingListInState([...newExistingListInState, item])
      let newList = availableItemInState.filter(items => items !== item)
      setAvailableItemInState(newList)
    }
  }

  const handleRemoveItemExisting = item => event => {
    let removeDay = item.slice(0, item.length - 13)
    let dayId = []
    days.forEach((day, i) => {
      if (day === removeDay) {
        dayId.push(i)
      }
    })

    if (dayId[0]) {
      setAvailableItemInState([...availableItemInState, dayId[0]])
      let newList = existingListInState.filter(items => items !== item)
      setExistingListInState(newList)
    }
    else {
      setAvailableItemInState([...availableItemInState, item])
      let newList = existingListInState.filter(items => items !== item)
      setExistingListInState(newList)
    }
  }

  const handleRemoveExisting = item => event => {
    let removeDay = item.slice(0, item.length - 13)
    let dayId = []
    days.forEach((day, i) => {
      if (day === removeDay) {
        dayId.push(i)
      }
    })
    if (dayId[0]) {
      setAvailableItemInState([...availableItemInState, dayId[0]])
      let newList = newExistingListInState.filter(items => items !== item)
      setNewExistingListInState(newList)
    }
    else {
      setAvailableItemInState([...availableItemInState, item])
      let newList = newExistingListInState.filter(items => items !== item)
      setNewExistingListInState(newList)
    }
  }

  const handleSave = () => {
    let itemToSave = existingListInState.concat(newExistingListInState)
    setAvailableItemInState([])
    setExistingListInState([])
    setNewExistingListInState([])
    props.handleSave(itemToSave)
    props.handleOpen(false)
  }

  const handleDateChange = prop => date => {
    // const newDate = new Date(date);
    let item = convertDateyyyymmdd(date)
    if ((item !== "NaN-NaN-NaN") && (item !== "1970-01-01")) {
      setNewExistingListInState([...newExistingListInState, item])
      let newList = availableItemInState.filter(items => items !== item)
      setAvailableItemInState(newList)
    }
  }

  const handleDayHourChange = prop => date => {
    if (prop.props === 'openTime') {
      let openattr = `open${days[prop.day]}`
      setOperational({
        ...operational,
        [openattr]: date
      })
    }
    else {
      let textattr = `close${days[prop.day]}`
      setOperational({
        ...operational,
        [textattr]: date
      })
    }
  }

  return (
    <Dialog id={props.id} fullWidth maxWidth={dialogWidth ? dialogWidth : 'sm'} open={props.open} onClose={handleClose} scroll={'paper'} >
      <Hidden smDown>
        <DialogTitle id="dialog-title" onClose={handleClose}>{dialogTitle}</DialogTitle>

        <DialogContent dividers>
          <Grid container direction="row" justify="flex-start" alignItems='flex-start' >
            <Grid item md={6}>
              <Typography variant='subtitle2'>{existingTitle}</Typography>
              <Typography variant='caption' color='textSecondary'>Click to remove/edit {existingTitle}</Typography>
              <List dense >
                {
                  existingListInState.map((item, i) => {
                    if (item === 'none') {
                      return <ListItemText primary={'- none'} key={i} />
                    }
                    else {
                      return <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                        <ListItemIcon>
                          <Checkbox
                            edge="start"
                            id={item}
                            value={item}
                            checked
                            onChange={handleRemoveItemExisting(item)}
                          />
                        </ListItemIcon>
                        <ListItemText primary={item} />
                      </Grid >
                    }
                  })
                }
                {
                  newExistingListInState[0] ? newExistingListInState.map((item, i) => (
                    <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                      <ListItemIcon>
                        <Checkbox
                          edge="start"
                          id={item.toString()}
                          value={item}
                          checked
                          onChange={handleRemoveExisting(item)}
                        />
                      </ListItemIcon>
                      <ListItemText secondary={item} />
                    </Grid >
                  ))
                    : existingListInState[0]
                      ? <div></div>
                      : <ListItemText primary={'- none'} />
                }
              </List>
            </Grid>

            {
              datePicker
                ? <Grid item md={6}>
                  <Typography variant='subtitle2' >{availTitle}</Typography>
                  <MuiPickersUtilsProvider utils={DateFnsUtils} >
                    <Grid container >
                      <KeyboardDatePicker
                        // style={{ width: 150, marginRight: 15 }}
                        size="small"
                        clearable
                        format="yyyy/MM/dd"
                        margin="dense"
                        id={"date-picker-edit"}
                        placeholder="2018/12/25"
                        // label={FormatText('app_from')}
                        // value={state[item + 'fromdate']}
                        onChange={handleDateChange('close_date')}
                        KeyboardButtonProps={{
                          'aria-label': 'change date',
                        }}
                        disablePast={true}
                      />
                    </Grid>
                  </MuiPickersUtilsProvider>
                </Grid>
                : timePicker
                  ? <Grid item md={6}>
                    <Typography variant='subtitle2' >{availTitle}</Typography>
                    <Typography variant='caption' color='textSecondary'>Click to add {existingTitle}</Typography>
                    <List dense >
                      {availableItemInState[0] ? availableItemInState.map((item, i) => (
                        <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                          <Grid item sm={1}  >
                            <ListItemIcon>
                              <Checkbox
                                name="timepicker"
                                edge="start"
                                onChange={handleAddItemExisting(item)}
                                checked={false}
                              />
                            </ListItemIcon>
                          </Grid>
                          <Grid item sm={2} style={{ marginLeft: 5 }}  >
                            <ListItemText secondary={days[item]} />
                          </Grid>
                          <Grid item sm={3} style={{ marginLeft: 25 }}  >
                            <MuiPickersUtilsProvider utils={DateFnsUtils} >
                              <TimePicker
                                size="small"
                                ampm={false}
                                margin="normal"
                                id={`openTime${i}`}
                                label="Open"
                                value={operational[`open${days[item]}`]}
                                onChange={handleDayHourChange({ day: item, props: "openTime" })}
                                InputLabelProps={{
                                  shrink: true,
                                  classes: {
                                    root: classes.labelRoot
                                  }
                                }}
                                InputProps={{
                                  classes: {
                                    root: classes.inputRoot
                                  }
                                }}
                              />
                            </MuiPickersUtilsProvider>
                          </Grid>
                          <Grid item sm={3} style={{ marginLeft: 15 }}  >
                            <MuiPickersUtilsProvider utils={DateFnsUtils} >
                              <TimePicker
                                size="small"
                                ampm={false}
                                margin="normal"
                                id={`closeTime${i}`}
                                label="Close"
                                value={operational[`close${days[item]}`]}
                                onChange={handleDayHourChange({ day: item, props: "closeTime" })}
                                InputLabelProps={{
                                  shrink: true,
                                  classes: {
                                    root: classes.labelRoot
                                  }
                                }}
                                InputProps={{
                                  classes: {
                                    root: classes.inputRoot
                                  }
                                }}
                              />
                            </MuiPickersUtilsProvider>
                          </Grid>
                        </Grid >
                      )) : <ListItemText primary={'- none'} />}
                    </List>
                  </Grid>

                  : <Grid item md={6}>
                    <Typography variant='subtitle2' >{availTitle}</Typography>
                    <Typography variant='caption' color='textSecondary'>Click to add {existingTitle}</Typography>
                    <List dense >
                      {availableItemInState[0] ? availableItemInState.map((item, i) => (
                        <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                          <ListItemIcon>
                            <Checkbox
                              edge="start"
                              onChange={handleAddItemExisting(item)}
                              checked={false}
                            />
                          </ListItemIcon>
                          <ListItemText secondary={item} />
                        </Grid >
                      )) : <ListItemText primary={'- none'} />}
                    </List>
                  </Grid>
            }


          </Grid>
        </DialogContent>

        <DialogActions>
          <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
            <Grid container item direction="row" justify='center' alignItems='center' md={6}>
              <Button onClick={handleClose} variant='outlined' color="primary">
                Cancel
          </Button>
            </Grid>
            <Grid container item direction="row" justify='center' alignItems='center' md={6}>
              <Button onClick={handleSave} variant='contained' color="primary">
                Save
          </Button>
            </Grid>
          </Grid>
        </DialogActions>
      </Hidden>
      <Hidden smUp>
        <DialogTitle id="dialog-title" onClose={handleClose}>{dialogTitle}</DialogTitle>
        <DialogContent dividers>
          <Typography variant='subtitle2'>{existingTitle}</Typography>
          <Typography variant='caption' color='textSecondary'>Click to remove/edit {existingTitle}</Typography>
          <Grid container>
            <List dense >
              {
                existingListInState.map((item, i) => {
                  if (item === 'none') {
                    return <ListItemText primary={'- none'} key={i} />
                  }
                  else {
                    return <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                      <ListItemIcon>
                        <Checkbox
                          edge="start"
                          id={item}
                          value={item}
                          checked
                          onChange={handleRemoveItemExisting(item)}
                        />
                      </ListItemIcon>
                      <ListItemText primary={item} />
                    </Grid >
                  }
                })
              }
              {
                newExistingListInState[0] ? newExistingListInState.map((item, i) => (
                  <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                    <ListItemIcon>
                      <Checkbox
                        edge="start"
                        id={item.toString()}
                        value={item}
                        checked
                        onChange={handleRemoveExisting(item)}
                      />
                    </ListItemIcon>
                    <ListItemText secondary={item} />
                  </Grid >
                ))
                  : existingListInState[0]
                    ? <div></div>
                    : <ListItemText primary={'- none'} />
              }
              {
                datePicker
                  ? <Grid item md={6}>
                    <Typography variant='subtitle2' >{availTitle}</Typography>
                    <MuiPickersUtilsProvider utils={DateFnsUtils} >
                      <Grid container >
                        <KeyboardDatePicker
                          // style={{ width: 150, marginRight: 15 }}
                          size="small"
                          clearable
                          format="yyyy/MM/dd"
                          margin="dense"
                          id={"date-picker-edit"}
                          placeholder="2018/12/25"
                          // label={FormatText('app_from')}
                          // value={state[item + 'fromdate']}
                          onChange={handleDateChange('close_date')}
                          KeyboardButtonProps={{
                            'aria-label': 'change date',
                          }}
                          disablePast={true}
                        />
                      </Grid>
                    </MuiPickersUtilsProvider>
                  </Grid>
                  : timePicker
                    ? <Grid>
                      <Typography variant='subtitle2' >{availTitle}</Typography>
                      <Typography variant='caption' color='textSecondary'>Click to add {existingTitle}</Typography>
                      <List dense >
                        {availableItemInState[0] ? availableItemInState.map((item, i) => (
                          <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                            <Grid item sm={1} xs={1} >
                              <ListItemIcon>
                                <Checkbox
                                  name="timepicker"
                                  edge="start"
                                  onChange={handleAddItemExisting(item)}
                                  checked={false}
                                />
                              </ListItemIcon>
                            </Grid>
                            <Grid item sm={2} xs={2} style={{ marginLeft: 5 }}  >
                              <ListItemText secondary={days[item]} />
                            </Grid>
                            <Grid item sm={3} xs={3} style={{ marginLeft: 25 }}  >
                              <MuiPickersUtilsProvider utils={DateFnsUtils} >
                                <TimePicker
                                  size="small"
                                  ampm={false}
                                  margin="normal"
                                  id={`openTime${i}`}
                                  label="Open"
                                  value={operational[`open${days[item]}`]}
                                  onChange={handleDayHourChange({ day: item, props: "openTime" })}
                                  InputLabelProps={{
                                    shrink: true,
                                    classes: {
                                      root: classes.labelRoot
                                    }
                                  }}
                                  InputProps={{
                                    classes: {
                                      root: classes.inputRoot
                                    }
                                  }}
                                />
                              </MuiPickersUtilsProvider>
                            </Grid>
                            <Grid item sm={3} xs={3} style={{ marginLeft: 15 }}  >
                              <MuiPickersUtilsProvider utils={DateFnsUtils} >
                                <TimePicker
                                  size="small"
                                  ampm={false}
                                  margin="normal"
                                  id={`closeTime${i}`}
                                  label="Close"
                                  value={operational[`close${days[item]}`]}
                                  onChange={handleDayHourChange({ day: item, props: "closeTime" })}
                                  InputLabelProps={{
                                    shrink: true,
                                    classes: {
                                      root: classes.labelRoot
                                    }
                                  }}
                                  InputProps={{
                                    classes: {
                                      root: classes.inputRoot
                                    }
                                  }}
                                />
                              </MuiPickersUtilsProvider>
                            </Grid>
                          </Grid >
                        )) : <ListItemText primary={'- none'} />}
                      </List>
                    </Grid>

                    : <Grid item md={6}>
                      <Typography variant='subtitle2' >{availTitle}</Typography>
                      <Typography variant='caption' color='textSecondary'>Click to add {existingTitle}</Typography>
                      <List dense >
                        {availableItemInState[0] ? availableItemInState.map((item, i) => (
                          <Grid container direction="row" justify="flex-start" alignItems='center' key={i} >
                            <ListItemIcon>
                              <Checkbox
                                edge="start"
                                onChange={handleAddItemExisting(item)}
                                checked={false}
                              />
                            </ListItemIcon>
                            <ListItemText secondary={item} />
                          </Grid >
                        )) : <ListItemText primary={'- none'} />}
                      </List>
                    </Grid>
              }
            </List>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Grid container direction="row" justify='space-between' item xs={12} sm={12}
            style={{
              paddingLeft: 50, marginTop: 5,
              marginBottom: 5, paddingRight: 50
            }}>
            <Button onClick={handleClose} variant='outlined' color="primary" style={{
              textTransform: 'none', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
              fontWeight: 'bold', fontSize: 12
            }}>
              Cancel
          </Button>
            <Button onClick={handleSave} variant='contained' color="primary" style={{
              textTransform: 'none', color: 'white', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
              fontWeight: 'bold', fontSize: 12
            }}>
              Save
          </Button>
          </Grid>
        </DialogActions>
      </Hidden>
    </Dialog>
  )
}


export default withStyles(styles)(EditWithReplaceDialog)
