
import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography';
import EditIcon from '@material-ui/icons/Edit';
import Paper from '@material-ui/core/Paper';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import SaveIcon from '@material-ui/icons/Save';
import ClearIcon from '@material-ui/icons/Clear';
import EditDialog from './EditDialog'

import { setOpenScreen } from "../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole,
} from '../../actions/ServiceActions'
import { Button, Tooltip } from '@material-ui/core';
import FormatText from '../../i18n';
import Config from '../../config';
import { sendImageToService } from '../../services/ServiceClient';

const UserProfile = props => {
  const { _setOpenScreen, userInfo } = props
  const [basic, setBasic] = useState([])
  const [editOpen, setEditOpen] = useState(false)
  const [editItem, setEditItem] = useState(false)

  useEffect(() => {
    _setOpenScreen("Manage Account")

  }, [_setOpenScreen])

  useEffect(() => {
    let text = FormatText('app_photo_personalize')
    let data = userInfo
    // let image = ""
    let basicInfo = []
    if (data.picture) {
      basicInfo = [
        { name: FormatText('common_photo'), value: text, action: true, key: 'picture', picture: data.picture },
        { name: FormatText('common_name'), value: data.name ? data.name : "", action: true, key: 'name' },
        { name: FormatText('common_email'), value: data.email ? data.email : "", action: false, key: 'email' },
        { name: FormatText('common_phone'), value: data.phone ? data.phone : "", action: true, key: 'phone' },
        { name: FormatText('common_address'), value: data.address ? data.address : "", action: true, key: 'address' },

      ]
      setBasic(basicInfo)
      // getBase64Image(data.picture).then(result => {
      //   console.log(result)
      //   basicInfo = [
      //     { name: FormatText('common_photo'), value: text, action: true, key: 'picture', picture: result },
      //     { name: FormatText('common_name'), value: data.name ? data.name : "", action: true, key: 'name' },
      //     { name: FormatText('common_email'), value: data.email ? data.email : "", action: true, key: 'email' },
      //     { name: FormatText('common_phone'), value: data.phone ? data.phone : "", action: true, key: 'phone' },
      //     { name: FormatText('common_address'), value: data.address ? data.address : "", action: true, key: 'address' },

      //   ]
      //   setBasic(basicInfo)
      // })
    } else {
      basicInfo = [
        { name: FormatText('common_photo'), value: text, action: true, key: 'picture', picture: data.picture, imageFile: "", imageType: "" },
        { name: FormatText('common_name'), value: data.name ? data.name : "", action: true, key: 'name' },
        { name: FormatText('common_email'), value: data.email ? data.email : "", action: false, key: 'email' },
        { name: FormatText('common_phone'), value: data.phone ? data.phone : "", action: true, key: 'phone' },
        { name: FormatText('common_address'), value: data.address ? data.address : "", action: true, key: 'address' },

      ]
      setBasic(basicInfo)
    }
  }, [userInfo])

  const handleEdit = item => {
    setEditItem(item)
    setEditOpen(true)
  }

  const handleEditClose = () => setEditOpen(false)

  const handleNewItem = item => {
    let newBasic = basic.map(itemBasic => {
      if (itemBasic.name === item.name) {
        return item
      }
      return itemBasic
    })
    setBasic(newBasic)
  }

  const handleSave = async () => {
    let attributeToUpdate = {}
    let oldData = userInfo
    let imageType = ""
    // console.log(basic)
    basic.map(item => {
      if (item.imageType) imageType = item.imageType
      return attributeToUpdate = { ...attributeToUpdate, [item.key]: item.key === 'picture' ? item.imageFile : item.value }
    })
    if (attributeToUpdate.name === oldData.name) delete attributeToUpdate.name
    if (attributeToUpdate.email === oldData.email) delete attributeToUpdate.email
    if (attributeToUpdate.phone === oldData.phone) delete attributeToUpdate.phone
    if (attributeToUpdate.address === oldData.address) delete attributeToUpdate.address
    if (attributeToUpdate.picture === oldData.picture) delete attributeToUpdate.picture
    let request = { attributeToUpdate }
    let method = "PATCH"
    let serviceCode = 'UPDATE_USER_INFO'

    const { name, email, phone, address, picture } = request.attributeToUpdate
    if (phone) request.attributeToUpdate.phone = phone[0] === '0' ? '+62' + phone.slice(1) : phone
    if (!name) delete request.attributeToUpdate.name
    if (!email) delete request.attributeToUpdate.email
    if (!phone) delete request.attributeToUpdate.phone
    if (!address) delete request.attributeToUpdate.address
    if (!picture) delete request.attributeToUpdate.picture

    if (attributeToUpdate.picture) {
      let response = await sendImageToService(attributeToUpdate.picture, imageType, attributeToUpdate.picture)
      if (response.ID) {
        request.attributeToUpdate.picture = Config.gatewayRepoFile + `/static-file-manager/retrieve?file=${response.ID}`
        props._setCompleteRequest({ request, method, serviceCode })
      }
      else console.log("Error")
    }
    else props._setCompleteRequest({ request, method, serviceCode })
    // console.log("DAta to save: ", request)
  }

  return (
    <Grid style={{ marginTop: 30 }} container direction="column" justify="flex-start" alignItems="center" spacing={2}>
      <Grid item sm={12} xl={12} style={{ marginTop: 30 }}>
        <Paper elevation={2} >
          <br />
          <Grid item >
            <center>
              <Typography variant='h6' color='primary' >{FormatText('app_account_profile')}</Typography>
            </center>
          </Grid>
          <List margin={20}  >
            
            {
              basic && basic.map((item, i) => {
                return (
                  <ListItem divider key={i} style={{ marginRight: 30 }} >
                    <ListItemText
                      secondary={item.name}
                      style={{ width: '20ch' }}
                    />
                    <ListItemText
                      secondary={item.name === 'Photo' || item.name === 'Foto' ? item.value : !item.value ? 'edit to fill' : ''}
                      primary={item.name !== 'Photo' && item.name !== 'Foto' ? item.value : ''}
                      style={{ width: '60ch' }}
                    // secondary={secondary ? 'Secondary text' : null}
                    />
                    <ListItemSecondaryAction style={{ marginRight: 15 }}>
                      {item.name === 'Photo' || item.name === 'Foto'
                        ? <Tooltip title="Edit" >
                          <Avatar
                            src={item.picture}
                            onClick={() => handleEdit(item)}
                          />
                        </Tooltip>
                        : item.action
                          ? <Tooltip title="Edit" >
                            <IconButton edge="end" aria-label="delete" onClick={() => handleEdit(item)}>
                              <EditIcon />
                            </IconButton>
                          </Tooltip>
                          : null}
                    </ListItemSecondaryAction>
                  </ListItem>
                )
              })
            }
          </List>

          <Grid container direction="row" justify="center" alignItems="center" >
            <Grid style={{ marginTop: 20, marginBottom: 20 }}>
              <Button
                onClick={() => props.history.push('/Home')}
                variant='outlined'
                color='secondary'
                startIcon={<ClearIcon />}
                style={{
                  width: 150,
                  textTransform: 'none',
                  fontWeight: 'bold',
                  marginRight: 15
                }}
              >
                {FormatText('app_cancel')}
              </Button>
              <Button
                onClick={handleSave}
                variant='outlined'
                color='primary'
                startIcon={<SaveIcon />}
                style={{
                  width: 150,
                  textTransform: 'none',
                  fontWeight: 'bold',
                  marginLeft: 15
                }}
              >
                {FormatText('common_save')}
              </Button>
            </Grid>
          </Grid>
        </Paper>
        <Grid >
          <div style={{ height: 150 }} />
        </Grid>

      </Grid>

      <EditDialog
        open={editOpen}
        handleEditClose={handleEditClose}
        editItem={editItem}
        handleNewItem={handleNewItem}
      />

      <canvas id="canvas" style={{ width: "300", display: 'none' }}></canvas>
    </Grid>
  )
}

const mapStateToProps = store => ({
  clientList: store.clientList.list,
  userDetail: store.clientList.detail,
  userFiltered: store.clientList.filteredList,
  userInfo: store.userInfo.info,
})

const mapDispatchToProps = dispatch => {
  return {
    _setOpenScreen: val => dispatch(setOpenScreen(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.urlParams))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(UserProfile)