import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'

import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, setSecondRequest
} from '../../../actions/ServiceActions'
import { setClinikDetail, setCityList } from "../../../actions/PaxListActions";

import { Button, Typography, Card, Grid, FormHelperText, FormControl } from '@material-ui/core';
import CardContent from '@material-ui/core/CardContent';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';

import { FormValidation } from '../../../utils/FormValidation';
import SimpleAutocomplete from '../../../components/autocomplete/SimpleAutocomplete';
import FormatText from '../../../i18n'
import Config from '../../../config'

const ClinicRegistration = props => {
  const { paxList, _setCompleteRequest, userInfo, _setCityList, _setOpenScreen, } = props

  const [state, setState] = useState({ city: '', submitErrorMessage: '', clinic_parents: [] })

  useEffect(() => {
    _setOpenScreen("Clinic Registration")

  }, [_setOpenScreen])

  useEffect(() => {
    // console.log("HMM")
    const { name, email, phone } = userInfo
    // let name = userInfo.name ? userInfo.name : ''
    // let email = userInfo.email ? userInfo.email : ''
    // let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let cityRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_LIST",
      urlParams: "/cities?&citykey=a",
      customHeaders
    }

    // let clinic_parent_request = {
    //   request: {},
    //   method: "GET",
    //   serviceCode: "GET_CLINIC_PARENTS",
    //   // urlParams: "/cities?&citykey=a",
    //   customHeaders
    // }

    _setCompleteRequest(cityRequest)
    // _setCompleteRequest(clinic_parent_request)
    // _setSecondRequest(cityRequest)
  }, [_setCompleteRequest, userInfo])

  useEffect(() => {
    // console.log("DIPANGGIL BERAPA KALI???")
    // if (paxList.clinic_parents.length === 1) setState({ ...state, parent_id: parseInt(paxList.clinic_parents[0].id) })
    setState(previousState => ({
      ...previousState,
      clinic_parents: paxList.clinic_parents,
      parent_id: paxList.clinic_parents.length === 1 ? parseInt(paxList.clinic_parents[0].id) : 0
    }))
  }, [paxList.clinic_parents])

  const handleChange = prop => event => {
    let valid = FormValidation(prop, event.target.value)
    setState({
      ...state,
      [prop]: event.target.value,
      [prop + 'ErrorMessage']: valid.errorMessage,
      isError: valid.error,
      submitErrorMessage: ''
    })
  }

  const handleInputChange = val => {
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let cityRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CITY_LIST",
      urlParams: `/cities?&citykey=${val}`,
      customHeaders
    }

    if (paxList.cityList && val) {
      _setCompleteRequest(cityRequest)
    }
    else {
      _setCityList([{ name: '' }])
    }
  }

  const handlePick = city => {
    // console.log("ONPICK: ", city)
    setState({
      ...state,
      city: city ? city.city : '',
      submitErrorMessage: '',
    })
  }

  const handleSubmit = e => {
    e.preventDefault()
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    const { city, clinicName, address, } = state    // parent_id } = state
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    if (city && clinicName && address) {
      // HARDCODED PARENT_ID
      let regisClinicRequest = {
        request: {
          city,
          name: clinicName,
          address,
          parent_id: Config.parentClinic,
          vendor_close: { day: [], date: [] },
          products: [],
          operational: [
            {
              "day": 1,
              "hour": "08:00-17:00"
            },
            {
              "day": 2,
              "hour": "08:00-17:00"
            },
            {
              "day": 3,
              "hour": "08:00-17:00"
            },
            {
              "day": 4,
              "hour": "08:00-17:00"
            },
            {
              "day": 5,
              "hour": "08:00-17:00"
            },
            {
              "day": 6,
              "hour": "08:00-17:00"
            }
          ]
        },
        method: "POST",
        serviceCode: "CLINIC_REGISTRATION",
        customHeaders
      }
      _setCompleteRequest(regisClinicRequest)
      // console.log(regisClinicRequest)

    } else {
      setState({
        ...state,
        submitErrorMessage: FormatText('err_submit'),
      })
    }

  }

  return (
    <Grid container direction="row" justify="center" alignItems="center" >
      <Card variant="elevation" style={{ marginTop: 20 }} >
        <CardContent style={{ width: 320, textAlign: 'center' }}>
          <Typography variant="h5" style={{ marginBottom: 15 }} ><b>{FormatText('app_register')}</b> </Typography>
          <Grid container item direction="column" >
            <FormControl fullWidth style={{ marginBottom: 15 }} >
              <InputLabel error={state.clinicNameErrorMessage ? true : false} htmlFor="nameClinic">{FormatText('clinic_name')}</InputLabel>
              <Input
                error={state.clinicNameErrorMessage ? true : false}
                id="standard-adornment-name"
                type='text'
                onChange={handleChange('clinicName')}
              />
              {state.clinicNameErrorMessage ? <FormHelperText error id="name-helper">{state.clinicNameErrorMessage}</FormHelperText> : null}
            </FormControl>

            <FormControl fullWidth style={{ marginBottom: 15, marginRight: 5 }} >
              <SimpleAutocomplete
                id="registerClinicChooseCity"
                options={props.paxList.cityList[0] ? props.paxList.cityList : []}
                // width={300}
                optionItem={'city'}
                label={FormatText('clinic_city')}
                notShrink={true}
                // onSelect={handleSelect}
                handleInputChange={handleInputChange}
                onPick={handlePick}
              />
            </FormControl>

            <FormControl fullWidth style={{ marginBottom: 15 }} >
              <InputLabel error={state.phoneErrorMessage ? true : false} htmlFor="standard-address">{FormatText('clinic_address')}</InputLabel>
              <Input
                error={state.addressErrorMessage ? true : false}
                id="standard-adornment-address"
                type='text'
                onChange={handleChange('address')}
              />
              {state.addressErrorMessage ? <FormHelperText error id="address-helper-text">{state.addressErrorMessage}</FormHelperText> : null}
            </FormControl>
            <br />
            <br />


            <Button onClick={handleSubmit} fullWidth variant="contained" color="primary">{FormatText('app_register')}</Button>
            {state.submitErrorMessage ? <FormHelperText error id="email-helper-text">{state.submitErrorMessage}</FormHelperText> : null}

          </Grid>
        </CardContent>
      </Card>
      <Grid container>
        <div style={{ height: window.screen.height - 550 }}></div>
      </Grid>
    </Grid>
  )
}

const mapStateToProps = store => ({
  clientList: store.clientList.list,
  userDetail: store.clientList.detail,
  userFiltered: store.clientList.filteredList,
  userInfo: store.userInfo.info,
  paxlistStatus: store.menu.paxList,
  paxList: store.paxList
})

const mapDispatchToProps = dispatch => {
  return {
    _setOpenScreen: val => dispatch(setOpenScreen(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.urlParams))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
    _setClinicDetail: val => dispatch(setClinikDetail(val)),
    _setCityList: val => dispatch(setCityList(val)),
    _setSecondRequest: val => dispatch(setSecondRequest(val)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClinicRegistration)