
import React, { Component } from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { FormValidation } from "../../utils/FormValidation";
import FormatText from '../../i18n';
import GoogleLogin from '../login/GoogleLogin'

class RegisterScreen extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name: null,
      nameErrorMessage: '',
      phone: null,
      phoneErrorMessage: '',
      email: '',
      emailErrorMessage: '',
      password: '',
      passwordErrorMessage: '',
      confirmPassword: "",
      confirmPasswordErrorMessage: '',
      showPassword: false,
      showConfirm: false,
      isError: true,
      submitErrorMessage: "",
    }
  }

  handleSubmit = event => {
    event.preventDefault()
    const { name, phone, email, password, confirmPassword, isError } = this.state

    if (name && email && password && confirmPassword && !isError) {
      let registerRequest = { name, email, password, redirectURI: "http://www.alamanak.co.id", }
      if (phone) registerRequest.phone = phone[0] === '0' ? '+62' + phone.slice(1) : phone
      this.props._setRequest(registerRequest)
      this.props._setServiceCode("register")
      // set CLient for development only in Live will automatically choosen by domain
      // coment out all on live
      this.props._setServiceCustomHeaders({
        // change clientID here in development
        clientId: 'voltras-health'
      })
      this.props._setServiceStatus("active")
    }
    else {
      this.setState({
        ...this.state,
        isError: true,
        submitErrorMessage: FormatText('err_submit')
      })
    }
  }

  handleChange = prop => event => {
    let valid = FormValidation(prop, event.target.value)
    if (prop === 'confirmPassword') {
      if (this.state.password !== event.target.value) {
        this.setState({
          ...this.state,
          isError: true,
          [prop]: event.target.value,
          [prop + 'ErrorMessage']: FormatText('password confirmation not match'),
        });
      }
      else {
        this.setState({
          ...this.state,
          isError: false,
          [prop]: event.target.value,
          [prop + 'ErrorMessage']: '',
          submitErrorMessage: "",
        });
      }
    }
    else if (prop === 'phone' && !event.target.value) {
      this.setState({
        ...this.state,
        isError: false,
        [prop]: event.target.value,
        [prop + 'ErrorMessage']: '',
        submitErrorMessage: "",
      });
    }
    else {
      this.setState({
        ...this.state,
        [prop]: event.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        isError: valid.error,
        submitErrorMessage: "",
      });
    }
  }

  handleMouseDownPassword(event) {
    event.preventDefault()
  }

  handleGoogleLogin = request => {
    /*
      Sementara hanya voltras-health yg bisa login by google
    */
    let domainClientId = 'voltras-health'
    let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'login', customHeaders: { clientId: domainClientId }, }
    this.props._setCompleteRequest(fullRequest)
  }


  render() {
    const { classes } = this.props;
    return (
      <div >
        <Grid container direction="row" justify="center" alignItems="center" >
          <Card className={classes.root} elevation={4}>
            <CardContent style={{ width: 320, textAlign: 'center' }}>
              <Typography color='primary' variant="h5" style={{ marginBottom: 15 }} ><b>{FormatText('app_sign_up')}</b> </Typography>
              <Grid container item direction="column" justify="center" alignItems="center" >

                <GoogleLogin
                  params={this.props.system.googleParams}
                  handleGoogleLogin={this.handleGoogleLogin}
                  buttonText={FormatText('common_signup_with')}
                  width={280}
                />

                <Typography variant='subtitle2' style={{ marginTop: 20 }} >{FormatText('common_or')}</Typography>
                <FormControl fullWidth style={{ marginBottom: 5 }} >
                  <InputLabel error={this.state.nameErrorMessage ? true : false} htmlFor="standard-adornment-password">
                    {FormatText('app_name')} <span style={{ color: 'red' }}>*</span>
                  </InputLabel>
                  <Input
                    error={this.state.nameErrorMessage ? true : false}
                    id="standard-adornment-name"
                    type='text'
                    onChange={this.handleChange('name')}
                  />
                  {
                    this.state.nameErrorMessage
                      ? <FormHelperText error id="name-helper-text">{this.state.nameErrorMessage}</FormHelperText>
                      : null
                  }
                </FormControl>

                <FormControl fullWidth style={{ marginBottom: 5 }} >
                  <InputLabel error={this.state.emailErrorMessage ? true : false} htmlFor="standard-adornment-password">
                    {FormatText('app_email')} <span style={{ color: 'red' }}>*</span>
                  </InputLabel>
                  <Input
                    error={this.state.emailErrorMessage ? true : false}
                    id="standard-adornment-email"
                    type='text'
                    onChange={this.handleChange('email')}
                  />
                  {
                    this.state.emailErrorMessage
                      ? <FormHelperText error id="email-helper-text">{this.state.emailErrorMessage}</FormHelperText>
                      : null
                  }
                </FormControl>

                <FormControl fullWidth style={{ marginBottom: 5 }} >
                  <InputLabel error={this.state.phoneErrorMessage ? true : false} htmlFor="standard-adornment-password">
                    {FormatText('app_phone')}
                  </InputLabel>
                  <Input
                    error={this.state.phoneErrorMessage ? true : false}
                    id="standard-adornment-phone"
                    type='text'
                    onChange={this.handleChange('phone')}
                  />
                  {
                    this.state.phoneErrorMessage
                      ? <FormHelperText error id="phone-helper-text">{this.state.phoneErrorMessage}</FormHelperText>
                      : <FormHelperText id="phone-helper-text">{FormatText('app_not_mandatory')}</FormHelperText>
                  }
                </FormControl>

                <FormControl fullWidth style={{ marginBottom: 5 }}  >
                  <InputLabel error={this.state.passwordErrorMessage ? true : false} htmlFor="standard-adornment-password">
                    {FormatText('app_password')} <span style={{ color: 'red' }}>*</span>
                  </InputLabel>
                  <Input
                    error={this.state.passwordErrorMessage ? true : false}
                    id="standard-adornment-password"
                    type={this.state.showPassword ? 'text' : 'password'}
                    value={this.state.password}
                    onChange={this.handleChange('password')}
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={() => this.setState({ ...this.state, showPassword: !this.state.showPassword })}
                          onMouseDown={this.handleMouseDownPassword}
                        >
                          {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    }
                  />
                  {
                    this.state.passwordErrorMessage
                      ? <FormHelperText error id="email-helper-text">{this.state.passwordErrorMessage}</FormHelperText>
                      : null
                  }
                </FormControl>

                <FormControl fullWidth style={{ marginBottom: 5 }}  >
                  <InputLabel error={this.state.confirmPasswordErrorMessage ? true : false} htmlFor="standard-adornment-password">
                    {FormatText('app_password_confirmation')} <span style={{ color: 'red' }}>*</span>
                  </InputLabel>
                  <Input
                    error={this.state.confirmPasswordErrorMessage ? true : false}
                    id="standard-adornment-confirm"
                    type={this.state.showConfirm ? 'text' : 'password'}
                    onChange={this.handleChange('confirmPassword')}
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle confirm visibility"
                          onClick={() => this.setState({ ...this.state, showConfirm: !this.state.showConfirm })}
                          onMouseDown={this.handleMouseDownPassword}
                        >
                          {this.state.showConfirm ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    }
                  />
                  {
                    this.state.confirmPasswordErrorMessage
                      ? <FormHelperText error id="email-helper-text">{this.state.confirmPasswordErrorMessage}</FormHelperText>
                      : null
                  }
                </FormControl>

                <Button
                  fullWidth
                  onClick={this.handleSubmit}
                  variant="contained"
                  color="primary"
                  style={{ textTransform: 'none', fontWeight: 'bold', marginTop: 20 }}
                >
                  {FormatText('app_sign_up')}
                </Button>
                {
                  this.state.submitErrorMessage
                    ? <FormHelperText error id="email-helper-text">{this.state.submitErrorMessage}</FormHelperText>
                    : null
                }

              </Grid>
              <Grid>

                <div style={{ marginTop: 10 }}>
                  <Typography variant='caption' >{FormatText('app_have_account')}</Typography>
                  <Button onClick={() => this.props.history.push('/Login')} color="primary" style={{ textTransform: 'none', fontWeight: 'bold' }} >
                    {FormatText('app_sign_in')}
                  </Button>
                </div>
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </div>
    )
  }
}

export const useLoginStyles = (theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  margin: {
    margin: 10,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  withoutLabel: {
    marginTop: theme.spacing(3),
  },
  textField: {
    width: '25ch',
  },
});

export default RegisterScreen