import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux'
import { Grid } from '@material-ui/core';
import { setOpenScreen } from "../../../actions/SystemActions";
import {
    setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
    setServiceParams, setServiceCustomHeaders, setServiceRole,
} from '../../../actions/ServiceActions'
// import ClientRUDTable from '../userlist/UserRUDTable'
import TableCustomize from '../../../components/table/TableCustomize'
import RequestDetail from './RequestDetail'
// import ConfirmationDialogRaw from '../userlist/ConfirmationDIalog'
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import AddRequestSpesificationDialog from './AddRequestSpesificationDialog'
import EditRequestSpesificationDialog from './EditRequestSpesificationDialog'

const RequestList = ({ ...props }) => {

    const { _setOpenScreen, _setCompleteRequest, userInfo, requestList, clientList } = props
    // const [updateRequest, setUpdateRequest] = useState(false)

    useEffect(() => {
        _setOpenScreen("Request List")
    }, [_setOpenScreen])

    const callbackRequest = () => {
        let name = userInfo.name
        let email = userInfo.email
        let phone = userInfo.phone
        let customHeaders = { name, email }
        if (phone) customHeaders.phone = phone

        let requestAllClient = {
            request: {},
            method: "GET",
            serviceCode: "SELECT_ALL_REQUEST",
            customHeaders
        }
        if (requestList.length === 0) { _setCompleteRequest(requestAllClient) }
        // callback('client')
    }

    useEffect(callbackRequest, [])

    const [values, setValues] = useState({
        columns: [
            { title: 'Client Id', field: 'clientId', },
            // { title: 'Origin URI', field: 'originURIs', },
            { title: 'Path', field: 'path', },
            { title: 'URL Destination', field: 'urlDestination' },
            { title: "Need Login", field: "isNeedLogin" },
            { title: "Priority Value", field: "priorityValue" },
            { title: "Default Role", field: "defaultRole" },
            // { title: "Actions"}
        ],
        data: [],
        dataClient: [],
        dataToProcess: null,
        dataToUpdate: null
    });

    useEffect(() => {
        setValues(values => ({
            ...values,
            data: requestList,
            dataClient: clientList
        }))
    }, [requestList, clientList])

    const [states, setStates] = useState({
        openDialogRequest: false,
        editDialogRequest: false

    })
    const handleOpenDialogRequest = (open, edit, data) => {
        // console.log(edit)
        if (edit) {
            setStates({
                ...states,
                editDialogRequest: open
            })
            setValues({
                ...values,
                dataToUpdate: data
            })
        } else {

            setStates({
                ...states,
                openDialogRequest: open
            })
        }
    }

    // const handleDelete = () => {

    // }

    const handleUpdateRequest = (state, isPriorityValueUpdate) => {
        let requestSpecificationId = state.requestSpecificationId
        delete state.method

        if (state.isNeedLogin === 'True' || state.isNeedLogin === true) {
            state.isNeedLogin = true
        }
        else {
            state.isNeedLogin = false
        }
        let request = {
            ...state,
            priorityValue: state.priorityValue ? Number(state.priorityValue) : null
        }

        // delete request.requestSpecificationId
        if (!state.priorityValue) {
            delete request.priorityValue
        }

        if (!isPriorityValueUpdate) {
            delete request.priorityValue
        }

        delete request.isPriorityValueUpdate
        let requestUpdate = {
            request,
            method: "PATCH",
            serviceCode: "UPDATE_REQUEST_SPESIFICATIONS",
            urlParams: `/request-specifications/${requestSpecificationId}`
        }
        // console.log('Request', request)
        _setCompleteRequest(requestUpdate)
        handleOpenDialogRequest(false, true)
    }
    // const handleRowClicked = () => {

    // }

    // const handleChoose = () => {

    // }

    const handleAddRequest = (state) => {
        let request = {
            ...state,
            priorityValue: state.priorityValue ? Number(state.priorityValue) : null
        }
        let requestAllClient = {
            request,
            method: "POST",
            serviceCode: "ADD_REQUEST_SPESIFICATIONS",
            urlParams: '/request-specifications'
        }
        // console.log('Request', request)
        _setCompleteRequest(requestAllClient)
        handleOpenDialogRequest(false, false)
    }
    const findDuplicateClientId = (arrayOfObj, key) => {
        return arrayOfObj.filter((item, index, array) => {
            return array.map((mapItem) => mapItem[key]).indexOf(item[key]) === index
        })
    }
    return (
        <div >
            {states.open
                ? <RequestDetail
                    completeRequest={_setCompleteRequest}
                    open={states.open}
                    handleClose={() => setStates({ ...states, open: false })}
                    data={values.dataToProcess}
                />
                : <Grid container direction="row" justify="center" alignItems="flex-start" >
                    {/* <ClientRUDTable
                        confirmdeletetext="Apakah anda yakin menghapus data ini?"
                        title="Backoffice Request List"
                        columns={values.columns}
                        data={values.data}
                        // updatedata={handleUpdate}
                        deletedata={handleDelete}
                        rowClicked={handleRowClicked}
                        action
                    /> */}
                    <TableCustomize
                        title='Backoffice Request List'
                        column={values.columns}
                        data={values.data}
                        handleEditOpen={handleOpenDialogRequest}
                        filterById={findDuplicateClientId}
                        options={{
                            pagination: true,
                            filterClientId: true
                        }}
                    />
                </Grid>}
            <Grid style={{ marginTop: 20 }} container direction="row" alignItems="flex-start" >
                <Button style={{ fontSize: 12, fontWeight: 'bold' }} onClick={() => handleOpenDialogRequest(true, false)} variant='outlined' color="secondary"
                    startIcon={<AddIcon />}
                >
                    Add Request Specification
                </Button>
            </Grid>

            <AddRequestSpesificationDialog
                open={states.openDialogRequest}
                handleClose={handleOpenDialogRequest}
                completeRequest={handleAddRequest}
                data={values.data}
                dataClient={values.dataClient}
            />
            <EditRequestSpesificationDialog
                open={states.editDialogRequest}
                handleClose={handleOpenDialogRequest}
                data={values.data}
                dataClient={values.dataClient}
                dataToUpdate={values.dataToUpdate}
                completeRequest={handleUpdateRequest}

            />
            {/* < ConfirmationDialogRaw
                id="ringtone-menu"
                keepMounted
                open={open}
                onClose={handleClose}
                value={value}
                handleChoose={handleChoose}
                userName={userName}
            /> */}
        </div>
    );
}

const mapStateToProps = store => ({
    requestList: store.requestList.list,
    clientList: store.clientList.list,
    userDetail: store.clientList.detail,
    userFiltered: store.clientList.filteredList,
    userInfo: store.userInfo.info,
})

const mapDispatchToProps = dispatch => {
    return {
        _setOpenScreen: val => dispatch(setOpenScreen(val)),
        _setCompleteRequest: (val) => {
            dispatch(setServiceRequest(val.request))
            dispatch(setServiceMethod(val.method))
            dispatch(setServiceCode(val.serviceCode))
            dispatch(setServiceParams(val.urlParams))
            dispatch(setServiceCustomHeaders(val.customHeaders))
            dispatch(setServiceRole(val.serviceRole))
            dispatch(setServiceStatus("active"))
        },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(RequestList)