
import React, { useState, useEffect } from 'react';
import MaterialTable from 'material-table';
import Grid from '@material-ui/core/Grid'

export default function MaterialTableCRUD({ ...props }) {
  // console.log("TABLE PROPS: ", props)
  const { columns, data, updatedata, deletedata, rowClicked, confirmdeletetext, title, action, update, roleUsed } = props

  // const [open, setOpen] = React.useState(false);
  const [state, setState] = useState({
    columns: [],
    data: [],
    rowData: {},
  });

  useEffect(() => {
    setState(prevState => ({
      ...prevState,
      columns: columns,
      data: data
    }))
  }, [columns, data])

  const handleUpdate = (oldData) => updatedata(oldData)

  const handleDelete = async oldData => await deletedata(oldData)

  // const handleClickOpen = () => {
  //   setOpen(true);
  // };

  // const handleClose = () => {
  //   setOpen(false);
  // };

  const handleRowClick = rowData => {
    setState({ ...state, rowData })
    // console.log(rowData)
    rowClicked(rowData)
  }

  return (
    <Grid container item xs={12} >
      <MaterialTable
        localization={{
          body: {
            editRow: {
              deleteText: confirmdeletetext
            }
          }
        }}
        style={{ width: '100%' }}
        title={title}
        columns={state.columns}
        data={state.data}
        actions={[update && roleUsed !== 'member' ? {
          icon: 'check',
          tooltip: 'Activate Clinic',
          onClick: (event, rowData) => handleUpdate(rowData)
        } : null
        ]}
        editable={action ? {
          // onRowAdd: (newData) =>
          //   new Promise((resolve) => {
          //     setTimeout(() => {
          //       resolve();
          //       setState((prevState) => {
          //         const data = [...prevState.data];
          //         data.push(newData);
          //         return { ...prevState, data };
          //       });
          //     }, 600);
          //   }),
          // onRowUpdate: (newData, oldData) =>
          //   new Promise((resolve) => {
          //     handleUpdate(newData, oldData).then(result => resolve())
          //   }),
          onRowDelete: (oldData) =>
            new Promise((resolve) => {
              handleDelete(oldData).then(result => resolve())
            }),
        } : null}
        onRowClick={(e, rowData) => handleRowClick(rowData)}
        options={{
          actionsColumnIndex: -1,
          search: roleUsed === 'member' ? false : true,
          rowStyle: rowData => ({
            color: (rowData.status === "inactive") ? 'grey' : (rowData.status === "register") ? '#00f100' : 'black',
          })
        }}
      />
    </Grid >
  );
}
