
const initialstate = {
  snackBar: {
    open: false,
    severity: "info",
    message: "",
    position: 'bottom-center',
    action: "",
    actionLabel: '',
  },
  dialog: {
    open: false,
    message: ""
  },
  stepper: {
    open: false,
    step: 0,
    message: ""
  },
}

const feedback = (state = initialstate, action) => {
  switch (action.type) {
    case "SET_SNACKBAR":
      return {
        ...state,
        snackBar: action.val
      }
    case "SET_DIALOG":
      return {
        ...state,
        dialog: action.val
      }
    case 'LOGOUT':
      return initialstate
    default:
      return state
  }
}

export default feedback