
import React from 'react';
import TextField from '@material-ui/core/TextField';
import {
  Grid, Typography, Button, MenuItem, Card, CardContent,
  CardActions, List, ListItemText, FormHelperText
} from '@material-ui/core';
import Checkbox from '@material-ui/core/Checkbox';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

const AddUserClient = ({ ...props }) => {
  const { form, showAddUserClient, setShowAddUserClient, handleChange,
    chooseClientList, handleResource, handleAddClientRole, fullmd } = props

  return (
    <Grid item xs={12} md={fullmd ? 12 : 6} style={{ marginTop: 10 }}>
      <Grid container direction="column" justify="center" alignItems="center" >
        <Typography color='primary' variant='subtitle2' >User Client</Typography>
        <Grid container direction="row" justify="flex-start" alignItems="flex-start" style={{ marginTop: 10 }} >
          <Grid item xs={6}>
            <Grid container direction="column" justify='flex-start' alignItems='center' >
              <Grid item>
                Client Id
              </Grid>
            </Grid>
            <Grid container direction="row" justify='flex-start' alignItems='flex-start' >
              <Grid item>
                <List>
                  {form.assignedUserRole[0]
                    ? form.assignedUserRole.map((item, i) => (<ListItemText key={i} secondary={'- ' + item.clientId} />))
                    : <ListItemText secondary={'- none'} />}
                </List>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={6}>
            <Grid container direction="column" justify='flex-start' alignItems='center' >
              <Grid item>
                Resources Allowed
              </Grid>
            </Grid>
            <Grid container direction="row" justify='flex-start' alignItems='flex-start' >
              <Grid item>
                <List>
                  {form.assignedUserRole[0]
                    ? form.assignedUserRole[0].resourcesAllowedList.map((item, i) => (
                      <ListItemText key={i} secondary={'- ' + item} />
                    ))
                    : <ListItemText secondary={'- none'} />
                  }
                </List>
              </Grid>
            </Grid>
          </Grid>
          <Grid container direction="column" justify='flex-start' alignItems='flex-start' style={{ margin: 20 }}>
            {
              !showAddUserClient
                ? <div>
                  {form.assignedUserRole[0]
                    ? null
                    : <div>
                      <Button
                        // disabled={form.assignedUserRole[0] ? true : false}
                        size='small'
                        variant='outlined'
                        color='secondary'
                        onClick={() => setShowAddUserClient(true)}
                      >
                        Choose Client for User
                      </Button>
                      <FormHelperText>Only one client allowed on Create User.</FormHelperText>
                      <FormHelperText>Update the User on User List to add more client.</FormHelperText>
                    </div>
                  }
                </div>
                : <Card >
                  <CardContent>
                    <Grid>
                      <Typography variant='subtitle2'> Add Client and Resources </Typography>
                    </Grid>
                    <Grid>
                      <TextField
                        margin="dense"
                        size="small"
                        onChange={handleChange('clientId')}
                        id="standard-clientId"
                        label="ClientId"
                        placeholder="ClientId"
                        select
                        fullWidth
                        error={form.roleIdErrorMessage ? true : false}
                        value={form.clientId}
                      >
                        {chooseClientList.map((option, i) => (
                          <MenuItem  value={option.value} key={i+21}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </TextField>
                    </Grid>
                    <Grid >
                      {form.resourcelist[0]
                        ? <FormControl component="fieldset" style={{ marginTop: 10 }} >
                          <FormLabel component="legend">Resources</FormLabel>
                          <FormGroup>
                            {form.resourcelist.map((item, i) => (
                              <FormControlLabel
                                control={
                                  <Checkbox
                                    onChange={handleResource}
                                    id={item.roleId}
                                    name={item.roleId}
                                  />
                                }
                                label={item.roleId}
                                key={i}
                              />
                            ))}
                          </FormGroup>
                        </FormControl>
                        : null
                      }
                    </Grid>
                  </CardContent>

                  <CardActions>
                    <Button size="small" color="primary" onClick={() => setShowAddUserClient(false)} >
                      Cancel
                    </Button>
                    <Button size="small" color="secondary" onClick={handleAddClientRole} >
                      Add
                    </Button>
                  </CardActions>

                </Card>
            }

          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}

export default AddUserClient
