
import React from 'react';
import TextField from '@material-ui/core/TextField';
import { Grid, MenuItem, } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles'

const style = theme => ({
  asterisk: {
    color: 'red'
  }
})

const clientType = [
  { value: 'public', label: 'Public' },
  { value: "confidential", label: 'Confidential' },
]
const clientProfile = [
  { value: 'Web Application', label: 'Web Application' },
  { value: 'iOS', label: 'iOS' },
  { value: 'Android', label: 'Android' },
]

const CreateClientForm = ({ ...props }) => {
  const { form, handleChange, setFormFocus, formFocus,
    helpTextOrigin, setHelpTextOrigin, helpTextRedirect, setHelpTextRedirect,classes } = props

  return (
    <Grid item xs={12} md={6}>
      <Grid container direction="column" justify="center" alignItems="center">
        <Grid item xs={12} md={8} style={{ marginLeft: 15, marginRight: 15 }} >
          <TextField
            size="small"
            onChange={handleChange('clientId')}
            id="standard-clientId"
            label="Client Id"
            required
            // placeholder="Placeholder"
            helperText={form.clientIdErrorMessage ? form.clientIdErrorMessage : ""}
            error={form.clientIdErrorMessage ? true : false}
            fullWidth
            margin='dense'
            value={form.clientId}
            onFocus={() => setFormFocus({ ...formFocus, clientId: true })}
            onBlur={() => form.clientId ? setFormFocus({ ...formFocus, clientId: true }) : setFormFocus({ ...formFocus, clientId: false })}
            InputLabelProps={{
              shrink: formFocus.clientId ? true : false,
              classes: {
                asterisk: classes.asterisk
              }
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('clientName')}
            id="standard-clientName"
            label="Client Name"
            required
            // placeholder="Placeholder"
            // helperText="Full width!"
            helperText={form.clientNameErrorMessage ? form.clientNameErrorMessage : ""}
            error={form.clientNameErrorMessage ? true : false}
            fullWidth
            margin="dense"
            value={form.clientName}
            onFocus={() => setFormFocus({ ...formFocus, clientName: true })}
            onBlur={() => form.clientName ? setFormFocus({ ...formFocus, clientName: true }) : setFormFocus({ ...formFocus, clientName: false })}
            InputLabelProps={{
              shrink: formFocus.clientName ? true : false,
              classes: {
                asterisk: classes.asterisk
              }
            }}
          />

          <TextField
            size="small"
            onChange={handleChange('clientType')}
            id="standard-clientType"
            required
            label="Client Type"
            select
            value={form.clientType}
            // placeholder="Placeholder"
            // helperText="Full width!"
            InputLabelProps={{
              classes: {
                asterisk: classes.asterisk
              }
            }}
            fullWidth
            margin="dense"
          >
            {clientType.map((option, i) => (
              <MenuItem value={option.value} key={i + 2}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
          <TextField
            size="small"
            onChange={handleChange('profile')}
            id="standard-clientProfile"
            required
            label="Client Profile"
            select
            value={form.profile}
            // placeholder="Placeholder"
            // helperText="Full width!"
            fullWidth
            margin="dense"
            InputLabelProps={{
              classes: {
                asterisk: classes.asterisk
              }
            }}
          >
            {clientProfile.map((option, i) => (
              <MenuItem value={option.value} key={i + 11}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
          <TextField
            size="small"
            onChange={handleChange('clientDomain')}
            id="standard-clientName"
            label="Client Domain"
            // placeholder="Placeholder"
            // helperText="Full width!"
            helperText={form.clientDomainErrorMessage ? form.clientDomainErrorMessage : ""}
            error={form.clientDomainErrorMessage ? true : false}
            fullWidth
            margin="dense"
            value={form.clientDomain}
            onFocus={() => setFormFocus({ ...formFocus, clientDomain: true })}
            onBlur={() => form.clientDomain ? setFormFocus({ ...formFocus, clientDomain: true }) : setFormFocus({ ...formFocus, clientDomain: false })}
            InputLabelProps={{
              shrink: formFocus.clientDomain ? true : false
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('emailSender')}
            id="standard-clientId"

            label="Email Sender"
            // placeholder="Placeholder"
            helperText={form.emailSenderErrorMessage ? form.emailSenderErrorMessage : ""}
            error={form.emailSenderErrorMessage ? true : false}
            fullWidth
            margin='dense'
            value={form.emailSender}
            onFocus={() => setFormFocus({ ...formFocus, emailSender: true })}
            onBlur={() => form.emailSender ? setFormFocus({ ...formFocus, emailSender: true }) : setFormFocus({ ...formFocus, emailSender: false })}
            InputLabelProps={{
              shrink: formFocus.emailSender ? true : false
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('emailSenderMaskingName')}
            id="standard-clientId"
            label="Email Sender Masking Name"
            // placeholder="Placeholder"
            helperText={form.emailSenderMaskingNameErrorMessage ? form.emailSenderMaskingNameErrorMessage : ""}
            error={form.emailSenderMaskingNameErrorMessage ? true : false}
            fullWidth
            margin='dense'
            value={form.emailSenderMaskingName}
            onFocus={() => setFormFocus({ ...formFocus, emailSenderMaskingName: true })}
            onBlur={() => form.emailSenderMaskingName ? setFormFocus({ ...formFocus, emailSenderMaskingName: true }) : setFormFocus({ ...formFocus, emailSenderMaskingName: false })}
            InputLabelProps={{
              shrink: formFocus.emailSenderMaskingName ? true : false
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('originURIs')}
            id="standard-originURIs"
            label="Origin URI"
            required
            placeholder="http://example.com"
            helperText={helpTextOrigin ? "Press Enter to add more URI!" : ''}
            fullWidth
            multiline
            margin="dense"
            value={form.originURIs}
            onFocus={() => { setFormFocus({ ...formFocus, originURIs: true }); setHelpTextOrigin(true) }}
            onBlur={() => form.originURIs ? setFormFocus({ ...formFocus, originURIs: true }) : setFormFocus({ ...formFocus, originURIs: false })}
            InputLabelProps={{
              shrink: formFocus.originURIs ? true : false,
              classes: {
                asterisk: classes.asterisk
              }
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('redirectURIs')}
            id="standard-redirectURIs"
            required
            label="Redirect URI"
            placeholder="http://example.com/home"
            helperText={helpTextRedirect ? "Press Enter to add more URI!" : ""}
            fullWidth
            multiline
            margin='dense'
            value={form.redirectURIs}
            onFocus={() => { setFormFocus({ ...formFocus, redirectURIs: true }); setHelpTextRedirect(true) }}
            onBlur={() => form.redirectURIs ? setFormFocus({ ...formFocus, redirectURIs: true }) : setFormFocus({ ...formFocus, redirectURIs: false })}
            InputLabelProps={{
              shrink: formFocus.redirectURIs ? true : false,
              classes: {
                asterisk: classes.asterisk
              }
            }}
          />
          <TextField
            size="small"
            onChange={handleChange('initialRole')}
            id="standard-initialRole"
            required
            label="Initial Role"
            // placeholder=""
            helperText={form.initialRoleErrorMessage ? form.initialRoleErrorMessage : ""}
            error={form.initialRoleErrorMessage ? true : false}
            fullWidth
            multiline
            margin='dense'
            value={form.initialRole}
            InputLabelProps={{
              classes: {
                asterisk: classes.asterisk
              }
            }}
          // onFocus={() => { setFormFocus({ ...formFocus, initialRole: true }); setHelpTextRedirect(true) }}
          // onBlur={() => form.initialRole ? setFormFocus({ ...formFocus, initialRole: true }) : setFormFocus({ ...formFocus, initialRole: false })}
          // InputLabelProps={{
          //   shrink: formFocus.initialRole ? true : false
          // }}
          />
        </Grid>
      </Grid>
    </Grid>
  )

}

export default withStyles(style)(CreateClientForm)
