
import React, { Component } from 'react';
import { connect } from 'react-redux'
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import { Button } from '@material-ui/core';
import {
  setServiceCode, setServiceCustomHeaders, setServiceMethod, setServiceParams,
  setServiceRequest, setServiceRole, setServiceStatus
} from '../../actions/ServiceActions';

const mapStateToProps = state => {
  return {
    feedback: state.feedback,
    locale: state.system.locale,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.urlParams))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },

  }
}

class MySnackBar extends Component {
  constructor(props) {
    super(props)
    this.state = {
      open: false,
      severity: "",
      message: "",
      action: false,
      actionLabel: ""
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    let feedback = nextProps.feedback.snackBar
    let open = feedback.open
    let message = feedback.message
    if (open && message) {
      this.setState({ ...this.state, ...feedback, open: true, })
      if (feedback.redirect) {
        setTimeout(() => {
          this.props.history.push(`/${feedback.redirect}`)
        }, 2000);
      }
    }
    else {
      this.setState({ ...this.state, open: false, })
    }
  }

  componentWillUnmount() {
    this.setState({
      open: false,
      severity: "",
      message: "",
      action: false,
      actionLabel: ""
    })
  }

  handleClose = () => {
    this.setState({ ...this.state, open: false })
  }

  handleAction = () => {
    let principalType = ''
    let lang = this.props.locale ? this.props.locale.slice(0, 2) : 'en'
    let redirectURI = "http://www.alamanak.co.id"
    let actionType = this.state.action
    if (actionType === 'SEND_EMAIL_VERIFICATION') {
      principalType = 'email'
    }
    if (actionType === 'SEND_PHONE_VERIFICATION') {
      principalType = 'phone'
    }
    //  GET /verification-code/{principalType}?lang={lang}&redirectURI={redirectURI}
    let actionRequest = {
      method: 'GET',
      request: {},
      serviceCode: actionType,
      urlParams: `/${principalType}?&lang=${lang}&redirectURI=${redirectURI}`
    }
    this.props._setCompleteRequest(actionRequest)
    this.setState({ ...this.state, open: false })
  }

  render() {
    const { open, message, position, severity, action, actionLabel, duration } = this.state
    return (
      <div >
        <Snackbar
          anchorOrigin={position
            ? { vertical: position.split('-')[0], horizontal: position.split('-')[1] }
            : { vertical: 'bottom', horizontal: 'center' }}
          open={open}
          autoHideDuration={duration ? duration : 4000}
          onClose={this.handleClose}
          message={message}
        >
          <MuiAlert
            elevation={6}
            variant="filled"
            onClose={this.handleClose}
            severity={severity}
            action={action ? (
              <Button color='primary' variant='contained' onClick={this.handleAction} style={{ textTransform: 'none', fontWeight: 'bold', }}>
                {actionLabel}
              </Button>
            ) : null}
          >
            {message}
          </MuiAlert>
        </Snackbar>
      </div>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MySnackBar)
