import React, { Component } from 'react'
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
// import QrReader from 'react-qr-reader'
import QrReader from 'react-qr-scanner'

class QrScreen extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isQrShow: false,
      result: 'no result',
    }
  }

  componentWillMount() {
    this.props._setOpenScreen("QR Scan")
    this.props._serviceStart()
  }

  componentWillUnmount() {
    this.props._serviceEnd()
  }

  handleQrShow = () => {
    this.setState({
      ...this.state,
      isQrShow: !this.state.isQrShow
    })
  }

  handleScan = data => {
    let next = this.props.userInfo.info
    let name = next.basicInformation ? next.basicInformation.name : ''
    let email = next.email ? next.email : ''
    let phone = next.phone ? next.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone


    if (typeof (data) === 'string' || typeof (data) === 'number') {
      let cekData = data.toString().replace(/\s/g, '').length
      // console.log("QR CEK DATA: ", cekData)
      if (cekData > 0) {
        this.props._serviceEnd()
        // console.log("QR RESULT DATA: ", data)
        // request by id to service
        let healthOrderRequest = {
          request: {},
          method: "GET",
          // serviceCode: "healthOrder",
          // params: `/${data.toString()}`,
          serviceCode: "PURCHASE_VOUCHER_NUMBER",
          params: `/purchases/${data.toString()}`,
          customHeaders
        }
        this.props._setCompleteRequest(healthOrderRequest)

        // // find id from paxlist
        // let list = this.props.paxList
        // let pax = list.filter(item => item.id.toString() === data.toString())
        // if (pax[0]) {
        //   this.props._setPaxDetail(pax[0])
        //   this.props.history.push('/PaxDetail')
        // }
        // else {
        //   this.props.history.push('/Home')
        // }

      }
      else {
        // sent error message to snackbar
        // back home
        let responseErr = {
          headers: {},
          body: {
            error: true,
            errorDescription: {
              id: "QR Code anda tidak teridentifikasi",
              en: "Unidentified QR Code"
            }
          }
        }
        this.props._setResponse(responseErr)
        this.props._serviceEnd()
        this.props.history.push('/Home')
      }
    }
  }

  handleError = err => {
    console.error(err)
  }


  render() {
    return (
      <div>
        <Grid container >
          <Grid md={4} item>

          </Grid>
          <Grid xs={12} sm={12} md={4} item>
            <Card>
              <CardContent >
                <div>
                  <QrReader
                    // delay={500}
                    onError={this.handleError}
                    onScan={this.handleScan}
                    style={{ width: '100%', }}
                    resolution={1200}
                    // legacyMode={true}
                    facingMode="rear"
                  />
                </div>
              </CardContent>
            </Card>
          </Grid>
          <Grid md={4} item>

          </Grid>
        </Grid>
      </div>
    )
  }

}

export default QrScreen
