
import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'

import { Grid, Card,Typography } from '@material-ui/core'
import 'date-fns';

import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, setSecondRequest,
} from '../../../actions/ServiceActions'
import { setClinikDetail } from "../../../actions/PaxListActions";
import { setSnackbar } from '../../../actions/FeedbackActions';

import ClinicRUDTable from '../../voltrasadmin/userlist/UserRUDTable'
import ConfirmationDialogRaw from '../../voltrasadmin/userlist/ConfirmationDIalog';
import FormatText from '../../../i18n'
import FilterForm from './FilterForm'
import Hidden from '@material-ui/core/Hidden'
import ListTableMobile from '../../../components/list/ListTableMobile'
import Delete from '@material-ui/icons/DeleteOutline'
import Check from '@material-ui/icons/Check'

const ClinicList = ({ ...props }) => {
  const { _setOpenScreen, _setCompleteRequest, userInfo, _setSecondRequest, _setSnackbar,
    paxList, _setClinicDetail, history, roleUsed } = props
  const [columns, setColumns] = useState([])
  const [data, setData] = useState([])
  const [open, setOpen] = useState(false);
  const [dataUpdate, setDataUpdate] = useState(null)

  useEffect(() => {
    _setOpenScreen("Clinic List")
    window.scrollTo(0, 0)
    let columns = [
      { title: "ID", field: 'id', width: 100 },
      { title: FormatText('clinic_name'), field: 'name', width: 300 },
      { title: FormatText('common_address'), field: 'address' },
      { title: 'Status', field: 'status' },
    ]
    setColumns(columns)
    const { name, email, phone } = userInfo
    // let name = userInfo.name ? userInfo.name : ''
    // let email = userInfo.email ? userInfo.email : ''
    // let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email, }
    if (phone) customHeaders.phone = phone

    const healthKlinikREquest = {
      request: {},
      method: "GET",
      serviceCode: "healthKlinik",
      urlParams: "",
      customHeaders,
      // serviceRole: 'health.admin',
    }

    // if (!paxList || (paxList && paxList.klinikList.length === 0)) {
    _setCompleteRequest(healthKlinikREquest)
    // }

  }, [_setOpenScreen, _setCompleteRequest, userInfo])

  useEffect(() => {
    let orderedData = paxList.klinikList ? paxList.klinikList : []
    if (Array.isArray(orderedData) && orderedData.length > 0) {
      orderedData.sort((a, b) => {
        let x = a.id
        let y = b.id
        return x - y
      })
      setData(orderedData)
    }
    else setData([])
  }, [paxList])

  const handleUpdate = data => {
    // console.log(data)
    if (data.status === 'active') {
      let successSnackbar = { open: true, severity: "success", message: FormatText('clinic_active'), }
      _setSnackbar(successSnackbar)
    }
    else {
      setDataUpdate(data)
      setOpen(true)
    }
  }

  const handleDelete = data => {
    // DELETE /health/admin/clinics/{id}/users/{userid}
    let id = data.id
    let name = userInfo.name ? userInfo.name : ''
    let email = userInfo.email ? userInfo.email : ''
    let phone = userInfo.phone ? userInfo.phone : ''
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    let deleteRequest = {
      request: {},
      method: "DELETE",
      serviceCode: "DELETE_CLINIC",
      urlParams: `/${id}`,
      customHeaders,
    }

    let getClinicRequest = {
      request: {},
      method: "GET",
      serviceCode: "healthKlinik",
      urlParams: "",
      customHeaders,
    }

    if (data.status === 'inactive') {
      let successSnackbar = { open: true, severity: "success", message: FormatText('clinic_inactive'), }
      _setSnackbar(successSnackbar)
    } else {
      _setCompleteRequest(deleteRequest)
      _setSecondRequest(getClinicRequest)
    }

  }

  const handleRowClicked = data => {
    _setClinicDetail(data)
    // history.push('/KSetting')
    // console.log("DATA: ", data)
    const id = data.id
    const { name, email, phone } = userInfo

    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    let getClinicUserRequest = {
      request: {},
      method: "GET",
      serviceCode: "GET_CLINIC_USER",
      urlParams: `/${id}`,
      customHeaders,
    }
    if (roleUsed === 'member') history.push('/KSetting')
    else _setCompleteRequest(getClinicUserRequest)
  }

  const handleChoose = val => {
    const { name, email, phone } = userInfo
    const customHeaders = { name, email, }
    if (phone) customHeaders.phone = phone
    let regisClinicRequest = {
      request: {},
      method: "POST",
      serviceCode: "SEND_CLINIC_ACTIVATION",
      urlParams: `/${dataUpdate.id}/activation`,
      customHeaders,
    }
    _setCompleteRequest(regisClinicRequest)

    // let addClinicUserRequest = {
    //   request: [
    //     { 
    //     name: dataUpdate.created_by.name, 
    //     email: dataUpdate.created_by.email, 
    //     phone: dataUpdate.created_by.name.phone, 
    //     role: "clinic.admin" }
    //   ],
    //   method: "POST",
    //   serviceCode: "ADD_CLINIC_USER",
    //   urlParams: `/${dataUpdate.id}/users`,
    //   customHeaders,
    // }
    // _setSecondRequest(addClinicUserRequest)
  }

  const handleClose = () => {
    setOpen(false);
  };

  const setFilteredData = filter => {
    // console.log(filter)
    let orderedData = paxList.klinikList ? paxList.klinikList : []
    let filteredData = []
    if (filter === 'active') {
      // console.log(orderedData)
      filteredData = orderedData.filter(item => item.status === 'active')
    }
    else if (filter === 'register') {
      // console.log(orderedData)
      filteredData = orderedData.filter(item => item.status === 'register')
    }
    else filteredData = orderedData


    setData(filteredData)
  }

  return (
    <Grid>
      <Hidden smDown>
        <FilterForm props={props} setFilteredData={setFilteredData} />
        <ClinicRUDTable
          title={'Clinic List'}
          columns={columns}
          data={data}
          parentProps={props}
          updatedata={handleUpdate}
          deletedata={handleDelete}
          rowClicked={handleRowClicked}
          confirmdeletetext={FormatText('clinic_inactive_confirmation')}
          update
          action={roleUsed === 'member' ? false : true}
          roleUsed={roleUsed}
        />
        <ConfirmationDialogRaw
          id="ringtone-menu"
          keepMounted
          open={open}
          onClose={handleClose}
          header="Activate"
          content={dataUpdate && dataUpdate.name}
          handleChoose={handleChoose}
        />
        <Grid container>
          <div style={{ height: window.screen.height - 700 }} />
        </Grid>

      </Hidden>
      <Hidden smUp>
      <FilterForm props={props} setFilteredData={setFilteredData} />
        <Card style={{ marginTop: 10 }}>
          <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
            <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
              Clinic List
              </Typography>
          </div>
          <div style={{ marginLeft: 15, marginRight: 15 }}>
            <ListTableMobile
              data={data}
              column={columns}
              button={true}
              arrow={true}
              onClickList={handleRowClicked}
              action={true}
              IconDeleteCustomize={<Delete/>}
              IconUpdateCustomize={<Check/>}
              onClickDelete={handleDelete}
              onClickUpdate={handleUpdate}
              titleTooltipDelete='Delete'
              titleTooltipUpdate='Active'
            />
          </div>
        </Card>
      </Hidden>
    </Grid>
  )
}

const mapStateToProps = store => ({
  clientList: store.clientList.list,
  userDetail: store.clientList.detail,
  userFiltered: store.clientList.filteredList,
  userInfo: store.userInfo.info,
  paxlistStatus: store.menu.paxList,
  paxList: store.paxList,
  roleUsed: store.userInfo.roleUsed,
})

const mapDispatchToProps = dispatch => {
  return {
    _setOpenScreen: val => dispatch(setOpenScreen(val)), _setSnackbar: val => dispatch(setSnackbar(val)),
    _setSecondRequest: val => dispatch(setSecondRequest(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.urlParams))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
    _setClinicDetail: val => dispatch(setClinikDetail(val)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ClinicList)
