
import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux'
import { Grid, Paper, Typography, Button, Divider, FormHelperText } from '@material-ui/core';
import { setOpenScreen } from "../../../actions/SystemActions";
import {
  setServiceRequest, setServiceCode, setServiceStatus,
  setServiceMethod, setServiceParams, setServiceCustomHeaders, setServiceRole,
} from '../../../actions/ServiceActions'
import { FormValidation } from '../../../utils/FormValidation';
import CreateUserForm from './CreateUserForm'
import AddUserClient from './AddUserClient'

const CreateUser = ({ ...props }) => {

  const { _setOpenScreen, _setCompleteRequest, userInfo, roleAuth, clientList, } = props
  // const [serviceRole, setServiceRole] = useState('')
  const [showPassword, setShowPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [showAddUserClient, setShowAddUserClient] = useState(false)
  const [formFocus, setFormFocus] = useState({
    name: false,
    phone: false,
    email: false,
    password: false,
    confirmPassword: false,

  })
  const [form, setForm] = useState({
    resource: [],
    assignedUserRole: [],
    resourcelist: [],
    userType: '',
    profile: '',
    submitNotError: true
  })

  useEffect(() => {
    _setOpenScreen("Create User")
    // let aud = ''
    // if (userInfo.aud) aud = userInfo.aud
    // else if (localStorage.getItem('aud')) aud = localStorage.getItem('aud')
    // if (aud === 'authentication-backoffice') aud = ''

    // let role = ''
    // if (userInfo.role && userInfo.role.includes(roleAuth.endUser)) role = roleAuth.endUser
    // if (userInfo.role && userInfo.role.includes(roleAuth.admin)) role = roleAuth.admin
    // if (userInfo.role && userInfo.role.includes(roleAuth.root)) role = roleAuth.root
    // console.log("GOMBAL...")
    let name = userInfo.name
    let email = userInfo.email
    let phone = userInfo.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let requestAllClient = {
      request: {},
      method: "GET",
      serviceCode: "SELECT_ALL_CLIENT",
      // params: '/clients' + (aud ? '/' + aud : ''),
      customHeaders
    }
    // setServiceRole(role)
    if (clientList.length < 1 && userInfo.roleUsed) _setCompleteRequest(requestAllClient)
    // if (clientList[0]) clientList.map(item => item.clientRoles = item.roleDetails)
  }, [_setOpenScreen, _setCompleteRequest, clientList, userInfo.email, userInfo.name, userInfo.phone, userInfo.roleUsed])

  // useEffect(() => {
  //   if (clientList[0]) clientList.map(item => item.clientRoles = item.roleDetails)
  // }, [clientList])

  const handleChange = prop => e => {
    e.preventDefault()
    let valid = FormValidation(prop, e.target.value)

    if (prop === 'confirmPassword') {
      if (form.password !== e.target.value) {
        setForm({
          ...form,
          [prop]: e.target.value,
          [prop + 'ErrorMessage']: 'password confirmation not match',
          submitNotError: true,
        });
      }
      else {
        setForm({
          ...form,
          [prop]: e.target.value,
          [prop + 'ErrorMessage']: '',
          submitNotError: true,
        });
      }
    }
    else if (prop === 'clientId') {
      let newResourcelist = []
      clientList.forEach(item => {
        if (item.clientId === e.target.value) {
          return newResourcelist = Array.isArray(item.clientRoles)
            ? item.clientRoles
            : item.clientRoles
              ? item.clientRoles.split(', ').map(item => ({ roleId: item }))
              : [{ roleId: 'client jadul jgn dipake' }]
        }
      })
      setForm({
        ...form,
        resourcelist: newResourcelist,
        [prop]: e.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        submitNotError: true,
      })
    }
    else if (prop === 'userType') {
      let val = e.target.value
      let role = ''
      if (val === "end-user") role = roleAuth.endUser
      if (val === "admin") role = roleAuth.admin
      if (val === "root") role = roleAuth.root
      setForm({
        ...form,
        [prop]: e.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        resource: [...form.resource, role],
        submitNotError: true,
      })

    }
    else {
      setForm({
        ...form,
        [prop]: e.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        submitNotError: true,
      })
    }
  }

  const handleSubmit = e => {
    e.preventDefault()
    setForm({ ...form, submitNotError: true })
    const { name, phone, email, password, confirmPassword,
      userType, assignedUserRole, submitNotError } = form

    let request = {
      assignedUserRole, confirmPassword, email, emailVerified: false, name, password,
      phone: phone[0] === '0' ? '+62' + phone.slice(1) : phone, phoneVerified: false, userType
    }
    let header_name = userInfo.name
    let header_email = userInfo.email
    let header_phone = userInfo.phone
    let customHeaders = { name: header_name, email: header_email }
    if (header_phone) customHeaders.phone = header_phone

    if (assignedUserRole && name && phone && email && password && confirmPassword && userType && submitNotError) {
      let submitRequest = {
        request,
        method: "POST",
        serviceCode: "CREATE_BO_USER",
        customHeaders
        // customHeaders: {
        //   email: userInfo.email,
        //   phone: userInfo.phone,
        //   name: userInfo.name ? userInfo.name : "",
        //   serviceRole,
        // },
      }
      // console.log(submitRequest)
      _setCompleteRequest(submitRequest)
    }
    else {
      setForm({ ...form, submitNotError: false })
    }
  }

  const handleAddClientRole = e => {
    e.preventDefault()
    setForm({
      ...form,
      roleId: '',
      roleDescription: '',
      assignedUserRole: [...form.assignedUserRole, { clientId: form.clientId, resourcesAllowedList: form.resource }]
    })
    setShowAddUserClient(false)
  }

  const handleCancel = e => {
    e.preventDefault()
    setFormFocus({
      name: false,
      phone: false,
      email: false,
      confirmPassword: false,
      password: false,
    })
    setForm({
      resource: [],
      name: '',
      phone: '',
      email: '',
      confirmPassword: '',
      password: '',
      assignedUserRole: [],
      resourcelist: [],
      clientRoles: [],
      userType: '',
      clientGroupId: "7ff9bf27-23bd-4253-a015-b2ffbe067f04",
      submitNotError: true,
    })
  }

  let chooseClientList = clientList.map(item => {
    return {
      value: item.clientId,
      label: item.clientName
    }
  })

  const handleResource = e => {
    let newResource = form.resource
    if (e.target.name && e.target.checked) {
      newResource.push(e.target.name)
      setForm({
        ...form,
        resource: newResource
      })
    }
    else if (e.target.name && !e.target.checked) {
      let filteredResource = []
      filteredResource = newResource.filter(item => item !== e.target.name)
      setForm({
        ...form,
        resource: filteredResource
      })
    }
  }

  return (
    <Grid
      container
      direction="column"
      justify="center"
      alignItems="center"
    >
      <Grid item xs={12} md={9}>
        <Paper>
          <Grid container direction="column" justify="center" alignItems="center">
            <Typography variant='h6' color='primary' style={{ marginTop: 20 }} >Create User</Typography>
          </Grid>

          <Grid container direction="row"  >
            <CreateUserForm
              handleChange={handleChange}
              form={form}
              setFormFocus={setFormFocus}
              formFocus={formFocus}
              setShowPassword={setShowPassword}
              showPassword={showPassword}
              setShowConfirmPassword={setShowConfirmPassword}
              showConfirmPassword={showConfirmPassword}
              // serviceRole={serviceRole}
              roleAuth={roleAuth}
            />

            <AddUserClient
              form={form}
              showAddUserClient={showAddUserClient}
              setShowAddUserClient={setShowAddUserClient}
              handleChange={handleChange}
              chooseClientList={chooseClientList}
              handleResource={handleResource}
              handleAddClientRole={handleAddClientRole}
            />

          </Grid>

          <Divider style={{ margin: 20 }} />

          <Grid container direction="row" justify="center" alignItems="center" style={{ margin: 10 }} >
            {
              form.submitNotError
                ? null
                : <Grid container direction="row" justify="center" alignItems="center" >
                  <FormHelperText error >Form not complete</FormHelperText>
                </Grid>
            }

            <Grid item>
              <Button onClick={handleCancel} variant="outlined" color="primary">Reset</Button>
            </Grid>
            <Grid style={{ margin: 10 }} item>
            </Grid>
            <Grid item>
              <Button onClick={handleSubmit} variant="contained" color="primary">Create</Button>
            </Grid>
          </Grid> <br />
        </Paper>
      </Grid>
    </Grid>
  );
}

const mapStateToProps = store => {
  return {
    userList: store.userList.list,
    userDetail: store.userList.detail,
    userFiltered: store.userList.filteredList,
    userInfo: store.userInfo.info,
    clientList: store.clientList.list,
    roleAuth: store.system.roleAuth,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setOpenScreen: val => dispatch(setOpenScreen(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.params))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateUser)