
import { connect } from 'react-redux'
import QrScreen from './QrScreen'


import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole,
} from '../../../actions/ServiceActions'
import { setOpenScreen, serviceStart, serviceEnd } from '../../../actions/SystemActions'
import { setPaxDetail } from "../../../actions/PaxListActions";
import { setResponse } from '../../../actions/ResponseActions'

const mapStateToProps = state => {
  return {
    paxList: state.paxList.list,
    detail: state.paxList.detail,
    system: state.system,
    userInfo: state.userInfo,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setRequest: val => {
      dispatch(setServiceRequest(val))
    },
    _setServiceCode: val => {
      dispatch(setServiceCode(val))
    },
    _setServiceStatus: val => {
      dispatch(setServiceStatus(val))
    },
    _setOpenScreen: val => {
      dispatch(setOpenScreen(val))
    },
    _setPaxDetail: val => {
      dispatch(setPaxDetail(val))
    },
    _serviceStart: () => {
      dispatch(serviceStart())
    },
    _serviceEnd: () => {
      dispatch(serviceEnd())
    },
    _setResponse: val => {
      dispatch(setResponse(val))
    },
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.params))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },

  }
}

const Qr = connect(mapStateToProps, mapDispatchToProps)(QrScreen)

export default Qr