
import Store from '../Store'
import appText from './app'
import errorText from './error'
import commonText from './common'
import clinicText from './clinic'



const textInternationalize = {
  ...appText,
  ...errorText,
  ...commonText,
  ...clinicText,
}

const FormatText = keyText => {
  let langAvailable = ['en', 'id',]
  let locale = Store.getState().system.locale
  let langUsed = ""

  if (langAvailable.includes(locale.slice(0, 2))) langUsed = locale.slice(0, 2)
  else langUsed = 'en'
  let keyedText = textInternationalize[keyText]

  if (!keyedText) return keyText
  else return keyedText[langUsed]
}

export default FormatText
