import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';

import './GenericJson.css';

import Typography from '@material-ui/core/Typography';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import IconButton from '@material-ui/core/IconButton';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Hidden from '@material-ui/core/Hidden'
import Card from '@material-ui/core/Card'
import Grid from '@material-ui/core/Grid'
import ListTableMobile from '../../components/list/ListTableMobile'

import { setConfigDetailModal } from '../../actions/GenericConfigurationActions';
import { setOpenScreen } from "../../actions/SystemActions";
import {
    setServiceRequest, setServiceCode, setServiceStatus,
    setServiceCustomHeaders, setServiceMethod, setServiceParams, setSecondRequest
} from '../../actions/ServiceActions';

import { converDateDDMMYY } from '../../utils/utils';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const GenericJson = ({ setCompleteRequest, setOpenScreen, configList,
    configDetailModal, setConfigDetailModal, configDetail, setSecondRequest }) => {

    const [editConfig, setEditConfig] = useState(false);
    const [modalConfigDetail, setModalConfigDetail] = useState(null);

    useEffect(() => {
        setOpenScreen("Generic Configuration");
    }, [setOpenScreen]);


    useEffect(() => {
        const initialConfiguration = () => {
            let fullRequest = {
                request: {},
                serviceMethod: 'GET',
                serviceCode: 'GENERIC_JSON_CONFIGURATION',
                customHeaders: {
                    clientId: 'voltras-health'
                }
            }

            setCompleteRequest(fullRequest);
        }

        initialConfiguration();
    }, [setCompleteRequest]);

    const handleChangeConfig = e => {
        const { value } = e.target;
        setModalConfigDetail(value);
    }

    const handleDetailConfig = (item) => {
        let fullRequest = {
            request: {},
            params: "/" + item.id,
            serviceMethod: 'GET',
            serviceCode: 'GENERIC_JSON_CONFIGURATION_DETAIL',
            customHeaders: {
                clientId: 'voltras-health'
            }
        }
        setCompleteRequest(fullRequest);
    }

    const handleCloseModal = () => {
        setConfigDetailModal(false);
        setEditConfig(false);
    }

    const handleSaveConfiguration = () => {
        const detail = JSON.parse(modalConfigDetail);
        let fullRequest = {
            request: detail,
            params: "/" + detail.id,
            serviceMethod: 'POST',
            serviceCode: 'GENERIC_JSON_CONFIGURATION_UPDATE',
            customHeaders: {
                clientId: 'voltras-health'
            }
        }
        setCompleteRequest(fullRequest);


        //Updtae
        let fullRequest2 = {
            request: {},
            method: 'GET',
            serviceCode: 'GENERIC_JSON_CONFIGURATION',
            customHeaders: {
                clientId: 'voltras-health'
            }
        }
        setSecondRequest(fullRequest2)
        setEditConfig(false);
    }

    const handleEditConfiguration = () => {
        const value = JSON.stringify(configDetail, undefined, 5);
        setModalConfigDetail(value)
        setEditConfig(true);
    }

    return (
        <div className='generic-json-container'>
            <Hidden smDown>
                <div className='generic-configuration-container'>
                    <div className='generic-header'>
                        <Typography variant="h6" gutterBottom>
                            Generic Configuration List
                    </Typography>
                    </div>

                    <Table aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>ID</TableCell>
                                <TableCell>Name</TableCell>
                                <TableCell>Updated On</TableCell>
                                <TableCell>Updated By</TableCell>
                                <TableCell>Action</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                configList.map((item) => (
                                    <TableRow key={item.id}>
                                        <TableCell>{item.id}</TableCell>
                                        <TableCell>{item.name}</TableCell>
                                        <TableCell>{converDateDDMMYY(item.update.on)}</TableCell>
                                        <TableCell>{item.update.by}</TableCell>
                                        <TableCell>
                                            <IconButton aria-label="delete" onClick={() => handleDetailConfig(item)}>
                                                <i style={{ fontSize: '18px' }} className="fas fa-info-circle"></i>
                                            </IconButton>
                                        </TableCell>
                                    </TableRow>
                                ))
                            }
                        </TableBody>
                    </Table>
                </div>
            </Hidden>
            <Hidden smUp>
                <Card style={{ marginTop: 10 }}>
                    <div style={{ display: 'flex', backgroundColor: '#f4f0ec', padding: 5 }}>
                        <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
                            Generic Configuration List
                        </Typography>
                    </div>
                    <div style={{ marginLeft: 15, marginRight: 15 }}>
                        <ListTableMobile
                            column={[
                                { title: 'ID', field: 'id' },
                                { title: 'Name', field: 'name' },
                                { title: 'Update On', field: 'on' },
                                { title: 'Update By', field: 'by' }
                            ]}
                            data={configList.map(item => ({
                                ...item,
                                on: converDateDDMMYY(item.update.on),
                                by: item.update.by
                            }))}
                            arrow={true}
                            button={true}
                            onClickList={handleDetailConfig}
                        />
                    </div>
                </Card>
            </Hidden>
            <Dialog
                open={configDetailModal}
                scroll={"paper"}
                TransitionComponent={Transition}
                keepMounted
                maxWidth={"xl"}

            >
                <DialogTitle>
                    <div className='dialog-header'>
                        <Typography variant="h6" gutterBottom>
                            Configuration Detail
                        </Typography>
                        <IconButton aria-label="delete" onClick={handleCloseModal}>
                            <i className="fas fa-times" ></i>
                        </IconButton>
                    </div>

                </DialogTitle>
                <Hidden smDown>
                    <DialogContent>
                        <div className='dialog-content'>
                            <TextField
                                label="JSON Config"
                                multiline
                                rowsMax={20}
                                value={modalConfigDetail ? modalConfigDetail : JSON.stringify(configDetail, undefined, 5)}
                                onChange={handleChangeConfig}
                                variant="outlined"
                                style={{ width: '100%' }}
                                disabled={!editConfig}
                            />
                        </div>
                    </DialogContent>
                    <DialogActions>
                        <div className='dialog-footer'>
                            {
                                editConfig ?
                                    <div>
                                        <Button style={{ marginRight: '10px' }} onClick={handleCloseModal} variant='outlined' color="primary">
                                            Cancel
                                </Button>
                                        <Button onClick={handleSaveConfiguration} variant='contained' color="primary">
                                            Save Configuration
                                </Button>
                                    </div>
                                    :
                                    <Button onClick={handleEditConfiguration} variant='contained' color="primary">
                                        Edit Configuration
                            </Button>
                            }
                        </div>

                    </DialogActions>
                </Hidden>
                <Hidden smUp>
                    <DialogContent>
                        {/* <div className='dialog-content'> */}
                        <TextField
                            label="JSON Config"
                            multiline
                            rowsMax={20}
                            value={modalConfigDetail ? modalConfigDetail : JSON.stringify(configDetail, undefined, 5)}
                            onChange={handleChangeConfig}
                            variant="outlined"
                            style={{ width: '100%' }}
                            disabled={!editConfig}
                            InputProps={{
                                style: {
                                    fontSize: 12,
                                    fontFamily: 'Arial, Helvetica, sans-serif',
                                }
                            }}
                        />
                        {/* </div> */}
                    </DialogContent>
                    <DialogActions>
                        {/* <div className='dialog-footer'> */}
                        {
                            editConfig ?
                                <Grid container justify='space-between'>
                                    <Button onClick={handleCloseModal} variant='outlined' color="primary"
                                        style={{
                                            textTransform: 'none', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                                            fontWeight: 'bold', fontSize: 12, marginRight: '10px'
                                        }} >
                                        Cancel
                                </Button>
                                    <Button onClick={handleSaveConfiguration} variant='contained' color="primary" style={{
                                        textTransform: 'none', color: 'white', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                                        fontWeight: 'bold', fontSize: 12
                                    }}>
                                        Save Configuration
                                </Button>
                                </Grid>
                                :
                                <Grid container justify='center'>
                                    <Button style={{
                                        textTransform: 'none', color: 'white', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                                        fontWeight: 'bold', fontSize: 12
                                    }} onClick={handleEditConfiguration} variant='contained' color="primary">
                                        Edit Configuration
                                        </Button>
                                </Grid>
                        }
                        {/* </div> */}
                    </DialogActions>
                </Hidden>
            </Dialog>
            <div style={{ height: window.screen.height - 550 }} >
            </div>
        </div>
    )
};


const mapDispatchToProps = (dispatch) => ({
    setCompleteRequest: (val) => {
        dispatch(setServiceRequest(val.request))
        dispatch(setServiceMethod(val.serviceMethod))
        dispatch(setServiceCode(val.serviceCode))
        dispatch(setServiceCustomHeaders(val.customHeaders))
        dispatch(setServiceParams(val.serviceParams))
        dispatch(setServiceParams(val.params))
        dispatch(setServiceStatus(val.serviceStatus || 'active'))
    },
    setOpenScreen: (val) => dispatch(setOpenScreen(val)),
    setConfigDetailModal: (val) => dispatch(setConfigDetailModal(val)),
    setSecondRequest: (val) => dispatch(setSecondRequest(val))
});

const mapStateToProps = state => ({
    configList: state.genericConfiguration.configurationList,
    configDetail: state.genericConfiguration.configDetail,
    configDetailModal: state.genericConfiguration.configDetailModal
});

export default connect(mapStateToProps, mapDispatchToProps)(GenericJson);