
const initialstate = {
  locale: '',
  serviceStart: false,
  openScreen: "",
  menuClickUrl: '',
  appConstant: null,
  roleAuth: {
    root: 'AUTHENTICATION_ROOT',
    admin: 'AUTHENTICATION_ADMIN',
    endUser: 'AUTHENTICATION_USER'
  },
  popChooseRole: false,
  // googleParams: null,
  googleParams: {
    access_type: "offline",
    scope: "https://www.googleapis.com/auth/userinfo.profile https://www.googleapis.com/auth/userinfo.email",
    response_type: "code",
    prompt: "consent",
    client_id: "321981158486-ac5nqdqbq0hjla14a299ln736s2kuj14.apps.googleusercontent.com"
  },
  otpID: '',
}

const system = (state = initialstate, action) => {
  switch (action.type) {
    case 'SERVICE_START': return { ...state, serviceStart: true }
    case 'SERVICE_END': return { ...state, serviceStart: false }
    case 'SET_SNACKBAR_MESSAGE': return { ...state, snackbarMsg: action.val }
    case 'SET_OPEN_SCREEN': return { ...state, openScreen: action.val }
    case 'SET_MENU_CLICK_URL': return { ...state, menuClickUrl: action.val }
    case 'SET_APP_CONSTANT': return { ...state, appConstant: action.val }
    case 'SET_LOCALE': return { ...state, locale: action.val }
    case 'POP_CHOOSE_ROLE': return { ...state, popChooseRole: action.val }
    case 'SET_GOOGLE_PARAMS': return { ...state, googleParams: action.val }
    case 'SET_SYSTEM_OTP_ID': return { ...state, otpID: action.val }
    case 'LOGOUT':
      return {
        ...state,
        serviceStart: false,
        menuClickUrl: '',
        roleAuth: {
          root: 'AUTHENTICATION_ROOT',
          admin: 'AUTHENTICATION_ADMIN',
          endUser: 'AUTHENTICATION_USER'
        },
        popChooseRole: false,
        otpID: '',
      }
    default:
      return state
  }
}

export default system
