
import React, { useState, forwardRef } from 'react';
import Dialog from '@material-ui/core/Dialog';
// import DialogActions from '@material-ui/core/DialogActions';
// import DialogContent from '@material-ui/core/DialogContent';
// import DialogContentText from '@material-ui/core/DialogContentText';
// import DialogTitle from '@material-ui/core/DialogTitle';
import { Button, Typography, Card, Input, Grid, FormHelperText, FormControl } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import FormatText from '../../i18n'
import { FormValidation } from '../../utils/FormValidation';

const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

export default function OtpDialog(props) {

  const [state, setState] = useState({
    otp: '',
    otpErrorMessage: '',
    onLoginErrorMessage: '',

  })

  const handleClose = () => {
    // console.log("dipanggil tak")
    props.handleDialogClose()
  };

  const handleSubmitOtp = () => {
    if (state.otp) {
      let clientId = props.clientId
      let challenge = state.otp
      let otpID = props.otpID
      let request = {
        challenge,
        otpID,
        redirectURI: "http://www.alamanak.co.id",
      }
      let fullRequest = { request, method: 'POST', serviceCode: 'VERIFICATION_CHECK', customHeaders: { clientId }, params:'/phone' }
      props.handleSubmitOtp(fullRequest)
    }
    setTimeout(() => {
      props.handleDialogClose()
    }, 50);
  }

  const handleChange = prop => event => {
    event.preventDefault()
    let valid = FormValidation(prop, event.target.value)
    // console.log(typeof event.target.value)
    setState({
      ...state,
      [prop]: event.target.value.slice(0, 6),
      [prop + 'ErrorMessage']: valid.errorMessage,
    })
  }

  const handleCancelOtp = () => {
    // console.log('CANCEL')
    props.handleDialogClose()
  }

  const handleResendOtp = () => {
    // console.log('clicked')
  }

  return (
    <div>
      <Dialog
        open={props.open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
      >
        <Card style={{ display: 'flex', flexWrap: 'wrap', width: 300, }} elevation={4}>
          <Grid style={{ margin: 30 }} container direction="column" justify="center" alignItems="center" >
            <Typography variant="h5" color='primary'><b>{FormatText('app_submit_otp')}</b> </Typography>
            <Typography variant="caption" color='textSecondary' >{FormatText('app_submit_otp_text')}</Typography>
            <Typography variant='subtitle2' color='primary' >{props.phone}</Typography>

            <FormControl style={{ margin: 10, width: '12ch', textAlign: 'center' }}  >
              <Input
                autoFocus
                style={{ fontSize: 28, textAlign: 'center' }}
                error={state.otpErrorMessage ? true : false}
                id="standard-adornment-otp"
                type='text'
                onChange={handleChange('otp')}
                value={state.otp}
                maxLength={6}
              />
            </FormControl>
            {
              state.otpErrorMessage
                ? <FormHelperText error id="email-helper-text">{state.otpErrorMessage}</FormHelperText>
                : null
            }

            <FormControl>
              <Grid>
                <Button onClick={handleCancelOtp} variant="outlined" color="primary" style={{ margin: 10, }}>
                  {FormatText('app_cancel')}
                </Button>
                <Button
                  onClick={handleSubmitOtp}
                  disabled={state.otp ? false : true}
                  style={{ margin: 10, }}
                  variant="contained"
                  color="primary">
                  {FormatText('app_submit')}
                </Button>
              </Grid>
              {
                state.onLoginErrorMessage
                  ? <FormHelperText error id="password-helper-text">{state.onLoginErrorMessage}</FormHelperText>
                  : null
              }
            </FormControl>
            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
            >
              <Typography variant="caption" color='textSecondary' >{FormatText('app_not_receive_otp')}</Typography>
              <Button
                size='small'
                onClick={handleResendOtp}
                color="primary"
                style={{ textTransform: 'none', fontWeight: 'bold', fontSize: 12 }}
              >
                {FormatText('app_resend_otp')}
              </Button>

            </Grid>
          </Grid>
        </Card>
        {/* <DialogTitle id="alert-dialog-slide-title">{props.dialogTitle}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            {props.dialogContent}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            {props.cancelTitle}
          </Button>
          <Button onClick={handleSubmit} color="primary" style={{ fontWeight: 'bold', }} >
            {props.submitTitle}
          </Button>
        </DialogActions> */}
      </Dialog>
    </div>
  );
}
