import React from 'react'
import { Grid, Card } from '@material-ui/core'
import CloseIcon from '@material-ui/icons/Close';
import { Dialog, DialogActions, DialogContent } from '@material-ui/core'
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import { List, ListItem } from '@material-ui/core'
import { IconButton } from '@material-ui/core'
import { Typography } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import PropTypes from 'prop-types'
import Divider from '@material-ui/core/Divider'

const styles = (theme) => ({
    labelRoot: {
        fontSize: 15,
        fontFamily: 'Arial, Helvetica, sans-serif',
        marginLeft: 20
    },
    inputRoot: {
        fontSize: 15,
        fontFamily: 'Arial, Helvetica, sans-serif',
        height: 20,
        width: 60,
        marginLeft: 20
    },
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});


const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const DialogDetail = props => {
    const column = props.column ? props.column : []
    let data = []
    data = props.detailData ? props.detailData.product ? props.detailData.product : [] : []
    const handleClose = () => {
        props.handleOpen(false)
    }
    return (
        <div>
            <Dialog fullWidth maxWidth='sm' open={props.open} onClose={handleClose} scroll={'paper'}>
                <DialogTitle id="dialog-title" onClose={handleClose}>{props.title}</DialogTitle>
                <DialogContent dividers>
                    <Card>
                        <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
                            <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
                                {props.detailData.name ? 'Clinic : ' + props.detailData.name : 'Test Date : ' + props.detailData.testdate}
                            </Typography>
                        </div>
                        <div style={{ marginLeft: 15, marginRight: 15 }}>

                            <List disablePadding={true}>
                                {
                                    data.map((item, index) => (
                                        <div key={index}>
                                            <ListItem>
                                                <Grid container justify='space-between'>
                                                    <Grid item sm={5} xs={5}>
                                                        {column.map((items, i) => (
                                                            <Grid item sm={12} xs={12} key={i + 8}>
                                                                <Typography style={{ color: 'grey', fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                                    {items.title}
                                                                </Typography>

                                                            </Grid>
                                                        ))}
                                                    </Grid>
                                                    <Grid item sm={!props.arrow ? 7 : 5} xs={!props.arrow ? 7 : 5}>
                                                        {column.map((items, idx) => (
                                                            <Grid item sm={12} xs={12} key={idx + 12}>
                                                                <Typography style={{ fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                                    {items.title ? item[items.field] ? item[items.field] : '-' : null}
                                                                </Typography>
                                                            </Grid>

                                                        ))}
                                                    </Grid>
                                                    {props.arrow ? <Grid item sm={2} xs={2} align='right' style={{ paddingTop: 30 }}>
                                                        <ChevronRightIcon />
                                                    </Grid> : null}

                                                </Grid>
                                            </ListItem>
                                            <Divider />
                                        </div>
                                    ))
                                }
                            </List>
                        </div>
                    </Card>
                </DialogContent>
                <DialogActions>

                </DialogActions>
            </Dialog>
        </div>
    )
}

DialogDetail.propTypes = {
    open: PropTypes.bool,
    title: PropTypes.string,
    handleOpen: PropTypes.func,
    detailData: PropTypes.object,
    column: PropTypes.array
}

export default DialogDetail