let gatewaySecurity = Config.gatewaySecurity
let gatewayBackOffice = Config.gatewayBackOffice
// let gatewayHealth = Config.gatewayMsHealth

const serviceUrl = serviceCode => {
  switch (serviceCode) {
    case "login":
      return gatewaySecurity + "/login"  

    case "logout":
      return gatewaySecurity + "/logout"

    case "register":
      return gatewaySecurity + "/register"  

    case 'UPDATE_USER_INFO':
      return gatewaySecurity + '/users/me'

    case 'OTP':
      return gatewaySecurity + "/validate-otp"   //   POST /validate-otp

    case 'RESET_PASSWORD_REQUEST':
      return gatewaySecurity + "/reset-password-request" 

    case 'CHANGE_PASSWORD_REQUEST':
      return gatewaySecurity + "/reset-password-recovery"  

    case 'SEND_EMAIL_VERIFICATION':
      return gatewaySecurity + "/verification-code"  

    case 'SEND_PHONE_VERIFICATION':
      return gatewaySecurity + "/verification-code"   

    case 'VERIFICATION_CHECK':
      return gatewaySecurity + "/verification-code"     

    case 'REQ_AUTH_GOOGLE_PARAMS':
      return gatewaySecurity + '/params/google-oauth'  

    case "SELECT_USER_ROLE":
      return gatewayBackOffice + "/user/select/role"

    case "SELECT_USER_FULL_INFO":
      return gatewayBackOffice + "/user/select/full"

    case 'SELECT_USER_DETAIL_FULL_INFO':
      return gatewayBackOffice + "/user/select/full"

    case "CREATE_BO_USER":
      return gatewayBackOffice + "/users"

    case 'SELECT_ALL_USER':
      return gatewayBackOffice + '/users'

    case 'DELETE_USER':
      return gatewayBackOffice

    case "UPDATE_USER_ROLE":
      return gatewayBackOffice   // 'http://172.16.10.51:31564'

    case "UPDATE_BASIC_USER":
      return gatewayBackOffice

    case "UPDATE_TEMPLATE_MESSAGE":
      return gatewayBackOffice

    case "SELECT_ALL_MESSAGE":
      return gatewayBackOffice

    case 'SELECT_ALL_CLIENT':
      return gatewayBackOffice + '/clients'

    case 'CREATE_CLIENT':
      return gatewayBackOffice + '/clients'

    case 'SELECT_ALL_REQUEST':
      return gatewayBackOffice +'/request-specifications'

    case 'UPDATE_CLIENT_DETAIL':
      return gatewayBackOffice
      
    case 'ADD_CLIENT_ROLE':
      return gatewayBackOffice

    case 'DELETE_CLIENT_ROLE':
      return gatewayBackOffice

    case 'DELETE_CLIENT':
      return gatewayBackOffice
      
    case 'ADD_REQUEST_SPESIFICATIONS':
      return gatewayBackOffice

    case 'UPDATE_REQUEST_SPESIFICATIONS':
      return gatewayBackOffice

    case 'HEALTH_ORDER_DETAIL':
      return gatewaySecurity + "/admin/orders"

    case "healthOrder":
      return gatewaySecurity + "/admin/orders"

    case "healthKlinik":
      return gatewaySecurity + "/admin/clinics"

    case "healthProduct":
      return gatewaySecurity + "/products"

    case 'GET_CITY_LIST':
      return gatewaySecurity + ''

    case 'GET_CITY_CLINIC_LIST':
      return gatewaySecurity + ''

    case 'GET_ALL_ORDER':
      return gatewaySecurity + "/admin/orders"

    case 'RESCHEDULE_ORDER':
      return gatewaySecurity + "/admin/orders"

    case 'CLINIC_REGISTRATION':
      return gatewaySecurity + "/admin/clinics"

    case 'SEND_CLINIC_ACTIVATION':
      return gatewaySecurity + "/admin/clinics"

    case 'ADD_CLINIC_USER':
      return gatewaySecurity + "/admin/clinics"

    case 'DELETE_CLINIC_USER':
      return gatewaySecurity + "/admin/clinics"

    case 'DELETE_CLINIC':
      return gatewaySecurity + "/admin/clinics"

    case 'GET_CLINIC_USER':
      return gatewaySecurity + "/admin/clinics"

    case 'GET_MY_CLINIC':
      return gatewaySecurity + "/admin/users/me"

    case 'GET_CLINIC_REPORT':
      return gatewaySecurity + "/admin/report"

    case 'RECAPT_CLINIC_SUMMARY':
      return gatewaySecurity + "/admin/report/logistic"

    case 'RECAPT_BIOFARMA_SUMMARY':
      return gatewaySecurity + "/admin/report"

    case 'ADD_CLINIC_LOGO':
      return gatewaySecurity + "/admin/clinics"

    case 'EDIT_CLINIC_SETTING':
      return gatewaySecurity + "/admin/clinics"
      
    case 'GENERIC_JSON_CONFIGURATION':
      return gatewaySecurity+'/admin/configurations';

    case 'GENERIC_JSON_CONFIGURATION_DETAIL':
      return gatewaySecurity+'/admin/configurations';

    case 'GENERIC_JSON_CONFIGURATION_UPDATE':
      return gatewaySecurity+'/admin/configurations';

    case 'GENERAL_PRODUCT_CATALOG':
      return gatewaySecurity+'/products'

    case 'GENERAL_PRODUCT_CATALOG_CREATE':
      return gatewaySecurity+'/admin/products'
    
    case 'GENERAL_PRODUCT_CATALOG_UPDATE':
      return gatewaySecurity+'/admin/products'

    case 'GET_CLINIC_PARENTS': 
      return gatewaySecurity + "/admin/clinics/parents" 
    
    case 'GET_PARENT_LIST':
      return gatewaySecurity + "/admin/persons" 

    case 'PURCHASE_VOUCHER_NUMBER':
      return gatewaySecurity + "/admin" 

    default:
      return gatewaySecurity
  }
}

export default serviceUrl
