
import { connect } from 'react-redux'
import Homes from './HomeScreen'

import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, setSecondRequest
} from '../../actions/ServiceActions'
import { setOpenScreen, setAppConstant } from '../../actions/SystemActions'
import { setPaxList, setPaxDetail } from "../../actions/PaxListActions";
import { setUserSessionInfo, setUserInfo, setRoleUsed, setVerificationCode } from "../../actions/UserInfoActions";
import { setUserMenu, setMenuCollapseStatus } from '../../actions/MenuActions';

const mapStateToProps = state => {
  return {
    clinic: state.paxList.clinicUser,
    paxList: state.paxList.list,
    detail: state.paxList.detail,
    userInfo: state.userInfo,
    system: state.system,
    menu: state.menu.menuItems,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setUserSessionInfo: val => dispatch(setUserSessionInfo(val)), _setUserInfo: val => dispatch(setUserInfo(val)),
    _setUserMenu: val => dispatch(setUserMenu(val)), _setMenuCollapseStatus: val => dispatch(setMenuCollapseStatus(val)),
    _setOpenScreen: val => dispatch(setOpenScreen(val)), _setPaxList: val => dispatch(setPaxList(val)),
    _setPaxDetail: val => dispatch(setPaxDetail(val)), _setRoleUsed: val => dispatch(setRoleUsed(val)),
    _setAppConstant: val => dispatch(setAppConstant(val)), _setVerificationCode: val => dispatch(setVerificationCode(val)),
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.params))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.serviceRole))
      dispatch(setServiceStatus("active"))
    },
    _setSecondRequest: val => dispatch(setSecondRequest(val)),
  }
}

const HomeScreen = connect(mapStateToProps, mapDispatchToProps)(Homes)

export default HomeScreen
