
import React, { useState, useEffect } from 'react'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid'
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Link from '@material-ui/core/Link';
import { Hidden } from '@material-ui/core';
import FormatText from '../../i18n'

const KlinikHome = ({ ...props }) => {
  const { _setCompleteRequest, _setSecondRequest, history, userInfo, _setVerificationCode, clinic } = props
  const [state, setState] = useState({
    voucherId: '',
    voucherIdErrorMessage: '',
    submitErrorMessage: '',
    disabled: true,
  });

  useEffect(() => {
    window.scrollTo(0, 0)
    function isMobile() {
      return ((window.innerWidth <= 800) && (window.innerHeight <= 600));
    }
    const { screen } = window
    if (!navigator.xr && isMobile() && screen.orientation && screen.orientation.lock) {
      screen.orientation.lock('landscape');
    }

    let name = userInfo.info.name ? userInfo.info.name : ''
    let email = userInfo.info.email ? userInfo.info.email : ''
    let phone = userInfo.info.phone ? userInfo.info.phone : ''
    let customHeaders = { email, name }
    if (phone) customHeaders.phone = phone

    let roleUsed = userInfo.roleUsed
    setState(prevState => ({ ...prevState, disabled: roleUsed === 'biofarma.admin' ? true : false }))

    if ((roleUsed === 'clinic.admin')) {
      let myClinicRequest = {
        request: {},
        method: "GET",
        serviceCode: "GET_MY_CLINIC",
        customHeaders,
      }
      _setCompleteRequest(myClinicRequest)
      _setSecondRequest(true)

    }
  }, [userInfo, _setCompleteRequest, _setSecondRequest])

  useEffect(() => {
    let name = userInfo.info.name ? userInfo.info.name : ''
    let email = userInfo.info.email ? userInfo.info.email : ''
    let phone = userInfo.info.phone ? userInfo.info.phone : ''
    let verificationCode = userInfo.verificationCode ? userInfo.verificationCode : ''
    let customHeaders = { email, name }
    if (phone) customHeaders = phone
    let request = { redirectURI: "http://www.alamanak.co.id", verificationCode }

    let verificationCheckRequest = {
      request,
      method: "POST",
      serviceCode: "VERIFICATION_CHECK",
      params: `/email`,
      customHeaders,
    }
    if (verificationCode) {
      _setCompleteRequest(verificationCheckRequest)
      _setVerificationCode(null)
    }
  }, [userInfo, _setCompleteRequest, _setVerificationCode])

  const handleChange = prop => event => {
    if (prop === 'voucherId') {
      if (event.target.value.length === 0) {
        setState({
          ...state,
          [prop]: event.target.value,
          isError: true,
          voucherIdErrorMessage: "OrderId must be filled"
        })
      }
      else {
        setState({
          ...state,
          [prop]: event.target.value,
          isError: false,
          voucherIdErrorMessage: "",
          submitErrorMessage: ""
        })
      }
    }
  }

  const handleEnter = event => {
    if (event.which === 13 && event.key === "Enter") {
      let orderId = state.voucherId

      let name = userInfo.info.name ? userInfo.info.name : ''
      let email = userInfo.info.email ? userInfo.info.email : ''
      let phone = userInfo.info.phone ? userInfo.info.phone : ''
      let customHeaders = { email, name }
      if (phone) customHeaders.phone = phone
      if (orderId) {
        let healthOrderRequest = {
          request: {},
          method: "GET",
          serviceCode: "HEALTH_ORDER_DETAIL",
          params: `/${orderId.toString()}`,
          customHeaders
        }
        _setCompleteRequest(healthOrderRequest)

      }
      else {
        setState({
          ...state,
          isError: true,
          voucherIdErrorMessage: "OrderId must be filled"
        })
      }
    }
  }

  const handleSubmit = event => {
    event.preventDefault()
    let orderId = state.voucherId

    let name = userInfo.info.name ? userInfo.info.name : ''
    let email = userInfo.info.email ? userInfo.info.email : ''
    let phone = userInfo.info.phone ? userInfo.info.phone : ''
    let customHeaders = { email, name }
    if (phone) customHeaders.phone = phone
    if (orderId) {
      let healthOrderRequest = {
        request: {},
        method: "GET",
        serviceCode: "HEALTH_ORDER_DETAIL",
        params: `/${orderId.toString()}`,
        customHeaders
      }
      _setCompleteRequest(healthOrderRequest)

    }
    else {
      setState({
        ...state,
        isError: true,
        voucherIdErrorMessage: "OrderId must be filled"
      })
    }
  }

  return (
    <Grid container direction="row" >
      <Hidden xsDown >
        <Grid item sm={state.disabled ? 12 : 7}>
          <Grid container direction="row" justify="center" alignItems='flex-start' style={{ marginTop: 40 }} >
            <Grid item sm={2}> </Grid>
            <Grid item sm={5} style={{ marginTop: 40 }} >
              <Grid >  <Typography variant='h6' color='primary' >{clinic ? clinic.name : "Clinic Backoffice"}</Typography>  </Grid>
              <Grid >  <Typography variant='h5' >{FormatText('app_welcome')}</Typography> </Grid>
              <Grid>  <Typography variant='subtitle2' >{userInfo.roleUsed ? userInfo.roleUsed : ''}</Typography> </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Hidden>
      <Grid item xs={12} sm={5}>
        {
          state.disabled
            ? null
            : <Grid container direction="row" justify='flex-start' alignItems='flex-start' style={{ marginTop: 15 }} >
              <Card variant="elevation" >
                <center>
                  <CardContent>
                    <Typography variant="h6" ><b>Order Details</b> </Typography>
                    <Typography variant="caption" style={{ marginBottom: 15 }} >Search by </Typography>
                    <div>
                      <br />
                      <FormControl disabled={state.disabled} fullWidth style={{ marginBottom: 5 }}  >
                        <InputLabel error={state.voucherIdErrorMessage ? true : false} htmlFor="standard-voucherId">Order ID</InputLabel>
                        <Input
                          autoFocus
                          error={state.voucherIdErrorMessage ? true : false}
                          id="standard-adornment-voucherId"
                          type="text"
                          value={state.voucherId}
                          onChange={handleChange('voucherId')}
                          onKeyPress={handleEnter}
                        />
                        {state.voucherIdErrorMessage
                          ? <FormHelperText error id="email-helper-text">{state.voucherIdErrorMessage}</FormHelperText>
                          : null}
                      </FormControl>
                      <br />
                      <br />

                      <Button
                        disabled={state.disabled}
                        fullWidth
                        style={{ marginBottom: 15 }}
                        onClick={handleSubmit}
                        variant="contained" color="primary"
                      >
                        Search
                      </Button>

                      {state.submitErrorMessage
                        ? <FormHelperText error id="email-helper-text">{state.submitErrorMessage}</FormHelperText>
                        : null}

                      <Typography variant="caption">or</Typography>
                      <div>
                        <Link disabled={state.disabled} style={{ marginTop: 15 }} onClick={() => history.push('/QrScreen')}
                          component="button"
                          variant="body2">Use QR Code</Link>
                      </div>
                    </div>
                  </CardContent>
                </center>
              </Card>
            </Grid>
        }

      </Grid>
      <Grid container>
        <div style={{ height: window.screen.height - 350 }} />
      </Grid>
    </Grid>
  )
}

export default KlinikHome
