
import React from 'react'
import { RadioGroup, FormControlLabel, Radio, Button, Typography, Card, Grid, FormHelperText, FormControl } from '@material-ui/core';
import FormatText from '../../i18n';

const ChooseRoleCard = props => {
  // console.log('ROLE CARD PROPS: ', props)
  const { classes, onRadioChange, userInfo, state, handleChooseRole, } = props

  return (
    <Card className={classes} elevation={4}>
      <Grid style={{ marginTop: 30 }} container direction="row" justify="center" alignItems="center" >
        <Grid container direction="row" justify="center" alignItems="center" >
          <Typography variant="h5" color='primary'>
            <b>{FormatText('app_choose_role')}</b>
          </Typography>
        </Grid>
        <Grid container direction="row" justify="center" alignItems="center" >
          <Typography variant="caption" color='textSecondary' >
            {FormatText('app_choose_role_used')}
          </Typography>
        </Grid>

        <Grid container direction="row" justify="flex-start" alignItems="flex-start" >
          <FormControl style={{ margin: 10, marginLeft: 25, }}  >
            <RadioGroup onChange={onRadioChange} >
              {
                userInfo.info.role
                  ? userInfo.info.role.map((item, i) => {
                    return (
                      <FormControlLabel value={item} control={<Radio />} label={item} key={i} />
                    )
                  })
                  : null
              }
            </RadioGroup>
            {
              state.otpErrorMessage
                ? <FormHelperText error id="email-helper-text">{this.state.otpErrorMessage}</FormHelperText>
                : null
            }
          </FormControl>
        </Grid>

        <FormControl>
          <Grid>
            <Button
              onClick={handleChooseRole}
              disabled={state.roleChoosen ? false : true}
              style={{ margin: 10, width: 120 }}
              variant="contained" color="primary">
              {FormatText('app_choose')}
            </Button>
          </Grid>
          {
            state.onLoginErrorMessage
              ? <FormHelperText error id="password-helper-text">{state.onLoginErrorMessage}</FormHelperText>
              : null
          }
        </FormControl>
      </Grid>
    </Card>
  )
}

export default ChooseRoleCard
