
const INITIAL_STATE = {
  autocomplete: null,
  inputValue: "",
  klinikList: [],
  klinikValue: {},
  klinikInputValue: ""
};

const autocomplete = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case 'SET_AUTOCOMPLETE_VALUE':
      return {
        ...state,
        autocomplete: action.val
      }
    case 'SET_INPUT_VALUE':
      return {
        ...state,
        inputValue: action.val
      }
    case 'SET_KLINIK_LIST_RESPONSE':
      return {
        ...state,
        klinikList: action.val
      }
    case 'SET_KLINIK_VALUE':
      return {
        ...state,
        klinikValue: action.val
      }
    case 'SET_KLINIK_INPUT_VALUE':
      return {
        ...state,
        klinikInputValue: action.val
      }
    default:
      return state;
  }
};

export default autocomplete;

