
import React, { useState, useEffect } from 'react';
import Typography from '@material-ui/core/Typography';
import Hidden from '@material-ui/core/Hidden';
import Grid from '@material-ui/core/Grid'
import Card from '@material-ui/core/Card';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import MaterialTable from 'material-table';
import ReactExport from 'react-data-export';
import { yyyymmddWithParams, convertDateyyyymmdd } from "../../../utils/utils";
import FormatText from '../../../i18n';
import ListTableMobile from '../../../components/list/ListTableMobile'
import { withStyles } from '@material-ui/core/styles'
import FilterDesktop from './FilterDesktop'

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;

let from = yyyymmddWithParams('lastWeek')
let to = yyyymmddWithParams('nextWeek')

const dateNow = new Date()
let lastWeek = new Date(dateNow.getTime() - 7 * 24 * 60 * 60 * 1000)
let nextWeek = new Date(dateNow.getTime() + 7 * 24 * 60 * 60 * 1000)
// let nextYear = new Date(dateNow.getTime() + 366 * 24 * 60 * 60 * 1000)

const datasetCreator = (dataToProcess, roleUsed) => {
  let columns = []
  let rawColumns = [
    { title: "OrderID", width: { wch: 7 } },
    { title: "Office Code", width: { wch: 12 } },
    { title: "Test Date", width: { wch: 12 } },
    { title: "Name", width: { wch: 20 } },
    { title: "Birth Date", width: { wch: 12 } },
    { title: "Status", width: { wch: 12 } },
    { title: "Clinic", width: { wch: 40 } },
    { title: "Redeem Date", width: { wch: 12 } },
    { title: "Redeem By", width: { wch: 12 } },
  ]

  let data = []
  dataToProcess.forEach(item => {
    let dataArr = []
    dataArr.push({ value: item.id })
    if (roleUsed === 'voltras.admin') dataArr.push({ value: item.office })
    dataArr.push({ value: item.testdate })
    dataArr.push({ value: item.name })
    dataArr.push({ value: item.date_of_birth })
    dataArr.push({ value: item.status })
    dataArr.push({ value: item.klinikname })
    dataArr.push({ value: item.redeem_on })
    dataArr.push({ value: item.redeem_by })

    data.push(dataArr)
  })

  if (roleUsed !== 'voltras.admin') {
    columns = rawColumns.filter(item => item.title !== "Office Code")
  }
  else columns = rawColumns

  let dataset = [{ ySteps: 1, columns, data }]
  return dataset
}

const tableCreator = data => {
  let simpleTable = []
  data && data.forEach(order => order.items && order.items.forEach(item => {
    let rowTable = {
      id: order.id,
      office: order.user_info.officeCode ? order.user_info.officeCode : "B2C",
      testdate: item.products[0].testdate,
      name: item.name,
      date_of_birth: item.date_of_birth,
      status: item.status
        ? item.status.toUpperCase() === "FINISH" ? "REDEEMED" : item.status.toUpperCase()
        : "",
      klinikname: item.products[0].klinik.name,
      redeem_on: item.redeem ? convertDateyyyymmdd(item.redeem.redeem_on) : "-",
      redeem_by: item.redeem ? item.redeem.redeem_by : "-"
    }
    simpleTable.push(rowTable)
  }))
  return simpleTable
}

const SimpleTable = ({ ...props }) => {
  let columnPush = []

  const { paxList, history, _setPaxList, _setOpenScreen, userInfo, _setCompleteRequest, classes } = props

  const [state, setState] = useState({
    order: -1,
    "Booking Datefromdate": lastWeek,
    "Booking Datetodate": nextWeek,
    "Test Datefromdate": lastWeek,
    "Test Datetodate": nextWeek,
    "Redeem Datefromdate": lastWeek,
    "Redeem Datetodate": nextWeek,
    fromdate: lastWeek,
    todate: nextWeek,
    open: false,
    name: "",
    'Test Date': true,
    'Booking Date': false,
    'Redeem Date': false,
    Paid: false,
    New: false,
    finish: false,
    unpaid: false,
    dataset: [],
    simpleTable: [],
    currentList: [],
    listLengthById: 0,
  })

  const tableColumns = [
    { title: "ID", field: 'id', width: 5 },
    { title: 'Office Code', field: 'office' },
    { title: FormatText('clinic_test_date'), field: 'testdate', },
    { title: FormatText('app_name'), field: 'name', },
    { title: FormatText('clinic_birth_date'), field: 'date_of_birth', },
    { title: 'Status', field: 'status', },
    { title: FormatText('clinic'), field: 'klinikname', },
    { title: FormatText('clinic_redeem_date'), field: 'redeem_on', },
    { title: FormatText('clinic_redeem_by'), field: 'redeem_by', },
  ]
  tableColumns.forEach(item => {
    if (item.field === 'date_of_birth' || item.field === 'redeem_by' || item.field === 'redeem_on' || item.field === 'klinikname') {

    }
    else {
      columnPush.push(item)
    }
  })

  useEffect(() => {
    let list = paxList.list
    let accessToken = localStorage.getItem('accessToken')
    if (accessToken) {
      _setOpenScreen(FormatText('clinic_order_list'))
      if (Array.isArray(list) && list.length > 0) {
        let simpleList = userInfo.roleUsed === 'voltras.admin'
          ? list.filter(item => (item.status !== "CANCELED_BY_TIMELIMIT"))
          : list.filter(item => (item.status !== "UNCONFIRMED") && (item.payment))
        let simpleTable = tableCreator(simpleList)
        let dataset = datasetCreator(simpleTable, userInfo.roleUsed)
        setState(state => ({
          ...state,
          simpleTable,
          dataset,
          listLengthById: simpleList.length
        }))
      }
      else setState(state => ({
        ...state,
        simpleTable: [],
        dataset: [],
        listLengthById: 0
      }))
    }
    else {
      history.push('/')
    }
  }, [paxList.list, history, _setOpenScreen, userInfo.roleUsed])

  useEffect(() => {
    const infos = userInfo
    let name = infos.info.name
    let email = infos.info.email
    let phone = infos.info.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let healthOrderRequest = {
      request: {},
      method: "GET",
      serviceCode: "healthOrder",
      params: `/list?&testdate_from=${from}&testdate_to=${to}`,
      customHeaders
    }

    // let list = paxList.list
    // if (state.currentList !== list) 
    _setCompleteRequest(healthOrderRequest)

  }, [_setPaxList, _setCompleteRequest, userInfo])

  const handleRowClick = rowData => {
    const infos = userInfo
    let name = infos.info.name
    let email = infos.info.email
    let phone = infos.info.phone
    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone

    let ID = rowData.id
    let healthOrderDetailRequest = {
      request: {},
      method: "GET",
      serviceCode: "HEALTH_ORDER_DETAIL",
      params: `/${ID}`,
      customHeaders
    }
    _setCompleteRequest(healthOrderDetailRequest)

    // let orderList = paxList.list
    // let order = orderList.filter(item => item.id === rowData.id)
    // props._setPaxDetail(order[0])
    // console.log(order[0])
    // history.push('/PaxDetail')
  }

  // const handleClick = row => (event) => {
  //   let orderList = paxList.list
  //   let order = orderList.filter(item => item.id === row)
  //   props._setPaxDetail(order[0])
  //   if (order.length > 0) history.push('/PaxDetail')
  // }

  const handleDateChange = prop => (date) => {
    setState({
      ...state,
      [prop]: date,
    })
  };

  // const handleFilterOpen = () => {
  //   setState({
  //     ...state,
  //     order: 1,
  //     fromdate: lastWeek,
  //     todate: nextWeek,
  //     open: true,
  //     name: "",
  //     New: false,
  //     finish: false,
  //   })
  // };

  // const handleClose = () => {
  //   setState({
  //     ...state,
  //     open: false,
  //   })
  // };

  // const handleReset = () => {
  //   setState({
  //     ...state,
  //     order: 1,
  //     fromdate: lastWeek,
  //     todate: nextWeek,
  //     open: false,
  //     name: "",
  //     New: false,
  //     finish: false,
  //     '4': false,
  //     '5': false
  //     // localList: paxList.list,
  //   })
  // };

  const handleChange = prop => event => {
    if (prop === 'Paid') {
      setState({
        ...state,
        [prop]: !state[prop],
        New: false,
        finish: false,
      })
    }
    else if (prop === 'New') {
      setState({
        ...state,
        [prop]: !state[prop],
        Paid: false,
        finish: false,
      })
    }
    else if (prop === 'finish') {
      setState({
        ...state,
        [prop]: !state[prop],
        New: false,
        Paid: false,
      })
    }
    else if (prop === 'Booking Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Test Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else if (prop === 'Redeem Date') {
      setState({
        ...state,
        [prop]: !state[prop],
      })
    }
    else {
      setState({
        ...state,
        [prop]: event.target.value,
      })
    }
  }

  // const handleKlinik = prop => event => {
  //   setState({
  //     ...state,
  //     [prop]: !state[prop],
  //   })
  // }

  const handleSubmitFilter = () => {
    const infos = props.userInfo

    let name = infos.info.name ? infos.info.name : ''
    let email = infos.info.email ? infos.info.email : ''
    let phone = infos.info.phone ? infos.info.phone : ''

    let payment_from = convertDateyyyymmdd(state['Booking Datefromdate'])
    let payment_to = convertDateyyyymmdd(state['Booking Datetodate'])
    let test_from = convertDateyyyymmdd(state['Test Datefromdate'])
    let test_to = convertDateyyyymmdd(state['Test Datetodate'])
    let redeem_from = convertDateyyyymmdd(state['Redeem Datefromdate'])
    let redeem_to = convertDateyyyymmdd(state['Redeem Datetodate'])

    let paymentParam = state['Booking Date'] ? `&paymentdate_from=${payment_from}&paymentdate_to=${payment_to}` : ""
    let testParam = state['Test Date'] ? `&testdate_from=${test_from}&testdate_to=${test_to}` : ""
    let redeemParam = state['Redeem Date'] ? `&redeemdate_from=${redeem_from}&redeemdate_to=${redeem_to}` : ""
    let statusParam = (state.Paid)
      ? '&status=PAID'
      : (state.finish)
        ? '&status=FINISH'
        : (state.New)
          ? '&status=NEW'
          : ""
    let nameParam = state.name ? `&name=${state.name}` : ''

    let isParams = paymentParam || testParam || redeemParam || statusParam || nameParam

    let customHeaders = { name, email }
    if (phone) customHeaders.phone = phone
    //  health/admin/orders?testdate_from=....&testdate_to=....redeem_status=NEW/REDEEMED
    let healthOrderRequest = {
      request: {},
      method: "GET",
      serviceCode: "healthOrder",
      params: isParams
        ? `/list?`
        + (paymentParam ? paymentParam : "")
        + (testParam ? testParam : "")
        + (redeemParam ? redeemParam : "")
        + (statusParam ? statusParam : "")
        + (nameParam ? nameParam : '')
        : `/list?&paymentdate_from=${payment_from}&paymentdate_to=${payment_to}`,
      customHeaders
    }
    props._setCompleteRequest(healthOrderRequest)
    setState({
      ...state,
      open: false,
    })
  }

  // const handleExport = () => {
  //   // console.log("Export Clicked")
  // }

  // const onRadioChange = e => {
  //   e.preventDefault()
  //   // console.log(e.target)
  //   setState({
  //     ...state,
  //     parent_id: parseInt(e.target.name)
  //   })
  // }


  // let orderList = paxList.list
  // let filteredList = paxList.filteredList
  // let listToMap = filteredList[0] ? filteredList : orderList

  return (
    <div>
      <Hidden xsDown>
        <Grid container style={{ marginTop: 5 }}>
          <FilterDesktop
            handleSubmitFilter={handleSubmitFilter}
            handleChange={handleChange}
            handleDateChange={handleDateChange}
            state={state}
            {...props}
          />
          {/* <Typography variant="subtitle1"  ><b>Filters</b> </Typography> */}

          <Grid item sm={12} container direction='row' justify='flex-start' alignItems='flex-start' style={{ marginBottom: 10, marginTop: 5 }} >

            <MaterialTable
              style={{ width: '100%' }}
              title={`TOTAL ORDER BY ID: ${state.listLengthById.toString()} `}
              columns={userInfo.roleUsed === 'voltras.admin' ? tableColumns : tableColumns.filter(item => item.field !== 'office')}
              data={state.simpleTable}
              onRowClick={(e, rowData) => handleRowClick(rowData)}
              options={{
                padding: 'dense',
                // tableLayout: 'fixed'
                // showTitle: false,

              }}
            />
          </Grid>
          <Grid container>
            <div style={{ height: 20 }} />
          </Grid>
        </Grid>
      </Hidden>

      <Hidden smUp>
        <Card style={{ marginBottom: 15 }}>
          <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
            <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
              Filter By
              </Typography>
          </div>
          <div style={{ marginLeft: 15, marginRight: 15 }}>

            <Grid container>
              <FormControl style={{ marginTop: 2 }}  >
                <Typography style={{ marginTop: 8, fontSize: 12, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>Date</Typography>
                {
                  ['Test Date', 'Redeem Date'].map((item, i) => {
                    return (
                      <Grid container direction="row" justify="flex-start" alignItems="center" key={i} >
                        <Grid item xs={5} sm={5}>
                          <FormControlLabel
                            control={<Checkbox size='small' checked={state[item]} onChange={handleChange(item)} name={item} />}
                            label={<Typography style={{ fontSize: 12, fontFamily: 'Arial, Helvetica, sans-serif' }}>{item}</Typography>}
                          />
                        </Grid>
                        <Grid item xs={3} sm={3} style={{ visibility: state[item] ? "visible" : 'hidden' }} align='right'>
                          <MuiPickersUtilsProvider utils={DateFnsUtils} >
                            <Grid container >
                              <DatePicker
                                style={{ width: 110 }}
                                size="small"
                                clearable
                                format="yyyy/MM/dd"
                                margin="dense"
                                id={"date-picker-from" + item}
                                label={FormatText('app_from')}
                                value={state[item + 'fromdate']}
                                onChange={handleDateChange(item + 'fromdate')}
                                InputLabelProps={{
                                  shrink: true,
                                  classes: {
                                    root: classes.labelRoot
                                  }
                                }}
                                InputProps={{
                                  classes: {
                                    root: classes.inputRoot
                                  }
                                }}
                              />
                            </Grid>
                          </MuiPickersUtilsProvider>
                        </Grid>
                        <Grid item xs={1} sm={1} style={{ visibility: state[item] ? "visible" : 'hidden' }} align='right'>
                        </Grid>
                        <Grid item xs={3} sm={3} style={{ visibility: state[item] ? "visible" : 'hidden' }} align='right'>
                          <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <Grid container>
                              <DatePicker
                                style={{ width: 110 }}
                                clearable
                                size="small"
                                format="yyyy/MM/dd"
                                margin="dense"
                                id={"date-picker-to" + item}
                                label={FormatText('app_to')}
                                value={state[item + 'todate']}
                                onChange={handleDateChange(item + 'todate')}
                                InputLabelProps={{
                                  shrink: true,
                                  classes: {
                                    root: classes.labelRoot
                                  }
                                }}
                                InputProps={{
                                  classes: {
                                    root: classes.inputRoot
                                  }
                                }}
                              />
                            </Grid>
                          </MuiPickersUtilsProvider>
                        </Grid>
                      </Grid>
                    )
                  })
                }
              </FormControl>

              <Grid item xs={12} sm={12} style={{ marginBottom: 10 }} >
                <FormControl fullWidth>
                  <Grid item xs={12} sm={12} >
                  </Grid>
                  <Grid item xs={12} sm={12}>
                    <Typography style={{ marginTop: 8, fontSize: 12, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>Status</Typography>
                  </Grid>
                  <FormControl>
                    <Grid container>
                      <Grid item xs={3} sm={3}>
                        <FormControlLabel
                          control={<Checkbox size='small' checked={state.Paid} onChange={handleChange('Paid')} name="Paid" />}
                          label={<Typography style={{ fontSize: 13, fontFamily: 'Arial, Helvetica, sans-serif' }}>{FormatText('app_paid')}</Typography>}
                        />
                      </Grid>
                      <Grid item xs={3} sm={3}>
                        <FormControlLabel
                          control={<Checkbox size='small' checked={state.finish} onChange={handleChange('finish')} name="finish" />}
                          label={<Typography style={{ fontSize: 13, fontFamily: 'Arial, Helvetica, sans-serif' }}>Redeemed</Typography>}
                        />
                      </Grid>
                      <Grid item xs={6} sm={6} style={{ paddingLeft: 30 }}>
                        <FormControlLabel
                          style={{ display: userInfo.roleUsed === 'voltras.admin' ? '' : 'none' }}
                          control={<Checkbox size='small' checked={state.New} onChange={handleChange('New')} name="New" />}
                          label={<Typography style={{ fontSize: 13, fontFamily: 'Arial, Helvetica, sans-serif' }}>New</Typography>}
                        />
                      </Grid>
                    </Grid>
                  </FormControl>
                </FormControl>
              </Grid>

              <Grid item xs={12} sm={12} container justify='space-between' style={{ paddingLeft: 10, paddingBottom: 10, paddingRight: 10, marginBottom: 10 }}>
                <ExcelFile
                  filename={`OrderReport`} //</Grid></Grid>{`OrderReport ${ convertDateyyyymmdd(state.fromdate) } to ${convertDateyyyymmdd(state.todate)} `}
                  element={
                    <Button size='small' variant="outlined" color="primary" style={{
                      textTransform: 'none', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                      fontWeight: 'bold', fontSize: 12
                    }}>
                      {FormatText('app_export_xls')}
                    </Button>}
                >
                  <ExcelSheet dataSet={state.dataset} name="Order Report" />
                </ExcelFile>

                <Button size='small' variant="contained" onClick={handleSubmitFilter} color="primary" style={{
                  textTransform: 'none', color: 'white', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                  fontWeight: 'bold', fontSize: 12
                }}>
                  {FormatText('app_submit_filter')}
                </Button>

                {/* <Button onClick={handleSubmitFilter} color="primary" variant='contained' style={{
                  textTransform: 'none', color: 'white', borderRadius: 20, fontFamily: 'Arial, Helvetica, sans-serif',
                  fontWeight: 'bold', fontSize: 12
                }}>
                  {FormatText('app_submit')}
                </Button> */}
              </Grid>
            </Grid>
          </div>
        </Card>

        <Card>
          <div style={{ backgroundColor: '#f4f0ec', padding: 5, width: '100%' }}>
            <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
              {`TOTAL ORDER BY ID: ${state.listLengthById.toString()} `}
            </Typography>
          </div>
          <ListTableMobile
            data={state.simpleTable.map(item => item)} button={true} arrow={true}
            column={columnPush} onClickList={handleRowClick} orderList={true}
          />
        </Card>
      </Hidden>

      <div style={{ height: window.screen.height - 700 }} />
    </div >
  )
}

const styles = (theme) => ({
  labelRoot: {
    fontSize: 12,
    fontFamily: 'Arial, Helvetica, sans-serif',
  },
  inputRoot: {
    fontSize: 12,
    fontFamily: 'Arial, Helvetica, sans-serif',
    height: 15
  }
})

export default withStyles(styles)(SimpleTable)
