
import crypto from 'crypto'

export const hash = (algortm, stringData) => {
  return crypto.createHash(algortm).update(stringData).digest('hex')
}

export const hmac = (algortm, key, stringData) => {
  return crypto.createHmac(algortm, key).update(stringData).digest('hex')
}

export const bodyForSignature = (request, method, serviceCode, urlParams) => {
  let bodySignature = request
  if (urlParams) {
    if (method === 'GET' && urlParams.includes('?')) {
      let pairs = urlParams.split('&').slice(1)
      var result = {};
      pairs.forEach(function (pair) {
        pair = pair.split('=');
        result[pair[0]] = decodeURIComponent(pair[1] || '');
      })
      bodySignature = { ...bodySignature, ...result }
      if (serviceCode === 'SEND_EMAIL_VERIFICATION') bodySignature.principalType = 'email'
      if (serviceCode === 'SEND_PHONE_VERIFICATION') bodySignature.principalType = 'phone'
    }
    else if (!urlParams.includes('?')) {
      // console.log("PARAMS SPLIT: ", urlParams.split('/'))
      if ((serviceCode === 'ADD_CLIENT_ROLE') || (serviceCode === 'DELETE_CLIENT')) bodySignature.clientId = urlParams.split('/')[2]
      if (serviceCode === 'DELETE_CLIENT_ROLE') {
        // console.log("BRUAHAHA: ", urlParams.split('/'))
        bodySignature.clientId = urlParams.split('/')[2]
        bodySignature.roleIds = urlParams.split('/')[4]
      }
      if (serviceCode === 'UPDATE_USER_ROLE') bodySignature.id = urlParams.split('/')[2]
      if (serviceCode === 'DELETE_USER') bodySignature.id = urlParams.split('/')[2]
      if (serviceCode === 'SELECT_ALL_CLIENT' && urlParams.split('/')[1]) bodySignature.clientId = urlParams.split('/')[1]

    }
  }
  return bodySignature
}

export const generateSignature = (headers, body, key) => {
  let orderedHeaders = {}
  Object.keys(headers).sort().forEach(v => {
    orderedHeaders[v] = headers[v]
  })

  let orderedBody = {}
  Object.keys(body).sort().forEach(v => {
    orderedBody[v] = body[v]
  })

  let stringHeader = JSON.stringify(orderedHeaders).replace(/\s/g, '');
  let stringBody = JSON.stringify(orderedBody).replace(/\s/g, '');
  let stringMsg = hash('sha1', stringHeader) + hash('sha1', stringBody);
  let signature = hmac('sha256', key, stringMsg)
  // console.log('Header sebelum hash: ', stringHeader)
  // console.log('Body sebelum hash: ', stringBody)
  // console.log("STRING HEADER: ", hash('sha1', stringHeader))
  // console.log("STRING BODY: ", hash('sha1', stringBody))
  // console.log("STRING MESSAGE: ", stringMsg)

  return signature
}

export const generateRoleToSend = (aud, role) => {
  let roleToSend = 'end-user'
  if (aud === 'voltras-health') {
    if (role.includes('voltras.admin')) roleToSend = 'voltras.admin'
    else if (role.includes('biofarma.admin')) roleToSend = 'biofarma.admin'
    else if (role.includes('clinic.admin')) roleToSend = 'clinic.admin'
    else if (role.includes('health.admin')) roleToSend = 'health.admin'
    else if (role.includes('clinic.operator')) roleToSend = 'clinic.operator'
    else roleToSend = 'member'
    // else roleToSend = 'end-user'
  }
  else {
    if (role.includes('root')) roleToSend = 'root'
    // else if (role.includes('AUTHENTICATION_ADMIN')) roleToSend = 'AUTHENTICATION_ADMIN'
    else roleToSend = 'member'
  }
  // console.log("HUAYOOO: ", aud, role)
  return roleToSend
}