
import React from 'react'
import { Button, Typography, Card, Input, Grid, FormHelperText, FormControl } from '@material-ui/core';
import FormatText from '../../i18n';

const InsertOTPCard = props => {
  const { classes, handleCancelOtp, handleResendOtp, state, handleSubmitOtp, handleChange } = props

  return (
    <Card className={classes} style={{ width: 300 }} elevation={4}>
      <Grid
        style={{ margin: 30 }}
        container
        direction="column"
        justify="center"
        alignItems="center"
      >
        <Typography variant="h5" color='primary'><b>{FormatText('app_submit_otp')}</b> </Typography>
        <Typography variant="caption" color='textSecondary' >{FormatText('app_submit_otp_text')}</Typography>
        <Typography variant='subtitle2' color='primary' >{state.email}</Typography>

        <FormControl style={{ margin: 10, width: '12ch', textAlign: 'center' }}  >
          <Input
            autoFocus
            style={{ fontSize: 28, textAlign: 'center' }}
            error={state.otpErrorMessage ? true : false}
            id="standard-adornment-otp"
            type='text'
            onChange={handleChange('otp')}
            value={state.otp}
          />
          {
            state.otpErrorMessage
              ? <FormHelperText error id="email-helper-text">{state.otpErrorMessage}</FormHelperText>
              : null
          }
        </FormControl>

        <FormControl>
          <Grid>
            <Button onClick={handleCancelOtp} variant="outlined" color="primary" style={{ margin: 10, }}>
              {FormatText('app_cancel')}
            </Button>
            <Button
              onClick={handleSubmitOtp}
              disabled={state.otp ? false : true}
              style={{ margin: 10, }}
              variant="contained"
              color="primary">
              {FormatText('app_submit')}
            </Button>
          </Grid>
          {
            state.onLoginErrorMessage
              ? <FormHelperText error id="password-helper-text">{state.onLoginErrorMessage}</FormHelperText>
              : null
          }
        </FormControl>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
        >
          <Typography variant="caption" color='textSecondary' >{FormatText('app_not_receive_otp')}</Typography>
          <Button
            size='small'
            onClick={handleResendOtp}
            color="primary"
            style={{ textTransform: 'none', fontWeight: 'bold', fontSize: 12 }}
          >
            {FormatText('app_resend_otp')}
          </Button>

        </Grid>
      </Grid>
    </Card>
  )
}

export default InsertOTPCard
