import React from 'react'
import Divider from '@material-ui/core/Divider'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import IconButton from '@material-ui/core/IconButton'
// import ListItemText from '@material-ui/core/ListItemText'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Tooltip from '@material-ui/core/Tooltip'
import propTypes from 'prop-types'

const styles = (theme) => ({
    root: {
        // width: '100%',
        // maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
    },
})

const ListTableMobile = (props) => {
    const { classes } = props
    let data = []
    data = props.data ? props.data : []
    const column = props.column ? props.column : []
    return (
        <div className={classes.root}>
            {data.length !== 0 ?
                <List disablePadding={true}>
                    {
                        data.map((item, index) => (
                            <div key={index}>
                                {
                                    props.action ? <ListItem >
                                        <Grid container justify='space-between'>
                                            <Grid item sm={5} xs={5}>
                                                {column.map((items, i) => (
                                                    <Grid item sm={12} xs={12} key={i + 8}>
                                                        <Typography style={{ color: 'grey', fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                            {items.title}
                                                        </Typography>

                                                    </Grid>
                                                ))}
                                                {
                                                    props.action ? <Grid item sm={12} xs={12}>
                                                        <Typography style={{ color: 'grey', fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                            {'Actions'}
                                                        </Typography>

                                                    </Grid> : ''
                                                }
                                            </Grid>
                                            <Grid item sm={!props.arrow ? 7 : 5} xs={!props.arrow ? 7 : 5}>
                                                {column.map((items, idx) => (
                                                    <Grid item sm={12} xs={12} key={idx + 12}>
                                                        <Typography style={{ fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                            {items.title ? item[items.field] ? item[items.field] : '-' : null}
                                                        </Typography>
                                                    </Grid>

                                                ))}
                                                {
                                                    props.action ? <Grid container item sm={12} xs={12}>
                                                        <IconButton style={{ padding: 0 }} onClick={() => props.onClickUpdate(item)}>
                                                            <Tooltip title={props.titleTooltipUpdate ? props.titleTooltipUpdate : 'Update'}>
                                                                {props.IconUpdateCustomize}
                                                            </Tooltip>
                                                        </IconButton>
                                                        <IconButton style={{ padding: 0, marginLeft: 5 }} onClick={() => props.onClickDelete(item)}>
                                                            <Tooltip title={props.titleTooltipDelete ? props.titleTooltipDelete : 'Delete'}>
                                                                {props.IconDeleteCustomize}
                                                            </Tooltip>
                                                        </IconButton>
                                                    </Grid> : ""
                                                }
                                            </Grid>
                                            {props.arrow ? <Grid item sm={2} xs={2} align='right'>
                                                <IconButton style={{ padding: 0 }} onClick={() => props.onClickList(item)}>
                                                    {/* <Tooltip title={'Detail'}> */}
                                                    <Tooltip title={'Detail'}>
                                                        <ChevronRightIcon style={{ padding: 0 }} />
                                                    </Tooltip>
                                                    {/* </Tooltip> */}
                                                </IconButton>
                                            </Grid> : null}

                                        </Grid>
                                    </ListItem> : <ListItem button={props.button ? true : false} onClick={props.button ? () => props.onClickList(item) : null}>
                                            <Grid container justify='space-between'>
                                                <Grid item sm={5} xs={5}>
                                                    {column.map((items, i) => (
                                                        <Grid item sm={12} xs={12} key={i + 8}>
                                                            <Typography style={{ color: 'grey', fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                                {items.title}
                                                            </Typography>

                                                        </Grid>
                                                    ))}
                                                    {
                                                        props.action ? <Grid item sm={12} xs={12}>
                                                            <Typography style={{ color: 'grey', fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                                {'Actions'}
                                                            </Typography>

                                                        </Grid> : ''
                                                    }
                                                </Grid>
                                                <Grid item sm={!props.arrow ? 7 : 5} xs={!props.arrow ? 7 : 5}>
                                                    {column.map((items, idx) => (
                                                        <Grid item sm={12} xs={12} key={idx + 12}>
                                                            <Typography style={{ fontSize: 11, marginBottom: 5, fontFamily: 'Arial, Helvetica, sans-serif' }} noWrap={true}>
                                                                {items.title ? item[items.field] ? item[items.field] : '-' : null}
                                                            </Typography>
                                                        </Grid>

                                                    ))}
                                                    {
                                                        props.action ? <Grid container item sm={12} xs={12}>
                                                            <IconButton style={{ padding: 0 }} onClick={() => props.onClickUpdate(item)}>
                                                                <Tooltip title={props.titleTooltipUpdate ? props.titleTooltipUpdate : 'Update'}>
                                                                    {props.IconUpdateCustomize}
                                                                </Tooltip>
                                                            </IconButton>
                                                            <IconButton style={{ padding: 0 }} onClick={() => props.onClickDelete(item)}>
                                                                <Tooltip title={props.titleTooltipDelete ? props.titleTooltipDelete : 'Delete'}>
                                                                    {props.IconDeleteCustomize}
                                                                </Tooltip>
                                                            </IconButton>
                                                        </Grid> : ""
                                                    }
                                                </Grid>
                                                {props.arrow ? <Grid item sm={2} xs={2} align='right'>
                                                    <ChevronRightIcon style={{ padding: 0 }} />
                                                </Grid> : null}

                                            </Grid>
                                        </ListItem>
                                }

                                <Divider />
                            </div>
                        ))
                    }
                </List> :
                <Typography style={{ textAlign: 'center', paddingTop: 30, paddingBottom: 30, fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey' }}>
                    No Record
                </Typography>
            }
        </div>
    )
}
ListTableMobile.propTypes = {
    arrow: propTypes.bool,
    button: propTypes.bool,
    column: propTypes.array.isRequired,
    data: propTypes.array.isRequired,
    onClickList: propTypes.func,
    action: propTypes.bool,
    onClickDelete: propTypes.func,
    onClickUpdate: propTypes.func,
    IconDeleteCustomize: propTypes.element,
    IconUpdateCustomize: propTypes.element,
    titleTooltipDelete: propTypes.string,
    titleTooltipUpdate: propTypes.string
}

export default withStyles(styles)(ListTableMobile)