
import Store from '../Store'
import ServiceUrl from './ServiceUrl'
import { serviceStart, serviceEnd, dispatchLogout } from '../actions/SystemActions'
import { resetService } from '../actions/ServiceActions'
// import { setResponse } from '../actions/ResponseActions'
import { setSnackbar } from '../actions/FeedbackActions'
import { generateSignature, bodyForSignature } from '../utils/Crypting'
import FormatText from '../i18n'
import Config from '../config';
import { setClinicRecaptLogistic } from '../actions/PaxListActions'

export const SendToService = (request, method, serviceCode, urlParams, customHeaders, role, file, onSucceed) => {
  let domain = window.location.origin
  // console.log("DOMAIN: ", domain)
  // console.log('CUSTOM HEADERS: ', customHeaders)
  Store.dispatch(serviceStart())
  let state = Store.getState()
  let userInfo = state.userInfo.info
  let aud = state.system.appConstant ? state.system.appConstant.aud : null
  let roleUsed = state.userInfo.roleUsed
  let locale = state.system.locale
  let stateAccessToken = state.userInfo.sessionInfo.accessToken
  let stateKey = state.userInfo.sessionInfo.key

  let clientId = null
  if (aud) clientId = aud
  if (userInfo.aud) clientId = userInfo.aud
  if (customHeaders) if (customHeaders.clientId) clientId = customHeaders.clientId

  let name = userInfo.name ? userInfo.name : customHeaders ? customHeaders.name : null
  let email = userInfo.email ? userInfo.email : customHeaders ? customHeaders.email : null
  let phone = userInfo.phone ? userInfo.phone : customHeaders ? customHeaders.phone : null
  let accessToken = stateAccessToken ? stateAccessToken : localStorage.getItem("accessToken") ? localStorage.getItem("accessToken") : null
  let key = stateKey ? stateKey : localStorage.getItem("key") ? localStorage.getItem("key") : null
  let roleToSend = role ? role : roleUsed ? roleUsed : null  // headersContent.role
  let hardcodeHeaders = {
    'access-token': accessToken,
    'role': roleToSend,
    "gadget-id": "mygadgetId",
  }
  const snackFeed = {
    open: true, severity: "success", message: "Successful", redirect: '',
    action: false, actionLabel: "", position: 'bottom-center', duration: 4000,
  }
  // console.log("HAYO CLIENT...: ", clientId)
  // console.log(userInfo.aud)
  if (clientId) hardcodeHeaders['client-id'] = clientId

  let requestJSON = JSON.stringify(request)
  let oReq = new XMLHttpRequest();

  // console.log("REQUEST URL: ", method, ServiceUrl(serviceCode) + (urlParams ? urlParams : ""))
  // console.log('REQUEST BODY: ', request)

  oReq.open(method, ServiceUrl(serviceCode) + (urlParams ? urlParams : ""), true)

  // oReq.setRequestHeader('Accept-Language', locale)
  oReq.setRequestHeader('gadget-id', "mygadgetId")
  if (roleToSend && (serviceCode !== 'login')) oReq.setRequestHeader("role", roleToSend)
  if (clientId) oReq.setRequestHeader('client-id', clientId)
  if (name) oReq.setRequestHeader('name', name)
  if (email) oReq.setRequestHeader('email', email)
  if (phone) oReq.setRequestHeader('phone', phone)
  oReq.setRequestHeader('Content-type', 'application/json')

  let bodySignature = urlParams ? bodyForSignature(request, method, serviceCode, urlParams) : request
  if ((serviceCode === 'SEND_CLINIC_ACTIVATION')
    || (serviceCode === 'GET_CLINIC_USER')
    || (serviceCode === 'ADD_CLINIC_USER')) bodySignature = request

  if (key) {
    oReq.setRequestHeader("access-token", accessToken)
    oReq.setRequestHeader('signature', generateSignature(hardcodeHeaders, bodySignature, key))
  }

  oReq.send(requestJSON)

  oReq.addEventListener('load', () => {
    let status = oReq.status.toString()
    Store.dispatch(resetService())
    if (oReq.readyState === 4 && status.slice(0, 1) === '2') {
      Store.dispatch(serviceEnd())
      // console.log("OPO TOH: ", typeof oReq.responseText)
      let response = {
        headers: {},
        body: oReq.responseText && (oReq.responseText !== 'null') ? JSON.parse(oReq.responseText) : {}
      }
      onSucceed(response)
    }
    else {
      Store.dispatch(serviceEnd())

      if (serviceCode === 'logout') {
        Store.dispatch(dispatchLogout())
      }
      if (status.slice(0, 1) === '4') {
        const respErr = JSON.parse(oReq.responseText)
        console.log("ERROR YA: ", serviceCode, respErr)
        let messageError = respErr.errorDescription ? respErr.errorDescription[locale.slice(0, 2)] : respErr.message ? respErr.message : respErr
        // const responseErr = {
        //   headers: {},
        //   body: {
        //     error: true,
        //     errorDescription: respErr.errorDescription
        //       ? respErr.errorDescription[locale.slice(0, 2)]
        //       : respErr.message ? respErr.message : oReq.responseText
        //   }
        // }

        if (
          (respErr.error === 'error.auth.securityValidation.tokenNotFound')
          || (respErr.error === 'error.auth.securityValidation.invalidAccessToken')
        ) {
          snackFeed.message = messageError
          snackFeed.severity = "error"
          snackFeed.redirect = "Login"
          Store.dispatch(setSnackbar(snackFeed))
          setTimeout(() => {
            localStorage.clear()
            Store.dispatch(dispatchLogout())
          }, 2000);
        }
        else if (respErr.error === "error.auth.securityValidation.emailNotVerified") {
          // responseErr.body.error = respErr.error
          // const snackFeedback = {
          //   open: true,
          //   message: messageError,
          //   severity: "warning",
          //   position: 'top-center',
          //   action: "SEND_EMAIL_VERIFICATION",
          //   actionLabel: FormatText('app_send_email_verify')
          // }
          // console.log("kesini?")
          snackFeed.message = messageError
          snackFeed.severity = "warning"
          snackFeed.position = 'top-center'
          snackFeed.action = "SEND_EMAIL_VERIFICATION"
          snackFeed.actionLabel = FormatText('app_send_email_verify')
          snackFeed.duration = 14000
          Store.dispatch(setSnackbar(snackFeed))
        }
        else if (respErr.error === "error.auth.securityValidation.phoneNotVerified") {
          // responseErr.body.error = respErr.error
          // const snackFeedback = {
          //   open: true,
          //   message: messageError,
          //   severity: "warning",
          //   position: 'top-center',
          //   action: "SEND_PHONE_VERIFICATION",
          //   actionLabel: FormatText('app_send_phone_verify')
          // }
          // console.log("kesini?")
          snackFeed.message = messageError
          snackFeed.severity = "warning"
          snackFeed.position = 'top-center'
          snackFeed.action = "SEND_PHONE_VERIFICATION"
          snackFeed.actionLabel = FormatText('app_send_phone_verify')
          snackFeed.duration = 14000
          Store.dispatch(setSnackbar(snackFeed))
        }
        else if (serviceCode === 'RECAPT_CLINIC_SUMMARY') {
          Store.dispatch(setClinicRecaptLogistic({ product: [], testsdate: "" }))
          snackFeed.message = messageError
          snackFeed.severity = "warning"
          Store.dispatch(setSnackbar(snackFeed))
        }
        else if (serviceCode === 'RECAPT_BIOFARMA_SUMMARY') {
          Store.dispatch(setClinicRecaptLogistic({ total: [], detail: [] }))
          snackFeed.message = messageError
          snackFeed.severity = "warning"
          Store.dispatch(setSnackbar(snackFeed))
        }
        else {
          // Store.dispatch(setResponse(responseErr))
          snackFeed.message = JSON.stringify(messageError)
          snackFeed.severity = "error"
          Store.dispatch(setSnackbar(snackFeed))
        }
      }
      else {
        // let responseErr = {
        //   headers: {},
        //   body: {
        //     error: true,
        //     errorDescription: oReq.responseText
        //   }
        // }
        // Store.dispatch(setResponse(responseErr))
        snackFeed.message = oReq.responseText
        snackFeed.severity = "error"
        Store.dispatch(setSnackbar(snackFeed))
      }
    }
  })

  const createResponseError = (e, desc) => {
    console.log("ERRORKAH? ", desc, JSON.stringify(e))
    // let response = {
    //   headers: {
    //     'statusCode': 400,
    //     'userMessage': "net::ERR_CONNECTION_TIMED_OUT",
    //   },
    //   body: {
    //     error: true,
    //     errorDescription: "net::ERR_CONNECTION_TIMED_OUT",
    //     ...e
    //   }
    // }
    if (domain === 'http://localhost:3030' || domain === 'http://172.16.10.98:3032') {
      // Store.dispatch(setResponse(response))
      snackFeed.message = "net::ERR_CONNECTION_TIMED_OUT"
      snackFeed.severity = "error"
      Store.dispatch(setSnackbar(snackFeed))
      Store.dispatch(serviceEnd())
    }
    else {
      // Store.dispatch(setResponse(response))
      snackFeed.message = "net::ERR_CONNECTION_TIMED_OUT"
      snackFeed.severity = "error"
      Store.dispatch(setSnackbar(snackFeed))
      Store.dispatch(serviceEnd())
      // setTimeout(() => {
      //   // window.location.reload()
      // }, 300)

    }
  }

  oReq.onerror = e => createResponseError(e, 'onError')

  oReq.ontimeout = e => createResponseError(e, 'onTimeout')
}

export const sendImageToService = (image, type, blobFile) => {
  const snackFeed = {
    open: true, severity: "success", message: "Successful", redirect: '',
    action: false, actionLabel: "", position: 'bottom-center', duration: 4000,
  }
  // let respErr = {
  //   headers: {
  //     'statusCode': 400,
  //     'userMessage': "net::ERR_CONNECTION_TIMED_OUT",
  //   },
  //   body: {
  //     error: true,
  //     errorDescription: "net::ERR_CONNECTION_TIMED_OUT",
  //   }
  // }
  return new Promise((resolve, reject) => {
    Store.dispatch(serviceStart())
    const urlConnection = Config.gatewayRepoFile + "/static-file-manager/insert";;
    var oReq = new XMLHttpRequest();
    oReq.open("POST", urlConnection, true);
    oReq.addEventListener('load', () => {
      if (oReq.readyState === 4 && oReq.status === 200) {
        const response = JSON.parse(oReq.responseText);
        resolve(response);
        Store.dispatch(serviceEnd())
      }
      else resolve({ type: "error", message: "Something went wrong", }); Store.dispatch(serviceEnd())
    })
    var blob = blobFile ? blobFile : new Blob([image], { type });
    // console.log("BLOB: ", blob)
    oReq.send(blob);

    oReq.onerror = e => {
      resolve({ type: "error", message: "Something went wrong", });
      // Store.dispatch(setResponse(respErr))
      // Store.dispatch(serviceEnd())
      snackFeed.message = "net::ERR_CONNECTION_TIMED_OUT"
      snackFeed.severity = "error"
      Store.dispatch(setSnackbar(snackFeed))
      Store.dispatch(serviceEnd())
    }

    oReq.ontimeout = e => {
      resolve({ type: "error", message: "Something went wrong", });
      // Store.dispatch(setResponse(respErr))
      // Store.dispatch(serviceEnd())
      snackFeed.message = "net::ERR_CONNECTION_TIMED_OUT"
      snackFeed.severity = "error"
      Store.dispatch(setSnackbar(snackFeed))
      Store.dispatch(serviceEnd())
    }

  })
}