
import React, { useState } from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Grid, Typography, Button } from '@material-ui/core'
import { FormValidation } from '../../../utils/FormValidation';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const AddUrlOriginDialog = ({ ...props }) => {
  // const [roleList, setRoleList] = useState([])
  // const [available, setAvailable] = useState([])
  // const [newRoleList, setNewRoleList] = useState([])
  // const [showAdd, setShowAdd] = useState(true)
  const [state, setState] = useState({
    originUris: [],
    redirectUris: []
  })
  // console.log('redirect', props.redirect)
  const handleClose = () => {
    setState({ addRoleList: [], })
    props.handleOpen(false)
    // setShowAdd(true)
  };

  const handleChange = props => e => {
    let valid = FormValidation(props, e.target.value)
    setState({
      ...state,
      [props]: { uri: e.target.value },
      [props + 'ErrorMessage']: valid.errorMessage,
    })
  }

  const handleSave = () => {
    if (!props.redirect) {
      props.handleSave(state.originUris)
    }
    else {
      props.handleSave(state.redirectUris)
    }
    props.handleOpen(false)
    // setShowAdd(true)
    setState({ ...state, addRoleList: [], })
  }
  const disabledButton = () => {
    let origin = state.originUris ? state.originUris.uri : ""
    let redirect = state.redirectUris ? state.redirectUris.uri : ""
    if (state.originUrisErrorMessage) return true
    else if (state.redirectUrisErrorMessage) return true
    else if (origin) return false
    else if (redirect) return false
    else return true
  }
  return (
    <Dialog fullWidth maxWidth={'sm'} open={props.open} onClose={handleClose} scroll={'paper'} >
      <DialogTitle
        id="max-width-dialog-title"
        onClose={handleClose}
      >
        Client: {props.data ? props.data.clientName : ''}
      </DialogTitle>
      <DialogContent dividers>
        {!props.redirect ?
          <TextField
            autoFocus
            margin="dense"
            size="small"
            onChange={handleChange('originUris')}
            id="standard-roleId"
            label="Origin URI"
            placeholder="Origin URI"
            value={state.originUris ? state.originUris.uri : ""}
            helperText={state.originUrisErrorMessage ? state.originUrisErrorMessage : ''}
            error={state.originUrisErrorMessage ? true : false}
          /> :
          <TextField
            autoFocus
            margin="dense"
            size="small"
            onChange={handleChange('redirectUris')}
            id="standard-roleId"
            label="Redirect URI"
            placeholder="Redirect URI"
            value={state.redirectUris ? state.redirectUris.uri : ""}
            helperText={state.redirectUrisErrorMessage ? state.redirectUrisErrorMessage : ''}
            error={state.redirectUrisErrorMessage ? true : false}
          />
        }
      </DialogContent>

      <DialogActions>
        <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button onClick={handleClose} variant='outlined' color="primary">
              Cancel
          </Button>
          </Grid>
          <Grid container item direction="row" justify='center' alignItems='center' md={6}>
            <Button onClick={handleSave} variant='contained' color="primary" disabled={disabledButton()}>
              Save
          </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  )
}

export default AddUrlOriginDialog
