
export const setUserMenu = val => {
  return {
    type: "SET_USER_MENU",
    val
  }
}

export const setMenuCollapseStatus = val => {
  return {
    type:"SET_MENU_COLLAPSE_STATUS",
    val
  }
}

export const toggleCollapseMenu = val => {
  return {
    type:"TOGGLE_COLLAPSE_MENU",
    val
  }
}
