const INITIAL_STATE = {
    configurationList: [],
    productCatalogList:[],
    configDetail: null,
    configDetailModal: false
}

const genericConfiguration = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case 'SET_GENERIC_CONFIG_LIST':
            return {
                ...state,
                configurationList: action.data
            }
        case 'SET_CONFIG_DETAIL':
            return {
                ...state,
                configDetail: action.data
            }
        case 'SET_CONFIG_DETAIL_MODAL':
            return {
                ...state,
                configDetailModal: action.val
            }
        case "SET_GENERAL_PRODUCT_CATALOG":
            return {
                ...state,
                productCatalogList: action.val
            }
        default :
            return state;
    }
};

export default genericConfiguration;