
let numOr0 = n => isNaN(n) ? 0 : n

export const createClinicSummaryTable = (items, clinic_product) => {
  let firstcolumn = ["Products"]
  let nextColumns = items[0] && items.map(item => item.testdate)
  let lastColumn = ["Total"]
  let allColumns = firstcolumn.concat(nextColumns).concat(lastColumn)

  let allRows = []
  let allDatasetRows = []
  let objItems = {}
  clinic_product.map(cp => objItems[cp] = 0)
  let newItems = []
  if (items[0]) {
    items.forEach(item => {
      let newObjItems = { ...objItems }
      item.product.map(prod => newObjItems[prod.name] = prod.total)
      newItems.push(newObjItems)
    })
    clinic_product.forEach(element => {
      let row = [element]
      let datasetRow = [{ value: element }]
      newItems.forEach((item, i) => {
        row.push(item[element])
        datasetRow.push({ value: item[element] })
      })
      let sum = row.reduce((a, b) => numOr0(a) + numOr0(b))
      row.push(sum)
      datasetRow.push({ value: sum })
      allRows.push(row)
      allDatasetRows.push(datasetRow)
    })
  }
  let datasetColumn = allColumns.map((item, i) => { return { title: item, width: { wch: i === 0 ? 25 : 12 } } })
  let dataSet = [{ ySteps: 1, columns: datasetColumn, data: allDatasetRows }]
  return { allColumns, allRows, dataSet }
}

export const createBiofarmaSummaryTable = (items, clinic_product) => {
  let allRows = []
  let dataSet = []

  if (items.detail && clinic_product[0]) {
    let detail_per_clinic = items.detail
    let total_per_product = items.total
    let default_row_per_clinic = ["clinic_name"]
    for (let index = 0; index < (clinic_product.length * 2); index++) {
      default_row_per_clinic.push(0)
    }
    detail_per_clinic.forEach(per_clinic => {
      let row = [...default_row_per_clinic]
      row[0] = per_clinic.name
      clinic_product.forEach((cp, p) => {
        per_clinic.product.forEach((prod, i) => {
          if (cp === prod.name) {
            if (p === 0) {
              row[1] = prod.patient
              row[2] = parseInt(prod.nta)
            }
            row[(p * 2) + 1] = prod.patient
            row[(p * 2) + 2] = parseInt(prod.nta)
          }
        })
      })
      row.push(per_clinic.total_patient)
      row.push(parseInt(per_clinic.total_nta))

      allRows.push(row)
    })


    let row_total = [...default_row_per_clinic]
    row_total[0] = "Total"
    clinic_product.forEach((cp, p) => {
      total_per_product.forEach((prod, i) => {
        if (cp === prod.name) {
          if (p === 0) {
            row_total[1] = prod.patient
            row_total[2] = parseInt(prod.nta)
          }
          row_total[(p * 2) + 1] = prod.patient
          row_total[(p * 2) + 2] = parseInt(prod.nta)
        }
      })
    })

    let total_pax_arr = []
    let total_nta_arr = []
    row_total.forEach((item, i) => {
      if (i % 2 === 0) total_nta_arr.push(item)
      else total_pax_arr.push(item)
    })
    let sum_patient = total_pax_arr.reduce((a, b) => numOr0(a) + numOr0(b))
    let sum_nta = total_nta_arr.reduce((a, b) => numOr0(a) + numOr0(b))
    row_total.push(sum_patient)
    row_total.push(sum_nta)

    allRows.push(row_total)


    // let per_clinic = items.detail
    // let per_column = items.total
    // let new_per_column = []
    let datasetColumn = [{ title: " ", width: { wch: 25 } }]
    let firstRow = [{ value: "Clinics", style: { font: { bold: true } } }]
    clinic_product.map(item => [item, " "].map((el, j) => datasetColumn.push({ title: el, width: { wch: j === 0 ? 5 : 12 } })))
    datasetColumn.push({ title: "TOTAL", width: { wch: 5 } })
    datasetColumn.push({ title: " ", width: { wch: 12 } })
    clinic_product.map(item => ["Pax", "NTA"].map(el => firstRow.push({ value: el, style: { alignment: { horizontal: "center" } } })))
    firstRow.push({ value: "Pax", style: { alignment: { horizontal: "center" } } })
    firstRow.push({ value: "NTA", style: { alignment: { horizontal: "center" } } })

    let objDataSet = { ySteps: 1, columns: datasetColumn, data: [] }
    objDataSet.data.push(firstRow)
    allRows.forEach(row => {
      let newRow = []
      row.forEach(rowItem => {
        newRow.push({ value: rowItem })
      })
      objDataSet.data.push(newRow)
    })

    dataSet.push(objDataSet)
  }
  return { allRows, dataSet }
}

