
import React, { useState, useEffect } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { FormValidation } from '../../utils/FormValidation';
import { Avatar, Typography } from '@material-ui/core';
import { resizeImage } from '../../utils/utils';
// import { sendImageToService } from '../../services/ServiceClient';
import FormatText from '../../i18n';

export default function EditDialog(props) {
  const { editItem } = props
  const [name, setName] = useState("")
  const [value, setValue] = useState('')
  const [key, setKey] = useState('')
  const [img, setImg] = useState('')
  const [action, setAction] = useState(false)
  const [imgPreview, setImgPreview] = useState('')
  const [validation, setValidation] = useState({})
  const [imageFile, setImageFile] = useState("")
  const [imageType, setImageType] = useState("")

  useEffect(() => {
    setValidation({
      error: false,
      errorMessage: '',
    })
    setImg(editItem.picture)
    setName(editItem.name)
    setKey(editItem.key)
    setValue(editItem.value)
    setAction(editItem.action)
  }, [editItem])

  const handleEditValue = prop => e => {
    let valid = FormValidation(prop, e.target.value)
    setValidation(valid)
    setValue(e.target.value)
  }

  const handleCancel = () => {
    setName(editItem.name)
    setKey(editItem.key)
    setValue(editItem.value)
    props.handleEditClose()
  }

  const handleYes = () => {
    if (imgPreview) {
      let newObj = { name, value, key, action, picture: imgPreview, imageFile, imageType }
      props.handleNewItem(newObj)
    }
    else props.handleNewItem({ name, value, key, action })
    props.handleEditClose()
  }

  const fileSelected = async e => {
    setValidation({ error: false, errorMessage: '', })
    let file = e.target.files[0]
    if (file) {
      if (!file.type.includes('image')) setValidation({ error: true, errorMessage: 'Must be image file', })
      // else if (file.size > 1000000) setValidation({ error: true, errorMessage: 'Image is bigger than 1 MB', })
      else {
        const data = await resizeImage(file);
        // console.log("DATA: ", data)
        setImgPreview(data.blobUrl)
        setImageFile(data.file)
        setImageType(data.type)
      }
    }
  }

  return (
    <div>
      <Dialog open={props.open} onClose={props.handleEditClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Edit {name}</DialogTitle>
        {
          <DialogContent>
            <center>
              <DialogContentText style={{ width: 290 }}>
                {FormatText('app_save_to_change')}
              </DialogContentText>
            </center>
            {
              key === 'picture'
                ? <div>
                  <input
                    accept="image/*"
                    onChange={fileSelected}
                    style={{ display: 'none' }}
                    id="raised-button-file"
                    multiple
                    type="file"
                  />
                  <center>
                    <Avatar style={{ width: 75, height: 75, marginBottom: 15 }} src={imgPreview ? imgPreview : img ? img : ""} />
                  </center>

                  <label htmlFor="raised-button-file">
                    <center>
                      <Typography color='error' variant='caption' >{validation.errorMessage}</Typography>
                      <Button variant='outlined' component="span" size='small' color='primary' >
                        {FormatText('app_select_photo')}
                      </Button>
                    </center>
                  </label>
                </div>
                : <TextField
                  autoFocus
                  margin="dense"
                  id="name"
                  label={name}
                  value={value}
                  onChange={handleEditValue(key)}
                  placeholder={name}
                  style={{ width: 280 }}
                  error={validation.error ? true : false}
                  helperText={validation.errorMessage ? validation.errorMessage : ""}
                />
            }
          </DialogContent>
        }
        <DialogActions>
          <Button onClick={handleCancel} color="secondary">
            {FormatText('app_cancel')}
          </Button>
          <Button onClick={handleYes} color="primary" disabled={validation.error ? true : false}>
            {FormatText('app_choose')}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
