
import { connect } from 'react-redux'
import PaxListScreen from './PaxListScreen'
import { setPaxList, setPaxDetail, setFilteredList } from "../../../actions/PaxListActions";
import { setOpenScreen } from '../../../actions/SystemActions'
import { setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod, setServiceParams, 
  setServiceCustomHeaders, setServiceRole } from '../../../actions/ServiceActions'

const mapStateToProps = state => {
  return {
    paxList : state.paxList,
    userInfo: state.userInfo
  }
}

const mapDispatchToProps = dispatch => {
  return {
    _setPaxList : val => {
      dispatch(setPaxList(val))
    },
    _setFilteredList: val => {
      dispatch(setFilteredList(val))
    },
    _setPaxDetail: val => {
      dispatch(setPaxDetail(val))
    },
    _setOpenScreen: val => {
      dispatch(setOpenScreen(val))
    },
    _setRequest: val => {
      dispatch(setServiceRequest(val))
    },
    _setServiceCode: val => {
      dispatch(setServiceCode(val))
    },
    _setServiceStatus: val => {
      dispatch(setServiceStatus(val))
    },
    _setServiceMethod: val => {
      dispatch(setServiceMethod(val))
    },
    _setServiceParams: val => {
      dispatch(setServiceParams(val))
    },
    _setCompleteRequest: (val) => {
      dispatch(setServiceRequest(val.request))
      dispatch(setServiceMethod(val.method))
      dispatch(setServiceCode(val.serviceCode))
      dispatch(setServiceParams(val.params))
      dispatch(setServiceCustomHeaders(val.customHeaders))
      dispatch(setServiceRole(val.role))
      dispatch(setServiceStatus("active"))
    }
  }
}

const PaxList = connect(mapStateToProps, mapDispatchToProps)(PaxListScreen)

export default PaxList