import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import TableFooter from '@material-ui/core/TableFooter';
import TablePagination from '@material-ui/core/TablePagination';

import Typography from '@material-ui/core/Typography';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import IconButton from '@material-ui/core/IconButton';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import LastPageIcon from '@material-ui/icons/LastPage';
// import Delete from '@material-ui/icons/Delete'
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardDatePicker,
    // DatePicker
} from '@material-ui/pickers';
import { FormValidation } from '../../utils/FormValidation'
import { InputAdornment } from '@material-ui/core'

import './GenericJson.css';

import { setOpenScreen } from "../../actions/SystemActions";
import {
    setServiceRequest, setServiceCode, setServiceStatus,
    setServiceCustomHeaders, setServiceMethod, setServiceParams, setSecondRequest
} from '../../actions/ServiceActions';
import { converDateDDMMYY } from '../../utils/utils';
import Hidden from '@material-ui/core/Hidden'
import Card from '@material-ui/core/Card'
import ListTableMobile from '../../components/list/ListTableMobile'
import Add from '@material-ui/icons/AddCircleOutline'
import Tooltip from '@material-ui/core/Tooltip'

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const CatalogProduct = (props) => {

    const { setOpenScreen, setCompleteRequest, productCatalogList } = props

    useEffect(() => {
        setOpenScreen("Generic Configuration");
    }, [setOpenScreen]);

    const getCatalogProduct = () => {
        let fullRequest = {
            request: {},
            serviceMethod: 'GET',
            serviceCode: 'GENERAL_PRODUCT_CATALOG',
            customHeaders: {
                clientId: 'voltras-health'
            }
        }

        setCompleteRequest(fullRequest);
    }

    useEffect(getCatalogProduct, [])

    // const [modalConfigDetail, setModalConfigDetail] = useState(null);
    // const [editConfig, setEditConfig] = useState(false);
    const [openModalCreate, setOpenModalCreate] = useState(false)
    const [openModalEdit, setOpenModalEdit] = useState(false)
    // const [detailCatalog, setDetailCatalog] = useState(null)
    const [createProduct, setCreateProduct] = useState({})
    const [updateProduct, setUpdateProduct] = useState({})
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [dataSearch, setDataSearch] = useState([])

    useEffect(() => {
        setDataSearch(productCatalogList)
    }, [productCatalogList])

    // const emptyRows = rowsPerPage - Math.min(rowsPerPage, dataSearch.length - page * rowsPerPage);


    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    const handeChangeProductUpdate = id => e => {
        let valid = FormValidation(id, e.target.value)
        setUpdateProduct({
            ...updateProduct,
            [id]: e.target.value,
            [id + 'ErrorMessage']: valid.errorMessage,
        })
    }

    const handleDetailConfig = (item) => {
        setUpdateProduct({
            ...updateProduct,
            id: item.id,
            product_name: item.name,
            price: item.pricing.price,
            nta: item.pricing.nta,
            ntsa: item.pricing.ntsa,
            discount_nta: item.pricing.discount_nta,
            discount_price: item.pricing.discount_price,
            validity_period: item.validity_period
        })
        setOpenModalEdit(true)
        // setDetailCatalog(item)
    }
    const handleOpenCreateCatalog = () => {
        setOpenModalCreate(true)
    }

    const handleCloseModal = () => {
        setCreateProduct({})
        setUpdateProduct({})
        setOpenModalEdit(false)
        setOpenModalCreate(false)
    }

    const handleEditConfiguration = () => {
        const detail = {
            product_name: updateProduct.product_name,
            price: Number(updateProduct.price),
            nta: Number(updateProduct.nta),
            ntsa: Number(updateProduct.ntsa),
            discount_nta: Number(updateProduct.discount_nta),
            discount_price: Number(updateProduct.discount_price),
            validity_period: updateProduct.validity_period
        }

        let fullRequest = {
            request: detail,
            params: "/" + updateProduct.id,
            serviceMethod: 'PATCH',
            serviceCode: 'GENERAL_PRODUCT_CATALOG_UPDATE',
            customHeaders: {
                clientId: 'voltras-health'
            }
        }
        setCompleteRequest(fullRequest);
        setOpenModalEdit(false)
    }

    const handleChangeDate = (id) => e => {
        // console.log('date', e)
        setCreateProduct({
            ...createProduct,
            [id]: new Date(e).getTime()
        })
    }

    const handleChangeDateUpdate = (id) => e => {
        // console.log('date', e)
        setUpdateProduct({
            ...updateProduct,
            [id]: new Date(e).getTime()
        })
    }

    const handleChangeCreateCatalog = id => e => {
        let valid = FormValidation(id, e.target.value)
        setCreateProduct({
            ...createProduct,
            [id]: e.target.value,
            [id + 'ErrorMessage']: valid.errorMessage,
        })
    }

    const handleSaveCatalog = () => {
        const request = {
            product_name: createProduct.product_name,
            price: Number(createProduct.price),
            nta: Number(createProduct.nta),
            ntsa: Number(createProduct.ntsa),
            discount_nta: Number(createProduct.discount_nta),
            discount_price: Number(createProduct.discount_price),
            validity_period: createProduct.validity_period
        }
        let fullRequest = {
            request: request,
            // params: "/" + detail.id,
            serviceMethod: 'POST',
            serviceCode: 'GENERAL_PRODUCT_CATALOG_CREATE',
            customHeaders: {
                clientId: 'voltras-health'
            }
        }
        setCompleteRequest(fullRequest);
        setOpenModalCreate(false)
    }


    const handleChangeSearch = (e) => {
        let search = e.target.value
        var condition = new RegExp(search, 'i');
        if (search) {
            let filter = productCatalogList.filter(item => {

                return condition.test(item.name)
            })
            setDataSearch(filter)
        }
        else {
            setDataSearch(productCatalogList)
        }
    }

    const disableButton = type => {
        if (type === 'create') {
            if (createProduct.product_name && createProduct.price && createProduct.nta && createProduct.discount_nta && createProduct.discount_price) {
                return false
            } else if (createProduct.product_nameErrorMessage || createProduct.ntaErrorMessage || createProduct.discount_priceErrorMessage || createProduct.discount_ntaErrorMessage || createProduct.priceErrorMessage) {
                return true
            }
            else {
                return true
            }
        }
        else {
            if (createProduct.product_name && createProduct.price && createProduct.nta && createProduct.discount_nta && createProduct.discount_price) {
                return false
            } else if (createProduct.product_nameErrorMessage || createProduct.ntaErrorMessage || createProduct.discount_priceErrorMessage || createProduct.discount_ntaErrorMessage || createProduct.priceErrorMessage) {
                return true
            }
            else {
                return true
            }
        }
    }

    // eslint-disable-next-line
    let dataMobile = []

    return (
        <div className='generic-json-container'>
            <Hidden smDown>
                <div className='generic-configuration-container'>


                    <div className='generic-header'>
                        <Typography variant="h6" gutterBottom>
                            Product List
                    </Typography>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'flex-end', marginBottom: 10 }}>
                        <div style={{ width: '100%' }} />
                        <TextField
                            style={{ width: '40%' }}
                            fullWidth
                            label='Search Product'
                            onChange={handleChangeSearch}
                            // placeholder='Search'
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                    </div>

                    <Table aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>ID</TableCell>
                                <TableCell>Name</TableCell>
                                <TableCell>Price</TableCell>
                                <TableCell>Validity Period</TableCell>
                                <TableCell>NTA</TableCell>
                                <TableCell>NTSA</TableCell>
                                <TableCell>Action</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                                dataSearch.sort((a, b) => a.id > b.id ? 1 : a.id < b.id ? -1 : 0)
                                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                    .map((item, index) => (<TableRow key={index}>
                                        <TableCell>{item.id}</TableCell>
                                        <TableCell>{item.name}</TableCell>
                                        <TableCell>{item.pricing.price}</TableCell>
                                        <TableCell>{converDateDDMMYY(item.validity_period)}</TableCell>
                                        <TableCell>{item.pricing.nta}</TableCell>
                                        <TableCell>{item.pricing.ntsa}</TableCell>
                                        <TableCell>
                                            <IconButton aria-label="delete" onClick={() => handleDetailConfig(item)}>
                                                <i style={{ fontSize: '18px' }} className="fas fa-info-circle"></i>
                                            </IconButton>
                                            {/* <IconButton aria-label="delete">
                                        <Delete />
                                    </IconButton> */}
                                        </TableCell>
                                    </TableRow>))

                            }
                            {/* {emptyRows > 0 && (
                            <TableRow style={{ height: 53 * emptyRows }}>
                                <TableCell colSpan={6} />
                            </TableRow>
                        )} */}
                        </TableBody>
                        <TableFooter>
                            <TableRow>
                                <TablePagination
                                    rowsPerPageOptions={[5, 10, 25, { label: 'All', value: -1 }]}
                                    colSpan={8}
                                    count={productCatalogList.length}
                                    rowsPerPage={rowsPerPage}
                                    page={page}
                                    SelectProps={{
                                        inputProps: { 'aria-label': 'rows per page' },
                                        native: true,
                                    }}
                                    onChangePage={handleChangePage}
                                    onChangeRowsPerPage={handleChangeRowsPerPage}
                                    ActionsComponent={TablePaginationActions}
                                />
                            </TableRow>
                        </TableFooter>
                    </Table>
                </div>
                <div style={{ marginTop: 15 }}>
                    <Button variant='contained' color="primary" onClick={handleOpenCreateCatalog}>
                        Create Product
                </Button>
                </div>
            </Hidden>
            <Hidden smUp>
                <div style={{ marginTop: 10 }}>
                    <TextField
                        // style={{ width: '40%' }}
                        fullWidth
                        variant='outlined'
                        label='Search Product'
                        onChange={handleChangeSearch}
                        // placeholder='Search'
                        InputLabelProps={{
                            shrink: true,
                            style: {
                                fontSize: 12,
                                fontFamily: 'Arial, Helvetica, sans-serif',
                            }
                        }}
                        InputProps={{
                            style: {
                                height: 40,
                                fontSize: 12,
                                fontFamily: 'Arial, Helvetica, sans-serif',
                            }
                        }}
                    />
                </div>
                {/* <Button variant='contained' color="primary" onClick={handleOpenCreateCatalog}>
                    Create Product
                </Button> */}
                <Card style={{ marginTop: 10 }}>
                    <div style={{ display: 'flex', backgroundColor: '#f4f0ec', padding: 5, width: '100%', justifyContent: 'space-between' }}>
                        <Typography style={{ fontSize: 15, fontFamily: 'Arial, Helvetica, sans-serif', color: 'grey', fontWeight: 'bold' }}>
                            Product List
                        </Typography>
                        <IconButton style={{ padding: 0 }} onClick={handleOpenCreateCatalog}>
                            <Tooltip title='Create Product' placement='top'>
                                <Add color='primary' style={{ padding: 0 }} />
                            </Tooltip>
                        </IconButton>
                    </div>
                    <div style={{ marginLeft: 15, marginRight: 15 }}>
                        <ListTableMobile
                            column={[
                                { title: "ID", field: 'id' },
                                { title: "Name Product", field: 'name' },
                                { title: "pricing", field: 'price' },
                                { title: "Validity Period", field: 'validity_period' },
                                { title: "NTA", field: 'nta' },
                                { title: "NTSA", field: 'ntsa' },
                            ]}
                            data={dataMobile = dataSearch.map(item => {
                                return {
                                    ...item,
                                    ...item.pricing,
                                    validity_period: converDateDDMMYY(item.validity_period)
                                }
                            })}
                            button={true}
                            arrow={true}
                            onClickList={handleDetailConfig}
                        />
                    </div>
                </Card>
            </Hidden>

            <Dialog
                open={openModalEdit}
                scroll={"paper"}
                TransitionComponent={Transition}
                keepMounted
                maxWidth={"xs"}

            >
                <DialogTitle>
                    <div className='dialog-header'>
                        <Typography variant="h6" gutterBottom>
                            Edit Product
                        </Typography>
                        <IconButton aria-label="delete" onClick={handleCloseModal}>
                            <i className="fas fa-times" ></i>
                        </IconButton>
                    </div>
                </DialogTitle>
                <DialogContent>
                    <div className='dialog-content'>
                        <TextField
                            label='Product Name'
                            fullWidth
                            variant='outlined'
                            value={updateProduct.product_name}
                            error={updateProduct.product_nameErrorMessage ? true : false}
                            helperText={updateProduct.product_nameErrorMessage ? updateProduct.product_nameErrorMessage : ''}
                            onChange={handeChangeProductUpdate('product_name')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='Price'
                            fullWidth
                            variant='outlined'
                            value={updateProduct.price}
                            error={updateProduct.priceErrorMessage ? true : false}
                            helperText={updateProduct.priceErrorMessage ? updateProduct.priceErrorMessage : ''}
                            onChange={handeChangeProductUpdate('price')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='NTA'
                            fullWidth
                            variant='outlined'
                            value={updateProduct.nta}
                            error={updateProduct.ntaErrorMessage ? true : false}
                            helperText={updateProduct.ntaErrorMessage ? updateProduct.ntaErrorMessage : ''}
                            onChange={handeChangeProductUpdate('nta')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='NTSA'
                            fullWidth
                            variant='outlined'
                            value={updateProduct.ntsa}
                            error={updateProduct.ntaErrorMessage ? true : false}
                            helperText={updateProduct.ntsaErrorMessage ? updateProduct.ntsaErrorMessage : ''}
                            onChange={handeChangeProductUpdate('ntsa')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='Discount Price'
                            fullWidth
                            variant='outlined'
                            value={updateProduct.discount_price}
                            error={updateProduct.discount_priceErrorMessage ? true : false}
                            helperText={updateProduct.discount_priceErrorMessage ? updateProduct.discount_priceErrorMessage : ''}
                            onChange={handeChangeProductUpdate('discount_price')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                            InputProps={{
                                endAdornment: < InputAdornment position='end' >
                                    %
                            </InputAdornment>
                            }}
                        />
                        <TextField
                            label='Discount Nta'
                            fullWidth
                            variant='outlined'
                            value={updateProduct.discount_nta}
                            error={updateProduct.discount_ntaErrorMessage ? true : false}
                            helperText={updateProduct.discount_ntaErrorMessage ? updateProduct.discount_ntaErrorMessage : ''}
                            onChange={handeChangeProductUpdate('discount_nta')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                            InputProps={{
                                endAdornment: < InputAdornment position='end' >
                                    %
                            </InputAdornment>
                            }}

                        />
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <KeyboardDatePicker
                                // style={{ width: 150, marginRight: 15 }
                                clearable
                                disablePast={true}
                                // size="small"
                                // variant="inline"
                                inputVariant="outlined"
                                fullWidth
                                format="yyyy/MM/dd"
                                // margin="dense"
                                // id={"date-picker-to" + item}
                                // label={FormatText('app_to')}
                                label='Validity Period'
                                value={updateProduct.validity_period ? new Date(updateProduct.validity_period) : new Date()}
                                onChange={handleChangeDateUpdate('validity_period')}
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                            />
                        </MuiPickersUtilsProvider>
                    </div>
                </DialogContent>
                <DialogActions>
                    <div style={{ display: 'flex', justifyContent: 'center', flexGrow: 1, alignItems: 'center' }}>
                        <Button style={{ marginRight: '10px' }} onClick={handleCloseModal} variant='outlined' color="primary">
                            Cancel
                        </Button>
                        <Button onClick={handleEditConfiguration} variant='contained' color="primary">
                            Edit Product
                        </Button>
                    </div>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openModalCreate}
                scroll={"paper"}
                TransitionComponent={Transition}
                keepMounted
                maxWidth={"xs"}

            >
                <DialogTitle>
                    <div className='dialog-header'>
                        <Typography variant="h6" gutterBottom>
                            Create Product
                        </Typography>
                        <IconButton aria-label="delete" onClick={handleCloseModal}>
                            <i className="fas fa-times" ></i>
                        </IconButton>
                    </div>
                </DialogTitle>
                <DialogContent>
                    <div className='dialog-content'>
                        <TextField
                            label='Product Name'
                            fullWidth
                            variant='outlined'
                            error={createProduct.product_nameErrorMessage ? true : false}
                            helperText={createProduct.product_nameErrorMessage ? createProduct.product_nameErrorMessage : ''}
                            onChange={handleChangeCreateCatalog('product_name')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='Price'
                            fullWidth
                            variant='outlined'
                            error={createProduct.priceErrorMessage ? true : false}
                            helperText={createProduct.priceErrorMessage ? createProduct.priceErrorMessage : ''}
                            onChange={handleChangeCreateCatalog('price')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='NTA'
                            fullWidth
                            variant='outlined'
                            error={createProduct.ntaErrorMessage ? true : false}
                            helperText={createProduct.ntaErrorMessage ? createProduct.ntaErrorMessage : ''}
                            onChange={handleChangeCreateCatalog('nta')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='NTSA'
                            fullWidth
                            variant='outlined'
                            error={createProduct.ntsaErrorMessage ? true : false}
                            helperText={createProduct.ntsaErrorMessage ? createProduct.ntsaErrorMessage : ''}
                            onChange={handleChangeCreateCatalog('ntsa')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                        <TextField
                            label='Discount Price'
                            fullWidth
                            variant='outlined'
                            error={createProduct.discount_priceErrorMessage ? true : false}
                            helperText={createProduct.discount_priceErrorMessage ? createProduct.discount_priceErrorMessage : ''}
                            onChange={handleChangeCreateCatalog('discount_price')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                            InputProps={{
                                endAdornment: < InputAdornment position='end' >
                                    %
                            </InputAdornment>
                            }}
                        />
                        <TextField
                            label='Discount Nta'
                            fullWidth
                            variant='outlined'
                            error={createProduct.discount_ntaErrorMessage ? true : false}
                            helperText={createProduct.discount_ntaErrorMessage ? createProduct.discount_ntaErrorMessage : ''}
                            onChange={handleChangeCreateCatalog('discount_nta')}
                            style={{ marginBottom: 10 }}
                            InputLabelProps={{
                                shrink: true
                            }}
                            InputProps={{
                                endAdornment: < InputAdornment position='end' >
                                    %
                            </InputAdornment>
                            }}

                        />
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <KeyboardDatePicker
                                // style={{ width: 150, marginRight: 15 }
                                clearable
                                disablePast={true}
                                // size="small"
                                // variant="inline"
                                inputVariant="outlined"
                                fullWidth
                                format="yyyy/MM/dd"
                                // margin="dense"
                                // id={"date-picker-to" + item}
                                // label={FormatText('app_to')}
                                label='Validity Period'
                                value={createProduct.validity_period ? createProduct.validity_period : new Date()}
                                onChange={handleChangeDate('validity_period')}
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                            />
                        </MuiPickersUtilsProvider>
                    </div>
                </DialogContent>
                <DialogActions>
                    {/* <div className='dialog-footer'> */}
                    <div style={{ display: 'flex', justifyContent: 'center', flexGrow: 1, alignItems: 'center' }}>
                        <Button style={{ marginRight: '10px' }} onClick={handleCloseModal} variant='outlined' color="primary">
                            Cancel
                                </Button>
                        <Button onClick={handleSaveCatalog} variant='contained' color="primary" disabled={disableButton('create')}>
                            Save Product
                                </Button>
                    </div>
                    {/* </div> */}
                </DialogActions>
            </Dialog>
        </div >)
}

const mapStateToProps = (state) => {
    return {
        productCatalogList: state.genericConfiguration.productCatalogList
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setCompleteRequest: (val) => {
            dispatch(setServiceRequest(val.request))
            dispatch(setServiceMethod(val.serviceMethod))
            dispatch(setServiceCode(val.serviceCode))
            dispatch(setServiceCustomHeaders(val.customHeaders))
            dispatch(setServiceParams(val.serviceParams))
            dispatch(setServiceParams(val.params))
            dispatch(setServiceStatus(val.serviceStatus || 'active'))
        },
        setOpenScreen: (val) => dispatch(setOpenScreen(val)),
        // setConfigDetailModal: (val) => dispatch(setConfigDetailModal(val)),
        setSecondRequest: (val) => dispatch(setSecondRequest(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CatalogProduct)

const useStyles1 = makeStyles((theme) => ({
    root: {
        flexShrink: 0,
        marginLeft: theme.spacing(2.5),
    },
}));

function TablePaginationActions(props) {
    const classes = useStyles1();
    const theme = useTheme();
    const { count, page, rowsPerPage, onChangePage } = props;

    const handleFirstPageButtonClick = (event) => {
        onChangePage(event, 0);
    };

    const handleBackButtonClick = (event) => {
        onChangePage(event, page - 1);
    };

    const handleNextButtonClick = (event) => {
        onChangePage(event, page + 1);
    };

    const handleLastPageButtonClick = (event) => {
        onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
    };

    return (
        <div className={classes.root}>
            <IconButton
                onClick={handleFirstPageButtonClick}
                disabled={page === 0}
                aria-label="first page"
            >
                {theme.direction === 'rtl' ? <LastPageIcon /> : <FirstPageIcon />}
            </IconButton>
            <IconButton onClick={handleBackButtonClick} disabled={page === 0} aria-label="previous page">
                {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            </IconButton>
            <IconButton
                onClick={handleNextButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="next page"
            >
                {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </IconButton>
            <IconButton
                onClick={handleLastPageButtonClick}
                disabled={page >= Math.ceil(count / rowsPerPage) - 1}
                aria-label="last page"
            >
                {theme.direction === 'rtl' ? <FirstPageIcon /> : <LastPageIcon />}
            </IconButton>
        </div>
    );
}

TablePaginationActions.propTypes = {
    count: PropTypes.number.isRequired,
    onChangePage: PropTypes.func.isRequired,
    page: PropTypes.number.isRequired,
    rowsPerPage: PropTypes.number.isRequired,
};