
import React, { useState, } from 'react';
import { Grid, Typography, Button, Card, FormControl, FormHelperText, TextField, } from '@material-ui/core';
import InputAdornment from '@material-ui/core/InputAdornment';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';

import { FormValidation } from '../../utils/FormValidation';
import FormatText from '../../i18n';
import SimpleAutocomplete from '../../components/autocomplete/SimpleAutocomplete';


const GlobalForm = props => {
  const domain = window.location.origin
  const [state, setState] = useState({
    showPassword: false,
    showConfirm: false,
  })

  const handleFormChange = prop => event => {
    // console.log(prop)
    let value = event.target.value
    let valid = FormValidation(prop, value)
    if (prop === 'confirmPassword') {
      if (state.password !== event.target.value) {
        setState({
          ...state,
          isError: true,
          [prop]: event.target.value,
          [prop + 'ErrorMessage']: FormatText('err_passconfirm_mismatch'),
          submitErrorMessage: ''
        });
      }
      else {
        setState({
          ...state,
          isError: valid.error,
          [prop]: event.target.value,
          [prop + 'ErrorMessage']: valid.errorMessage,
          submitErrorMessage: ''
        });
      }
    }
    else {
      setState({
        ...state,
        isError: valid.error,
        [prop]: event.target.value,
        [prop + 'ErrorMessage']: valid.errorMessage,
        submitErrorMessage: ''
      });
    }
  }

  const handleSubmit = () => {
    let newPassword = state.password
    let confirmNewPassword = state.confirmPassword
    if (newPassword && (newPassword === confirmNewPassword)) {
      let request = { newPassword, confirmNewPassword }
      props.submitChangePassword(request)
    }
    else {
      setState({
        ...state,
        submitErrorMessage: FormatText('err_submit'),
      });
    }
  }

  const handleMouseDownPassword = (event) => {
    event.preventDefault()
  }

  return (
    <Grid
      container
      direction="row"
      justify="center"
      alignItems="center"
    >
      <Card elevation={4} style={{ margin: 20, width: 285 }} >
        <Grid
          container
          direction="column"
          justify="center"
          alignItems="center"
        >

          {
            domain === 'http://health.voltras.net'
              ? <div></div>
              : domain === 'https://health.voltras.net'
                ? <div></div>
                : domain === 'https://localhost:3030'
                  ? <div></div>
                  : <FormControl style={{ width: '30ch', marginTop: 20 }} >
                    <SimpleAutocomplete
                      label={'App to Login'}
                      options={[{ name: 'Klinik App' }, { name: 'Voltras Back Office' }, { name: 'Almanak' },]}
                      onPick={v => props.handleClient(v)}
                      // notShrink={this.state.clientId ? false : true}
                      optionItem={'name'}
                    />
                  </FormControl>
          }

          <Typography color='primary' variant="h5" style={{ marginTop: 20, fontWeight: 'bold' }} >{props.formLabel}</Typography>
          <TextField
            size="small"
            onChange={handleFormChange('password')}
            id="standard-pass"
            label={FormatText('common_new_password')}
            type={state.showPassword ? 'text' : 'password'}
            helperText={state.passwordErrorMessage ? state.passwordErrorMessage : ""}
            error={state.passwordErrorMessage ? true : false}
            // fullWidth
            margin='dense'
            // value={state.password}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setState({ ...state, showPassword: !state.showPassword })}
                    onMouseDown={handleMouseDownPassword}
                  >
                    {state.showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
          />
          <TextField
            size="small"
            onChange={handleFormChange('confirmPassword')}
            id="standard-pass"
            label={FormatText('app_password_confirmation')}
            type={state.showConfirm ? 'text' : 'password'}
            helperText={state.confirmPasswordErrorMessage ? state.confirmPasswordErrorMessage : ""}
            error={state.confirmPasswordErrorMessage ? true : false}
            // fullWidth
            margin='dense'
            // value={state.confirmPassword}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setState({ ...state, showConfirm: !state.showConfirm })}
                    onMouseDown={handleMouseDownPassword}
                  >
                    {state.showConfirm ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              )
            }}
          />

          <br />
          {
            state.submitErrorMessage
              ? <FormHelperText error id="email-helper-text">{state.submitErrorMessage}</FormHelperText>
              : null
          }
          <Button
            variant="contained"
            color="primary"
            style={{ textTransform: 'none', fontWeight: 'bold', width: 200, marginBottom: 20, marginTop: 10 }}
            onClick={handleSubmit}
          >
            Send
          </Button>
        </Grid>
      </Card>
    </Grid>
  )
}

export default GlobalForm
