
import React, { Component } from 'react'
import { connect } from 'react-redux'
import JwtDecoder from 'jwt-decode';

import {
  setServiceRequest, setServiceCode, setServiceStatus, setServiceMethod,
  setServiceParams, setServiceCustomHeaders, setServiceRole, clearSecondRequest, clearThirdRequest,
} from './actions/ServiceActions'
import { setResponse } from './actions/ResponseActions'
import { setUserInfo, setUserSessionInfo, setUserOtpId, setRoleUsed, setResetPasswordCode } from "./actions/UserInfoActions";
import { setUserMenu, setMenuCollapseStatus } from "./actions/MenuActions";
import {
  setPaxList, setPaxDetail, setPaxProduct, setKlinikList, setCityList, setClinikUser,
  setClinikDetail, setClinicRecapt, setMyClinic, setClinicsParents, setParentList, setClinicRecaptLogistic
} from "./actions/PaxListActions";
import { setSnackbar, setDialog } from './actions/FeedbackActions'
import { dispatchLogout, setPopChooseRole, setGoogleParams, setSystemOtpId } from './actions/SystemActions'
import { setUserList, setUserDetail, setUserFilteredList } from "./actions/UserListActions";
import { setClientList, setClientMessagingList } from "./actions/ClientListActions";
import { setRequestList } from './actions/RequestListAction'
import { setGenericConfigList, setConfigDetail, setConfigDetailModal, setGeneralProductCatalog } from './actions/GenericConfigurationActions';

import { SendToService } from './services/ServiceClient'
import { openMenuStructure } from "./utils/MenuStructure";
import { menuCreator } from './utils/AppStructures';
import FormatText from './i18n'

const mapStateToProps = state => ({ service: state.service, userInfo: state.userInfo.info, clientList: state.clientList })

const mapDispatchToProps = dispatch => ({
  _logout: () => { dispatch(dispatchLogout()); dispatch(setUserMenu(openMenuStructure)) },
  _setGoogleParams: val => dispatch(setGoogleParams(val)), _setResetPasswordCode: val => dispatch(setResetPasswordCode(val)),
  _setClinicRecapt: val => dispatch(setClinicRecapt(val)), _setPopChooseRole: val => dispatch(setPopChooseRole(val)),
  _setResponse: val => dispatch(setResponse(val)), _setUserMenu: val => dispatch(setUserMenu(val)),
  _setMenuCollapseStatus: val => dispatch(setMenuCollapseStatus(val)), _setPaxList: val => dispatch(setPaxList(val)),
  _setPaxDetail: val => dispatch(setPaxDetail(val)), _setPaxProduct: val => dispatch(setPaxProduct(val)),
  _setKlinikList: val => dispatch(setKlinikList(val)), _setUserSessionInfo: val => dispatch(setUserSessionInfo(val)),
  _setUserInfo: val => dispatch(setUserInfo(val)), _setUserOtpId: val => dispatch(setUserOtpId(val)),
  _setUserList: val => dispatch(setUserList(val)), _setUserDetail: val => dispatch(setUserDetail(val)),
  _setClientList: val => dispatch(setClientList(val)), _setSnackbar: val => dispatch(setSnackbar(val)),
  _setDialog: val => dispatch(setDialog(val)), _setUserFilteredList: val => dispatch(setUserFilteredList(val)),
  _setCityList: val => dispatch(setCityList(val)), _setClinicUser: val => dispatch(setClinikUser(val)),
  _setRoleUsed: val => dispatch(setRoleUsed(val)), _clearSecondRequest: () => dispatch(clearSecondRequest()),
  _clearThirdRequest: () => dispatch(clearThirdRequest()), _setClinikDetail: val => dispatch(setClinikDetail(val)),
  _setCompleteRequest: (val) => {
    dispatch(setServiceRequest(val.request)); dispatch(setServiceMethod(val.method)); dispatch(setServiceCode(val.serviceCode));
    dispatch(setServiceParams(val.urlParams)); dispatch(setServiceCustomHeaders(val.customHeaders));
    dispatch(setServiceRole(val.serviceRole)); dispatch(setServiceStatus("active"));
  },
  _setSystemOtpId: val => dispatch(setSystemOtpId(val)), _setMyClinic: val => dispatch(setMyClinic(val)),
  setGenericConfigList: (data) => dispatch(setGenericConfigList(data)),
  setConfigDetail: (val) => dispatch(setConfigDetail(val)),
  setConfigDetailModal: (val) => dispatch(setConfigDetailModal(val)),
  _setGeneralProductCatalog: val => dispatch(setGeneralProductCatalog(val)),
  _setClinicParent: val => dispatch(setClinicsParents(val)),
  _setRequestList: val => dispatch(setRequestList(val)),
  _setClientMessagingList: val => dispatch(setClientMessagingList(val)),
  _setParentList: val => dispatch(setParentList(val)), setClinicRecaptLogistic: val => dispatch(setClinicRecaptLogistic(val)),
})

class Executor extends Component {
  UNSAFE_componentWillReceiveProps(nextProps) {
    const { setGenericConfigList, setConfigDetailModal, setConfigDetail, _setGeneralProductCatalog } = this.props;
    let domain = window.location.origin
    let old = this.props.service.status
    let next = nextProps.service.status
    if (old !== next) {
      const { request, method, params, serviceCode, status, customHeaders, role, file } = nextProps.service
      let successSnackbar = { open: true, severity: "success", message: "Successful", action: false, actionLabel: "" }

      if (serviceCode && (status === "active")) {
        SendToService(request, method, serviceCode, params, customHeaders, role, file, response => {
          let phone = this.props.userInfo.phone ? this.props.userInfo.phone : ""
          let customHeaders = {
            email: this.props.userInfo.email,
            phone: this.props.userInfo.phone,
            name: this.props.userInfo.name ? this.props.userInfo.name : "",
          }
          if (phone) customHeaders.phone = phone
          let requestAllClient = {
            request: {},
            method: "GET",
            serviceCode: "SELECT_ALL_CLIENT",
            customHeaders,
          }
          let requestAllUser = {
            request: {},
            method: "GET",
            serviceCode: "SELECT_ALL_USER",
            customHeaders,
          }
          let getClinicUserRequest = {
            request: {},
            method: "GET",
            serviceCode: "GET_CLINIC_USER",
            customHeaders,
          }
          let healthKlinikRequest = {
            request: {},
            method: "GET",
            serviceCode: "healthKlinik",
            urlParams: "",
            customHeaders,
          }

          let body = response.body
          this.props._setResponse(response)
          if (!body.error) {
            switch (serviceCode) {
              case "login":
                if (body.otpID) {
                  this.props._setUserOtpId(body.otpID)
                }
                else {
                  // accessToken to localStorage
                  localStorage.setItem("accessToken", body.accessToken)
                  localStorage.setItem("key", body.key)
                  localStorage.setItem("idToken", body.idToken)
                  let userInfo = JwtDecoder(body.idToken)
                  let oldRole = userInfo.role
                  let newRole = []
                  if (oldRole.length > 1 && oldRole.includes('member')) newRole = oldRole.filter(item => item !== 'member')
                  else newRole = oldRole
                  userInfo.role = newRole
                  this.props._setUserInfo(userInfo)
                  this.props._setUserSessionInfo(body)
                  let aud = userInfo.aud
                  localStorage.setItem("aud", aud)
                  let role = newRole
                  if (role.length > 1) {
                    this.props._setPopChooseRole(true)
                  }
                  else {
                    localStorage.setItem("roleUsed", role[0])
                    let newMenu = menuCreator(domain, aud, role[0])
                    let obj = {}
                    if (newMenu.length > 0) newMenu.map(item => {
                      return obj = {
                        ...obj,
                        [item.url]: false,
                      }
                    })
                    this.props._setMenuCollapseStatus(obj)
                    this.props._setRoleUsed(role[0])
                    this.props._setUserMenu(newMenu)
                    this.props.history.push('/Home')
                  }
                }
                break;

              case 'OTP':
                if (body.otpID) {
                  this.props._setUserOtpId(body.otpID)
                }
                else {
                  // accessToken to localStorage
                  localStorage.setItem("accessToken", body.accessToken)
                  localStorage.setItem("key", body.key)
                  localStorage.setItem("idToken", body.idToken)
                  let userInfo = JwtDecoder(body.idToken)
                  let oldRole = userInfo.role
                  let newRole = []
                  if (oldRole.length > 1 && oldRole.includes('member')) newRole = oldRole.filter(item => item !== 'member')
                  else newRole = oldRole
                  userInfo.role = newRole
                  this.props._setUserInfo(userInfo)
                  this.props._setUserSessionInfo(body)
                  let aud = userInfo.aud
                  localStorage.setItem("aud", aud)
                  let role = newRole
                  if (role.length > 1) {
                    this.props._setPopChooseRole(true)
                  }
                  else {
                    localStorage.setItem("roleUsed", role[0])
                    let newMenu = menuCreator(domain, aud, role[0])
                    let obj = {}
                    if (newMenu.length > 0) newMenu.map(item => {
                      return obj = {
                        ...obj,
                        [item.url]: false,
                      }
                    })
                    this.props._setMenuCollapseStatus(obj)
                    this.props._setRoleUsed(role[0])
                    this.props._setUserMenu(newMenu)
                    this.props.history.push('/Home')
                  }
                }
                break;

              case 'logout':
                this.props._setUserMenu(openMenuStructure)
                localStorage.clear()
                this.props.history.push('/Login')
                this.props._logout()
                break;

              case "register":
                successSnackbar.message = FormatText('app_regis_sucess_msg')
                this.props._setSnackbar(successSnackbar)
                this.props.history.push('/Login')
                break;

              case 'UPDATE_USER_INFO':
                localStorage.setItem("idToken", body.idToken)
                let updatedUserInfo = JwtDecoder(body.idToken)
                this.props._setUserInfo(updatedUserInfo)
                successSnackbar.message = FormatText('app_success_update_account')
                this.props._setSnackbar(successSnackbar)
                break

              case 'RESET_PASSWORD_REQUEST':
                successSnackbar.message = "Send email successful"
                successSnackbar.severity = "info"
                this.props._setSnackbar(successSnackbar)
                break;

              case 'CHANGE_PASSWORD_REQUEST':
                successSnackbar.message = "Change password successful, proceed to sign in"
                successSnackbar.severity = "info"
                this.props._setSnackbar(successSnackbar)
                this.props._setResetPasswordCode(null)
                break;

              case 'SEND_EMAIL_VERIFICATION':
                successSnackbar.message = FormatText('app_email_success_msg')
                successSnackbar.severity = "info"
                successSnackbar.position = 'bottom-center'
                successSnackbar.action = false
                this.props._setSnackbar(successSnackbar)
                break;

              case 'SEND_PHONE_VERIFICATION':
                this.props._setSystemOtpId(body.otpID)
                // successSnackbar.message = FormatText('app_sms_success_msg')
                // successSnackbar.severity = "info"
                // successSnackbar.position = 'bottom-center'
                // successSnackbar.action = false
                // this.props._setSnackbar(successSnackbar)
                break;

              case 'VERIFICATION_CHECK':
                successSnackbar.message = FormatText('app_verification_succes')
                successSnackbar.severity = "success"
                successSnackbar.position = 'bottom-center'
                successSnackbar.action = false
                this.props._setSnackbar(successSnackbar)
                break;

              case 'REQ_AUTH_GOOGLE_PARAMS':
                this.props._setGoogleParams(body)
                break

              case 'HEALTH_ORDER_DETAIL':
                if (Array.isArray(body) && body.length > 0) {
                  this.props._setPaxDetail(body)
                  this.props.history.push('/PaxDetail')
                }
                else {
                  successSnackbar.message = "Order not found"
                  successSnackbar.severity = 'error'
                  this.props._setSnackbar(successSnackbar)
                }
                break

              case "healthOrder":
                if (Array.isArray(body) && body.length > 0) {
                  body.sort((a, b) => {
                    let x = a.id
                    let y = b.id
                    return y - x
                  })
                  this.props._setPaxList(body)
                }
                else {
                  this.props._setPaxList([])
                  successSnackbar.message = "Order not found"
                  successSnackbar.severity = 'error'
                  this.props._setSnackbar(successSnackbar)
                  // this.props._setPaxList([])
                }
                break;

              case 'PURCHASE_VOUCHER_NUMBER':
                if (Array.isArray(body) && body.length > 0) {
                  this.props._setPaxDetail(body)
                  this.props.history.push('/PaxDetail')
                }
                else {
                  this.props._setPaxList([])
                  successSnackbar.message = "Order not found"
                  successSnackbar.severity = 'error'
                  this.props._setSnackbar(successSnackbar)
                }
                break

              case 'GET_ALL_ORDER':
                this.props._setClinicRecapt(body)
                break

              case 'RESCHEDULE_ORDER':
                successSnackbar.message = "Reschedule order successful"
                this.props._setSnackbar(successSnackbar)
                this.props.history.push('/PaxList')
                break

              case "healthProduct":
                this.props._setPaxProduct(body)
                if (this.props.service.secondRequest) this.props._setCompleteRequest(this.props.service.secondRequest)
                this.props._clearSecondRequest()
                break;

              case 'healthKlinik':
                this.props._setKlinikList(body)
                break;

              case "SELECT_USER_FULL_INFO":
                this.props._setUserInfo(body)
                break;

              case 'SELECT_USER_DETAIL_FULL_INFO':
                this.props._setUserDetail(body)
                this.props._setUserList([body])
                break;

              case 'SELECT_ALL_USER':
                this.props._setUserList(body.items)
                break;

              case 'CREATE_BO_USER':
                successSnackbar.message = "Create User Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllUser)
                this.props.history.push('/VUserList')
                break;

              case 'UPDATE_USER_ROLE':
                successSnackbar.message = "Update User Role Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllUser)
                this.props._setUserFilteredList(body.items)
                break;

              case 'DELETE_USER':
                successSnackbar.message = "Delete User Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllUser)
                break;

              case 'UPDATE_BASIC_USER':
                successSnackbar.message = "Update Basic Info User Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllUser)
                break;
              case 'UPDATE_TEMPLATE_MESSAGE':
                successSnackbar.message = "Update Client Detail Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllClient)
                break;

              case 'SELECT_ALL_MESSAGE':
                let messagingList = body.items
                if (Array.isArray(messagingList) && messagingList.length > 0) {
                  messagingList.sort((a, b) => {
                    let x = a.creationDate
                    let y = b.creationDate
                    return y - x
                  })
                  this.props._setClientMessagingList(messagingList)
                }
                else this.props._setClientMessagingList([])
                break;

              case 'SELECT_ALL_CLIENT':
                let clientList = body.items
                if (Array.isArray(clientList) && clientList.length > 0) {
                  clientList.sort((a, b) => {
                    let x = a.creationDate
                    let y = b.creationDate
                    return y - x
                  })
                  this.props._setClientList(clientList)
                }
                else this.props._setClientList([])
                break;
              case 'SELECT_ALL_REQUEST':
                let requestList = body.items
                if (Array.isArray(requestList) && requestList.length > 0) {
                  requestList.sort((a, b) => {
                    let x = a.creationDate
                    let y = b.creationDate
                    return y - x
                  })
                  this.props._setRequestList(requestList)
                }
                else this.props._setRequestList([])
                this.props._setCompleteRequest(requestAllClient)
                break;
              case 'CREATE_CLIENT':
                successSnackbar.message = "Create Client Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllClient)
                this.props.history.push('/VClientList')
                break;

              case 'ADD_CLIENT_ROLE':
                successSnackbar.message = "Add Client Role Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllClient)
                break;
              case "UPDATE_CLIENT_DETAIL":
                successSnackbar.message = "Update Client Detail Successful"
                let messagingEdit = this.props.clientList.messagingEdit
                if (messagingEdit.updateMessageTemplate) {
                  let messageTemplateId = messagingEdit.messagingTemplateId
                  let method = "PATCH"
                  let serviceCode = "UPDATE_TEMPLATE_MESSAGE"
                  let urlParams = `/clients/${messagingEdit.clientId}/messaging-templates/${messageTemplateId}`
                  let request = {
                    ...messagingEdit
                  }
                  // delete request.clientId
                  delete request.updateMessageTemplate
                  // console.log('request',request)
                  let completeRequests = { request, method, serviceCode, urlParams }
                  this.props._setCompleteRequest(completeRequests)
                  break;
                }
                else {
                  this.props._setSnackbar(successSnackbar)
                  this.props._setCompleteRequest(requestAllClient)
                  break;
                }
              case 'DELETE_CLIENT_ROLE':
                successSnackbar.message = "Delete Client Role Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllClient)
                break;

              case 'DELETE_CLIENT':
                successSnackbar.message = "Delete Client Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(requestAllClient)
                break;
              case 'ADD_REQUEST_SPESIFICATIONS':
                successSnackbar.message = "Add Request Spesifiactions Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest({
                  ...requestAllClient,
                  serviceCode: 'SELECT_ALL_REQUEST'
                })
                break;
              case 'UPDATE_REQUEST_SPESIFICATIONS':
                successSnackbar.message = "Update Request Spesifiactions Successful"
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest({
                  ...requestAllClient,
                  serviceCode: 'SELECT_ALL_REQUEST'
                })
                break;

              case 'GET_CITY_LIST':
                let newArr = []
                if (Array.isArray(body)) {
                  if (body.length > 0) {
                    body.forEach(item => {
                      if (item.name) newArr.push(item)
                      else newArr.push({ city: item })
                    })
                    this.props._setCityList(newArr)
                  }
                }
                break;

              case 'GET_CITY_CLINIC_LIST':
                let newCLinicArr = []
                if (Array.isArray(body)) {
                  if (body.length > 0) {
                    body.forEach(item => {
                      if (item.name) newCLinicArr.push(item)
                      else newCLinicArr.push({ city: item })
                    })
                    this.props._setKlinikList(newCLinicArr)
                  }
                  else this.props._setKlinikList(newCLinicArr)
                }
                break;


              case 'CLINIC_REGISTRATION':
                successSnackbar.message = FormatText('clinic_regis_sucess')
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(healthKlinikRequest)
                if (this.props.userInfo.roleUsed !== 'member') this.props.history.push('/KClinicList')
                else this.props.history.push('/Home')
                break;

              case 'SEND_CLINIC_ACTIVATION':
                successSnackbar.message = FormatText('clinic_activation_success')
                this.props._setSnackbar(successSnackbar)
                this.props._setCompleteRequest(healthKlinikRequest)
                break;

              case 'ADD_CLINIC_USER':
                successSnackbar.message = FormatText('clinic_add_user_success')
                this.props._setSnackbar(successSnackbar)
                if (this.props.service.secondRequest) this.props._setCompleteRequest(this.props.service.secondRequest)
                this.props._clearSecondRequest()
                break;

              case 'DELETE_CLINIC_USER':
                successSnackbar.message = FormatText('clinic_delete_user_success')
                this.props._setSnackbar(successSnackbar)
                if (this.props.service.secondRequest) this.props._setCompleteRequest(this.props.service.secondRequest)
                this.props._clearSecondRequest()
                break;

              case 'DELETE_CLINIC':
                successSnackbar.message = FormatText('clinic_delete_sucess')
                this.props._setSnackbar(successSnackbar)
                if (this.props.service.secondRequest) this.props._setCompleteRequest(this.props.service.secondRequest)
                this.props._clearSecondRequest()
                break;

              case 'GET_CLINIC_USER':
                this.props._setClinicUser(body)
                if (!this.props.service.secondRequest) this.props.history.push('/KSetting')
                this.props._clearSecondRequest()
                break;

              case 'GET_MY_CLINIC':
                if (body[0]) {
                  getClinicUserRequest.urlParams = `/${body[0].clinic_id}`
                  this.props._setCompleteRequest(getClinicUserRequest)
                  this.props._setMyClinic(body[0])
                }
                break;

              case 'GET_CLINIC_REPORT':
                this.props._setClinicRecapt(body)
                break;

              case 'RECAPT_CLINIC_SUMMARY':
                this.props.setClinicRecaptLogistic(body)
                break;

              case 'RECAPT_BIOFARMA_SUMMARY':
                this.props.setClinicRecaptLogistic(body)
                break;

              case 'ADD_CLINIC_LOGO':
                successSnackbar.message = FormatText('clinic_change_logo_success')
                this.props._setSnackbar(successSnackbar)
                break

              case 'EDIT_CLINIC_SETTING':
                successSnackbar.message = "Edit clinic setting success."
                this.props._setSnackbar(successSnackbar)
                this.props._setClinicUser(body)
                break

              case 'GENERIC_JSON_CONFIGURATION':
                setGenericConfigList(body);
                break;
              case 'GENERIC_JSON_CONFIGURATION_DETAIL':
                setConfigDetail(body);
                setConfigDetailModal(true);
                break;
              case 'GENERIC_JSON_CONFIGURATION_UPDATE':
                setConfigDetailModal(false);
                // setGenericConfigList(body);
                successSnackbar.message = 'Update Configuration Success'
                successSnackbar.severity = "success"
                successSnackbar.position = 'bottom-center'
                successSnackbar.action = false
                this.props._setSnackbar(successSnackbar);

                // console.log('SECOND REQUEST', this.props.service.secondRequest)
                //Panggil request ulang
                if (this.props.service.secondRequest) this.props._setCompleteRequest(this.props.service.secondRequest)
                this.props._clearSecondRequest()
                break;
              case 'GENERAL_PRODUCT_CATALOG':
                _setGeneralProductCatalog(body)
                break
              case 'GENERAL_PRODUCT_CATALOG_CREATE':
                setConfigDetailModal(false);
                // setGenericConfigList(body);
                successSnackbar.message = 'Create product catalog is success'
                successSnackbar.severity = "success"
                successSnackbar.position = 'bottom-center'
                successSnackbar.action = false
                this.props._setSnackbar(successSnackbar);
                this.props._setCompleteRequest({
                  request: {},
                  method: 'GET',
                  serviceCode: 'GENERAL_PRODUCT_CATALOG',
                  customHeaders: {
                    clientId: 'voltras-health'
                  }
                })

                break
              case 'GENERAL_PRODUCT_CATALOG_UPDATE':
                setConfigDetailModal(false);
                // setGenericConfigList(body);
                successSnackbar.message = 'Update Product catalog success'
                successSnackbar.severity = "success"
                successSnackbar.position = 'bottom-center'
                successSnackbar.action = false
                this.props._setSnackbar(successSnackbar);
                // this.props._setCompleteRequest(this.props.service.secondRequest)
                this.props._setCompleteRequest({
                  request: {},
                  method: 'GET',
                  serviceCode: 'GENERAL_PRODUCT_CATALOG',
                  customHeaders: {
                    clientId: 'voltras-health'
                  }
                })
                break
              case 'GET_CLINIC_PARENTS':
                this.props._setClinicParent(body)
                if (this.props.service.secondRequest) this.props._setCompleteRequest(this.props.service.secondRequest)
                this.props._clearSecondRequest()
                break

              case 'GET_PARENT_LIST':
                this.props._setParentList(body)
                break


              default:
                break;

            }
          }
          else if (serviceCode === 'logout') this.props.history.push('/Login')
        })
      }
    }
  }
  render() { return <div > </div> }
}

export default connect(mapStateToProps, mapDispatchToProps)(Executor)
