class LoginScreen extends Component {
  constructor(props) {
    super(props)
    this.state = {
      otp: '',
      otpErrorMessage: '',
      email: '',
      emailErrorMessage: '',
      password: '',
      passwordErrorMessage: '',
      showPassword: false,
      clientId: '',
      clientIdErrorMessage: '',
      onLoginErrorMessage: '',
      isError: true,
      roleChoosen: '',
      forgotOpen: false,
    }
  }

  UNSAFE_componentWillMount() {
    let origin = window.location.origin
    this.props._setOpenScreen("Login")
    this.props._setUserMenu(openMenuStructure)
    //TODO: hardcoded app structure must be generic by window.location.origin
    if (this.props.system.appConstant === null) {
      let app = AppStructures.filter(item => item.domain.includes(origin))
      this.props._setAppConstant(app[0] ? app[0] : AppStructures[0])
      // document.title = app[0].title
      resetMenu(app[0] ? app[0].loggedMenu : AppStructures[0].loggedMenu, app[0] ? app[0].aud : AppStructures[0].aud)
    }
  }

  componentDidMount() {
    this.props._setOpenScreen("Login")
    let language = navigator.language
    this.props._setLocale(language)
    let clientId = this.props.system.appConstant ? this.props.system.appConstant.aud : null
    let request = {}
    let fullRequest = {
      request,
      serviceMethod: 'GET',
      serviceCode: 'REQ_AUTH_GOOGLE_PARAMS',
      serviceParams: '?redirectURI=http://www.alamanak.co.id',
      customHeaders: { clientId },
    }
    if (clientId) this.props._setCompleteRequest(fullRequest)
    let verificationCode = this.props.userInfo.verificationCode ? this.props.userInfo.verificationCode : ''
    let requestVerify = { redirectURI: "http://www.alamanak.co.id", verificationCode }
    // console.log(clientId, verificationCode)
    let verificationCheckRequest = {
      request: requestVerify,
      serviceMethod: "POST",
      serviceCode: "VERIFICATION_CHECK",
      serviceParams: `/email`,
    }
    if (verificationCode) {
      this.props._setCompleteRequest(verificationCheckRequest)
      this.props._setVerificationCode(null)
    }
  }

  handleResendOtp = e => {
    let clientId = this.state.clientId
    this.setState({
      ...this.state,
      onLoginErrorMessage: "", otp: ""
    })
    let request = {
      redirectURI: "http://www.alamanak.co.id",
      provider: "internal",
      responseType: "token",
    }
    let email = this.state.email
    let password = this.state.password
    if (regexEmail.test(email)) {
      request.email = email
      request.password = password
    }
    else request.phone = email

    localStorage.setItem('email', email)
    this.props._setUserInfo({ email })
    let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'login', customHeaders: { clientId }, }
    this.props._setCompleteRequest(fullRequest)
  }

  handleSubmitOtp = e => {
    if (this.state.otp) {
      let clientId = this.state.clientId
      this.props._setRequest({})
      this.props._setServiceCode("")
      this.props._setServiceStatus("")
      let challenge = this.state.otp
      let otpID = this.props.userInfo.otpId
      let request = {
        challenge,
        otpID,
        redirectURI: "http://www.alamanak.co.id",
      }
      let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'OTP', customHeaders: { clientId }, }
      this.props._setCompleteRequest(fullRequest)
    }
  }

  handleCancelOtp = () => {
    this.props._setUserOtpId('')
    this.setState({ ...this.state, email: '' })
  }

  handleSubmit = event => {
    let domain = window.location.origin
    event.preventDefault()
    let clientId = this.state.clientId
    this.props._setRequest({})
    this.props._setServiceCode("")
    this.props._setServiceStatus("")
    if (clientId) {
      if (!this.state.isError) {
        this.setState({
          ...this.state,
          onLoginErrorMessage: ""
        })
        let request = {
          redirectURI: "http://www.alamanak.co.id",
          provider: "internal",
          responseType: "token",
        }
        let email = this.state.email
        let password = this.state.password
        if (regexEmail.test(email)) {
          request.email = email
          request.password = password
          localStorage.setItem('email', email)
          this.props._setUserInfo({ email })
        }
        else request.phone = email[0] === '0' ? '+62' + email.slice(1) : email
        // console.log(request)
        let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'login', customHeaders: { clientId }, }
        this.props._setCompleteRequest(fullRequest)
      }
    }
    /**
     * LOGIN WITH REAL DOMAIN
     */
    else if ((domain === 'https://health.voltras.net')) {
      let domainClientId = 'voltras-health'
      if (!this.state.isError) {
        this.setState({
          ...this.state,
          onLoginErrorMessage: ""
        })
        let email = this.state.email
        let password = this.state.password
        let loginRequest = {
          redirectURI: "http://www.alamanak.co.id",
          provider: "internal",
          responseType: "token",
        }
        if (regexEmail.test(email)) {
          loginRequest.email = email
          loginRequest.password = password
          localStorage.setItem('email', email)
          this.props._setUserInfo({ email })
        }
        else loginRequest.phone = email[0] === '0' ? '+62' + email.slice(1) : email

        let fullRequest = { request: loginRequest, serviceMethod: 'POST', serviceCode: 'login', customHeaders: { clientId: domainClientId }, }
        this.props._setCompleteRequest(fullRequest)
      }
    }
    else {
      this.setState({
        ...this.state,
        onLoginErrorMessage: "App to Login must be chosen"
      })
    }
  }

  handleChange = prop => event => {
    if (prop === 'password' && event.target.value.length === 0) {
      this.setState({
        ...this.state,
        [prop]: event.target.value,
        isError: true,
        passwordErrorMessage: FormatText('err_password_empty')
      })
    }
    else if (prop === 'password' && event.target.value.length < 8) {
      this.setState({
        ...this.state,
        [prop]: event.target.value,
        isError: true,
        passwordErrorMessage: FormatText('err_not_six_char')
      })
    }
    else if (prop === 'email') {
      if (
        (regexEmail.test(event.target.value) || regexPhone.test(event.target.value))
        && event.target.value.length > 0
      ) {
        this.setState({
          ...this.state,
          [prop]: event.target.value,
          isError: false,
          emailErrorMessage: ''
        })
      } else if (event.target.value.length === 0) {
        this.setState({
          ...this.state,
          [prop]: event.target.value,
          isError: true,
          emailErrorMessage: FormatText('err_email_phone_empty')
        })
      }
      else {
        this.setState({
          ...this.state,
          [prop]: event.target.value,
          isError: true,
          emailErrorMessage: FormatText('err_email_phone_format')
        })
      }
    }
    else {
      this.setState({
        ...this.state,
        emailErrorMessage: "",
        passwordErrorMessage: "",
        isError: false,
        [prop]: event.target.value
      })
    }
  }

  handleEnter = event => {
    if (event.which === 13 && event.key === "Enter") {
      this.handleSubmit(event)
    }
  }

  handleClickShowPassword = (event) => {
    this.setState({
      ...this.state,
      showPassword: !this.state.showPassword
    })
  }

  handleRegister = (event) => {
    event.preventDefault()
    this.props.history.push('/Register')
  }

  handleClient = value => {
    // console.log(value)
    let client = [
      {
        label: 'Voltras Health',
        clientId: 'voltras-health'
      },
      {
        label: 'Voltras Backoffice',
        clientId: 'authentication-backoffice'
      },
      {
        label: 'Dummy App',
        clientId: 'cerita nya Almanak'
      },

    ]
    if (value) {
      let clientId = client.filter(item => item.label === value.name)[0].clientId
      // console.log(clientId)
      this.setState({ ...this.state, clientId, onLoginErrorMessage: '' })
    }
  }

  onRadioChange = e => {
    e.preventDefault()
    // console.log(e.target.value)
    this.setState({
      ...this.state,
      roleChoosen: e.target.value
    })
  }

  handleChooseRole = () => {
    /**
     * MAJOR CODE FOR ENTERING THE APP
     * 1. Read domain and role choose by user.
     * 2. Choose Homepage and Menu to show.
     */

    let domain = window.location.origin
    let roleChoosen = this.state.roleChoosen
    let userInfo = this.props.userInfo.info
    let clientId = userInfo.aud
    let newMenu = menuCreator(domain, clientId, roleChoosen)

    let obj = {}
    if (newMenu.length > 0) newMenu.map(item => {
      return obj = {
        ...obj,
        [item.url]: false,
      }
    })
    this.props._setMenuCollapseStatus(obj)
    this.props._setUserMenu(newMenu)
    this.props._setRoleUsed(roleChoosen)
    localStorage.setItem("roleUsed", roleChoosen)
    this.props._setPopChooseRole(false)
    this.props.history.push('/Home')
  }

  handleDialogClose = () => this.setState({ ...this.state, forgotOpen: false })

  handleDialogSubmit = () => {
    // console.log('dialog submit process')
    let domain = window.location.origin
    // event.preventDefault()
    let clientId = this.state.clientId
    this.props._setRequest({})
    this.props._setServiceCode("")
    this.props._setServiceStatus("")
    if (clientId) {
      if (!this.state.isError) {
        this.setState({
          ...this.state,
          onLoginErrorMessage: ""
        })
        let request = {
          redirectURI: "http://www.alamanak.co.id",
          email: this.state.email
        }
        let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'RESET_PASSWORD_REQUEST', customHeaders: { clientId }, }
        this.props._setCompleteRequest(fullRequest)

        this.setState({ ...this.state, email: "" })
      }
    }
    /**
     * LOGIN WITH REAL DOMAIN
     */
    else if ((domain === 'https://health.voltras.net')) {
      let domainClientId = 'voltras-health'
      if (!this.state.isError) {
        this.setState({ ...this.state, onLoginErrorMessage: "" })
        let email = this.state.email
        let request = { email, redirectURI: "http://www.alamanak.co.id", }
        let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'RESET_PASSWORD_REQUEST', customHeaders: { clientId: domainClientId } }
        this.props._setCompleteRequest(fullRequest)

        this.setState({ ...this.state, email: "" })
      }
    }
    else {
      this.setState({
        ...this.state,
        onLoginErrorMessage: "App to Login must be chosen"
      })
    }
  }

  submitChangePassword = request => {
    // console.log("kesinikah: ", request)
    let domain = window.location.origin
    let clientId = this.state.clientId
    this.props._setRequest({})
    this.props._setServiceCode("")
    this.props._setServiceStatus("")
    if (clientId) {
      request.redirectURI = "http://www.alamanak.co.id"
      request.resetPasswordCode = this.props.userInfo.resetPasswordCode
      let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'CHANGE_PASSWORD_REQUEST', customHeaders: { clientId }, }
      this.props._setCompleteRequest(fullRequest)
    }
    /**
     * LOGIN WITH REAL DOMAIN
     */
    else if ((domain === 'https://health.voltras.net')) {
      let domainClientId = 'voltras-health'
      request.redirectURI = "http://www.alamanak.co.id"
      request.resetPasswordCode = this.props.userInfo.resetPasswordCode
      let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'CHANGE_PASSWORD_REQUEST', customHeaders: { clientId: domainClientId }, }
      this.props._setCompleteRequest(fullRequest)
    }
    else {
      this.setState({
        ...this.state,
        onLoginErrorMessage: "App to Login must be chosen"
      })
    }
  }

  handleGoogleLogin = request => {
    /*
      Sementara hanya voltras-health yg bisa login by google
    */
    let domainClientId = 'voltras-health'
    let fullRequest = { request, serviceMethod: 'POST', serviceCode: 'login', customHeaders: { clientId: domainClientId }, }
    this.props._setCompleteRequest(fullRequest)
  }

  handleForgotOpen = () => this.setState({ ...this.state, forgotOpen: true })

  render() {
    // let domain = window.location.origin
    const { classes, system } = this.props;
    return (
      <div >
        <center>
          <CircularProgress
            style={{ visibility: system.serviceStart && system.openScreen === 'Login' ? "visible" : "hidden" }}
            color="secondary"
            size={30}
            thickness={5}
          />
        </center>
        <ForgotPasswordDialog
          open={this.state.forgotOpen}
          handleDialogClose={this.handleDialogClose}
          dialogTitle="Change password request"
          dialogContent={`A confirmation email will be sent to: ${this.state.email}`}
          cancelTitle='Cancel'
          submitTitle='Send'
          handleDialogSubmit={this.handleDialogSubmit}
        />
        {
          this.props.userInfo.resetPasswordCode
            ? <ForgotPasswordForm
              formLabel={FormatText('common_change_password')}
              submitChangePassword={this.submitChangePassword}
              state={this.state}
              handleClient={this.handleClient}
            />
            : <Grid container direction="row" justify="center" alignItems="flex-start" >
              <Hidden mdDown>
                <div
                  style={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    width: 600
                  }} >
                  <br />

                  <div style={{ marginTop: 100 }} >
                    <Typography variant="subtitle2" >
                      {FormatText('app_welcome')}
                    </Typography>
                    <Typography variant="h4" color='primary' >
                      {system.appConstant ? system.appConstant.welcome : ""}
                    </Typography>

                  </div>
                </div>
              </Hidden>
              <div style={{ alignSelf: "center", justifySelf: "center" }}  >
                <br />
                {this.props.system.popChooseRole
                  ? <ChooseRoleCard
                    onRadioChange={this.onRadioChange}
                    userInfo={this.props.userInfo}
                    state={this.state}
                    handleChooseRole={this.handleChooseRole}
                    classes={classes.root}
                  />
                  : this.props.userInfo.otpId
                    ? <InsertOTPCard
                      classes={classes.root}
                      state={this.state}
                      handleChange={this.handleChange}
                      handleCancelOtp={this.handleCancelOtp}
                      handleSubmitOtp={this.handleSubmitOtp}
                      handleResendOtp={this.handleResendOtp}
                    />
                    : <LoginCard
                      handleChange={this.handleChange}
                      handleClient={this.handleClient}
                      handleGoogleLogin={this.handleGoogleLogin}
                      handleEnter={this.handleEnter}
                      handleClickShowPassword={this.handleClickShowPassword}
                      handleMouseDownPassword={this.handleMouseDownPassword}
                      handleSubmit={this.handleSubmit}
                      handleRegister={this.handleRegister}
                      handleForgotOpen={this.handleForgotOpen}
                      state={this.state}
                      setState={this.setState}
                      // system={this.props.system}
                      {...this.props}
                    />
                }
              </div>
            </Grid>
        }
        <div style={{ height: window.screen.height - 500 }} >
        </div>
      </div>
    )
  }
}

export const useLoginStyles = (theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    width: 285,
  },
  margin: {
    margin: theme.spacing(1),
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  withoutLabel: {
    marginTop: theme.spacing(3),
  },
  textField: {
    width: '30ch',
  },
});

export default LoginScreen