import React, { useState, useEffect } from 'react'
import { Dialog, DialogTitle, DialogActions, DialogContent } from '@material-ui/core'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import { Grid, Button, MenuItem, TextField } from '@material-ui/core'

const styles = (theme) => ({
    rootForm: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            // width: '25ch',
        },
    },
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    asterisk: {
        color: 'red'
    },
    addButton: {
        color: 'white'
    }
});

const MUIDialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (<DialogTitle disableTypography className={classes.root} {...other}>
        <Typography variant="h6">{children}</Typography>
        {onClose ? (
            <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                <CloseIcon />
            </IconButton>
        ) : null}
    </DialogTitle>)
})

const EditMessagingTemplateDialog = ({ ...props }) => {
    const { classes } = props
    const dataToUpdt = props.dataToUpdate
    // console.log('datadddd',props.data)
    const [state, setState] = useState({
        dataToUpdate: {}
    })
    // const [data, setData] = useState([])

    useEffect(() => {
        if (dataToUpdt) {
            setState({
                dataToUpdate: dataToUpdt
            })
            // setData(props.data ? props.data : [])
        }
    }, [dataToUpdt,props.data])
    const handleClose = () => {
        // setState({})
        props.handleClose(false, true)
    }

    const handleChange = id => e => {
        setState({
            ...state,
            dataToUpdate: {
                ...state.dataToUpdate,
                [id]: e.target.value,
                [id + 'Edit']: e.target.value
            }

        })
    }

    const handleEditRequest = () => {
        delete state.dataToUpdate['messagingTemplateCodeEdit']
        delete state.dataToUpdate['messagingTemplateLangEdit']
        delete state.dataToUpdate['subjectTemplateEdit']
        delete state.dataToUpdate['contentTemplateEdit']
        props.handleClose(false)
        props.handleSave(state.dataToUpdate)
    }
   
    return <Dialog fullWidth maxWidth={'xs'} open={props.open} onClose={handleClose} scroll={'paper'}>
        <MUIDialogTitle
            id="max-width-dialog-title"
            onClose={handleClose}
        >
            Edit Messaging Template
        </MUIDialogTitle>
        <DialogContent>
            <form className={classes.rootForm} noValidate autoComplete="off">
                <TextField
                    fullWidth
                    id='messagingTemplateCode'
                    onChange={handleChange('messagingTemplateCode')}
                    value={state.dataToUpdate ? state.dataToUpdate.messagingTemplateCode : ''}
                    label='Code'
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
                <TextField
                    fullWidth
                    select
                    onChange={handleChange('messagingTemplateLang')}
                    id='messagingTemplateLang'
                    value={state.dataToUpdate ? state.dataToUpdate.messagingTemplateLang : ''}
                    label='Languange'
                    InputLabelProps={{
                        shrink: true,
                    }}
                >
                    <MenuItem value={'en'}>
                        EN
                    </MenuItem>
                    <MenuItem value='id'>
                        ID
                    </MenuItem>
                </TextField>
                <TextField
                    fullWidth
                    label='Subject'
                    id='subjectTemplate'
                    onChange={handleChange('subjectTemplate')}
                    value={state.dataToUpdate ? state.dataToUpdate.subjectTemplate : ''}
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
                <TextField
                    fullWidth
                    multiline
                    rows={5}
                    onChange={handleChange('contentTemplate')}
                    id='contentTemplate'
                    label='Content'
                    value={state.dataToUpdate ? state.dataToUpdate.contentTemplate : ''}
                    InputLabelProps={{
                        shrink: true,
                    }}
                />
            </form>
        </DialogContent>
        <DialogActions>
            <Grid container direction="row" justify='center' alignItems='center' style={{ margin: 5 }}>
                <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                    <Button color='primary' variant='contained'
                        className={classes.addButton} onClick={handleEditRequest}>
                        Edit
                        </Button>
                </Grid>
                <Grid container item direction="row" justify='center' alignItems='center' md={6}>
                    <Button onClick={handleClose} color='secondary' variant='contained' className={classes.addButton}>
                        Cancel
                        </Button>
                </Grid>
            </Grid>
        </DialogActions>
    </Dialog>
}

export default withStyles(styles)(EditMessagingTemplateDialog)