
import React from 'react'
import { Button } from '@material-ui/core';
import { useGoogleLogin } from 'react-google-login';
// import FormatText from '../../i18n';
// import { refreshTokenSetup } from '../../utils/GoogleRefreshToken';

function LoginHooks(props) {
  // console.log("GOOGLE PROPS: ", props)
  const { client_id, access_type, prompt, response_type, } = props.params

  const onSuccess = (res) => {
    // console.log('Login Success: currentUser:', res);
    // alert(
    //   `Logged in successfully welcome ${res} . \n See console for full profile object.`
    // );
    // refreshTokenSetup(res);
    const requestLogin = {
      "provider": "google",
      "responseType": "token",
      "code": res.code,
      "redirectURI": "http://localhost:3000",
      "nonce": ""
    }
    if (res.code) props.handleGoogleLogin(requestLogin)

  };

  const onFailure = (res) => {
    console.log('Login failed: res:', res);
    // alert(
    //   `Failed to login.  Please ping this to repo owner twitter.com/sivanesh_fiz`
    // );
  };

  const { signIn } = useGoogleLogin({
    onSuccess,
    onFailure,
    clientId: client_id,
    isSignedIn: false,
    accessType: access_type,
    responseType: response_type,
    redirectUri: "http://localhost:3000",
    prompt,
  });

  return (
    <Button
      fullWidth={props.fullWidth}
      color='primary'
      size='large'
      onClick={signIn}
      style={{
        width: props.width || 240,
        textTransform: 'none',
        color: 'black'
      }}
      variant='outlined'
      startIcon={<img style={{ width: 24, }} src="icons/google.svg" alt="google login" className="icon" />}
    >
      {/* <img style={{ width: 24, marginRight: 15 }} src="icons/google.svg" alt="google login" className="icon" /> */}
      &nbsp;  {props.buttonText} Google
    </Button>
    // <button onClick={signIn} className="button">
    //   <img src="icons/google.svg" alt="google login" className="icon"></img>

    //   <span className="buttonText">Sign in with Google</span>
    // </button>
    // <IconButton aria-label="google" onClick={signIn} color='secondary' >
    //   <i style={{ fontSize: 20 }} className={"fab fa-google"} />
    // </IconButton>
  )
}

